<template>
  <div id="money-center">
    <!--对帐中心-->
    <panel-title
      :crumbs="[
        {
          name:$t('withdrawalcheckCenterpage.withdrawalcheckCenterpage'),
          route:{
            path:'/MoneyCenter',
            query:{
              from:'MainOrderManagement',
            }
          }
        },
        {
          name:'老师对账',
          route:'',
        },
      ]"
    ></panel-title>
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
        <div class="mytheme-mg-top-20">
          <!--老师昵称-->
          <span>老师名称：{{teacherName}}</span>
          <span class="mytheme-mg-left-70">课程名称：{{curriculumName}}</span>
          <span class="mytheme-mg-left-70">班级名称：{{classname}}</span>
        </div>
       <Table :loading="loading" :columns="colData" :data="tabData" class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe></Table>
        <pagination class="mg-top-20" :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
      </Card>
    
	  <Modal v-model="isShowDelModal" width="650">
    	 <div slot="header">上课明细</div>
        <Card dis-hover class="mytheme-noborder">
          <table style="width:100%;">
            <tr>
              <td class="td"><p>课程名称：</p><p>{{curriculumName}}</p></td>
              <td class="td"><p>班级名称：</p><p>{{classname}}</p></td>
            </tr>
            <tr>
              <td class="td"><p>课时名称：</p><p>{{curriculumtiemName}}</p></td>
              <td class="td"><p>上课时间：</p><p>{{classtar }}</p></td>
            </tr>
          </table>
          <template>
            <Table :loading="loading1" :columns="colData1" :data="tabData1"  class="mytheme-mg-top-30 mytheme-table noborder-table" stripe ></Table>
          </template>
        <pagination :total="total1" :display="display1" :current-page="current1" @pagechange="pageChange1" v-if='total1>0'></pagination>
      </Card>
      <div slot="footer"></div>
    </Modal>
    
  </div>
</template>

<script>
import {
  Table,
  Button,
  ButtonGroup,
  Card,
  Input,
  Icon,
  DatePicker,
  Modal
} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import right from "@/assets/images/right.png";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "money-center",
  components: {
    Table,
    Button,
    ButtonGroup,
    right,
    Card,
    panelTitle,
    Input,
    pagination,
    Icon,
    Server,
    Path,
    DatePicker,
    Modal
  },
  data() {
    return {
      id: this.$route.query.id,
      right,
      isShowDelModal:false,
      teacherName: this.$route.query.name,
      colData: [
        { title: '编号', key: "id", align: "center" },
        { title: '课时名称', key: "periodname", align: "center" }, //课时名称
        { title: '授课时间', key: "starttime", align: "center" }, //授课时间
        { title: '应到人数', key: "countOrder", align: "center" }, //应到人数
        { title: '实到人数', key: "realnumber", align: "center" }, //实到人数
        {
          title: "查看",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',{
                on: {
                	click: () => {
                		this.isShowDelModal = true;
                		this.classId = params.row.id;
                		this.getDataList1(this.current1);
                		this.curriculumtiemName = params.row.periodname;//课时名称
                		this.classtar = params.row.starttime;//上课时间
                	}
                }
                ,style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'查看')
          }
        }
      ],
      colData1: [
        { title: '学生ID', key: "studentid", align: "center" },//学生ID
        { title: '学生姓名', key: "studentname", align: "center" }, //课程名称
        { title: '出勤情况',
          key: "attendancestatusStr",
          align: "center",
           render: (h, params) => {
                  let color='#4D4D4D';
                  if(params.row.attendancestatus=='0'){
                     color='#EA1616';
                  }
                 return h("div",{style:{color: color}},params.row.attendancestatusStr)}
         } //授课时间
      ],
      tabData: [],
      tabData1: [],
      current: 1,
      total: 0,
      display: 20,
      loading: true,
      current1: 1,
      total1: 0,
      display1: 20,
      loading1: true,
      classId:null,
      num:[],
      go: '出勤',
      back:'缺勤',
      curriculumName:'',//课程名称
      curriculumtiemName:'',//课时名称
      classname:'',//班级名称
      classtar:''//上课时间
    };
  },
  created() {
    this.getDataList(this.current);
    this.curriculumName = this.$route.query.curr;//课程名称
    this.classname = this.$route.query.class;//班级名称
    this.techername = this.$route.query.name;//老师名称
  },
  methods: {
    // 查询订单列表
    getDataList(page = 1) {
      let {id} = this.$route.query;

      Server.post(
        Path.getTecher,
        {
          pagenum: page,
          id: id
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.tabData = [];
            if (code === 0) {
              var datas = data.data;
              this.tabData = datas.map((el, index) => {
                return {
                  id: el.id,
                  periodname: el.periodname,
                  starttime: el.starttime,
                  countOrder: el.countOrder,
                  OrdertMoney: el.paynum,
                  realnumber: el.realnumber,
                  Make: '查看'
                };
              });
              this.current = Number(data.pageinfo.pagenum);
              this.total = data.pageinfo.total;
              this.display = data.pageinfo.pagesize;
            }
            this.loading = false;
          } else {
            // this.$Message.error(res.info);
            this.loading = false;
          }
        }
      );
    },
    getDataList1(page = 1) {//详情页请求
      Server.post(
        Path.getSudent,
        {
          pagenum: page,
          id: this.classId
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.tabData1 = [];
            if (code === 0) {
              var datas = data.data;
              this.tabData1 = datas.map((el, index) => {
                return {
                  studentid: el.studentid,
                  studentname: el.studentname,
                  attendancestatusStr: el.attendancestatusStr

                };
              });
              this.current1 = Number(data.pageinfo.pagenum);
              this.total1 = data.pageinfo.total;
              this.display1 = data.pageinfo.pagesize;
            }
            this.loading1 = false;
          } else {
            // this.$Message.error(res.info);
            this.loading1 = false;
          }
        }
      );
    },
    // 分页请求
    pageChange(currentPage) {
    	this.current = currentPage;
      this.loading = true;
      this.getDataList(currentPage);
    },
    pageChange1(currentPage) {
    	this.current1 = currentPage;
      this.loading1 = true;
      this.getDataList1(currentPage);
    }
  },
  computed: {}
};
</script>

<style scoped lang='less' >
.date-change {
  overflow: hidden;
  padding: 0 0 20px 0;
}
.date-change li {
  cursor: pointer;
  margin-right: 20px;
}
.time-box {
  overflow: hidden;
}
.btn {
  width: 120px;
  line-height: 20px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px 0;
  border-color:none ;
}
.custom-btn-cancel:hover{
	color: #5E6266;
}
.custom-btn-warning:hover{
	color: #FFFFFF;
}
.mr-20{
	margin-left: 120px;
}
.td{
  width: 50%;
  p:first-child{
    display: inline-block;
     width: 70px;
    color: #4D4D4D;
    font-weight: bold;
  }
  p:last-child{
    display: inline-block;
    width: 200px;
    // overflow:hidden;
    // text-overflow:ellipsis;
    // white-space:nowrap;
  }
}
</style>



