<template>
  <div id="Course">
    <panel-title :crumbs="crumbs" ></panel-title> 
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <Form :label-width="85" inline >
         <span>用户名{{$t('Common.colon')}}</span>
         <Input
          v-model="SearchName"
          icon="ios-search" 
          placeholder="用户名"
          style="width: 200px;"
          @on-enter="getAdminList(SearchName,current)"
          @on-click="getAdminList(SearchName,current)"
        />
        <Button type="primary"  class="mytheme-mg-left-70" @click="addAdmin()">添加成员</Button>
      </Form>
      <!-- 成员列表 -->
      <template>
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1"></i-table>
      </template>
      <!-- 分页 -->
      <template>
        <pagination :total="total" :current-page='current' :display="display" @pagechange="pagechange" v-if='total>0'></pagination>
      </template>
    </Card>
    

    <Modal v-model="isShowDelModal" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除管理员？</div>
      <div slot="footer">
        <Button type="primary" @click="deleteAdmin(deleteid)">确认</Button>
        <Button @click="isShowDelModal = false" >取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, Button, Modal, Col,FormItem,Row, Form  } from "iview";
import Server from '@/service/request'
import Path from '@/service/Path'
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    Button,
    Modal,
    "i-Col":Col,
    FormItem,
    Form,
    Row
  },
  created() {
    //do something after creating vue instance
    this.getAdminList(null,this.current);
  },
  methods: {
    //添加成员
    addAdmin() {
      this.$router.push('/AddMembers?from=MainRightManagement');
    },
    //是否启用
    changeStatus(id,flag){
      Server.post(Path.switchAdminFlag,{
        adminid:id,
        flag:Number(flag)
      },res=>{
        let {code,data}=res;
        if(code===0){
          this.$Message.success(res.info);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    //获取成员列表
    getAdminList(name='',page=1){
      Server.post(Path.getAdminList,{
        'username':name,
	    	'pagenum':page,
      },res=>{
        let { code, data } = res;
        if(code===0){
          this.data1 = [];
          if(data.length === 0){
          }else{
            this.total = data.pageinfo.total;
            this.display = data.pageinfo.pagesize;
            let datas = data.data;
            for (var i = 0; i < datas.length; i++) {
              var obj = {};
              obj.username = datas[i].username;
              obj.adminname = datas[i].adminname;
              obj.userRole = datas[i].groupstr;
              obj.Phone = datas[i].mobile;
              obj.AddTime = datas[i].addtime;
              obj.LastLoginTime = datas[i].logintime;
              obj.Status = datas[i].status;
              obj.Make = datas[i].uid;
              this.data1.push(obj);
            }
          }
        }else{
          this.data1 = [];
        }
      })
    },
    pagechange: function(currentPage) {
      this.current = currentPage;
      this.loading = true;
      this.getAdminList(null,this.current);
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
    },
    //删除成员
    deleteAdmin(id) {
      Server.post(Path.delAdminUser, {
        adminid: id
      }, (res) => {
        this.$Message.info(res.info);
        if(res.code === 0) {
          this.getAdminList(null,1);
          this.isShowDelModal = false;
        }
      });
    }
  },
  data(){
    return {
      crumbs: [{
        name: '成员管理',
        route: ''
      }],
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      loading:true,
      isShowDelModal: false,
      SearchName:'',
      InputText: "",
      deleteid: '',
      columns1: [
        {
          title: "用户名",
          align: "center",
          key: "username"
        },
        {
          title: "用户昵称",
          align: "center",
          key: "adminname"
        },
        {
          title: "分组",
          align: "center",
          key: "userRole"
        },
        {
          title: "手机号",
          align: "center",
          key: "Phone"
        },
        {
          title: "添加时间",
          align: "center",
          key: "AddTime"
        },
        {
          title: "最后登录时间",
          align: "center",
          key: "LastLoginTime"
        },
        {
          title: "是否启用",
          align: "center",
          key: "TopPrice",
          render: (h, params, index) => {
            return h("div", [
              h(
                "i-switch",
                {
                  on: {
                    "on-change": value => {
                      this.changeStatus(params.row.Make,value?'0':'1')
                    }
                  },
                  props:{
                    value : !Boolean(params.row.Status)
                  }
                }
              )
            ]);
          }
        },
        {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                    to:'/AddMembers?id='+params.row.Make+'&from=MainRightManagement'
                },
                style: {
                  cursor:'pointer',
                  color: '#2693ff',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'编辑'),
              h('div',{
                on: {
                  'on-change': (value) => {
                    // console.log(params.index,value);
                  },
                  'click': () => {
                    this.isShowDelModal = true;
                    this.deleteid = params.row.Make;
                  }
                },
                style: {
                  cursor:'pointer',
                  color: 'red',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn red'
                }
              },'删除')
            ])
          }
        }
      ],
      data1: []
    }
  }
};
</script>
<style lang="less" scoped>
 @import "~@/assets/less/tab.less";


</style>



