<template>
  <div id="money-center" style="min-height:500px;">
    <!--对帐中心-->
    <panel-title :crumbs="[
      {
        name:$t('withdrawalcheckCenterpage.withdrawalcheckCenterpage'),
        route:''
      },
    ]" />
	    	<div>
		      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
		        <div class="mytheme-mg-top-20">
			            <!--老师姓名-->
			            <span >老师姓名：</span>
			            <!--输入老师姓名-->
			            <Input class="input-normal-width input-max-height" placeholder="输入老师姓名"  @on-enter="getOrderList(current,serchaName,keName,className)"  v-model="serchaName"/>
			            
			            <!--课程名称-->
			            <span class="mytheme-mg-left-70">课程名称：</span>
			            <!--输入课程名称-->
			            <Input class="input-normal-width input-max-height" placeholder="输入课程名称" @on-enter="getOrderList(current,serchaName,keName,className)" v-model="keName" />
			
			            <!--班级-->
			            <span class="mytheme-mg-left-70">班级名称：</span>
			            <!--输入班级姓名-->
			            <Input class="input-normal-width input-max-height" placeholder="输入班级名称" v-model="className" @on-enter="getOrderList(current,serchaName,keName,className)"/>
				          <Button type="primary" class="mytheme-mg-left-70" @click="getOrderList(current,serchaName,keName,className)">{{$t('withdrawalcheckCenterpage.search')}}</Button>
		        </div>
		       	<Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :loading="loading" :columns="colData" :display="display" :data="tabData"></Table>
				    <pagination class="mg-top-20" :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
		     </Card>
	     </div>
  </div>
</template>

<script>
import {
  Table,
  Button,
  ButtonGroup,
  Card,
  Input,
  Icon,
  DatePicker
} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "money-center",
  components: {
    Table,
    Button,
    ButtonGroup,
    Card,
    panelTitle,
    Input,
    pagination,
    Icon,
    Server,
    Path,
    DatePicker
  },
  data() {
    return {
      colData: [
        { title: '老师ID', key: "teacherid", align: "center" }, //老师ID
        { title: '老师姓名', key: "teachername", align: "center" }, //老师姓名
        { title: '课程名称', key: "curriculumname", align: "center" }, //课程名称
        { title: '班级名称', key: "gradename", align: "center" }, //班级名称
        { title: '已完成课时/总课时', key: "num", align: "center" }, //班级名称
        {
          title: "查看",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                    to:`/TeacherMoney?id=${this.tabData[params.index].id}&curr=${params.row.curriculumname}&class=${params.row.gradename}&name=${params.row.teachername}&from=MainOrderManagement`
                },
                style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'查看')
            ])
          }
        }
      ],
      tabData: [],
      current: 1,
      total: 0,
      display: 20,
      loading: true,
      classId: 0,
      className:'',//班级名称
      keName:'',//课程名称
      serchaName:''//老师名称
    };
  },
  created() {
    this.getOrderList(this.current,this.serchaName,this.keName,this.className);
  },
  methods: {
    // 查询订单列表
    getOrderList(current = 1,teacher,curr,grade) {
      Server.post( Path.getBill,{
          'pagenum':current,
					'teachername':teacher,//老师名称
					'curriculumname':curr,//课程名称
					'gradename':grade//班级名称
        },
        res => {
        	this.tabData = [];
          let { code, data } = res;
          if (code === 0) {
          	let datas =data.data;
						for (let i = 0; i < datas.length; i++ ) {
							let obj = {};
							obj.id = datas[i].id;
							obj.teacherid = datas[i].teacherid;
							obj.teachername = datas[i].teachername;
							obj.curriculumname = datas[i].curriculumname;
							obj.gradename = datas[i].gradename;
							obj.periodnum = datas[i].periodnum;
							obj.completenum = datas[i].completenum;
							obj.num = datas[i].completenum+'/'+datas[i].periodnum;
							this.tabData.push(obj);
						}
						this.total = data.pageinfo.total;
			      this.display = data.pageinfo.pagesize;
          }else{
            // this.$Message.error(res.info);
          }
          this.loading = false;
        }
      );
    },
    // 分页请求
    pageChange(currentPage) {
    	this.current = currentPage;
      this.loading = true;
      this.getOrderList(currentPage,this.serchaName,this.keName,this.className);
    }
  },
  computed: {}
};
</script>

<style scoped lang='less'>
.date-change {
  overflow: hidden;
  padding: 0 0 20px 0;
}
.date-change li {
  cursor: pointer;
  margin-right: 20px;
}
.br8{
  overflow: initial;
}
</style>



