<template>
  <div>
    <panel-title :crumbs="[{name:'签到统计',route:''}]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
          <!--学生姓名-->
          <span>学生姓名：</span>
          <Input class="input-normal-width input-max-height"
             @on-enter="getTableInfo(current)"
             v-model="nickname"
             placeholder="学生姓名"/>

            <Button class="mytheme-mg-left-70" type="primary" @click="getTableInfo(current)">搜索</Button>

      </div>
      <!--年级题库列表-->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!--分页-->
      <pagination  class="mg-top-20" :total="total" :currentPage="current" :display="display" @pagechange="pagechange" v-if='total>0'></pagination>
    </Card>
    <!--获奖明细模态框-->
    <Modal  width="800px"
      v-model="listModal"
      title="奖励明细" >
      <Row>
        <Col>
            <!--获奖列表-->
          <i-table   border ref="selection"   :loading=false :columns="colReward" :data="rewardData"></i-table>
        </Col>
      </Row>
      <Row>
        <Col>
          <pagination  class="mg-top-20" :total="rewardTotal" :currentPage="current" :display="display" @pagechange="pagechangeModal" v-if=' rewardTotal>0'></pagination>
        </Col>
      </Row>
      <div slot="footer">
        <!--取消-->
        <Button type="text" shape="circle" @click="listModal = false;">{{$t('Common.cancel')}}</Button>
        <!--确认-->
        <Button type="warning" shape="circle" >{{$t('Common.sure')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import RewardInfo from '@/assets/testInfo/rewardInfo';
import Server from "@/service/request";
import Path from "@/service/Path";
import { Card, Table, Button,Input,Modal,Row,Col} from "iview";
export default {
  name: "",
  components:{panelTitle,pagination,Card,'i-table':Table, Button,Input,Modal,Row,Col},
   data(){
    return {
      nickname:'',//学生名称
      total: 0, // 记录总条数
      rewardTotal:0,// 获奖信息模态框总条数
      display: 10, // 每页显示条数
      current: 1, // 当前的页数
      currentModal:1,//当前modal页数
      listModal:false,  //获奖信息modal默认关闭
      colReward:[
        {
          title: '序号',
          key: 'number',
          align: "center",
          width:100
        },
        {
          title: '奖品名称',
          key: 'rewardName',
          align: "center",
        },
        {
          title: '获取条件',
          key: 'rewardGain',
          align: "center",
          width:250
        },
        {
          title: '获取时间',
          key: 'date',
          align: "center",
        },
      ],
      rewardData:[

      ],
      colData: [
        {
          title: '编号',
          key: 'id',
          align: "center",
          width:120
        },
        {
          title: '学生',
          key: 'nickname',
          align: "center",
        },
        {
          title: '注册时间',
          key: 'addtime',
          align: "center"
        },
        {
          title: '签到天数',
          key: 'totalsignin',
          align: "center"
        },
        {
          title: '连续签到天数',
          key: 'consignin',
          align: "center"
        },
        // {
        //   title: '获得奖励',
        //   render: (h, params) => {
        //     return h("div", [
        //       h("a",{
        //         style: {
        //           color: "#f89240",
        //           display: "inline-block",
        //           marginRight: "10px"
        //         },
        //         on: {
        //           'click': () => {
        //             // 模态框进行显示
        //             this.listModal = true;
        //             // 获取奖品信息数据
        //             this.getReward();
        //           }
        //         }
        //       },"查看")
        //     ]);
        //   },
        //   align: "center"
        // }

      ],
      tabData: [
      ]
    }
  },
  methods: {
    getTableInfo(current=1) {
      Server.post(Path.getSigninList,{
       nickname:this.nickname,
       pagenum:current
      },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = Number(res.data.pageinfo.pagesize);
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
            this.tabData = [];
          }
        }
      );
    },
    pagechange(currentPage) {
        this.current = currentPage;
        this.getTableInfo(this.current);
    },
    pagechangeModal(page){
      this.currentModal = page;
      this.getReward();
    },
    getReward(){
      let nowData = RewardInfo.rewardData.slice(this.currentModal*10-10,this.currentModal*10);
      for (let i in nowData){
        nowData[i].number = parseInt(this.currentModal*10-10)+parseInt(i)+1;
      }
      this.rewardData = nowData;
      this.rewardTotal = RewardInfo.rewardData.length;
    }
  },
  created() {
    this.getTableInfo(this.current);
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";

</style>



