<template>
<!--班级管理-->
  <div id="classManagement">
    <!--$t('classifyManagepage.classifyManagepage')-->
    <panel-title :crumbs="crumbs"></panel-title>
    <template>
      <Card dis-hover class="mg-bottom-20 clear">
        <div class="Money-box clearfloat">
          <div class="fl Money-item Money-Use">
            <!--18个班级-->
            <p style="font-size: 24px">共 <span style="color:#387BFF;">{{total}}</span> 个班级</p>
          </div>
          <div class="fr">
            <!--开班-->
            <Button type="primary" @click="addClass()">开班</Button>
          </div>
        </div>
        <div class="tip-box">
          <ul>
            <li>
              <!--您可以选择已经添加的课程开设班级，每门课程可以开设多个班级，每个班级您可以制定不同的上课时间、不同的学生。-->
              您可以选择已经添加的课程开设班级，每门课程可以开设多个班级，每个班级您可以制定不同的上课时间、不同的学生；
            </li>
            <li>
              <!--您可以根据需要随时修改任何一个未上课时的时间；-->
              您可以根据需要随时修改任何一个未上课时的时间；
            </li>
            <li>
              <!--已经上完的课时，您不能再修改其上课时间，但是您可以查看该课时的回放、统计数据等；-->
              已经上完的课时，您不能再修改其上课时间，但是您可以查看该课时的回放、统计数据等；
            </li>
            <li>
              <!-- 一个班级的所有课时都结束后，您需要点击结课按钮，表示该班级的课程已经授完。-->
              一个班级的所有课时都结束后，您需要点击结课按钮，表示该班级的课程已经授完。
            </li>
          </ul>
        </div>
      </Card>
    </template>
    
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <template>
        <Form :label-width="85" inline >
          <!--课程名称-->
          <span class='fl-text'>班级名称{{$t('Common.colon')}}</span>
          <Input
            style="width: 200px;"
            placeholder="班级名称"
            v-model="coursename"
            icon="ios-search" 
            @on-click="getSchedulingList(current, coursename, instatus)"
            @on-enter="getSchedulingList(current, coursename, instatus)"
          />
          <span style="margin-left: 15px;">班级状态{{$t('Common.colon')}}</span>
          <i-select 
            v-model="instatus" 
            style="width: 200px; border:1px solid #dddee1; border-radius: 4px;"
            @on-change="getSchedulingList(current, coursename, instatus)">
            <i-option value="">全部</i-option>
            <i-option value="0">未招生</i-option>
            <i-option value="1">招生中</i-option>
            <i-option value="3">已满员</i-option>
            <i-option value="4">授课中</i-option>
            <i-option value="5">已结束</i-option>
            <i-option value="6">已超时</i-option>
          </i-select>
          <!-- <span>{{this.instatus}}</span> -->
          <!-- <div class="fr">
            <Button type="primary" shape="circle" class='fr h-search-btn' @click="getSchedulingList(current, coursename, instatus)">查询</Button>
          </div> -->
        </Form>
      </template>
      <!-- 班级列表 -->
      <template>
        <i-table  class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns" :data="datalist"></i-table>
      </template>
      <!-- 分页 -->
      <template>
        <pagination :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
      </template>
    </Card>
    <Modal
      v-model="isOverconfirm"
      :closable="false" >
      <div slot="header">温馨提示</div>
      <div class="type-content" v-if="this.isDelClass == false">
        <div class="input-box">
          您确定要删除该课程吗？
        </div>
      </div>
      <div class="type-content" v-if="this.isDelClass == true">
        <div>该班级已有人报名，删除班级后会影响学生正常上课，请谨慎删除！</div>
        <i-table :columns="columnscur" :data="delClass"></i-table>
        <div style="overflow: hidden">
          <pagination :total="totalpage" :display="displaypage" :current-page='curpage' @pagechange="pagechangemodal"  v-if='total>0'></pagination>
        </div>
      </div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="showOverModal()"> 确定</Button>
        <!--取消-->
        <Button  @click="isOverconfirm = false;"> 取消 </Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Card, Form, Input, Button, Table,Switch,FormItem, Modal, Row, Col, Select, Option} from 'iview';
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Form,
    FormItem,
    Input,
    Button,
    "i-table": Table,
    "i-switch": Switch,
    pagination,
    Server,
    Path,
    Modal,
    Col,
    Row,
    'i-select': Select,
    'i-option': Option

  },
  data() {
    return {
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      SearchName: '',
      coursename: "",
      instatus: '',
      isOverconfirm: false,
      activeItem: 0,
      activeId: '',
      state: '',
      classnum:'',
      crumbs: [{
        name:'班级管理',
        route:''
      }],
      columns: [
        {
          title: "编号",
          align: "center",
          key: 'id'
        },
        {
          title: "课程封页",
          align: "center",
          render: (h, params) => {
            return h('img', {
              attrs: {
                src: params.row.imageurl
              },
              on: {
                'error': () => {
                  params.row.imageurl = this.defaultCover
                }
              },
              style: {
                width: '72px',
                height: '54px',
                margin: '10px 0 5px 0',
                borderRadius: '4px'
              }
            });
          }
        },
        {
          title: "课程名称",
          align: "center",
          key: 'coursename'
        },
        {
          title: "班级名称",
          align: "center",
          key: "gradename"
        },
        {
          title: "老师",
          align: "center",
          key: 'teachername'
        },
        {
          title: "价格",
          align: "center",
          key: 'price'
        },
        {
          title: "课时数",
          align: "center",
          key: 'periodnum'
        },
        {
          title: "学生数",
          align: "center",
          key: 'payordernum'
        },
        {
          title: "班级状态",
          align: "center",
          key: 'classstatusStr'
        },
        {
          title: "上下架",
          align: "center",
          key: 'status',
          render: (h, params, index) => {
            return h("div", [
              h(
                "i-switch",
                {
                  on: {
                    "on-change": value => {
                      this.switchChange(params.row.id,value);
                    }
                  },
                  props: {
                    //value: Boolean(params.row.value)
                    value: params.row.status === 1
                  }
                }
              )
            ]);
          }
        },
        {
          title: "操作",
          align: "center",
          render: (h, params, index) => {
            return h('div', [
              h('a', {
                style: {
                  cursor: 'pointer',
                  color: '#F89240',
                  display: 'inline-block',
                  marginRight: '10px'
                },
                on: {
                  'click': () => {
                    this.$router.push({
                      path: '/classView',
                      query: {
                        id: params.row.id,
                        curriculumid: params.row.curriculumid,
                        type: 'view',
                        from: 'MainLessonManagement'
                      }
                    });
                  }
                },
                attrs: {
                  class: 'tablebtn blue',
                }
              }, "查看"),
              h('a', {
                style: {
                  cursor: 'pointer',
                  color: '#2693ff',
                  display: 'inline-block',
                  marginRight: '10px'
                },
                on: {
                  'click': () => {
                    this.$router.push({
                      path: '/EditClassInfo',
                      query: {
                        id: params.row.id,
                        curriculumid: params.row.curriculumid,
                        type: 'edit',
                        from: 'MainLessonManagement'
                      }
                    });
                  }
                },
                attrs: {
                  class: 'tablebtn blue',
                }
              }, "编辑"),
              h('a', {
                style: {
                  cursor: 'pointer',
                  color: '#F00',
                  display: 'inline-block',
                  marginRight: '10px'
                },
                on: {
                  'click': () => {
                    this.activeId = params.row.id;
                    this.isDelClassModel();
                    //this.isOverconfirm = true;
                  }
                },
                attrs: {
                  class: 'tablebtn blue',
                }
              }, "删除")
            ])
          }
        }
      ],
      datalist: [],
      isDelClass: false,
      delCourse: [],
      totalpage: 0,
      curpage: 1,
      displaypage: 20,
      columnscur: [{
        title: "编号",
        align: "center",
        key: "orderid"
      },{
        title: "学生姓名",
        align: "center",
        key: "studentname"
      }],
      delClass: []
    }
  },
  created() {
    this.getSchedulingList(this.current, this.coursename, this.instatus)
  },
  methods: {
    // 筛选
    Screen(index) {
      this.activeItem = index;
      if(index==0){
        this.$router.push({
          path: '/isTeaching',
          query: {from:'MainLessonManagement'}
        })
      }else{
        this.$router.push({
          path:'/isOver',
          query:{from:'MainLessonManagement',type:index }
        })
      }
    },
    addClass() {
      this.$router.push('/EditClassInfo?from=MainLessonManagement')
    },
    showOverModal() {
      let {activeId} = this;

      Server.post(Path.deleteScheduling,{
        id: activeId
      }, res => {
        if(res.code == 0){
          this.$Message.success(res.info);
          this.isOverconfirm = false;
          this.getSchedulingList(this.current, this.coursename, this.instatus)

        }else {
          this.$Message.error(res.info);
        }
      })
    },
    // 查询课程列表
    getSchedulingList(current=1, coursename, instatus) {
      Server.post(Path.getSchedulingList, {
        pagenum: current,
        name: this.coursename,
        instatus: this.instatus
      }, (res) => {
        if(res.code === 0){
          this.datalist = res.data.data;
          this.total = res.data.pageinfo.total;
          this.current = Number(res.data.pageinfo.pagenum);
          this.display = res.data.pageinfo.pagesize;
          this.classnum = res.data.data.length;
        }else {
          this.datalist = [];
        }
      })
    },
    pageChange(currentPage) {
      this.loading = true;
      this.getSchedulingList( currentPage, this.coursename, this.instatus);
    },
    //删除班级对应的学生信息
    getSchedulingOrder() {
      Server.post(Path.getSchedulingOrder,{
        id: this.activeId,
        limit: this.curpage
      }, res => {
        if(res.code == 0){
          this.delClass = res.data.data;
          this.isDelClass = true;
          this.curpage = Number(res.data.pageinfo.pagenum);
          this.displaypage = res.data.pageinfo.pagesize;
          this.totalpage = res.data.pageinfo.total;
        }else {
          this.isDelClass = false;
          this.delClass = [];
        }
      })
    },
    pagechangemodal(currentPage) {
      this.getSchedulingOrder(this.activeId, currentPage);
    },
    isDelClassModel() {
      this.isOverconfirm = true;
      this.getSchedulingOrder();
    },
    //更改班级上下架状态
    switchChange(id, classstatus) {
      Server.post(Path.enrollStudent, {
        id: id,
        status: classstatus ? 1:0
      }, (res) => {
        if (res.code == 0) {
          this.$Message.success(res.info);
          this.getSchedulingList(this.current, this.coursename, this.instatus)
        }else{
          this.$Message.error(res.info);
          this.getSchedulingList(this.current, this.coursename, this.instatus)
        }

      });
    },
  }
}
</script>
<style lang="less" scoped>
@import "~@/assets/less/tab.less";
@import "~@/assets/base.less";
.Money-box {
  padding: 20px 60px 20px 70px;
  border-bottom: 1px solid #e6e6e6;
  margin: 0 -16px;
  .ivu-btn{
    padding: 6px 33px;
    font-size: 16px;
    margin-top: 19px;
    // box-shadow: 4px 0px 6px rgba(73,125,246,1);
  }
}
.tip-box {
  padding: 20px 70px;
}
.tip-box ul li {
  line-height: 30px;
  position: relative;
  font-size: 14px;
  color: #8E9DAA;
}
.tip-box ul li::after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: @color1;
  left: -15px;
  top: 11px;
  position: absolute;
}
.clear{
  overflow: hidden;
}
</style>



