<template>
  <div id="order-details">
    <!-- <panel-title>退款申请 > 服务单详情</panel-title> -->
    <!-- 订单详情 -->
    <panel-title
      :crumbs="[
        {
          name:$t('orderList.orderList'),//全部订单
          route:{
            path:'/AccountDetails',
            query:{
              from:'MainOrderManagement',
            }
          }
        },
        {
          name:$t('orderDetails.orderDetails'),//订单详情
          route:'',
        },
      ]"
    ></panel-title>
    <template>
      <Card dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30">
        <!-- 订单信息 -->
        <div slot="title" class="head">
          <img :src="orderForm">
          <span>{{$t('orderDetails.orderinfo')}}</span>
        </div>
        <!-- <p></p> -->
        <!--买家信息-->
        <div class="callout callout-info" v-if="isHasOrderInfo">
          <div class="callout-item">
            <!-- 订单编号： -->
            <label style="">{{$t('orderManagepage.reference')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.ordernum}}</span>
          </div>
          <div class="callout-item">
            <!-- 订单状态： -->
            <label style="">{{$t('orderManagepage.orderstatus')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.orderstatus}}</span>
          </div>
          <div class="callout-item">
            <!-- 下单时间： -->
            <label style="">{{$t('orderManagepage.ordertime')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.ordertime}}</span>
          </div>
          <div class="callout-item">
            <!-- 学生： -->
            <label style="">{{$t('orderManagepage.student')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.studentname}}</span>
          </div>
          <div class="callout-item">
            <!-- 下单渠道： -->
            <label style="">{{$t('orderDetails.webtype')}}{{$t('Common.colon')}}</label>
            <!-- 1pc 2手机 -->
            <span>{{buyer.ordersource}}</span>
  
          </div>
          <div class="callout-item">
            <!-- 支付方式： -->
            <label style="">{{$t('orderList.payway')}}{{$t('Common.colon')}}</label>
            <span >{{buyer.paytype}}</span>
          </div>
        </div>
        <!--卖家信息-->
        <div class="callout callout-warning" v-if="isHasOrderInfo">
          <div class="callout-item">
            <!-- 原课程总价： -->
            <label style="">{{$t('orderDetails.originprice')}}{{$t('Common.colon')}}</label>
            <span >￥{{seller.originprice}}</span>
          </div>
          <div class="callout-item">
            <!-- 优惠金额： -->
            <label style="">{{$t('orderDetails.discount')}}{{$t('Common.colon')}}</label>
            <span >￥{{seller.discount}}</span>
          </div>
          <div class="callout-item">
            <!-- 交易金额： -->
            <label style="">{{$t('orderDetails.amount')}}{{$t('Common.colon')}}</label>
            <span>￥{{seller.amount}}</span>
          </div>
          <div class="callout-item">
            <!-- 课程名称： -->
            <label style="">{{$t('courseManagepage.coursetitle')}}{{$t('Common.colon')}}</label>
            <span >{{seller.coursename}}</span>
          </div>
          <!-- 班级名称： -->
          <!-- <div class="callout-item">
            <label style="">{{$t('arrangManage.classname')}}{{$t('Common.colon')}}</label>
            <span >{{seller.classname}}</span>
          </div> -->
          <div class="callout-item">
            <!-- 授课老师： -->
            <label style="">{{$t('orderManagepage.lecturer')}}{{$t('Common.colon')}}</label>
            <span >{{seller.teachername}}</span>
          </div>
          <div class="callout-item">
            <!-- 收货地址： -->
            <label style="">收货地址{{$t('Common.colon')}}</label>
            <span >{{seller.address}}</span>
          </div>
        </div>
        <!--没有符合数据-->
        <div v-if="!isHasOrderInfo">{{message}}</div>
      </Card>
    </template>
  </div>
</template>

<script>
  import {Card, Button, Form, FormItem, Col, Row} from 'iview'
  import panelTitle from '@/components/panelTitle'
  import orderForm from '../img/orderForm@2x.png'
  import callout from '@/components/callout'
  import Server from '@/service/request'
  import Path from '@/service/Path'

  export default {
    name: "order-details",
    components: {
      Card,
      Button,
      Form,
      FormItem,
      panelTitle,
      callout,
      Col,
      Row
    },
    created() {
      this.getOrderInfo();
    },
    data() {
      return {
        num:'',
        orderForm,
        buyer: {
          ordernum: '', //订单编号
          orderstatus: '', //订单状态
          ordertime: '', // 下单时间
          studentname: '', //学生
          ordersource: '', //下单渠道
          paytype: '', //支付方式
        },
        seller: {
          originprice: '', //课程原价
          discount: '', //优惠金额
          amount: '', //交易金额
          coursename: '', //课程名称
          classname: '', //班级名称
          teachername: '', //教师名称 授课老师
          address:'',//收货地址
        },
        refund: {},
        isFinishe: false,
        isRefund: false,
        isHasOrderInfo: false,
        message: ''
      }
    },
    methods: {
      getOrderInfo() {
        let  id  = this.$route.query.id;
        let  num  = this.$route.query.num;
        Server.post(Path.addWithdraw, {
            id: id
        }, (res) => {
          console.log(res)
          let { code, data } = res;
          if(res.code === 0) {
            // 买家信息
            this.buyer.ordernum = num;//订单编号
            this.buyer.ordertime = res.data.paytime;//支付时间
            this.buyer.paytype = res.data.paytype;//支付方式
            this.buyer.ordersource = res.data.ordersource;//下单渠道
            this.buyer.studentname = res.data.studentname;//学生姓名
            this.buyer.orderstatus = res.data.orderstatus;//订单状态
            // 卖家信息
            this.seller.originprice = res.data.originprice;//原价课程
            this.seller.discount = res.data.discount;//优惠金额
            this.seller.amount = res.data.amount;//实付金额
            this.seller.coursename = res.data.coursename;//课程名称
            this.seller.classname = res.data.classname;//班级名称
            this.seller.teachername = res.data.teachername;//老师姓名
            this.seller.address = res.data.address;//老师姓名
            this.isHasOrderInfo = true;
          } else {
            this.isHasOrderInfo = false;
            this.message = res.info;
          }
        });
      },
      //支付方式
      // paytype(val) {
      //   if(val==1){
      //     return this.$t('orderList.paytype2')//余额
      //   }else if(val==2){
      //     return this.$t('orderList.paytype3')//微信
      //   }else if(val==3){
      //     return this.$t('orderList.paytype4')//支付宝
      //   }else if(val==4){
      //     return this.$t('orderList.paytype5')//银联
      //   }else if(val==5){
      //     return 'paypal' //paypal
      //   }else{
      //     return this.$t('orderList.paytype1')//其他
      //   }
      // },
      //订单状态
      orderstatus(val){
        if(val==10){
          return this.$t('orderList.status3')//已取消
        }else if(val==20){
          return this.$t('orderList.status4')//已支付
        }else if(val==0){
          return this.$t('orderList.status5')//已下单
        }else if(val==30){
          return this.$t('orderList.status6')//申请退款
        }else if(val==40){
          return this.$t('orderList.status7')//已退款
        }else if(val==50){
          return this.$t('orderList.status8')//退款驳回
        }
      }
    }
  }
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
 .head{
    display: flex;
    align-items: center;
    img{
      width: 16px;
      height: 20px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
.ivu-card-head{
  background: @bg1;
}
  .callout {
    padding: 30px 0 30px 60px;
    margin: 20px 40px 30px;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 4px;
  }
  .callout-item {
    color: @fz-Color1;
    line-height: 32px;
  }
  .callout-item label {
    display: inline-block;
    text-align: right;
    width: 100px;
    font-size: 16px;
  }
  .callout-item span {
    padding: 0 0 0 50px;
    font-size: 16px;
  }

  .callout,
  .callout-default {
    border-left-color: #999;
    background: #eee;
  }

  .callout-info {
    border-left-color: #469af8;
    background: #f2f7fb;
  }

  .callout-warning {
    border-left-color: #f89240;
    background: #fffbf8;
  }

  .callout-danger {
    border-left-color: #f5496d;
    background: #f2dbe0;
  }

  .callout-primary {
    border-left-color: #cae3ff;
    background: #fff;
  }

  .callout-success {
    border-left-color: #3adfa7;
    background: #f7fffc;
  }
</style>



