<template>
  <div id="EditLessonView">
    <panel-title
      :crumbs="[
        {
          name:'课程管理',
          route:{
            path:'/Small',
            query:{
              from:'MainLessonManagement'
            }
          }
        },
        {
          name:'编辑课程信息',
          route:'',
        },
      ]"
    />
    <step :step='step' :desc='desc'/>
    <router-view></router-view>
  </div>
</template>
<script>
import panelTitle from '@/components/panelTitle';
import step from '@/components/step';
export default {
  name: "EditLessonView",
  components: {
    step,
    panelTitle,
  },
  data: () => ({
    step:1,
    desc:[
      '填写课程信息',
      '填写课时信息',
      '设置'
    ],
  }),
  created(){
    this.stepActive()
  },
  watch:{
    "$route": "stepActive"
  },
  methods: {
    stepActive(){
      this.step = Number(this.$route.query.step);
    }
  }
}
</script>
<style scoped>

</style>



