<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <Card dis-hover class="br8">
      <Form :label-width="200" ref='formData' :model="formData" :rules="ruleCustom"  class="mg-top-20">
        <Row>
          <i-Col >
            <FormItem label="分组名：" prop="GroupName">
              <Input placeholder="请输入分组名称，不超过10个字" :maxlength="10" v-model.trim="formData.GroupName" style=" width: 300px;"/>
            </FormItem>
          </i-Col>
          <i-Col >
            <FormItem label="可操作菜单：" prop="GroupMenu">
              <Tree :data="menulist" ref="menuTree" v-model="formData.GroupMenu" show-checkbox @on-check-change="getcheckpath" multiple></Tree>
            </FormItem>
          </i-Col>
          <i-Col>
          	 <FormItem label="">
          	 	<Button @click="goBack" style="margin-right: 20px;">取消</Button>
            	<Button type="primary" @click="saveData('formData')">提交</Button>
            </FormItem>
          </i-Col>
        </Row>
      </Form>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import { Card, Input, Button, Icon, Form, Row, Col, FormItem, Select, Option, Tag, Tree} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    Icon,
    Form,
    Row,
    Select,
    Option,
    'i-Col':Col,
    FormItem,
    Tag,
    Tree
  },
  data() {
    return {
      crumbs: [
        {name: '权限组', route: '/RightsGroup?from=RightsGroup'},
        {name: '添加分组', route: '/AddGroup?from=RightsGroup'},
      ],
      menulist:[],
      nodeData: [],
      
      formData: {
        GroupName: "",
        GroupMenu: "",
      },
      ruleCustom: {
        GroupName: [
          { required: true, message: '分组名不能为空', trigger: 'blur' }
        ],
        GroupMenu: [
          { required: true, message: '可选菜单不能为空', }
        ]
      }
    }
  },
  created() {
    this.menuListTree();
    let  id  = this.$route.query.id;
    //console.log(id);
    if(id){
      this.formData.GroupName = this.$route.query.name;
      this.crumbs[1].name = '编辑权限组';
      
    }else {
      this.formData.GroupName ='';
      this.crumbs[1].name = '添加权限组';
      this.formData.GroupMenu = ''
    }
  
  },
  methods: {
    //左侧菜单列表树
    menuListTree() {
      let self = this;
      let id = self.$route.query.id;
      Server.post(Path.menuListTree, {
        id: id ? id : ''
      }, res => {
        if(res.code == 0){
          //区分this的指向
          //self.$options.methods.vue里的方式，调用另外一个方法
          this.menulist =  self.$options.methods.getTree(res.data);
          //console.log(1, JSON.stringify(this.menulist));
          let arrpath= [];
          this.menulist.forEach((el, index) => {
            el.children.forEach((e,i) => {
              if(e.checked == true){
                arrpath.push(e.path);
                //this.formData.GroupMenu = e.path;
              }
            })
          })
          this.formData.GroupMenu = arrpath.join();
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    //获取渲染菜单列表树的转换数据
    getTree(tree) {
      let self = this;
      let arr = [];
      if (tree && tree.length !== 0) {
        tree.forEach(item => {
            //这里在this，是指的 forEach，所以要在上面定义下this
            let obj = {};
            obj.title = item.name;
            obj.id = item.id;
            obj.fatherid = item.fatherid;
            obj.url = item.url;
            //obj.selected = !Boolean(item.instatus);
            obj.checked = !Boolean(item.instatus);
            obj.path = item.path;
            obj.children = self.getTree(item.list); // 递归调用
            arr.push(obj);
        });
      }
      return arr;
    },
    //获取点击的树节点的path
    getcheckpath(){
      this.nodeData = this.$refs.menuTree.getCheckedNodes();
			let nodeId = [];  
			for (let i = 0 ; i < this.nodeData.length; i++){
					nodeId.push(this.nodeData[i].path);
			}
      this.formData.GroupMenu = nodeId.join();
    },
    //取消
    goBack(){
      this.$router.push('/RightsGroup?from=RightsGroup');
    },
    //确认添加编辑权限组
    saveData(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          //this.$Message.success('表单验证成功');
          let id = this.$route.query.id;
          Server.post(Path.addGroup, {
            id: id ? id :'',
            name: this.formData.GroupName,
            treepath: this.formData.GroupMenu
          }, res => {
            if(res.code == 0){
              this.$Message.success(res.info);
              this.$router.push('/RightsGroup?from=RightsGroup');
            }else{
              this.$Message.error(res.info);
            }
          })
        } 
      })
      
    }
  }
  
}
</script>
<style scoped lang = "less">

</style>



