<template>
  <!--分类管理-->
  <div id="ClassType">
    <panel-title :crumbs="[{name:$t('classifyManagepage.classifyManagepage'),route:''}]" />
    <template>
      <Card class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" dis-hover>
        <template>
          <!--添加分类 -->
          <Button type="primary" @click="showTypeEditModal('add')">{{$t('classifyManagepage.Addclassification')}}</Button>
        </template>
        <!-- 分类列表 -->
        <template>
            <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe  :columns="columns1" :data="data1"></i-table>
        </template>
      </Card>
    </template>

    <Modal
      v-model="LessonTypeEditModel"
      width="680"
      :closable="false">
    <div slot="header">{{LessonTypeText}}</div>
      <div class="type-content">
        <Form :label-width="180" ref='formData' :model="formData" :rules="ruleCustom">
          <Row>
            <Col>
              <!--分类名称-->
              <FormItem label="分类名称：" prop="cateName">
                <Input placeholder="请输入数字、字母或汉字，不支持特殊字符,不超过4个字" :maxlength="4" v-model.trim="formData.cateName" style=" width: 380px;"/>
                <!-- <div class="error hui">为保证官网展示效果，最多不超过4个字</div> -->
              </FormItem>
            </Col>
            <Col>
              <FormItem label="分类图片：">
                <Upload
                  :data="uploadid"
                  @success="getHeaderUrl" style="width:156px;">
                  <div class="loadImg">
                    <img class="photo" :src="formData.CateImgUrl" alt="" width="156" style="cursor:pointer;">
                  </div>
                </Upload>
                <!-- <div class="error hui">请上传156×156px的图片</div> -->
              </FormItem>
            </Col>
            <Col>
              <FormItem label="分类图标(选中)：">
                <Upload
                  :data="uploadid"
                  @success="getHeaderIconUrl" style="width:32px;">
                  <div class="loadImg">
                    <img class="photo" :src="formData.CateIconUrl" alt="" width="32" height="32" style="cursor:pointer;">
                  </div>
                </Upload>
                <div class="error hui">请上传16×16px的图标</div>
              </FormItem>
            </Col>
            <Col>
              <FormItem label="分类图标(未选中)：">
                <Upload
                  :data="uploadid"
                  @success="getHeaderIcon2Url" style="width:32px;">
                  <div class="loadImg">
                    <img class="photo" :src="formData.CateIcon2Url" alt="" width="32" height="32" style="cursor:pointer;">
                  </div>
                </Upload>
                <div class="error hui">请上传16×16px的图标</div>
              </FormItem>
            </Col>
            <Col>
              <!--分类描述：-->
              <FormItem label="分类描述：" prop="cateDes">
                <Input style="width:380px;" type="textarea" :maxlength="200" placeholder="请输入分类概述，最多输入200字" :autosize="{minRows: 3,maxRows: 6}" v-model.trim="formData.cateDes" @keyup.enter.native="ok('formData')"/>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="ok('formData')">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button  @click="reset('formData')"> {{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  <Modal
    v-model="LessonTypeDeleteModel"
    :closable="false">
    <div slot="header">温馨提示</div>
    <div class="type-content" v-if="this.isCourse == false">
      <div class="input-box">
        <!--删除该分类，其子类也会被删除，你确定要删除吗？-->
        {{$t('classifyManagepage.remind1')}}
      </div>
    </div>
    <div class="type-content" v-if="this.isCourse == true">
      <div class="input-box">
        该分类下已有关联课程，请先删除课程，在进行此操作！
      </div>
      <i-table :columns="columnscur" :data="datacur"></i-table>
      <div class="input-box" style="overflow: hidden">
        <pagination :total="total" :display="display" :current-page='current' @pagechange="pagechange"  v-if='total>0'></pagination>
      </div>
    </div>
    <div slot="footer">
      <!--确认-->
      <Button v-if="this.isCourse == false" type="primary" @click="okDelete"> {{$t('Common.sure')}}</Button>
      <!--取消-->
      <Button v-if="this.isCourse == false" @click="LessonTypeDeleteModel = false;"> {{$t('Common.cancel')}}</Button>
      <!-- 知道了 -->
      <Button v-if="this.isCourse == true" type="warning" @click="LessonTypeDeleteModel = false;"> 知道了</Button>
    </div>
  </Modal>
  <!-- <msg-modal msger="123" :isShow="isShow" /> -->
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Upload from "@/components/UploadPicture";
import {
  Card,
  Input,
  Table,
  Switch,
  Button,
  Modal,
  Form,
  Row,
  Col,
  FormItem
} from "iview";
import CateImg from "@/assets/images/lesson_default.png";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    Modal,
    Form,
    Col,
    Row,
    Upload,
    FormItem
  },
  created() {
    //do something after creating vue instance
    this.getClassData();
    //var origin = JSON.parse(window.localStorage.getItem("userinfo"));
    //this.uploadid = [2, origin.organid, 1];
  },
  methods: {
    deleteItem(id) {
      Server.post(
        Path.deleteCategory,
        {
          id
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.getClassData();
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
        }
      );
    },
    moveItem(id, sort, operate, rank) {
      Server.post(
        Path.shiftCategory,
        {
          id,
          sort,
          operate,
          rank
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.getClassData();
            this.$Message.success(res.info);
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    addClassName(id=0) {
      
      let { cateName, CateIconUrl, CateImgUrl, cateDes, CateIcon2Url } = this.formData;
      Server.post(
        Path.addCategory,
        {
          fatherid: id,
          categoryname: cateName,
          describe: cateDes,
          imgs: CateImgUrl,
          icos: CateIconUrl,
          icostwo: CateIcon2Url
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            //console.log("bbb");
            this.getClassData(1);
          } else {
            this.$Message.error(res.info);
          }
          this.LessonTypeEditModel = false;
        }
      );
    },
    //编辑分类
    editClassName(id) {
      let { cateName, CateIconUrl, CateImgUrl, cateDes, CateIcon2Url } = this.formData;
      Server.post(
        Path.editCategoryId,
        {
          id,
          categoryname: cateName,
          describe: cateDes,
          imgs: CateImgUrl,
          icos: CateIconUrl,
          icostwo: CateIcon2Url
          //status: 1
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.getClassData();
          } else {
            this.$Message.error(res.info);
          }
          this.LessonTypeEditModel = false;
        }
      );
    },
    isShowClass(id, type) {
      Server.post(
        Path.editCategoryId,
        {
          id,
          status: Number(type)
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$Message.success(res.info);
          } else {
            this.$Message.error(res.info);
            this.getClassData();
            //location.reload()
          }
        }
      );
    },
    
    getClassData(page = 1) {
      Server.post(
        Path.getCategoryIdList,
        {
          fatherid: 0,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.data1 = [];
            let datas = data.data;
            for (var i = 0; i < datas.length; i++) {
              var obj = {};
              obj.TypeName = datas[i].categoryname;
              obj.Level = datas[i].rankstr;
              obj.TypeNumber = datas[i].juniorcount;
              obj.Order = { sort: datas[i].sort, rank: datas[i].rank };
              obj.isShow = datas[i].status;
              obj.Make = datas[i].id;
              obj.Img = datas[i].imgs;
              obj.Icon = datas[i].icos;
              obj.Icon2 = datas[i].icostwo;
              obj.Des = datas[i].describe;

              //console.log(obj.TypeNumber )
              this.data1.push(obj);
            }
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        }
      );
    },
    //确认
    ok(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let { edtiType } = this;
          if (edtiType == "add") {
            //console.log('aaa');
            this.addClassName();
          } else {
            this.editClassName(this.editId);
          }
        }
      })
      
    },
    //取消
    reset(name){
      this.$refs[name].resetFields();
      this.LessonTypeEditModel = false;
    },
    okDelete() {
      this.deleteItem(this.delId);
      this.LessonTypeDeleteModel = false;
    },
    showTypeEditModal(type) {
      if (type == "add") {
        this.formData.cateName = "";
        this.formData.CateIconUrl = CateImg;
        this.formData.CateIcon2Url = CateImg;
        this.formData.CateImgUrl = CateImg;
        this.formData.cateDes = "";
        // 添加分类
        this.LessonTypeText = this.$t("classifyManagepage.Addclassification");
        if(this.$refs.formData!=null){
          this.$refs.formData.resetFields()
        }
      } else {
        //编辑分类
        this.LessonTypeText = this.$t("classifyManagepage.editclassification");
      }
      this.edtiType = type;
      this.LessonTypeEditModel = true;
    },
    showTypeDeleteModal() {
      this.LessonTypeDeleteModel = true;
      this.getCatergoryCurricu();
    },
    //上传图片
    getHeaderUrl(data) {
      this.formData.CateImgUrl = data;
    },
    //上传图标
    getHeaderIconUrl(data){
      this.formData.CateIconUrl = data;
    },
    //上传图标2
    getHeaderIcon2Url(data){
      this.formData.CateIcon2Url = data;
    },
    //删除对应的信息
    getCatergoryCurricu(delId ,current) {
     // console.log('del', this.delId)
      Server.post(Path.getCatergoryCurricu, {
        id: this.delId,
        limit: current
      }, res => {
        if(res.code == 0){
          this.isCourse = true;
          this.datacur = res.data.data;
          this.total = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
          this.current = Number(res.data.pageinfo.pagenum);
        }else{
          this.isCourse = false;
          this.datacur = [];
        }
      })
    },
    //删除信息对应的分页
    pagechange(currentPage) {
      this.loading = true;
      this.getCatergoryCurricu(this.delId, currentPage);
      //console.log(currentPage)
    }
  },
  computed: {
    getstate() {
      return this.$store.store.state.status;
    }
  },
  
  data() {
    const cateRegex = (rule, value, callback) => {
      let tstRegex = /^[a-zA-Z0-9_\u4e00-\u9fa5]+$/
      if (value === '') {
          callback(new Error('分类名称不能为空'));
      } else if (!tstRegex.test(value)) {
          callback(new Error('请输入数字、字母或汉字，不支持特殊字符'))
      } else {
          callback();
      }
    };
    return {
      LessonTypeText: this.$t("classifyManagepage.Addclassification"), //添加分类
      MsgModal: false,
      isCourse: false,
      total: 0, // 记录总条数
      display: 10, // 每页显示条数
      current: 1, // 当前的页数
      
      editId: 0, //分类编辑ID
      delId: 0, //分类删除
      loading: true,
      InputText: "",
      LessonTypeEditModel: false, //编辑分类弹窃笑
      LessonTypeDeleteModel: false, //删除分类弹窃笑
      edtiType: "",
      disabled: "",
      uploadid: [1, 1, 1],
      
      formData: {
        cateName: "", //分类名称
        CateImgUrl: CateImg,//分类图片
        CateIconUrl: CateImg,//分类图标
        CateIcon2Url: CateImg, //分类图标未选中
        cateDes: "", //分类描述
      },
      ruleCustom: {
        cateName: [
          { required: true,  validator:cateRegex, trigger: 'blur'}
        ]
      },
      columns1: [
        {
          //编号
          title: "编号",
          align: "center",
          key: "Make"
        },
        {
          //分类名称
          title: this.$t("classifyManagepage.systematicname"),
          align: "center",
          key: "TypeName"
        },
        {
          // 级别
          title: this.$t("classifyManagepage.level"),
          align: "center",
          key: "Level"
        },
        {
          //子类数量
          title: this.$t("classifyManagepage.Numbersubclasses"),
          align: "center",
          key: "TypeNumber"
        },
        {
          //是否显示
          title: this.$t("classifyManagepage.Whethershown"),
          align: "center",
          key: "isShow",
          render: (h, params, index) => {
            return h("div", [
              h("i-switch", {
                on: {
                  "on-change": value => {
                    // console.log( params.row.isShow);
                    this.isShowClass(params.row.Make, value);
                  }
                },
                props: {
                  value: Boolean(params.row.isShow)
                  // disabled:Boolean(params.row.TypeNumber)
                }
              })
            ]);
          }
        },
        {
          //排序
          title: this.$t("classifyManagepage.sort"),
          align: "center",
          key: "Order",
          render: (h, params, index) => {
            return h("div", [
              h(
                "a",
                {
                  style: {
                    display: "inline-block",
                    color: "#469af8",
                    marginRight: "10px"
                  },
                  on: {
                    click: value => {
                      let { Make, Order } = params.row;
                      this.moveItem(Make, Order.sort, 0, Order.rank);
                    }
                  }
                },
                "上移"
              ),
              h(
                "a",
                {
                  style: {
                    display: "inline-block",
                    color: "#469af8"
                  },
                  on: {
                    click: value => {
                      let { Make, Order } = params.row;
                      this.moveItem(Make, Order.sort, 1, Order.rank);
                    }
                  }
                },
                "下移"
              )
            ]);
          }
        },
        {
          //操作
          title: this.$t("classifyManagepage.operation"),
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "span",
                {
                  style: {
                    cursor: "pointer",
                    color: "#ffb932",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn yellow"
                  },
                  on: {
                    click: () => {
                      this.$router.push({
                        path: `/TwoClassType/${
                          params.row.Make
                        }?from=MainLessonManagement`,
                        query: {
                          route: this.$route.fullPath,
                          name: this.$t("classifyManagepage.classifyManagepage")
                        }
                      });
                    }
                  }
                },
                "查看子类"
              ),
              h(
                "a",
                {
                  on: {
                    click: value => {
                      this.formData.cateName = params.row.TypeName;
                      this.formData.CateImgUrl = params.row.Img;
                      this.formData.CateIconUrl = params.row.Icon;
                      this.formData.CateIcon2Url = params.row.Icon2;
                      this.formData.cateDes = params.row.Des;
                      this.editId = params.row.Make;
                      this.showTypeEditModal();
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn blue"
                  }
                },
                "编辑"
              ),
              h(
                "a",
                {
                  on: {
                    click: value => {
                      this.delId = params.row.Make;
                      this.showTypeDeleteModal();
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "red",
                    display: "inline-block",
                    marginRight: "5px"
                  },
                  attrs: {
                    class: "tablebtn red"
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      columnscur: [{
        title: "编号",
        align: "center",
        key: "id"
      },{
        title: "课程名称",
        align: "center",
        key: "coursename"
      }],
      data1: [],
      datacur: []
    };
  }
};
</script>
<style lang="less" scoped>
@import "~@/assets/less/tab.less";
.mg-bottom-10 {
  margin-bottom: 10px;
}
.must {
  color: #f00;
}
</style>



