<template>
  <div id="Course">
   <!--  素材管理 -->
    <panel-title :crumbs="crumbs"></panel-title>
    <Card class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" dis-hover>
      <div class="mytheme-mg-top-20">
       <!--  输入搜索 -->
        <span class='mytheme-input-label'>关键词/文件夹名称{{$t('Common.colon')}}</span>
        <!-- 课件名称 -->
        <Input
          v-model="InputText"
          icon="ios-search-strong"
          :placeholder="$t('materialManagepage.Namecourseware')"
          @on-click="getMaterInfo(InputText, current, $route.query.fileid)"
          @on-enter="getMaterInfo(InputText, current, $route.query.fileid)"
          style="width: 300px"
        />
        <Button class="mytheme-mg-left-70" type="primary" v-if="this.$route.query.filetype == 1 ? 0: 1"  @click="isShaowdown = true">上传</Button>
      </div>
      <!-- 素材列表 -->
      <template>
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :columns="columns1" :data="data1"></i-table>
      </template>
      <!-- 分页 -->
      <template>
        <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='total>0'></pagination>
      </template>
      
    </Card>
    <!--上传素材-->
	  <Modal v-model="isShaowdown" class="crlvv" :closable="false">
	      <p slot="header" style="font-size: 18px;color: #7a8599;padding: 0 0 0 12px;margin-top: 5px;">
								上传素材
				</p>
				<Row style="margin-bottom: 20px;" offset="1">
	        <Col span="12" offset="1" style="width: 100%;">
	        <!-- 课件分类-->
		        		<RadioGroup v-model="filetype" @on-change="changeHost(filetype)">
		                <Radio label="1">直播素材</Radio>
		                <Radio label="2" style="margin-left: 90px;">录播素材</Radio>
		            </RadioGroup>
	        </Col>
	    	</Row>
	    	
	   	<p style="display: inline-block; margin:0 20px 0 20px;">选择上传文件：</p>
	      <Upload
	      	ref="upload"
	        name="uploadFile"
	        :action="host"
	        :data="filetype == '1' ? fileData : fiemData"
	        :max-size="102400"
          :accept="accept"
	        :format="format"
          :on-success="onsuccess"
	        :show-upload-list="false"
	        :on-format-error="handleFormatError"
	        :on-exceeded-size="handleMaxSize"
	        :headers="head"
	        @click.native="setHeader"
	        style="cursor:pointer;margin:0 0 30px 130px;"
	      >
	          <div  shape="circle" style="width: 140px; height: 140px;">
	        					 <img :src="mImg" alt="图片" style="width: 100%;height: 100%;"/>
	        					 <p style="text-align: center;">选择课件</p>
	          </div>
	      </Upload>
	      			<p style="color:#BDC2C8;">{{uploadTip}}</p>
        <template >
            <Progress v-for="(el,i) in uploadList" v-if="el.showProgress" :percent="el.percentage" :key="i"  hide-info></Progress>
        </template>
	  	<div slot="footer">
	    	<!-- 取消 -->
		    <!--<Button type="text" shape="circle" @click="isShaowdown = false">取消</Button>-->
		   	<!--  确认 -->
		    <!--<Button type="warning" shape="circle">{{$t('Common.sure')}}</Button>-->
		  </div>
		</Modal>
    <Modal
      v-model="DeleteModal"
      :closable="false">
      <div slot="header">{{$t('Common.kindlyreminder')}}</div>
      <!--  删除该文件后，会导致已经关联该文件的课程无法查看，您确定要删除吗？ -->
      {{$t('materialManagepage.remind3')}}
      <div slot="footer">
       <!--  确认 -->
        <Button type="primary" @click="deleteFile(deleteFileid)">{{$t('Common.sure')}}</Button>
        <!-- 取消 -->
        <Button @click="DeleteModal = false">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    <!-- 素材预览 -->
    <Modal
      v-model="viewModal"
      :closable="false"
      width="660">
      <div slot="header">素材预览</div>
      <div class="content">
        <iframe v-if="typeFlag == 1" :src="pathUrl" width="600" frameborder="no" border="0"></iframe>
        <div v-if="typeFlag == 2" class="img-wrap"><img :src="pathUrl" alt=""></div>
        <video v-if="typeFlag == 3" autoplay="autoplay" constrols="controls" width="600" muted="muted" loop="loop" :src="pathUrl">
          <!-- <source :src="pathUrl" type="video/ogg">
          <source :src="pathUrl" type="video/mp4"> -->
        </video>
        <audio v-if="typeFlag == 4" controls="controls" autoplay="autoplay" muted="muted" loop="loop" width="600" :src="pathUrl">
          <!-- <source :src="pathUrl" type="audio/mpeg">
          <source :src="pathUrl" type="audio/ogg"> -->
        </audio>
        <div v-if="typeFlag == 5" class="noData">该格式暂不支持预览！</div>
      </div>
      <div slot="footer">
        <!--  确认 -->
        <Button type="primary" @click="viewModal = false">确认</Button>
        <!-- 取消 -->
        <Button @click="viewModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/common/pagination";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Card, Input, Table, Button, Modal, Form, Row, Col, Upload,RadioGroup, Radio,FormItem,Progress} from "iview";
  import sha1 from "sha1";
  import md5 from "md5";
  import mediaFormat from '@/assets/js/mediaFomat.js';
  import uploadImg from './../assets/img/yun.png';

  export default {
    name: "",
    components: {
      panelTitle,
      Card,
      Input,
      'i-table': Table,
      pagination,
      Button,
      Modal,
      Form,
      Row,
      Col,
      Upload,
      RadioGroup,
      Radio,
      FormItem,
      Progress,
      mediaFormat
    },
    data() {
      return {
        //host: window.origin === 'http://127.0.0.1:8080' ? 'http://www.lemonedu.com/'+ Path.Uploadb : location.origin +'/'+Path.Uploadb, //直播
        // host1: window.origin === 'http://127.0.0.1:8080' ? 'http://www.lemonedu.com/'+ Path.Uploadbbb :location.origin +'/'+ Path.Uploadbbb,//录播
        host: window.origin === 'http://127.0.0.1:8080' ? 'http://www.lemonedu.com/'+ Path.Uploadb : location.origin +'/'+Path.Uploadb, //默认直播素材
        DeleteModal: false,
        uploadList:'', 
        format: mediaFormat.LiveBroadcast, //直播
        accept: "",
        mImg: uploadImg,
        isShaowdown:false,
        loading: true,
        total: 0, // 记录总条数
        display: 20, // 每页显示条数
        current: 1, // 当前的页数
        InputText: "",
        uploadTip: '（支持上传word、Excel、PPT、pdf、MP3、mp4、jpg等格式课件）',
        crumbs: [{
          name: "资源文件夹",
          route:{
            path:'/MaterialList',
            query:{
              from:'MainLessonManagement',
            }
          }
        }, {
            name:this.$route.query.filename,
            route:'',
        }],
        columns1: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            // 编号
            title: this.$t("arrangManage.serial"),
            align: "center",
            key: "fileid"
          },
          {
            // 课件名称
            title: this.$t("materialManagepage.Namecourseware"),
            align: "center",
            key: "showname"
          },
          {
            // 用途
            title: "用途",
            align: "center",
            key: "usetype"
          },
          {
            //大小(MB)
            title: this.$t("materialManagepage.size")+'(MB)',
            align: "center",
            key: "sizes"
          },
          {
            //添加时间
            title:  this.$t("materialManagepage.addtime"),
            align: "center",
            key: "addtimestr",
          },
          {
            //转换状态
            title:  "转换状态",
            align: "center",
            key: "previewpath",
            render: (h, params, index) => {
              let _val = "";
              if(params.row.previewpath == ""){
                _val = "失败"
              }else{
                _val = "成功"
              }
              return h('div', {  
                props: {
                  },
              }, _val)
            }
          },
          {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div', [
              h("span", {
                on: {
                  'click': (value) => {
                    this.openViewModal(params.row.fileid, params.row.previewpath, params.row.showname);
                  }
                },
                style: {
                  cursor: "pointer",
                  color: "#ffb932",
                  display: "inline-block",
                  marginRight: "15px"
                },
                attrs: {
                  class: "tablebtn"
                }
              }, '预览'),
              h("span", {
                on: {
                  'click': (value) => {
                    this.openModal(params.row.fileid);
                  }
                },
                style: {
                  cursor: "pointer",
                  color: "#f00",
                  display: "inline-block",
                },
                attrs: {
                  class: "tablebtn blue"
                }
              }, '删除')
            ])
          }
        }],
        data1: [],
        deleteFileid: 0,
        head:{
          sign:'',
          token:'',
          starttime:'',
        },
        filetype:'1',
        fiemData:{
        	allpathnode:[8,1,1],
        	filetype:'3',
        	fatherid:''
        },//普通课件上传时对象
        fileData:{
        	fatherid:''
        },//普通课件上传时对象
        viewModal: false,
        pathUrl: "",
        typeFlag: 0
      }
    },
    created() {
    	//console.log(this.host)
      this.fiemData.fatherid = this.$route.query.fileid;
      this.fileData.fatherid = this.$route.query.fileid;
      this.getMaterInfo(this.InputText, this.current, this.$route.query.fileid);
    },
		mounted () {
		    this.uploadList = this.$refs.upload.fileList;
		},
    methods: {
      changeHost(param) {
        if (param == 2) {
          //录播
          this.host = window.origin === 'http://127.0.0.1:8080' ? 'http://www.lemonedu.com/'+ Path.Uploadbbb :location.origin +'/'+ Path.Uploadbbb;
          this.format = mediaFormat.VideoBroadcasting;
          this.uploadTip = '（支持上传mp4、WebM、Ogg格式课件，大小不超过100M）';
          this.accept = "video/*";
        } else if (param == 1) {
          //直播
          this.host = window.origin === 'http://127.0.0.1:8080' ? 'http://www.lemonedu.com/'+ Path.Uploadb : location.origin +'/'+Path.Uploadb;
          this.format = mediaFormat.LiveBroadcast;
          this.uploadTip = '（支持上传word、Excel、PPT、pdf、MP3、mp4、jpg等格式课件）';
          this.accept = "";
        }
      },
      //设置请求头
      setHeader(){
        var token = JSON.parse(window.sessionStorage.getItem("userinfo")).token;
        //console.log('token', token);
        var starttime = Date.parse(new Date());
        this.head = {
          sign: this.getStr(
            this.fileData,
            window.sessionStorage.getItem("key"),
            starttime,
            token
          ),
          token: token,
          starttime:starttime,
        }
      },
      getStr(arr, key, starttime, token) {
        var tostr = arr ? this.getStrings(arr, "") : "";
        return md5(sha1(md5(tostr) + md5(key) + starttime) + token);
      },
      getStrings(data, str) {
        for (var x in data) {
          if (this.isJson(data[x])) {
            var i = 0;
            for (var k in data[x]) {
              i++;
              break;
            }
            if (i > 0) {
              str += x;
              str = this.getStrings(data[x], str);
            }
          } else if (this.isArray(data[x])) {
            if (data[x].length != 0) {
              str += x;
              str = this.getStrings(data[x], str);
            }
          } else {
            if (
              data[x] !== undefined &&
              data[x] !== null &&
              data[x] !== "" &&
              data[x] !== false &&
              data[x] !== true
            ) {
              str += x.toString() + data[x].toString();
            }
          }
        }
        return str;
      },
      getStrings111(data, str) {
        for (var x in data) {
          if (this.isJson(data[x]) || this.isArray(data[x])) {
            str += x;
            str = this.getStrings(data[x], str);
          } else {
            str += x + data[x];
          }
        }
        return str;
      },
      isArray(o) {
        return Object.prototype.toString.call(o) == "[object Array]";
      },
      isJson(obj) {
        return (
          typeof obj == "object" &&
          Object.prototype.toString.call(obj).toLowerCase() == "[object object]" &&
          !obj.length
        );
      },
      onsuccess(file) {
        if(file.code != 0 && file.code !=29007){
          //文件上传失败
          this.$Message.warning(this.$t("materialManagepage.remind4"));
        }else if(file.code === 29007){
          this.$Message.warning("上传的文件名不可以有下划线、空格等特殊字符");
        }else{
          //上传成功
          this.$Message.info(this.$t("materialManagepage.success"));
          this.getMaterInfo(this.InputText, this.current, this.$route.query.fileid);
          this.isShaowdown =false;
        }
      },
      handleFormatError (res,file) {
        //文件格式不正确
       this.$Message.warning(this.$t("materialManagepage.remind5"));
      },
      handleMaxSize (file) {
       //文件过大
        this.$Message.warning(this.$t("materialManagepage.remind6"));
      },
      pagechange: function (currentPage) {
        this.loading = true;
        this.current = currentPage;
        this.getMaterInfo(this.InputText, currentPage, this.$route.query.fileid);
        // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      },
      getMaterInfo(showname, page = 1, fileid) {
        Server.post(Path.getFileList, {
          'showname': showname, 'pagenum': page, 'fatherid': fileid
        }, (res) => {
          let {code, data} = res;
          if (code === 0) {
            this.data1 = [];
            let {pagenum, pagesize, total} = data.pageinfo;
            this.total = total;
            this.display = pagesize;
            // this.current = Number(pagenum);
            this.data1 = res.data.data;
            // let datas = data.data;
            // for (var i = 0; i < datas.length; i++) {
            //   var obj = {};
            //   obj.TypeName = datas[i].categoryname;
            //   obj.Level = datas[i].rankstr;
            //   obj.TypeNumber = datas[i].juniorcount;
            //   obj.Order = {sort:datas[i].sort,rank:datas[i].rank};
            //   obj.isShow = datas[i].status;
            //   obj.Make = datas[i].id;
            //   this.data1.push(obj);
            //   // console.log(this.data1);
            // }
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        })
      },
      //删除课件
      deleteFile(fileid) {
        Server.post(Path.deleteFile, {
          fileid: fileid,
          // teacherid: ''
        }, (res) => {
          if(res.code === 0) {
            this.$Message.success(res.info);
            this.getMaterInfo(this.InputText, this.current, this.$route.query.fileid);
            this.DeleteModal = false;
          }else{
            this.$Message.error(res.info);
          }
        });
      },
      openModal(deleteFileid) {
        this.deleteFileid = deleteFileid;
        this.DeleteModal = true;
      },
      //素材预览
      openViewModal(viewId, viewPath, viewType){
        this.viewModal = true;
        let _str = viewType;
        if(_str != ""){
          this.pathUrl = viewPath;
          //console.log("id： " + viewId + " path： " + viewPath + " type： " + viewType);
          let index =""
          index = _str.lastIndexOf("\.");
          _str = _str.substring(index+1, _str.length);
          //console.log("index: " + index + " str： " + _str);
          if(_str == "ppt" || _str == "pptx"){
            this.typeFlag = 1;
          }else if( _str == "png" || _str == "jpg" || _str == "gif" || _str == "jpeg" ){
            this.typeFlag = 2;
          }else if(_str == "mp4" || _str == "WebM" || _str == "MP4" || _str == "Ogg" || _str == "flv" || _str == "FLV" || _str == "swf" || _str == "SWF"){
            this.typeFlag = 3;
          }else if(_str == "mp3" || _str == "MP3"){
            this.typeFlag = 4;
          }else{
            this.typeFlag = 5;
          }
        }else{
          this.typeFlag = 5;
        }
      }
    },
  };
</script>
<style scoped lang='less'>
@import "~@/assets/less/tab.less";
.tip-box {
  padding: 11px 30px 11px 50px;
}
.tip-box ul li {
  line-height: 30px;
  position: relative;
  font-size: 14px;
  color: #525866;
  word-break: break-all;
}
.tip-box ul li::after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  // background-color: @color1;
  left: -20px;
  top: 11px;
  position: absolute;
}
.img-wrap{
  width: 100%;
  img{
    width: 100%;
    height: 100%;
  }
}
.noData{
  font-size: 16px;
  text-align: center;
}
</style>



