<template>
  <div id="EssayManagement">
    <panel-title :crumbs="crumbs"></panel-title>
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="head">
        <img :src="statisticsIcon">
        <span>作文统计</span>
        </div>
      <div class="statistics">
        <dl v-for="(i,index) in statistics" :key="index">
          <dt><img :src="i.img" alt=""></dt>
          <dd>
            <p>{{i.title}}</p>
            <p>{{i.num}}</p>
          </dd>
        </dl>
      </div>
    </Card>
    <div>
      <Tabs class="mytheme-tab"
            type="card"
            :animated="false"
            v-model="topBarIndex"
            @on-click="getMaterData(current, topBarIndex)">
        <TabPane name='0' label="未批阅"></TabPane>
        <TabPane name='1' label="已批阅"></TabPane>
      </Tabs>
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
          <span class="mytheme-input-label">学员姓名：</span>
          <Input
            class="input-normal-width input-max-height"
            placeholder="请输入学员姓名"
            v-model="nickname"
            @on-enter="getMaterData(current, topBarIndex)"
          />
          <Button
            class="mytheme-mg-left-70"
            type="primary"
             @click="getMaterData(current, topBarIndex)"
          >搜索
          </Button>
        </div>
         <Table
          class="mytheme-table noborder-table thead-max-height tbody-min-height mytheme-mg-top-30"
          :stripe="true"
          :disabled-hover="true"
          :columns="columns"
          no-data-text="暂无数据"
          :data="datas">
        </Table>
        <Pagination
          class="mytheme-mg-top-20"
          v-if="total > 0"
          :total="total"
          :current-page='current'
          :display="display"
          @pagechange="pagechange">
        </Pagination>
      </Card>
    </div>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import Pagination from '@/components/common/pagination';
  import statisticsIcon from './img/statistics.png';
  import job from './img/job@2x.png';
  import modify from './img/modify@2x.png';
  import noModify from './img/no-modify@2x.png';
  import {
    Card,
    Input,
    Button,
    Tabs,
    TabPane,
    Table
  } from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "",
    components: {
      panelTitle,
      Card,
      Input,
      Button,
      Tabs,
      TabPane,
      Table,
      Pagination,
    },
    watch: {
      topBarIndex(val){
        this.changeTableColumns(val)
        if(val==0){
           this.crumbs=[{name:'作文管理', route: '/EssayManagement'},{ name:'未批阅', route: '/EssayManagement'}]
        }else{
          this.crumbs=[{name:'作文管理', route: '/EssayManagement'},{ name:'已批阅', route: '/EssayManagement'}]
        }
      }
    },
    methods: {
      getCompositionStatistics(){//统计
         Server.post(Path.getCompositionStatistics, {}, res => {
           let {code,data,info}=res
           if(code==0){
             this.statistics[0].num=data.total;
             this.statistics[1].num=data.reviewed;
             this.statistics[2].num=data.total-data.reviewed;
           }else{
             this.$Message.error(info);
           }
         })
      },
        getTableColumns(index) {//自定义列表头
          let data = [];
          const tableData = {
            id: {
              key: 'id',
              title: '编号',
              align: 'center'
            },
            studentname: {
              key: 'nickname',
              title: '学员姓名',
              align: 'center'
            },
            thesisTitle: {
              key: 'title',
              title: '作文标题',
              align: 'center'
            },
            type: {
              key: 'type',
              title: '类型',
              align: 'center',
              render:(h,params)=>{
                let typeText=""
                if(params.row.type==1){
                  typeText="作文"
                }else{
                  typeText="日记"
                }
                return h('div',{},typeText)
              }
            },
            addtime: {
              key: 'addtime',
              title: '提交时间',
              align: 'center'
            },
            teacher: {
              title: '',
              key: 'pretechername',
              align: 'center',
              renderHeader:(h, params) => {
              let tableTitel = "上次批改人";
              if (index == 0) {
                tableTitel = "上次批改人";
              } else{
                tableTitel ="批改人";
              }
                return h('div',{},tableTitel);
            },
             render: (h, params) => {
               let teacher=''
                if (index == 0) {
                  teacher = params.row.pretechername;
                } else{
                  teacher =params.row.techername;
                }
                return h('div', {},teacher);
              }
            },
            lastTime: {
              title: '',
              key: 'prereviewtime',
              align: 'center',
              renderHeader:(h, params) => {
              let tableTitel = "上次批阅时间";
              if (index == 0) {
                tableTitel = "上次批阅时间";
              } else{
                tableTitel ="批阅时间";
              }
                return h('div',{},tableTitel);
            },
             render: (h, params) => {
               let time=''
                if (index == 0) {
                  time = params.row.prereviewtime;
                } else{
                  time =params.row.reviewtime;
                }
                return h('div', {},time);
              }
            },
            sorce: {
              title: '学生评分',
              key:'studentreviewscore',
              align: 'center'
            },
            view: {
              title: '操作',
              render: (h, params) => {
                return h('a', {
                  style:{
                    color: "#3F9CE8",
                    cursor:'pointer'
                  },
                  on: {
                    'click': () => {
                      this.$router.push({
                        path: '/EssayDetail',
                        query: {
                          from:"EssayManagement",
                          type: index,
                          compositionid:params.row.id
                          }
                      });
                    }
                  }
                }, '查看');
              },
              align: 'center'
            },
          }
          if (index ==0) {
            data.push(tableData.id, tableData.studentname, tableData.thesisTitle,tableData.type,tableData.addtime, tableData.teacher, tableData.lastTime,tableData.view);
          } else {
            data.push(tableData.id, tableData.studentname, tableData.thesisTitle,tableData.type,tableData.addtime, tableData.teacher, tableData.lastTime, tableData.sorce, tableData.view);
          }
          return data;
      },
      changeTableColumns(idx) {
        this.columns = this.getTableColumns(idx);
        this.nickname="";
        this.getMaterData(this.current, idx);
      },
      getMaterData(page = 1, status) {//获取作文列表
        this.datas = [];
        this.total = 0;
        this.display =20;
        this.current =1;
        Server.post(Path.getCompositionList, {
          status:status,
	    		nickname: this.nickname,
	    		pagenum: page
        }, res => {
          let {code,data,info} = res;
          if (code === 0 && data.data.length != 0) {
            this.datas=data.data;
            this.datas.forEach(el => {
              if (!el.prereviewtime) {
                el.prereviewtime="---"
              }
              if (!el.pretechername) {
                 el.pretechername="---"
              }
              if (el.studentreviewscore==null) {
                 el.studentreviewscore="---"
              }
            });
            this.total = data.pageinfo.total;
            this.display =data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }).catch(err => {
          throw err;
        })
      },
      /**
       * 分页请求
       * @param current 目标页码
       */
      pagechange(current) {
        this.current = current;
        this.getMaterData(current,this.topBarIndex);
      }
    },
    created() {
      this.getCompositionStatistics();
      this.changeTableColumns(0);
    },
    data() {
      return {
        statisticsIcon,
        nickname:'',//关键字 学生名称
        crumbs: [{
          name:'作文管理',
          route: '/EssayManagement'
        },{
          name:'未批阅',
          route: ''
        }],
        statistics:[{
            img:job,
            title:"提交总数",
            num:""
          },{
            img:modify,
            title:"已批阅数",
            num:""
          },{
            img:noModify,
            title:"未批阅数",
            num:""
          }],
        topBarIndex: 0,//tab默认值
        columns: [],
        datas:[],
        total: 0,
        display: 20,
        current: 1,
      };
    }
  };
</script>
<style scoped lang="less">
  .head{
    display: flex;
    align-items: center;
    padding-bottom:20px;
    border-bottom: solid 1px #E6E6E6; 
    img{
      width: 25px;
      height: 25px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
  .statistics{
    padding: 28px 0;
    display: flex;
    dl{
      display: flex;
      justify-content: center;
      width: 345px;
      dt{
        margin-right: 20px;
        width:48px;
        height:48px;
        img{
          width: 100%;
        }
      }
      dd{
        p:first-child{
          font-size:16px;
          color:#4D4D4D;
        }
        p:last-child{
         font-size:20px;
        color:#FFA400;
        }
      }
    }
    dl:nth-child(2){
       border: solid 1px #ccc;
       border-top: 0;
       border-bottom: 0;
    }
}
</style>



