<template>
  <div>
   <panel-title :crumbs="[
   {
        name:'作业管理',
        route:{path:'/homework',query:{from:'TeachingAffairs'}}
      },
      {
        name:SubTitle,
        route:{path:'/homework',query:{from:'TeachingAffairs'}}
      },
      {
        name:'查看详情',
        route:{path:'/'+$route.query.type,query:{from:'TeachingAffairs',classid:$route.query.classid,lessonid:$route.query.lessonid}}
      },
      {
        name:'作业详情',
        route:''
      },
    ]" />
    <Card dis-hover class="br8">
      <div class="testPaper">
        <h3>{{choiceQues.periodname}}</h3>
        <p>学生：{{choiceQues.studentname}}</p>
        <!--分数表
        <i-table size="small" border :columns="colData" :data="tabData" class="mg-bottom-20"></i-table>-->
        <table border="1" class="mg-bottom-20">
          <thead style="background-color: #f5faff;">
             <tr style="height:40px;">
              <th>题号</th>
              <th v-for="(val,key,index) in getQuesNum(choiceQues.subject)" :key="val.id">第{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}题</th>
              <th>总分</th>
             </tr>
          </thead>
          <tbody>
              <tr style="height:40px;">
                <td>分数</td>
                <td v-if="isRead" v-for="val in getQuesNum(choiceQues.subject)" :key="val.id">{{totalScore(val,2)}}</td>
                <td v-else></td>
                <td>{{isRead==false?'':totalSum(choiceQues.subject)}}</td>
             </tr>
          </tbody>
        </table>
       <!-- 试卷展示-->
         <div v-for='(item,k,index) in  getQuesNum(choiceQues.subject)' :key="item.id">
           <p>
              <span>{{index==0?'一':(index==1?'二':(index==2?'三':'四'))}}、</span>
              <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
              <span>({{totalScore(item,1)}}分)</span>
            </p>
            <dl v-for='(i,indexs) in item' :key="i.indexs" :class="((k==1||k==2)&i.answers!=i.correctanswer)?'red':''">
                <dt>{{indexs+1}}、{{i.name}}</dt>
                <dd  v-if="i.imageurl" >
                      <img :src="'http://'+img" v-for="img in transfer(i.imageurl)" :key="img.id" style="width:150px;height:100px;margin-right:15px;border:solid 1px #eee;" />
                </dd>
                <dd v-if="k==1||k==2">
                    <p v-for="option in i.options" :key="option.id">{{option}}</p>
                </dd>
                <!--<dd v-if="i.analysis">解析：{{i.analysis}}</dd>-->
                <dd>
                  <p v-if="i.analysis">解析：{{i.analysis}}</p>
                  <p v-if="i.answers">学生答案：{{i.answers}}</p>
                  <p v-if="i.correctanswer">正确答案：{{i.correctanswer}}</p>
                  <div class="grade" v-if="isRead">
                    <p v-if="i.teacherscore">老师评分：<span>{{i.teacherscore}}分</span></p>
                    <p v-if="i.comment">老师评语：<span>{{i.comment}}</span></p>
                  </div>
                </dd>
            </dl>
          </div>
        </div>
    </Card>
  </div>
</template>
<script>
import { Card,Button,Table} from "iview";
import panelTitle from "@/components/panelTitle";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "homeworkDetail",
  components: {panelTitle,Card,Button,"i-table":Table},
   data(){
    return {
          isRead:false,
          SubTitle:'未批阅',
          choiceQues:{
            courseid:'',
            periodid:'',
            periodname:'',
            subject:{"1":[],"2":[],"3":[],"4":[]}
        },
        status:'1'
    }
  },
  methods: {
     //查看作业
     viewHomework() {
        Server.post(Path.viewHomework,{
          classid:this.$route.query.classid,
          lessonid:this.$route.query.lessonid,
          studentid:this.$route.query.studentid,
          status:this.status
        },res => {
          if (res.code == 0) {
              this.choiceQues=res.data;
          }
        }
      );
    },
     //获取题号
     getQuesNum(obj){
         for( let k in obj){
           if(obj[k].length==0) {
             delete obj[k]
           }
         }
         return obj;
     },
     //计算满分
     totalScore(arr,type){
         let sum = 0;
         arr.forEach(item=>{
           if(type==1){
             sum += parseInt(item.score);
           }else {
              sum += parseInt(item.teacherscore);
           }
         })
         return sum;
     },
     //计算学生的最后得分
     totalSum(arr){
         let sum = 0;
         for (const key in arr) {
            arr[key].forEach(item=>{
                sum += parseInt(item.teacherscore);
          })
         }
         return sum;
     },
   transfer(img) {//图片格式的转换
          return img.split(',');
      },
  },
  created() {
      if(this.$route.query.type=='haveSH'){
          this.isRead=true;
          this.SubTitle='已批阅';
          this.status='2'
      }else{
        this.isRead==false;
        this.SubTitle='未批阅';
        this.status='1'
      };
    this.viewHomework()
  },
 mounted(){},
 computed:{},
 watch:{
 }
};
</script>
<style scoped lang='less'>
table{
  border-collapse:collapse;
  width:750px;
  text-align: center;
  font-size:16px;
}
.red{
  color:red;
}
.testPaper{
    width:750px;
    margin:0 auto;
    >h3{
        text-align:center;
        line-height:45px;
    }
    >p{
        text-align:right;
        line-height:40px; 
    }
    dt{
      text-indent:1rem;
      line-height:40px;
    }
    dd{
      text-indent:2rem;
      line-height:30px;
    }
    .grade{
      color:#888;
      font-size:16px;
      line-height:40px;
    } 
}
</style>



