<template>
<div>
  <panel-title :crumbs="[
    {
      name:'首页轮播图',
      route:{path:'/AdvertIndexList',query:{from:'MainBuyManagement'}}
    },
    {
      name:'设置推荐信息',
      route:''
    }
  ]" />
  <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="title">广告信息</div>
      <Form style="margin-top:50px;" ref="data" :model="data" :rules="ruleValidate" :label-width="110">
        <Row>
          <Col>
            <FormItem prop="remark" label="宣传语：">
                <Input placeholder="请输入内容，长度不超过20个汉字" :maxlength="20" v-model.trim="data.remark" style="width:560px;"></Input>
                <div class="font">*（对广告图片的描述，方便管理员管理广告，不在前台显示）</div>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col>
            <FormItem style="margin:50px 0;" label="广告图：">
                <Upload
                :data="uploadid"
                @success="getHeaderUrl">
                <div class="photo_box">
                  <img v-if="data.imagepath" :src="data.imagepath" alt="" >
                  <div class="icon_photo_box" v-else >
                    <!-- v-if="data.imagepath.length <= 0" -->
                    <img :src="uploadIcon" alt="" />
                    <p style="color:#999999">上传图片</p>
                  </div>
                </div>
                </Upload>
                <div class="font">*（图片尺寸为1920*450,为保证官网展示效果，图片大小不超过14兆）</div>
            </FormItem>
         </Col>
        </Row>
        <Row>
          <Col>
            <FormItem  prop="urltype" label="跳转至：">
                <Select v-model="data.urltype" style="width:200px;border:solid 1px #dddee1;">
                      <Option v-for='item in selects' :value="item.id" :key="item.id">{{item.name}}</Option>
                </Select>
            </FormItem>
          </Col>
      </Row>
        <Row v-if="data.urltype!=''">
          <Col>
          <FormItem  prop="url" label="">
                <Input v-if='data.urltype==3' placeholder="请输入跳转链接" v-model.trim="data.url" style="width:560px;"></Input>
                <p v-if="data.urltype!=3" @click="getList(current)"  style="font-size:16px;color:#469af8;width:76px;cursor:pointer;">
                  <span style="font-size:19px;">+</span><span v-if='data.urltype==1'>选择课程</span><span v-if='data.urltype==2'>选择老师</span>
                </p>
                <Tag v-if="data.urltype!=3&&jumpPage.length" closable @on-close="handleClose">{{jumpPage[0].coursename||jumpPage[0].teachername}}</Tag>
          </FormItem>
        </Col>
      </Row>
       <Row  class="mytheme-pd-bottom-30">
          <Col>
           <FormItem label="">
              <!-- <router-link :to="{path:'/AdvertIndexList',query:{from:'MainBuyManagement'}}">
                  <div class="btn custom-btn-cancel" style="margin-right: 20px;">取消</div>
              </router-link> -->
              <Button type="primary" @click="handleSubmit('data')" >提交</Button>
            </FormItem>
	       </Col>
        </Row>
      </Form>
    </Card>

    <!-- 选择模块 -->
   <Modal v-model="isShow" width=700 :closable="false">
	      <div slot="header">{{data.urltype==1?'课程素材':'老师列表'}}</div>
				<div style="margin-bottom:20px;">
	        <span style="margin-bottom:20px;color: #7a8599;">{{data.urltype==1?'课程名称':'老师名称'}}：</span>
	        <Input
          class="input-normal-width input-max-height"
	        v-model="InputText"
	        placeholder="请输入关键字"
          @on-enter="HomeCourseList(current)"/>
          <Button type="primary" class="mytheme-mg-left-70" @click="HomeCourseList(current)">搜索</Button>

        </div>
        <i-table class="mytheme-mg-top-30 mytheme-table noborder-table" stripe :columns="columns" :data="tabData" ></i-table>
        <div style="height:40px;">
            <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='total>0'></pagination>
        </div>
		  	<div slot="footer">
            <Button type="primary" @click="ok()" >确认</Button>
            <Button @click="cancel">取消</Button>
			  </div>
		</Modal> 
</div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import iconPhotoUpdate from "@/container/CouponsManagement/assets/images/icon_photo_update.png";
import {Card,Input,Button,Modal,Form,Row,Col,FormItem,Select,Option,Tag,Table} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import Upload from "@/components/UploadPicture";
import uploadIcon from '@/assets/images/icon_addfolder@2x.png';

export default {
  name: "",
  components: {
    panelTitle,Card,Input,Button,pagination, "i-table": Table,Modal,Form,Col,Row,FormItem,Upload,Select,Option,Tag
  },
  created() {
    let { id } = this.$route.query;
    if (!id) {
      this.path = Path.HomepageAdsUpload;
      //  this.$refs.data.resetFields()
    } else {
      this.path = Path.HomepageAdsEdit;
      this.GetHomepageAds();
    }
  },
  methods: {
    getHeaderUrl(url) {//获取图片上传成功的参数
      this.data.imagepath = url;
    },
    getList(page){
      this.InputText="";
      this.HomeCourseList(page);
    },
    HomeCourseList(page=1) {//获取课程或老师列表
      this.isShow = true;
      let { id } = this.$route.query;
      let partRouter = "";
      let obj = {};
      if (!id) {
        partRouter = Path.HomeCourseList;
        obj = {
          listtype: this.data.urltype,
          name: this.InputText,
          page_num: page
        };
      } else {
        partRouter = Path.HomeAdsExpress;
        obj = {
          expresstype: this.data.urltype,
          express:this.data.urltype == 1? this.data.courseid: (this.data.urltype == 2 ? this.data.teacherid : ""),
          pagenum: page,
          name: this.InputText
        };
      }
      Server.post(partRouter, obj, res => {
        let { code, data } = res;
        if (code === 0) {
          this.tabData = data.courselist;
          this.total = data.pageinfo.total;
          this.current = data.pageinfo.pagenum;
          this.display = Number(data.pageinfo.pagesize);
        } else {
          this.$Message.error(res.info);
          this.data = [];
        }
      });
      this.data.courseid = "";
      this.data.teacherid = "";
    },
    pagechange(currentPage) {
      //页面分页逻辑
      this.loading = true;
      this.current = currentPage;
      this.HomeCourseList(this.current);
    },
    // selectSuccess(selection) {//弹框复选框 选中数据  处理
    //   // let arr = [];
    //   // let str='';
    //   for( let i = 0;  i < selection.length; i++) {
    //       // str=(selection[i].id)||(selection[i].teacherid)
    //       // arr.push(str);
    //       this.jumpPage=selection[i];
    //   }
    //    if(this.data.urltype==1){
    //       this.data.courseid=this.jumpPage.id;
    //   }else if(this.data.urltype==2){
    //     this.data.teacherid=this.jumpPage.teacherid;
    //   }
    // },
    handleClose() {
      this.jumpPage = [];
      this.data.courseid = "";
      this.data.teacherid = "";
    },
    cancel(){
       this.isShow= false;
        if (this.data.urltype == 1) {
          this.jumpPage[0]=JSON.parse(localStorage.getItem("course"));
          this.data.courseid = this.jumpPage[0].id;
        } else if (this.data.urltype == 2) {
          this.jumpPage[0]=JSON.parse(localStorage.getItem("teacher"));
          this.data.teacherid = this.jumpPage[0].teacherid;
        }
    },
    ok(){
      this.jumpPage[0]=this.tabData[this.SelectIndex];
      if (this.jumpPage[0] == undefined||this.jumpPage[0] == null) {
        this.$Message.error("请选择要提交的参数");
        return false;
      }else{
        this.isShow= false;
        if (this.data.urltype == 1) {
          this.data.courseid = this.jumpPage[0].id;
        } else if (this.data.urltype == 2) {
          this.data.teacherid = this.jumpPage[0].teacherid;
        }

      }
    },
    GetHomepageAds() {//回显编辑数据
      Server.post(
        Path.GetHomepageAds,{
          adsid: this.$route.query.id
        },res => {
          let { code, data } = res;
          if (code === 0) {
            this.data = data.adsarray[0];
            let obj={}
             if(this.data.urltype==1){
                  obj.id=this.data.courseid
                  obj.coursename=this.$route.query.pageName
                  this.jumpPage[0]=obj;
                  localStorage.setItem("course",JSON.stringify(obj));
              }else if(this.data.urltype==2){
                obj.teacherid=this.data.teacherid
                obj.teachername=this.$route.query.pageName
                this.jumpPage[0]=obj;
                localStorage.setItem("teacher",JSON.stringify(obj));
              }else if(this.data.urltype==3){
                this.jumpPage=[]
              }
          } else {
            this.$Message.error(res.info);
            // this.data = [];
          }
        }
      );
    },
    handleSubmit(name) {//保存数据
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.data.imagepath == "") {
            this.$Message.error("上传图片不能为空");
            return false;
          }
          if (this.data.urltype == 1) {
            if (this.data.courseid == "") {
              this.$Message.error("请选择一门课程");
              return false;
            }
          }
          if (this.data.urltype == 2) {
            if (this.data.teacherid == "") {
              this.$Message.error("请选择一位老师");
              return false;
            }
          }
          Server.post(this.path,{
              ...this.data,
              adsid: this.$route.query.id
            },res => {
              let { code, data } = res;
              if (code === 0) {
                this.$Message.success(res.info);
                this.$router.go(-1);
                localStorage.removeItem("course");
                localStorage.removeItem("teacher");
              } else {
                this.$Message.error("1");
              }
            }
          );
        }
       })
    }
  },
  data() {
    const emptyRegex = (rule, value, callback) => {
     if (this.data.urltype == 3&&value === "") {
          callback(new Error("请填写相应的跳转路径"));
      }
      callback();
    };
    return {
      uploadIcon,
      input: "",
      iconPhotoUpdate,
      data: {
        remark: "",
        imagepath: "",
        urltype: "",
        teacherid: "",
        courseid: "",
        url: ""
      },
      ruleValidate: {
        remark: [{ required: true, message:"宣传语不能为空",trigger: 'blur' }],
        // imagepath: [{ required: true, message:"图片不能为空",trigger: 'change' }],
        urltype: [{ required: true,  message:"跳转类型不能为空"}],
        url: [{ required: true,  validator:emptyRegex,trigger: 'blur' }],
      },
      selects: [//下拉框数据
        {
          id: 1,
          name: "课程"
        },
        {
          id: 2,
          name: "老师"
        },
        {
          id: 3,
          name: "其它"
        }
      ],
      isShow: false, //控制弹框的显示与隐藏
      InputText: "", //弹框的关键字搜索
      path: "",
      uploadid: [2,JSON.parse(window.sessionStorage.getItem("userinfo")).organid,2],
      columns: [
        {
          title: "选中",
          width: 80,
          align: "center",
          key: "checkBox",
          render: (h, params) => {
            return h("div", [
              h("Checkbox", {
                props: {
                  value: params.row.checked==1?true:false
                },
                on: {
                  "on-change": e => {
                    //   this.$set(items, "checkBox", false);
                     this.tabData.forEach(item => {
                          item.checked= false;//先取消已选中对象的勾选，checkBox设置为false
                          return item;
                    });
                    this.tabData[params.index].checked = e;//再将勾选的对象的checkBox设置为true
                    this.SelectIndex=params.index;
                  }
                }
              })
            ]);
          }
        },
        // {
        //   title: "编号",
        //   align: "center",
        //   key: "id"
        // },
        {
          title: "",
          renderHeader:(h, params) => {
             let tableTitel = "";
            if (this.data.urltype == 1) {
              tableTitel = "课程列表";
            } else if (this.data.urltype == 2) {
              tableTitel ="老师列表";
            }
              return h('div',{},tableTitel);
          },
          align: "center",
          render: (h, params) => {
            let name = "";
            if (this.data.urltype == 1) {
              name = params.row.coursename;
            } else if (this.data.urltype == 2) {
              name = params.row.teachername;
            }
            return h("div", {}, name);
          }
        }
      ],
      tabData: [],
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      jumpPage: [], //展示选择的标签
      SelectIndex:'',
    };
  }
};
</script>
<style scoped>
.title{
  padding-bottom:20px;
  border-bottom: solid 1px #E6E6E6; 
  font-size:16px;
  font-family:MicrosoftYaHei-Bold;
  font-weight:bold;
  color:#666;
}
.font{
  font-size:12px;
  font-family:MicrosoftYaHei;
  color: #999999;;
  line-height:18px;
}
.btn {
  width: 120px;
  line-height: 40px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px auto;
}
.photo_box {
  width: 560px;
  height: 200px;
  text-align: center;
  border: dashed 1px #cccccc;
  /* background: rgba(238, 246, 245, 1);
  border-radius: 4px; */
  overflow: hidden;
  display: flex;
  align-items: center; 
  justify-content: center; 
  margin: 0 auto;

}
.photo_box > img {
  text-align: center;
  width: 100%;
  height: 100%;
}
.icon_photo_box {
  width: 100px;
  cursor: pointer;
}
.icon_photo_box img {
  width: 50%;
}
.icon_photo_box p {
  line-height: 18px;
}
#upload-picture {
  width: 560px;
  cursor: pointer;
}
</style>



