<template>
  <div id="Course">
    <panel-title :crumbs="crumbs" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <template>
	      <table class="tabInfo" >
	      		<tr>
	      			<th rowspan="2" style="font-size: 16px; color: #292c33;">购买课时</th>
	      			<th>可使用课程</th>
	      			<th>使用门槛</th>
	      			<th>课时</th>
	      			<!-- <th>状态</th> -->
	      			<th>有效期</th>
	      			<th>已购买</th>
	      			<th>已使用</th>
	      			<th>未使用</th>
	      			
	      		</tr>
	      		<tr>
	      			<td style="border-left:none;">{{classTime.bug.trialtype}}</td>
	      			<td>{{classTime.bug.threshold}}</td>
	      			<td>{{classTime.bug.bughour}}</td>
	      			<!-- <td>{{classTime.bug.overdue}}</td> -->
	      			<td>{{classTime.bug.efftype}}</td>
	      			<td>{{classTime.bug.bug}}</td>
	      			<td>{{classTime.bug.suse}}</td>
	      			<td>{{classTime.bug.nouse}}</td>
	      		</tr>
	      		<tr>
	      			<th rowspan="2" style="font-size: 16px; color: #292c33;">赠送课时</th>
	      			<th>可使用课程</th>
	      			<th>使用门槛</th>
	      			<th>课时</th>
	      			<!-- <th>状态</th> -->
	      			<th>有效期</th>
	      			<th>已购买</th>
	      			<th>已使用</th>
	      			<th>未使用</th>
	      		</tr>
	      		<tr>
	      			<td style="border-radius:0px ;border-left:none;">{{classTime.give.trialtype}}</td>
	      			<td>{{classTime.give.threshold}}</td> 
	      			<td>{{classTime.give.bughour}}</td> 
	      			<!-- <td>{{classTime.give.overdue}}</td> -->
	      			<td>{{classTime.give.efftype}}</td>
	      			<td>{{classTime.give.bug}}</td>
	      			<td>{{classTime.give.suse}}</td>
	      			<td>{{classTime.give.nouse}}</td>
	      		</tr>
	     	</table>
      </template>
    </Card>
    <template>
        <i-table :loading="loading" class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1"></i-table>
    </template>
    <template>
        <pagination :total="total" class="mg-top-20 " :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
    </template>



    <Modal v-model="isShowDelModal" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除套餐？</div>
      <div slot="footer">
        <Button type="primary" @click="deleteAdmin(deleteid)">确认</Button>
        <Button @click="isShowDelModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, Button, Modal } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    Button,
    Modal
  },
  created() {
    this.changeStatus(null, this.current);
    this.getclassTime();
  },
  methods: {
    stamp(times) {//时间转换器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
    changeStatus(usestatus, page = 1) {
      Server.post(
        Path.getdetailed,
        {
          usestatus: usestatus,
          setid: this.$route.query.id,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.loading = false;
            this.data1 = res.data.setdata;
            for (let items in this.data1) {
              let model = this.data1[items];
              model.bugtime = this.stamp(model.bugtime);
              if (model.usetime==null) {
                 model.usetime = '---';
              } else {
                model.usetime = this.stamp(model.usetime);
              }
              switch (model.ifuse) {
                case 0:
                  model.ifuse = "未使用 ";
                  break;
                case 1:
                  model.ifuse = "使用";
                  break;
                default:
                  model.ifuse = "已过期 ";
              }
            }
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    getclassTime() {
      let classid = this.$route.query.id;
      Server.post(
        Path.getClassTime,
        {
          setid: classid
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            data.bug?this.classTime.bug =data.bug[0]:this.classTime.bug =[];
            data.give?this.classTime.give =data.give[0]:this.classTime.give =[];
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    pagechange: function(currentPage) {
      this.current = currentPage;
      this.loading = true;
      this.getAdminList(currentPage);
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
    },
    deleteAdmin(id) {
      Server.post(
        Path.delAdminUser,
        {
          adminid: id
        },
        res => {
          if (res.code === 0) {
            this.$Message.success(res.info);
            this.getAdminList();
            this.isShowDelModal = false;
          }else{
             this.$Message.error(res.info);
          }
        }
      );
    }
  },
  data() {
    return {
      crumbs: [
        { name: "套餐列表", route: "/SetMealList?from=MainBuyManagement" },
        { name: "套餐明细", route: "" }
      ],
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      loading: true,
      isShowDelModal: false,
      SearchName: "",
      InputText: "",
      deleteid: "",
      usestatus: "",
      classTime: {
        bug: [],
        give: []
      },
      columns1: [
        {
          title: "序号",
          type: "index",
          align: "center",
          key: "id"
        },
        {
          title: "学生姓名",
          align: "center",
          key: "username"
        },
        {
          title: "学生手机号码",
          align: "center",
          key: "mobile"
        },
        {
          title: "购买时间",
          align: "center",
          key: "bugtime"
        },
        {
          title: "当前状态",
          align: "center",
          key: "ifuse"
        },
        {
          title: "使用时间",
          align: "center",
          key: "usetime"
        }
      ],
      data1: []
    };
  }
};
</script>
<style scoped>
@import "~assets/common.css"
.mg-bottom-20 {
  border-radius: 8px;
}
.mg-search {
  font-size: 18px;
  color: #7a8599;
  padding: 0 0 0 14px;
}
.mg-query {
  float: right;
  width: 106px;
}
.br21 {
  border-radius: 21px;
}
.custom-btn-cancel {
  display: inline-block;
  padding: 0 34px;
  height: 38px;
  line-height: 38px;
  border-radius: 21px;
  cursor: pointer;
  text-align: center;
  font-size: 16px;
  border: none;
}
.tabInfo {
  width: 100%;
  height: 100%;
  border-collapse: collapse;
}
/*.tabInfo  tr th,.tabInfo tr th {
border: 1px solid rgb(233, 234, 236);
padding: 5px;
}*/
.tabInfo tr th,
.tabInfo tr td {
  border-right: 1px solid #d4d8da;
  border-bottom: 1px solid #d4d8da;
  padding: 5px;
}
.tabInfo {
  font-size: 14px;
  text-align: center;
  margin: 0 auto;
  border-collapse: separate;
  border-spacing: 0;
  border-top: 1px solid rgb(233, 234, 236);
  border-radius: 8px;
}
.tabInfo tr th,
.tabInfo tr td {
  border-right: 1px solid rgb(233, 234, 236);
  border-bottom: 1px solid rgb(233, 234, 236);
}

.tabInfo tr th:first-child,
.tabInfo tr td:first-child {
  border-left: 1px solid rgb(233, 234, 236);
}
.tabInfo tr th {
  background-color: rgba(247, 247, 247, 1);
  font-size: 16px;
  height: 54px;
  color: #292c33;
  font-weight: 400;
}
.tabInfo tr td {
  font-size: 14px;
  color: #525866;
  height: 48px;
}
/* .tabInfo tr:first-child th:first-child {
    border-top-left-radius: 8px;
}

.tabInfo tr:first-child th:last-child {
    border-top-right-radius: 8px;
}

.tabInfo tr:last-child td:first-child {
    border-bottom-left-radius: 8px;
}

.tabInfo tr:last-child td:last-child {
    border-bottom-right-radius: 8px;
} */
</style>



