import Vue from "vue";
import Vuex from "vuex";
import Server from '@/service/request';
import Path from '@/service/Path';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    status: -1,
    headimg: 0,
    istipbox:0
  },
  mutations: {
    setStatus(state,status) {
      state.status = status;
    },
    setTip(state, t) {
      state.istipbox = t;
    },
    uploadHeadimg(state, d) {
      state.headimg = d;
    },
  },
  actions:{
    /*getStatus({commit,dispatch},obj){
      Server.post(Path.getAuditResByOrganId,{
        organid:obj.id
      },res=>{
        let {code,data} = res;
        if(code === 0){
          // commit('setStatus', Number(data.auditstatus));
          // -1 初始值 3 是成功进入下一步 4 审核成功但没有购买套餐 5
          if (Number(data.auditstatus) == 3 || Number(data.auditstatus) == 4) {
            dispatch('isBuyOrder', obj);

          }else{
            commit('setStatus', Number(data.auditstatus));
          }

        }
      })
    },*/
    isBuyOrder({commit,dispatch},obj){
      Server.post(Path.checkOrganValidTime,{
        organid: obj.id
      },res=>{
        let {code,data} = res;
        if(code === 0){
          if (obj.newtype == '-1'){
            commit('setStatus', 3); //已续费  成功
            setTimeout(e => {
              commit('setStatus', -1);
            }, 1000);
          }else{
            dispatch('checkClass', obj.newtype);
          }
        }else{
          commit('setStatus',4); //没续费
        }
      })
    },
    checkClass({commit},newtype) { // 1 添加课程检查分类 2 开课检查课程"
      Server.post(Path.checkClass, {
        type: newtype
      }, (res) => {
        if (res.code === 0) {
          commit('setStatus', 3); //已续费  成功
          setTimeout(e => {
            commit('setStatus', -1);
          }, 1000);
        }else{
          commit('setTip', 1);
        }
      });
    },
  }
})

export default {
  store
};



