<template>
  <div>
    <panel-title :crumbs="[{name:'知识配置',route:''}]" />
    
      <div class="form">
          <Card dis-hover style="width:804px;margin-right:31px;">
            <Row style="border-bottom:solid 1px #E6E6E6;padding-bottom:10px;">
              <div class="fl head">
                <img :src="backgroundImage">
                <span>背景图</span>
              </div>
              <router-link :to="{name:'ConfigAllImg',query:{from:'TeachingAffairs'}}">
                <Button type="primary" class='fr'>查看全部</Button>
              </router-link>
            </Row>
            <!-- <FormItem label="背景图:"> -->
              <div style="display:flex;padding-top:30px;">
                <div v-if="imgBgArr.length">
                  <div class="demo-upload-list" v-for="i in threeImgs(imgBgArr)" :key='i.id'>
                    <img :src="'http://'+i.imageurl">
                    <div class="demo-upload-list-cover">
                        <Icon class="del" type="ios-trash-outline" @click.native="handleRemove(i.id)"></Icon>
                    </div>
                  </div>
                  <!-- <img :src="'http://'+i.imageurl" v-for="i in threeImgs(imgBgArr)" :key='i.id'  style="width:100px;height:150px;margin-right:40px;" alt=""> -->
                </div>
                <div class="addImg" span="10" style="width:80px;height:80px;margin:14px 0 0 40px;">
                  <Upload 
                    class="cursorChange"
                    :action="imgDate"
                    ref="upload"
                    name="uploadFile"
                    :data="{allpathnode:[5,1,1],filetype:'4'}"
                    :show-upload-list="false"
                    :on-success="handleSuccessBg">
                    <!-- font-size:60px;font-weight:100; -->
                    <img :src="uploadIcon" style="width:100%;height:100%;">
                    <!-- <p style="color:#fff;">+</p> -->
                  </Upload>
                  <span>上传图片</span>
                </div>
              </div>
            <!-- </FormItem> -->
         </Card>
        <Card dis-hover style="width:804px;">
        <!--二维码上传-->
        <Row style="border-bottom:solid 1px #E6E6E6;padding-bottom:20px;">
           <div class="fl head">
              <img :src="twoCode">
              <span>二维码</span>
            </div>
          <!-- <Button type="primary" class='fr'>下载</Button> -->
        </Row>
            <!-- <FormItem label="二维码:"> -->
            <div class="addImg" span="10" style="height:120px;margin:44px 0 34px 110px;">
              <Upload
                  class="cursorChange"
                  :action="imgDate"
                  ref="upload"
                  name="uploadFile"
                  :data="{allpathnode:[10,1,1],filetype:'5'}"
                  :show-upload-list="false"
                  :on-success="handleSuccessCode">
                <!-- <p style="color:#fff;">添加</p> -->
                 <img v-if="codeImg==''" :src="uploadIcon" style="width:100%;height:100%;">
                <img v-else :src="'http://'+codeImg" style="width:120px;height:120px;" alt="">
              </Upload>
            </div>
            <!-- </FormItem> -->
        </Card>
      </div>

     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <!--添加知识类型列表-->
      <div class="mytheme-mg-top-20" style="display:flex;justify-content: space-between;padding-bottom:10px;border-bottom:solid 1px #E6E6E6">
           <div class="head">
              <img :src="knowledgeType">
               <span>知识类型</span>
            </div>
          <Button type="primary" @click="typeOperate('addType','')">添  加</Button>
       </div>
        <!--年级题库列表-->
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :loading=false :columns="colData" :data="tabData"></i-table>
        <!--分页-->
        <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange"></pagination>
    </Card>


    <!--编辑标签模态框-->
    <Modal
      v-model="isShowEditModal"
      :closable="false">
        <div slot="header">{{LessonTypeText}}</div>
        <Form class="mg-top-20" ref="formInline"  :model="formInline" :rules="ruleValidate">
          <Row>
            <Col span="20" offset="2">
              <!-- 联系邮箱： 请输入内容-->
              <FormItem label="类型名称" prop="cateName" :label-width="100">
                <Input :placeholder="typePlaceHolder" :maxlength="10"  v-model.trim="formInline.cateName"  />
              </FormItem>
            </Col>
          </Row>
        </Form>
      <!--</div>-->
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="ok('formInline')">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button @click="cancel"> {{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    <!--删除列表项莫泰框-->
    <Modal
      v-model="deleteItemModal"
     :closable="false">
        <div slot="header">温馨提示</div>
        <p> 删除该类型后，该类型下的小知识也将删除，您确定要删除吗？</p>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="okDelete">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button @click="deleteItemModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import { Card,Col, Row, Button,FormItem,Form,Table,Modal,Input,Upload,Icon} from "iview";
import pagination from '@/components/common/pagination';
import backgroundImage from './img/background-image@2x.png';
import twoCode from './img/two-code@2x.png';
import knowledgeType from './img/knowledge-type@2x.png';
import uploadIcon from '@/assets/images/icon_addfolder@2x.png';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {Card,Col, Row, Button,FormItem,Form,'i-table':Table,Modal,Input,panelTitle,Upload,Icon,pagination},
   data(){
     const EmptyRegex = (rule, value, callback) => {
       if (value === '') {
         callback(new Error('内容不能为空'));
       } else {
         callback();
       }
     };
    return {
      backgroundImage,twoCode,knowledgeType,
      imgDate: window.location.origin === 'http://127.0.0.1:8080' ? "http://www.lemonedu.com/teacher/Uploadfile/Upload" : "/teacher/Uploadfile/Upload",
      host: (window.origin === 'http://192.168.1.206:8080' || 'http://192.168.1.206:8081') ? 'http://www.lemonedu.com/teacher/Uploadfile/Upload' : '/teacher/Uploadfile/Upload',
     uploadIcon,
     LessonTypeText:'新增分类', //模态框标题
      typePlaceHolder:'请输入类型名称，长度不超过10个汉字',
      isShowEditModal:false,    //添加类型莫泰框默认关闭
      deleteItemModal:false,    //删除列表子项模态框默认关闭
      formInline:{
        cateName:'',
      },
      deleteId:null,   //记录删除的子项ID
      ruleValidate:{
        cateName:  [  { required: true,validator:EmptyRegex } ]
      },
      colData:[
        {title: '编号', key: 'id', align: 'center'},
        {title: '类型名称', key: 'name', align: 'center'},
        {title: '创建时间', key: 'addtime', align: 'center'},
        {
          title: '操作',
          render: (h, params, index) => {
            return h("div", [
              h("a",{
                style: {
                  color: "#3F9CE8",
                  display: "inline-block",
                  marginRight: "35px"
                },
                on: {
                  'click': () => {
                    // console.log(params);
                    // 记录操作ID
                    this.deleteId = params.row.id;
                    this.formInline.cateName = params.row.name;
                    // rows信息里的类型名称
                    let typeName = params.row.typeName;
                    this.typeOperate('modify',typeName);
                  }
                }
              },"编辑"),
              h("a",{
                style: { color: "#ED3825", display: "inline-block"},
                on: {
                  'click': () => {
                    // 记录删除ID
                    this.deleteId = params.row.id;
                    // 打开删除确认模态框
                    this.deleteItemModal = true;
                  }
                }
              },"删除") //删除
            ]);
          },
          align: "center"
        }
      ],
     tabData:[],
     total:0,
     display:20,
     current:1,
     imgBgArr:[],
    codeImg:'',
    }
  },
  methods: {
    // 图片的成功回调
    handleSuccessBg (res) {
        // let {code, data} = res;
        // this.imgBgArr.unshift(data);
        this.getSigninbgiList () 
    },
    // 图片局部展示
    threeImgs(arr) {
        return arr.slice(0,3);
    },
     //签到背景图列表
    getSigninbgiList () {
       Server.post(Path.getSigninbgiList,{},
        res => {
            if (res.code === 0) {
                this.imgBgArr = res.data.data;
            }else{
              this.imgBgArr=[]
            }
        }
        )
    },
    // //删除签到背景图
    handleRemove (id) {
         Server.post(Path.deleteSignInBgi,{
           id:id
         },
        res => {
            if (res.code === 0) {
                 this.$Message.success(res.info);
                 this.getSigninbgiList()
            }
        }
        )
    },
    // 二维码的成功回调
    handleSuccessCode(res){
      if (res.code === 0) {
        this.getQrList()
      }
    },
    //获取二维码图片
    getQrList () {
        Server.post(Path.getQrList,{},res => {
          if (res.code === 0) {
                this.codeImg=res.data[0].imageurl;
          }else{
            this.$Message.console.erro(res.info);
            this.codeImg="";
          }
        })
        // this.codeImg=data;
    },
    typeOperate(type,typeName){// 打开添加类别模态框
      // 判断模态框类别
      if(type=='modify'){
        this.LessonTypeText = '编辑';
        this.typePlaceHolder = typeName;
      }else{
        this.LessonTypeText = '新增分类';
        this.typePlaceHolder = '请输入类型名称，长度不超过10个汉字';
        this.formInline.cateName = '';//初始化名称
      }
      this.isShowEditModal = true;
    },
    ok(name){
      this.$refs[name].validate((valid) => {
        if (valid) {
          // 判断是哪个模块框
          if(this.LessonTypeText=='编辑'){
            Server.post(Path.updateKnowledgeType,{
                id: this.deleteId,
                name: this.formInline.cateName
              },
              res => {
                if (res.code === 0) {
                  this.getTableInfo(this.current);
                  this.$Message.success(res.info);
                }
              }
            )

          }else{
            Server.post(Path.addKnowledgeType,{
              name: this.formInline.cateName,
              },
              res => {
                if (res.code === 0) {
                  this.getTableInfo(this.current);
                  this.$Message.success(res.info);
                }
              }
            )
          }
          // 关闭模块框
          this.isShowEditModal = false;

        }
      })
    },
    cancel(){ // 关闭模块框
      this.isShowEditModal = false;
    },
    okDelete(){// 删除列表子项
       Server.post(Path.deleteKnowledgeType,{
        id:this.deleteId,
        },
        res => {
          if (res.code ===0) {
            this.$Message.success(res.info);
            this.deleteItemModal = false;
            this.getTableInfo(this.current);
          }
        }
      );
    },
    getTableInfo(current=1) {//知识类型列表
      Server.post(Path.getKnowledgeTypeList,{
        name: '',
	    	pagenum:current
        },
        res => {
          if (res.code === 0||res.code ===70100) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
     //分页
    pagechange(currentPage) {
     this.current=currentPage;
     this.getTableInfo(this.current);
  },
  },
  created() {
    this.getSigninbgiList () ;
    this.getTableInfo(this.current);
    this.getQrList();
  },
 mounted(){},
 computed:{},
 watch:{
   isShowEditModal(val){
      if(val==false){
        this.$refs.formInline.resetFields()
      }
    }
 }
};
</script>
<style scoped lang='less'>
 .head{
    display: flex;
    align-items: center;
    // padding-bottom:20px;
    // border-bottom: solid 1px #E6E6E6; 
    img{
      width: 18px;
      height: 18px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
  .form{
    display: flex;
    margin-bottom:30px;
  }
  span{
    font-size: 14px;
    color: #7A8599;
    padding: 0 0 0 14px;
  }
  .addImg{
    width:120px;
    // background:gray;
  }
  .cursorChange{
    width: 100%;
    height: 100%;
    margin-bottom: 15px;
    p{
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .cursorChange:hover{
    cursor:Pointer
  }
  //背景图片
  .demo-upload-list{
        display: inline-block;
        width: 100px;
        height: 150px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        // border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        // box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 20px;
        margin-bottom: 20px;
    }
    .demo-upload-list img{
        width: 100%;
        height: 100%;
    }
    .demo-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .demo-upload-list:hover .demo-upload-list-cover{
        display: block;
    }
    .demo-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
    .del{
      line-height: 150px;
    }
</style>




