// Vue.component('msg-modal',MsgModal);
if (
  sessionStorage.getItem("userinfo") == null || sessionStorage.getItem("userinfo") == "" ||
  sessionStorage.getItem("key") == null || sessionStorage.getItem("key") == ""
) {
  sessionStorage.removeItem('userinfo');
  sessionStorage.removeItem('key');
  window.localStorage.removeItem("menuData");

  //ie兼容
  if (window["context"] == undefined) {
    if (!window.location.origin) {
      location.origin = window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
    }
    window["context"] = location.origin + "/V6.0";
  }
  // window.location.href = location.origin + "/admin/login/login.html";
  // window.location.href = location.origin + "/#/login";
}

// 自动设置语言
const navLang = (navigator.language || navigator.browserLanguage).toLowerCase();
const lang = navLang.indexOf('zh') != -1 ? 'zh-cn' : 'en-us';
const i18n = new vueI18n({
  locale: lang,
  messages: {
    'en-us': en,
    'zh-cn': zh
  }
})

/* eslint-disable no-new */
new Vue({
  el: "#app",
  i18n,
  router,
  store,
  components: { App },
  template: "<App />"
});



