<template>
  <div id="teacher-manage">
    <!--老师管理-->
    <panel-title :crumbs="[{name:$t('teacherManagepage.teacherManagepage'), route:''},]"/>
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20" style="position: relative;">
         <!--按老师姓名查询-->
        <span>老师姓名{{$t('symbol.colon')}}</span>
        <Input
           placeholder="请输入老师姓名"
          class="input-normal-width input-max-height"
          v-model.trim="searchNickName"
          @on-enter="getTeacherList(current)"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getTeacherList(current)">搜索</Button>

         <!--录入/导入老师-->
          <div class="butGroup">
            <Button type="primary" class="mytheme-mg-right-50" @click="cancel">{{$t('teacherManagepage.addteacher')}}</Button>
            <Upload 
              style="display: inline-block;"
              name="uploadFile"
              :action="upload.Path"
              :data='upload.data'
              :format="upload.format"
              :on-success="onsuccess"
              :headers="head"
              @click.native="setHeader(upload.data)"
              >
              <Button type="warning" >导入老师</Button>
            </Upload>
          </div>
      </div>

      <!-- 老师列表 -->
      <Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :columns="colData" :data="tabData"></Table>
   
      <!-- 分页 -->
      <pagination :total="total" :currentPage="current" :display="display" @pagechange="pageChange" class="mg-top-20" v-if='total>0'></pagination>
    </Card>
    
    <!-- 删除老师的弹框 -->
    <Modal v-model="isShowDeleteModal" :closable="false">
      <!--温馨提示-->
      <div slot="header">{{$t('Common.kindlyreminder')}}</div>
      <div> {{delTeacherText}} </div>
      <div slot="footer">
        <div v-if="!isShowSendTip">
          <!--确认-->
          <Button type="primary" @click="isDeleteTeacher">{{$t('Common.sure')}}</Button>
          <!--取消-->
          <Button @click="isShowDeleteModal = false">{{$t('Common.cancel')}}</Button>
        </div>
         <!--已发送 知道了-->
        <Button v-if="isShowSendTip" type="warning" @click="isShowDeleteModal = false">{{$t('Common.know')}}</Button>
      </div>
    </Modal>

    <!--添加/编辑老师弹框-->
    <Modal v-model="isShowEnterMobile" :closable="false">
      <div slot="header">{{addTeacherText}}</div>
        <Form :label-width="120" ref="addTeacherInfo" :model="addTeacherInfo" :rules="ruleValidate">
          <FormItem prop="roleidArr" label="老师权限：">
            <CheckboxGroup v-model='addTeacherInfo.roleidArr' @on-change="handleCheck">
                <Checkbox  v-for="(item,index) in radioCheck" :key="index"  :label="item.label">
                    {{item.name}}
                </Checkbox>
            </CheckboxGroup>
          </FormItem>
          <FormItem prop="teacherName" label="老师姓名：">
              <Input type="text" placeholder="请输入老师姓名，长度不超过15个字" :maxlength="15" v-model.trim="addTeacherInfo.teacherName" size="large" style="width:280px;"/>
          </FormItem>
          <FormItem prop="mobile" label="手机号码：">
            <div class="login-area">
              <AreaCode editCode ='86' @selectValue = 'selectValue' class='login-selectValue'></AreaCode>
              <!-- padding-left: 84px!important; -->
              <Input type="text" style="width:280px;" :maxlength="11" placeholder="请输入手机号" v-model.trim="addTeacherInfo.mobile" size="large"/>
            </div>
          </FormItem>
          <FormItem prop="school" label="所在学校：">
              <Input type="text" placeholder="请输入学校名称，长度不超过15个字" :maxlength="15" v-model.trim="addTeacherInfo.school" size="large" style="width:280px;"/>
          </FormItem>
           <FormItem prop="grade" label="所在年级：">
              <Input type="text" placeholder="请输入年级，长度不超过15个字" :maxlength="15" v-model.trim="addTeacherInfo.grade" size="large" style="width:280px;"/>
          </FormItem>
           <FormItem prop="class" label="所在班级：">
              <Input type="text" placeholder="请输入班级，长度不超过15个字" :maxlength="15" v-model.trim="addTeacherInfo.class" size="large" style="width:280px;"/>
          </FormItem>
          <FormItem v-if="notPass" prop="password" label="登录密码：">
              <Input type="password" placeholder="密码为6~16位数字、字母、下划线" :maxlength="16" v-model.trim="addTeacherInfo.password" size="large" style="width:280px;"/>
          </FormItem>
          <FormItem v-if="notPass" prop="rePassword" label="确认密码：">
              <Input type="password" placeholder="请再次输入密码" :maxlength="16" v-model.trim="addTeacherInfo.rePassword" size="large" style="width:280px;"/>
          </FormItem>
        </Form>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="addTeacher('addTeacherInfo')">确认</Button>
        <!--取消-->
        <Button @click="handleReset('editTch',isShowEnterMobile=false)">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>

    <!-- 查看/重置密码 -->
    <Modal v-model="isShowResetPassword" :closable="false">
      <div slot="header">{{editPassText}}</div>
        <Form :label-width="120" ref="editTch" :model="editTch" :rules="resetRule">
          <FormItem label="老师权限：">
            <CheckboxGroup v-model='addTeacherInfo.roleidArr' disabled>
               <!-- @on-change="handleCheck" -->
                <Checkbox disabled v-for="(item,index) in radioCheck" :key="index"  :label="item.label">
                    {{item.name}}
                </Checkbox>
            </CheckboxGroup>
          </FormItem>
          <FormItem label="老师姓名：">{{editTch.nickname}}</FormItem>
          <FormItem label="手机号码：">{{editTch.mobile}}</FormItem>
          <FormItem label="所在学校：">{{editTch.school}}</FormItem>
          <FormItem label="所在年级：">{{editTch.grade}}</FormItem>
          <FormItem label="所在班级：">{{editTch.class}}</FormItem>
          <FormItem v-if="notPass" prop="resetPassword" label="登录密码：">
              <Input type="password" placeholder="密码为6~16位数字、字母、下划线" :maxlength="16" v-model.trim="editTch.resetPassword" size="large" style="width:280px;"/>
          </FormItem>
          <FormItem v-if="notPass" prop="resetPasswords" label="确认密码：">
              <Input type="password" placeholder="请再次输入密码" :maxlength="16" v-model.trim="editTch.resetPasswords" size="large" style="width:280px;"/>
          </FormItem>
        </Form>
      <div slot="footer">
        <!--重置密码 确认 -->
        <Button type="primary" @click="editTeacher('editTch')">{{$t('Common.sure')}}</Button>
        <!--重置密码 取消-->
        <Button  @click="handleReset('editTch',isShowResetPassword=false)">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import requestPath from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Button,
  Modal,
  Row,
  Col,
  Form,
  FormItem,
  CheckboxGroup,
  Checkbox,
  Upload
} from "iview";
import AreaCode from "@/components/areaCode";
import "@/assets/areaCode.css";

import Clipboard from "clipboard";
let clipboard = new Clipboard(".copyBtn");
export default {
  name: "teacher-manage",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    Table,
    Button,
    Modal,
    Row,
    Col,
    Form,
    FormItem,
    CheckboxGroup,
    Checkbox,
    Upload,
    AreaCode
  },
  data() {
    //非空验证
    const emptyRegex = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("内容不能为空"));
      }
      callback();
    };
    //老师权限
    // const roleRegex = (rule, value, callback) => {
    //   if (this.addTeacherInfo.roleid === "") {
    //     callback(new Error("内容不能为空"));
    //   }
    //   callback();
    // };
    //手机号正则
    const mobileRegex = (rule, value, callback) => {
      let regex = /^1[3|5|6|7|8|9][0-9]{9}$/;
      if (value === "") {
        callback(new Error("内容不能为空"));
      } else if (!regex.test(value)) {
        callback(new Error("请输入正确的手机号"));
      } else {
        callback();
      }
    };
    //密码正则
    const passwordRegex = (rule, value, callback) => {
      let regex = /^\w{6,16}$/;
      if (this.notPass == false) {
        callback();
      }
      if (value === "") {
        callback(new Error("内容不能为空"));
      } else if (!regex.test(value)) {
        callback(new Error("密码只可以是数字、字母或者下划线，6-16位"));
      } else {
        callback();
      }
    };
    //重复密码正则
    const rePasswordRegex = (rule, value, callback) => {
      if (this.notPass == false) {
        callback();
      }
      if (value === "") {
        callback(new Error("内容不能为空"));
      } else if (value != this.addTeacherInfo.password) {
        callback(new Error("两次密码不一致!"));
      } else {
        callback();
      }
    };
    //修改密码重复正则
    const sameRegex = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("内容不能为空"));
      } else if (value != this.editTch.resetPassword) {
        callback(new Error("两次密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      upload: {
        //导入文件的参数
        Path:
          window.location.origin === "http://127.0.0.1:8080"
            ? "http://www.lemonedu.com/admin/Importdata/Import"
            : "/admin/Importdata/Import",
        data: { importtype: 3 },
        format: ["xlsx", "xls", "xlsm", "xltx", "xltm", "xlsb", "xlam"]
      },
      head:{
        sign:'',
        token:'',
        starttime:'',
      },
      searchNickName: "", //昵称查询关键字
      colData: [
        // {
        //   type: "selection",
        //   width: 60,
        //   align: "center"
        // },//全选框
        {
          title: this.$t("teacherManagepage.teacherid"),
          key: "teacherid",
          align: "center"
        }, //老师ID
        {
          title: "老师姓名",
          key: "nickname",
          align: "center"
        }, //老师姓名
        {
          title: "手机号码",
          key: "mobile",
          render: (h, params) => {
            return h(
              "div",
              {},
              "+" + params.row.prphone + " " + params.row.mobile
            ); //查看
          },
          align: "center"
        }, //手机号码
        //{title: this.$t('teacherManagepage.loginAcount'), key: 'teachername', align: 'center'},//登录帐号
        { title: "添加时间", key: "addtime", align: "center" }, //添加时间
        {
          title: this.$t("studentManagepage.Accountactivationstatus"),
          key: "accountstatus",
          align: "center", //账户启用状态
          render: (h, params) => {
            return h("i-switch", {
              props: {
                value: params.row.accountstatus === 0
              },
              on: {
                "on-change": () => {
                  this.switchStatus(
                    params.row.teacherid,
                    params.row.accountstatus
                  );
                }
              }
            });
          }
        },
        {
          title: this.$t("Common.mark"), //操作
          render: (h, params) => {
            return h("div", [
              h(
                "a",
                {
                  style: {
                    color: "#3F9CE8",
                    display: "inline-block",
                    marginRight: "20px"
                  },
                  on: {
                    click: () => {
                      Server.post(
                        requestPath.getTeacherMsg,
                        {
                          teachid: params.row.teacherid
                        },
                        res => {
                          let { data, code } = res;
                          if (code == 0) {
                            this.isShowResetPassword = true;
                            this.notPass = false;
                            this.editPassText = "查看老师详情";
                            this.addTeacherInfo.roleidArr = data.roleid.split(
                              ","
                            );
                            this.editTch.nickname = data.baseinfo.nickname;
                            this.editTch.mobile = data.baseinfo.mobile;
                            this.editTch.school = data.baseinfo.school;
                            this.editTch.grade = data.baseinfo.grade;
                            this.editTch.class = data.baseinfo.class;
                          }
                        }
                      );
                    }
                  }
                },
                "查看"
              ), //查看
              h(
                "a",
                {
                  style: {
                    color: "#FFA80D",
                    display: "inline-block",
                    marginRight: "20px"
                  },
                  on: {
                    click: () => {
                      this.deleteTeachId = params.row.teacherid;
                      Server.post(
                        requestPath.getTeacherMsg,
                        {
                          teachid: this.deleteTeachId
                        },
                        res => {
                          let { data, code } = res;
                          if (code == 0) {
                            this.isShowEnterMobile = true;
                            this.notPass = false;
                            this.addTeacherText = "编辑老师信息";
                            this.restTeacherId = data.baseinfo.teacherid;
                            window.localStorage.setItem('roleid',data.roleid);
                            this.addTeacherInfo.roleidArr = data.roleid.split(",");
                            this.addTeacherInfo.teacherName =data.baseinfo.nickname;
                            this.addTeacherInfo.mobile = data.baseinfo.mobile;
                            this.addTeacherInfo.school = data.baseinfo.school;
                            this.addTeacherInfo.grade = data.baseinfo.grade;
                            this.addTeacherInfo.class = data.baseinfo.class;
                          }
                        }
                      );
                    }
                  }
                },
                "编辑"
              ), //编辑
              h(
                "a",
                {
                  style: {
                    color: "#FFA80D",
                    display: "inline-block",
                    marginRight: "20px"
                  },
                  on: {
                    click: () => {
                      Server.post(
                        requestPath.getTeacherMsg,
                        {
                          teachid: params.row.teacherid
                        },
                        res => {
                          let { data, code } = res;
                          if (code == 0) {
                            this.isShowResetPassword = true;
                            this.notPass = true;
                            this.editPassText = "重置密码";
                            this.editTch.resetPassword = "";
                            this.editTch.resetPasswords = "";
                            this.restTeacherId = data.baseinfo.teacherid;
                            this.addTeacherInfo.roleidArr = data.roleid.split(
                              ","
                            );
                            this.editTch.nickname = data.baseinfo.nickname;
                            this.editTch.mobile = data.baseinfo.mobile;
                            this.editTch.school = data.baseinfo.school;
                            this.editTch.grade = data.baseinfo.grade;
                            this.editTch.class = data.baseinfo.class;
                          }
                        }
                      );
                    }
                  }
                },
                "重置密码"
              ), //重置密码
              h(
                "a",
                {
                  style: { color: "#ED3825", display: "inline-block" },
                  on: {
                    click: () => {
                      this.isShowDeleteModal = true;
                      this.deleteTeachId = params.row.teacherid;
                      Server.post(
                        requestPath.checkTeacherHaveCourse,
                        {
                          teacherid: this.deleteTeachId
                        },
                        res => {
                          if (res.code == 0) {
                            this.isShowSendTip = false;
                            this.delTeacherText = "您确定删除该老师吗？";
                          } else {
                            this.isShowSendTip = true;
                            this.delTeacherText = res.info;
                          }
                        }
                      );
                    }
                  }
                },
                "删除"
              ) //删除
            ]);
          },
          align: "center"
        }
      ],
      tabData: [],
      current: 1,
      total: 0,
      display: 20,

      deleteTeachId: "",
      isShowDeleteModal: false, //删除老师Modal
      // isSHowAddTchModal: false, //添加老师Modal
      isShowEnterMobile: false, //添加老师弹框
      isShowSendTip: false, //已添加老师Modal

      radioCheck: [{ label: "1", name: "课程" }, { label: "2", name: " 作文" }],
      roleid: [], //多选项
      areaCode: 86, //区号
      addTeacherInfo: {
        //添加老师信息
        roleidArr: [], //多选项
        roleid: "",
        teacherName: "",
        mobile: "",
        school: "",
        grade: "",
        class: "",
        password: "",
        rePassword: ""
      },
      ruleValidate: {
        //添加老师的正则
        roleidArr: [
          {
            required: true,
            type: "array",
            min: 1,
            max: 2,
            message: "请选择老师权限"
          }
        ],
        teacherName: [
          { required: true, validator: emptyRegex, trigger: "blur" }
        ],
        mobile: [{ required: true, validator: mobileRegex, trigger: "blur" }],
        school: [{ validator: emptyRegex, trigger: "blur" }],
        grade: [{ validator: emptyRegex, trigger: "blur" }],
        class: [{ validator: emptyRegex, trigger: "blur" }],
        password: [
          { required: true, validator: passwordRegex, trigger: "blur" }
        ],
        rePassword: [
          { required: true, validator: rePasswordRegex, trigger: "blur" }
        ]
      },
      resetRule: {
        //修改密码的正则
        resetPassword: [
          { required: true, validator: passwordRegex, trigger: "blur" }
        ],
        resetPasswords: [
          { required: true, validator: sameRegex, trigger: "blur" }
        ]
      },
      isShowResetPassword: false, //重置密码bol
      restTeacherId: "",
      editTch: {
        //重置密码的回显信息
        nickname: "",
        mobile: ""
      },
      delTeacherText: "",
      notPass: true, //是否显示修改密码
      addTeacherText: "添加老师",
      editPassText: "重置密码"
    };
  },
  created() {
    this.getTeacherList(this.current);
  },
  watch: {
    isShowEnterMobile(val) {
      if (val == false) {
        this.$refs.addTeacherInfo.resetFields();
      }
    }
  },
  computed: {},
  methods: {
    //设置请求头
    setHeader(data) {
      this.head = this.$Utils.setHeader(data);
    },
    onsuccess(res) {
      //导入成功的回调
      let { code, data, info } = res;
      if (code == 0) {
        this.getTeacherList(this.current);
        let text = "成功导入" + data.num + "位老师信息";
        this.$Message.success(text);
      } else if (code == 40502) {
        this.$Message.warning(info);
      }
    },
    // 搜索老师列表
    getTeacherList(current = 1) {
      Server.post(
        requestPath.ugetTeacherList,
        {
          nickname: this.searchNickName,
          pagenum: current
        },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          } else {
            this.tabData = [];
          }
        }
      );
    },
    //选择手机区号
    selectValue(val) {
      this.areaCode = val;
    },
    handleCheck(nData) {
      //多选题时获取所有选中的选项
      if (nData == null || nData == "") {
        this.addTeacherInfo.roleid = "";
      } else {
        this.addTeacherInfo.roleid = nData.join();
      }
    },
    // 添加老师
    addTeacher(name) {
      let host =
        window.location.origin === "http://127.0.0.1:8080"
          ? "http://www.lemonedu.com"
          : window.location.host;
      let addPth = requestPath.addTeacherMsg;
      let obj = {};
      if (this.notPass == true) {
        addPth = requestPath.addTeacherMsg;
        obj = {
          roleid: this.addTeacherInfo.roleid,
          nickname: this.addTeacherInfo.teacherName,
          prphone: this.areaCode,
          mobile: this.addTeacherInfo.mobile,
          school: this.addTeacherInfo.school,
          grade: this.addTeacherInfo.grade,
          class: this.addTeacherInfo.class,
          password: this.addTeacherInfo.password,
          repassword: this.addTeacherInfo.rePassword
        };
      } else {
        addPth = requestPath.updateTeacherMsg;
        if (this.addTeacherInfo.roleid=="") {
          this.addTeacherInfo.roleid=window.localStorage.getItem('roleid');
        }
        obj = {
          teacherid: this.deleteTeachId,
          roleid: this.addTeacherInfo.roleid,
          nickname: this.addTeacherInfo.teacherName,
          prphone: this.areaCode,
          mobile: this.addTeacherInfo.mobile,
          school: this.addTeacherInfo.school,
          grade: this.addTeacherInfo.grade,
          class: this.addTeacherInfo.class
        };
      }
      this.$refs[name].validate(valid => {
        if (valid) {
          Server.post(addPth, obj, res => {
            let { code, info } = res;
            if (code === 0) {
              //添加老师成功
              this.$Message.success(res.info);
              this.getTeacherList(this.current);
              window.localStorage.removeItem('roleid');
            } else if (code === 40100) {
              this.$Message.error(info);
            }
            this.isShowEnterMobile = false;
          }).catch(err => {
            throw err;
          });
          this.$refs[name].resetFields();
        }
      });
    },
    handleReset(name, bol) {
      //取消按钮
      this.$refs[name].resetFields();
      bol;
    },
    // 删除老师
    isDeleteTeacher() {
      Server.post(
        requestPath.deleteTeacher,
        {
          teacherid: this.deleteTeachId
        },
        res => {
          if (res.code == 0) {
            this.$Message.info(res.info);
            this.getTeacherList(this.current);
          }
        }
      );
      this.isShowDeleteModal = false;
    },
    // 更改老师账户启用状态
    switchStatus(id, status) {
      Server.post(
        requestPath.switchTeachStatus,
        {
          teacherid: id,
          dataflag: status ? 0 : 1
        },
        res => {
          this.$Message.info(res.info);
          this.getTeacherList(this.current);
        }
      );
    },
    // 编辑老师密码
    editTeacher(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          Server.post(
            requestPath.editTeachPass,
            {
              teachid: this.restTeacherId,
              password: this.editTch.resetPassword,
              reppassword: this.editTch.resetPasswords
            },
            res => {
              this.$Message.success(res.info);
            }
          );
          this.$refs[name].resetFields();
        }
      });
      this.isShowResetPassword = false;
      this.restTeacherId = "";
      this.editTch.resetPassword = "";
      this.editTch.resetPasswords = "";
    },

    pageChange(index) {
      this.current = index;
      this.getTeacherList(this.current);
    },
    cancel() {
      this.isShowEnterMobile = true;
      this.notPass = true;
      this.addTeacherText = "添加老师";

      this.addTeacherInfo = {
        teacherName: "",
        mobile: "",
        password: "",
        rePassword: ""
      };
    }
  }
};
</script>

<style scoped lang='less'>
.butGroup {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: flex-start;
}
.form-group {
  //外层盒子
  width: 300px;
  height: 44px;
  line-height: 44px;
  position: relative;
  display: inline-block;
}

.login-selectValue {
  display: inline-block;
}
.ivu-select-single .ivu-select-selection {
  height: 40px !important;
}
.addteacher-modal {
  text-align: center;
  .addteacher-title {
    font-size: 18px;
    color: #292c33;
  }
  .addteacher-text {
    padding-top: 12px;
    font-size: 14px;
    color: #93959b;
  }
  .addteacher-sharecode {
    position: relative;
    margin: 26px auto 26px auto;
    width: 112px;
    height: 112px;
    border: 1px solid #dbdbdb;
    outline: none;
    img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      width: 98px;
      height: 98px;
    }
  }
  .addteacher-footer {
    .footer-left {
      vertical-align: top;
      display: inline-block;
      padding: 0 16px;
      margin-right: 12px;
      max-width: 288px;
      height: 40px;
      line-height: 40px;
      background: #f5f7fd;
      color: #292c33;
      text-align: left;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .copyBtn {
      background: #fdb44d;
      border: none;
      color: #fff;
      border-radius: 2px;
      box-shadow: 0 0 10px rgba(210, 132, 21, 0.49);
    }
  }
}

.type-content .ivu-row {
  margin-bottom: 20px;
}

.test {
  display: none;
}

.selectMobile {
  display: inline-block;
  width: 87px;
  height: 32px;
  line-height: 1.5;
  padding: 4px 7px;
  font-size: 12px;
  border: 1px solid #dddee1;
  border-radius: 4px;
  color: #495060;
  background-color: #fff;
  background-image: none;
  position: relative;
}

.ivu-modal-body .ivu-form-item {
  margin-bottom: 24px;
}
</style>



