<template>
		  <div id="has-order-list  " >
			      <Card  dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
			        <div class="mytheme-mg-top-20" >
			          	<!--学生姓名-->
			          	 <span>学生姓名：</span>
			            <!--输入学生-->
			           	 <Input v-model="SearchName" class="input-normal-width input-max-height" placeholder="输入学生姓名" @on-enter="getlist(SearchName,current,times)"/>       
			            <!--时间-->
			            <span class="mytheme-mg-left-70">起始时间：</span>
			            <!--选择日期-->
			            <DatePicker
                  size="large"
			              type="daterange"
			              :editable="false"
			             	format="yyyy-MM-dd"
			              :placeholder="$t('withdrawalcheckCenterpage.choosetime')"
			              @on-change="timeChange">
			            </DatePicker>	
				          <Button type="primary" class="mytheme-mg-left-70" @click="getlist(SearchName,current,times)">{{$t('withdrawalcheckCenterpage.search')}}</Button>
								<!--<div style="width: 100%;height: 40px;margin-bottom: 10px;"></div>  class="crdv"-->
				        </div>
			         <Table :loading="loading" :columns="colData" :data="tabData" class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe style="width: 100%;"></Table>
			         <pagination :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
			      </Card>
	  </div>
</template>

<script>
import { Table, Card, Input, Icon, Modal , Form, FormItem, Col, Row , Button, DatePicker} from "iview";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
	SearchName: '',
  name: "has-order-list",
  components: {
    Button,
    Table,
    Card,
    Input,
    pagination,
    Icon,
    Server,
    Modal,
    Form,
    FormItem,
    Col,
    Row,
    DatePicker
  },
  data() {
    return {
      isShowAddModal:false,
      colData: [
        { title: '编号', key: "id", align: "center" },//编号
        { title: this.$t('orderManagepage.student'), key: "studentname", align: "center" },//学生
        { title: this.$t('courseManagepage.coursetitle'), key: "coursename", align: "center" }, //课程名称
        { title: '班级名称', key: "classname", align: "center" }, //班级名称
        { title: this.$t('orderManagepage.reference'), key: "ordernum", align: "center" }, //订单编号
        { title: '订单金额', key: "amount", align: "center" }, //订单金额
        { title: '付款时间', key: "paytime", align: "center" }, //付款时间
        { title: '收货地址', key: "address", align: "center" }, //收货地址
        {
          title: '操作', //订单状态
          key: "orderstatus",
          align: "center",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
              props:{
                  to:`/OrderDetails?id=${params.row.id}&num=${params.row.ordernum}&from=MainOrderManagement`
              }

                ,
                style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'查看')
            ])
          }
        }
      ],
      tabData: [],
      SearchName:'',
      tips: '---',
      times:'',
      loading: true,
      current: 1,//当前页数
      total: 0,//记录总条数
      display: 20//每页显示条数
    };
  },
  created() {
//  this.getOrderList();
		this.getlist(this.SearchName, this.current, this.times);
  },
  methods: {
    getlist(name='',page=1, item) {
    	let  id  = this.$route.query.id;
    	Server.post( Path.getAccountCount,{
          'id':id,
          'pagenum':page,
          'studentname':name,
         	'intime':item
        },
        res => {
  					this.tabData=[];
          let { code, data } = res;
	          if (code === 0) {
								let datas =data.data;
								for ( let i = 0 ; i < datas.length; i++  ){
									let obj = {};
									obj.id = datas[i].id;//编号
									obj.studentname = datas[i].studentname;//学生姓名
									obj.coursename = datas[i].coursename;//课程名称
									obj.classname = datas[i].classname == null ? this.tips : datas[i].classname;//班级名称
									obj.ordernum = datas[i].ordernum;//订单编号
									obj.amount ='￥'+ datas[i].amount;//订单金额
									obj.paytime = datas[i].paytime;//付款时间
									obj.address = datas[i].address == null ? this.tips : datas[i].address;//收货地址
									this.tabData.push(obj);
								}
			          this.total = data.pageinfo.total;
			          this.display = data.pageinfo.pagesize;
	          }
	           this.loading = false;
        }
      );
   },
   timeChange (time) {
   		 this.times = time.join('~');
   		 this.times == '~' ? this.times='' : null
   },
    // 查询订单列表
//  getOrderList(page = 1, type = 1) {
//    this.loading = true;
//    Server.post(
//      Path.getAccount,
//      {
//        pagenum: page,
//        type,
//      },
//      res => {
//        let { code, data } = res;
//        this.tabData = [];
//        if (code === 0) {
//          var datas = data.data;
//          for (var i = 0; i < datas.length; i++) {
//            var obj = {};
//            obj.ordernum = datas[i].out_trade_no;
//            obj.coursename = datas[i].coursename;
//            obj.teachername = datas[i].teachername;
//            obj.ordertime = datas[i].addtimestr;
//            obj.studentname = datas[i].studentname;
//            obj.amount = datas[i].paynum;
//            obj.paytype = datas[i].paytype || this.$t('Common.NO'); //暂无
//            obj.orderstatus = datas[i].orderstatus;
//            this.tabData.push(obj);
//          }
//          this.current = Number(data.pageinfo.pagenum);
//          this.total = data.pageinfo.total;
//          this.display = data.pageinfo.pagesize;
//        }
//        this.loading = false;
//      }
//    );
//  },
    // 筛选
    Screen(index) {
      this.activeItem = index;
      this.getOrderList(1, index ? 0 : 1);
    },
    // 查看详情
    showDetails(id) {
      this.$router.push({
        path: "/OrderDetails?from=MainOrderManagement",
        query: { orderid: id }
      });
    },
    // 分页请求
    pageChange(currentPage) {
    	 this.current = currentPage;
    	       this.loading = true;
      this.getlist(this.SearchName,currentPage,this.times);

    }
  },
  computed: {}
};
</script>

<style scoped>
	.fl-font{
		font-size: 18px;
    color: #7a8599;
	}
	.ivu-table th{
		border-top:1px solid #e6e6e6 ;
	}
</style>



