<template>
  <div>
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <div slot="title" class="title">
            <!-- 课时信息 -->
            学生信息
            <Button type="primary" class='fr' @click="pushMsgModel" >推送消息</Button>
            <!-- <Button type="primary" class='fr h-search-btn' >发送短信</Button> -->
        </div>
        <template>
          <i-table border ref="selection" :columns="columns" :data="datalist.data" @on-selection-change="checkedIdArr"></i-table>
        </template>
      </Card>
    </template>
    <Modal
      v-model="isShowMsgModal"
      :closable="false" >
      <div slot="header">{{LessonTypeText}}</div>
      <div>
        <Form  ref="formInline"  :label-width="120" :model="formInline" :rules="ruleValidate">
          <!--推送标题-->
          <Row  >
            <Col>
              <FormItem prop="title" label="推送标题：">
                <Input v-model="formInline.title" placeholder="请输入推送标题,不能超过14个字" :maxlength="14" ></Input>
              </FormItem>
            </Col>
          </Row>
          <!--知识内容-->
          <Row >
            <Col >
              <FormItem prop="content" label="推送内容：" >
                <Input  v-model="formInline.content" type="textarea" :rows="6" :maxlength="50" placeholder="请推送内容，不能超过50个字"></Input>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="pushMsg('formInline')">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button  @click="resetSel">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import {Card, Button, Table, Modal, Row, Col, FormItem, Form, Input} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "classStudent",
  components: {
    panelTitle,
    Card,
    Button,
    "i-table": Table,
    Modal,
    Row,
    Col,
    FormItem,
    Form,
    Input
  },
  props:[
    'datalist'
  ],
  data() {
    return {
      LessonTypeText: "推送消息",
      checkedIds: [],
      isShowMsgModal: false,
      formInline:{
        title:'',
        content:''
      },
      classifyOne:[],
      ruleValidate:{  //验证
        title:  [{ required: true, message: '请输入推送标题', trigger: 'blur' }],
        content:  [{ required: true, message: '请输入推送内容', trigger: 'blur'} ]
      },
      columns: [
      //   {
      //   width: 50,
      //   render: (h, params) => {
      //     return h('Checkbox', {
      //       props: {
      //         value: params.row.ischeck
      //       },
      //       on: {
      //         'on-change': () => {
      //           this.checkedIds = [];
      //           this.datalist.data[params.index].ischeck = !this.datalist.data[params.index].ischeck;
      //           params.row.ischeck = true;
      //           this.datalist.data.forEach(el => {
      //             if (el.ischeck) {
      //               this.checkedIds.push(el.studentid);
      //               //console.log(this.checkedIds)
      //             }
      //           });
      //         }
      //       }
      //     })
      //   },
      //   title: ' ',
      // }
      {
        type: 'selection',
        width: 60,
        align: 'center'
      },{
        //编号
        title: "序号",
        align: "center",
        key: "studentid"
      }, {
      //学生姓名
        title: "学生姓名",
        align: "center",
        key: "studentname"
      }, {
        //学生手机号
        title: "学生手机号",
        align: "center",
        key: "mobile",
        render: (h, params) => {
            return h('div', {}, '+' + params.row.prphone+' '+params.row.mobile)
          }
      }]
    }
  },
  created() {
   
  },
  methods: {
    //推送消息
    pushMsgModel() {
      if (this.checkedIds.length > 0) {
         this.isShowMsgModal = true;
      } else {
        this.$Message.error('请选择要推送消息的学生');
      }
    },
    //取消
    resetSel() {
      this.isShowMsgModal = false;
      this.checkedIds = [];
      // this.datalist.data.forEach(el => {
      //   el.ischeck = false;
      // });
      this.$refs.selection.selectAll(false);
    },
    //获取学生选中id
    checkedIdArr(item) {
      let arr = [];
      item.forEach((el, i) => {
        arr.push(el.studentid);
      })
      this.checkedIds = arr;
    },
    //推送消息确定
    pushMsg(name) {
      this.$refs[name].validate((valid) => {
        if(valid){
          //console.log(111);
          Server.post(Path.sendMessage, {
            id: this.$route.query.id,
            type: 1,
            ids: this.checkedIds,
            title: this.formInline.title,
            content: this.formInline.content
          }, res => {
            if(res.code === 0){
              this.$Message.info(res.info);
              this.isShowMsgModal = false;
              // this.datalist.data.forEach(el => {
              //   this.$set(el, 'ischeck', false);
              // })
              this.formInline.title = '';
              this.formInline.content = '';
              this.checkedIds = [];
              this.$refs.selection.selectAll(false);
            }else{
              this.$Message.error(res.info);
              this.isShowMsgModal = false;
            }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import "~@/assets/less/tab.less";
.title {
  color: #5e6266;
  font-size: 18px;
  font-weight: normal;
  overflow: hidden;
}
</style>



