<template>
  <div id="EditLesson">
    <panel-title :crumbs="crumbs"></panel-title>
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
          <!--基本信息-->
          {{$t('courseManagepage.basicinformation')}}
        </p>
        <Form :label-width="150" class='form-confrol mg-top-20' ref="formValidate" :model="formData" :rules="ruleValidate">
          <Row>
            <!--班级名称-->
            <Col span="20" offset="2">
              <FormItem label="班级名称：" prop='gradeName'>
                 <i-input v-model.trim="formData.gradeName"  placeholder="班级名称长度不超过20个汉字"  autocomplete="off" :maxlength="20" style="width: 300px"/>
              </FormItem>
            </Col>
            <!--选择课程-->
            <Col span="20" offset="2">
              <FormItem label="选择课程：" prop='LessonName'>
                 <i-select v-model="formData.LessonName" size="large" style="width: 300px; border: 1px solid #CFDCE7; border-radius: 4px">
                    <i-option
                      style="width: 300px"
                      v-for="item in courselist"
                      :value="item.id"
                      :key = "item.id"
                    >{{item.coursename}}</i-option>
                </i-select>
              </FormItem>
            </Col>
            <!--课程价格-->
            <Col span="20" offset="2">
              <FormItem label="课程价格:" prop='courseprice'>
                 <i-input v-model.trim="formData.courseprice"  placeholder="请输入课程价格"  autocomplete="off" :maxlength="20" style="width: 300px"/> 元
              </FormItem>
            </Col>
            <!--班级人数上限-->
            <Col span="20" offset="2">
              <FormItem label="班级人数上限:" prop='limitnum'>
                 <i-input v-model.trim="formData.limitnum"  placeholder="请输入班级人数上限"  autocomplete="off" :maxlength="20" style="width: 300px"/> 人
              </FormItem>
            </Col>
          </Row>
        </Form>
        <div class="btnbox">
          <!-- 下一步 -->
          <!-- <next-btn  @click.native="saveData('formValidate')">下一步</next-btn> -->
          <Button type="primary"  @click.native="saveData('formValidate')">下一步</Button>
        </div>
      </Card>
    </template>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import NextBtn from "@/components/NextBtn/NextBtn";
  import { Card, Input , Row, Col, Form, FormItem, Select , Option, Button } from "iview";
  import Server from "@/service/request";
  import Path from "@/service/Path";
  export default {
    name: "EditClassInfo",
    components: {
      panelTitle,
      Card,
      'i-input':Input,
      NextBtn,
      Row,
      Col,
      Form,
      FormItem,
      "i-select":Select ,
      "i-option": Option,
      Button
    },
    data() {
      const PriceCourse = (rule, value, callback) => {//价格正则
        let PriceRegex = /^[0-9]+(.[0-9]+)?$/;
        if (value === '') {
          callback(new Error('课程价格不能为空'));
        } else if (!PriceRegex.test(value)) {
          callback(new Error('请输入正确课程价格'))
        } else {
          callback();
        }
      };
      const Limit = (rule, value, callback) => {//人数正则
        let limitRegex = /^[1-9][0-9]*$/;
        if (value === '') {
          callback(new Error('请输入班级人数上限！'));
        } else if (!limitRegex.test(value)) {
          callback(new Error('请输入正确班级人数！'))
        } else {
          callback();
        }
      };
      return {
        crumbs: [{
          name:'班级管理',
          route:'/ClassManagement?from=MainLessonManagement'
        }, {
          name: "开班编辑",
          route: ''
        }],
        formData: {
          gradeName: '',//班级名称
          LessonName: '',//课程名称
          courseprice: '',//课程价格
          limitnum: '',//班级人数上限
        },
        ruleValidate: {
          gradeName: [
            {required: true, message: "班级名称不能为空", trigger: 'blur' } 
          ],
          LessonName: [
            {required: true, message: "课程名称不能为空！"}
          ],
          courseprice: [
            {required: true, validator: PriceCourse, trigger: 'blur' }
          ],
          limitnum: [
            {required: true, validator: Limit, trigger: 'blur' }
          ]
          
        },
        courselist: [],
        curriculumid: '',
        periodnum:''
      }
    },
    created() {
      this.getCurricukum();
      this.isHaveId();
      this.curriculumid = this.$route.query.curriculumid;
    },
    methods: {
      //课程列表 
      getCurricukum() {
        Server.post(Path.getCurricukum, {
          coursename: '',
          pagenum: 1
        }, (res) => {
          if(res.code == 0){
            this.courselist = res.data.data;
          }else {
            this.courselist = []
          }
        })
      },
      //开班编辑数据
      isHaveId() {
        let {id, curriculumid} = this.$route.query;
        if(id) {
          let { gradeName, LessonName, courseprice, limitnum ,teacherid, formData } = this;
          Server.post(Path.getSchedulingInfo, {
            id: this.$route.query.id,
            curriculumid: this.$route.query.curriculumid,
            type: 2
          }, res => {
            if( res.code == 0){
              this.formData.gradeName = res.data.gradename;
              this.formData.LessonName = res.data.curriculumid;
              this.formData.courseprice = res.data.price;
              this.formData.limitnum = res.data.fullpeople;
              this.teacherid = res.data.teacherid;
              var inperiod = res.data.list;
              this.periodnum = res.data.periodnum;
              //console.log(0, JSON.stringify(res.data));
           
            if(inperiod){
              window.localStorage.setItem('inperiod',JSON.stringify(inperiod));
            }
            window.localStorage.setItem('LessonData',JSON.stringify({
              id: res.data.id,
              teacherid: res.data.teacherid,
              periodnum: res.data.periodnum
            }))
            }else {
              this.$Message.error(res.info);
            }
          })
        }else{
          window.localStorage.removeItem('inperiod');
          window.localStorage.removeItem('LessonData');
        }
      },
      //下一步，保存数据
      saveData(name){
        this.$refs[name].validate((valid) => {
          if (valid) {
            let { id } = this.$route.query;
            let { gradeName, LessonName, courseprice, limitnum } = this.formData;
            Server.post(Path.addOneSchedu, {
              'gradename': gradeName,
              'price': courseprice,
              'fullpeople': limitnum,
              'curriculumid': LessonName,
              'id':id ? id : ''
            }, res => {
              if(res.code == 0){
                setTimeout(e=>{
                  this.$router.push({
                    name:'EditCourseTime',
                    path:'/EditCourseTime',
                    query:{
                      id: this.$route.query.type == 'edit'? id: res.data,
                      type: this.$route.query.type,
                      curriculumid: this.formData.LessonName,
                      teacherid: this.$route.query.type == 'edit'? this.teacherid : '',
                      periodnum: this.$route.query.type == 'edit'? this.periodnum :'',
                      from:'MainLessonManagement'
                    }
                  })
                },500)
              }else {
                this.$Message.error(res.info);
              }
            })
          }
        })
      }
    }
  }
</script>
<style scoped lang='less'>
  @import "~@/assets/base.less";
  .title {
    color: #5e6266;
    font-size: 18px;
    font-weight: normal;
  }
  .orange {
    display: inline-block;
    vertical-align: middle;
  }
  .error {
    font-size: 12px;
    display: block !important;
    padding-left: 205px;
    padding-top: 8px;
  }
  .input-box * {
    vertical-align: middle !important;
    overflow: hidden;
  }
  .btnbox {
    overflow: hidden;
    padding: 5px 0 33px;
    margin-left: 245px;
  }
  .input-box{
    height: 40px;
    width: 70%;
  }
  .ivu-select-dropdown-list li{
    margin-bottom: 0;
    font-size: 16px;
  }
</style>



