<template>
  <div>
    <Card class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" dis-hover>
        <div class="mytheme-mg-top-20">
            <!--一级分类-->
            <span>一级分类：</span>
            <Select class="input-max-height input-normal-width" v-model="classifyOneId" @on-change="courseCategoryListTwo(classifyOneId)" placeholder="请选择一级分类" style="width:200px;border: 1px solid #dddee1;border-radius: 2px;">
                <Option v-for="item in classifyOne" :value="item.id" :key="item.id">{{item.categoryname}}</Option>
            </Select>
            <!--二级分类-->
            <span class='mytheme-mg-left-70'>二级分类：</span>
            <Select class="input-max-height input-normal-width" v-model="classifyTowId" placeholder="请选择二级分类" style="width:200px;border: 1px solid #dddee1;border-radius: 2px;">
                <Option v-for="item in classifyTow" :value="item.id" :key="item.id">{{item.categoryname}}</Option>
            </Select>
            <!--搜索 -->
            <Button class="mytheme-mg-left-70" type="primary" @click="getSchedulinglessonList(current) ">搜索</Button>
        </div>
          <!--年级题库列表-->
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
        <!--分页-->
        <pagination class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
        </pagination>
      </Card>
   
    <!--查看弹框-->
     <Modal class='testModel'
            v-model="seeTestModel"
             :closable="false">
        <h4 slot="header" style="text-align: center;color:#333;">{{choiceQues.periodname}}</h4>
        <div class="type-content">
          <div v-for='(item,k,index) in  getQuesNum(choiceQues.subject)' :key="item.id">
           <p>
                <span>{{index=='0'?'一':(index=='1'?'二':(index=='2'?'三':'四'))}}、</span>
                <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
                <span>({{totalScore(item)}}分)</span>
            </p>
            <dl v-for='(i,indexs) in item' :key="i.id">
                <dt>{{indexs+1}}、{{i.name}}</dt>
                 <dd  v-if="i.imageurl" style="padding-left:30px;text-indent: 0rem;">
                      <img :src="'http://'+img" v-for="img in transfer(i.imageurl)" :key="img.id" style="width:150px;height:100px;margin-right:15px;border:solid 1px #eee;" />
                 </dd>
                <dd v-if="k==1||k==2">
                    <p v-for="option in i.options" :key="option.id">{{option}}</p>
                </dd>
                <!--<dd v-if="i.analysis">解析：{{i.analysis}}</dd>-->
                <dd>
                  <span v-if="i.correctanswer">答案：{{i.correctanswer}}</span>
                  <span v-if="i.analysis">解析：{{i.analysis}}</span>
                </dd>
            </dl>
        </div>
        </div>
         <div slot="footer" style="text-align: center;">
          <!--确认-->
          <Button type="primary" @click="seeTestModel = false;">确认</Button>
          <!--取消-->
          <Button @click="seeTestModel = false;">取消</Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import { Card, Input, Table, Button,Select,Option,Modal} from "iview";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "QuestionBank",
  components: {Card,Input,"i-table": Table,Button,pagination,Select,Option,Modal},
   data(){
    return {
    classifyOne: [],
    classifyOneId: '',
    classifyTow: [],
    classifyTowId: '',
     colData:[{
              title: '编号',
              key: 'lessonid',
             align: 'center'
          },{
            title: '课程名称/分类',
              // key: 'courseAndType',
              align: 'center',
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.curriculumname),
                  h("p",{},params.row.categoryname)
                ]);
              }
          },{
            title: '班级名称',
              key: 'gradename',
              align: 'center' 
          },{
            title: '习题名称',
              key: 'periodname',
              align: 'center' 
          },{
            title: '老师',
              key: 'teachername',
              align: 'center' 
          },{
            title: '提交人数/总人数',
              // key: 'submitJob',
              align: 'center' ,
               render: (h, params) => {
                return h("div",{},params.row.submitedcount + "/" + params.row.realnum
              )}
          },{
            title: '未阅/提交人数',
              // key: 'notRead',
              align: 'center',
               render: (h, params) => {
                return h("div",{},params.row.notreviewcount + "/" + params.row.submitedcount
              )}
          },{
            title: '操作',
              key: 'handle',
              align: 'center',
              render: (h, params, index) => {
              return h('div',[
                   h('div',{
                on: {
                  'click': () => {
                    this.seeTestModel = true;
                    this.PreviewExercise(params.row.classid,params.row.lessonid)
                  }
                },
                  style:{
                    cursor:'pointer',
                    color: '#FFA80D',
                    display: 'inline-block',
                    marginRight:'35px'
                  },
                  attrs:{
                   class: 'tablebtn yellow',
                  }
                },'预览'),//预览
                h('router-link',{
                  props:{
                    to:'/notSH?from=TeachingAffairs&type=notSH&classid='+params.row.classid+'&lessonid='+params.row.lessonid
                  },
                  style:{
                    cursor:'pointer',
                    color: '#3F9CE8',
                    display: 'inline-block'
                  },
                  attrs:{
                    class: 'tablebtn bule'
                  }
                },'查看')//查看
                ])
               }
          }],
     tabData:[],
     total:0,
     display:20,
     current:1,
     seeTestModel: false, //预览
     choiceQues:{},
    }
  },
  methods: {
     //课程一级分类
     courseCategoryListOne() {
      Server.post(Path.courseCategoryListOne,{},
        res => {
          if (res.code === 0) {
            this.classifyOne = res.data;
          }
        }
      );
    },
    //课程二级分类
     courseCategoryListTwo(id) {
      Server.post(Path.courseCategoryListTwo,{
       category_level_one: id
      },
        res => {
          if (res.code === 0) {
            this.classifyTow = res.data;
          }
        }
      );
    },
    //排班习题列表
     getSchedulinglessonList(current = 1) {
      Server.post(Path.getSchedulinglessonList,{
        reviewstatus: '0',
        category_level_one: this.classifyOneId,
        category_level_two: this.classifyTowId,
        pagenum:current,
      },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
             this.tabData =[];
          }
        }
      );
    },
    //分页
     pagechange(currentPage) {
      this.getSchedulinglessonList(currentPage);
    },
    //预览习题
     PreviewExercise(classid,lessonid) {
      Server.post(Path.PreviewExercise,{
        classid:classid,
        lessonid: lessonid
      },
        res => {
          if (res.code === 0) {
            this.choiceQues=res.data;
          }
        }
      );
    },
    transfer(img) {//图片格式的转换
          return img.split(',');
      },
    //获取题号
     getQuesNum(obj){
         for( let k in obj){
           if(obj[k].length==0) {
             delete obj[k]
           }
         }
         return obj;
     },
     //计算满分
     totalScore(arr){
         let sum = 0;
         arr.forEach(item=>{
             sum += parseInt(item.score);
         })
         return sum;
     }
  },
   created() {
    this.courseCategoryListOne()
    this.getSchedulinglessonList(this.current)
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
 .testModel {
    .type-content dt{
      text-indent:1rem;
    }
    .type-content dd{
      text-indent:2rem;
    }
}
</style>



