<template>
  <div class="menu" id="menu">
    <Layout>
      <Sider hide-trigger
             :style="{background: '#fff', position: 'fixed', height: '100vh', left: 0, top: '80px', overflow: 'auto', boxShadow: '0px 10px 16px rgba(21,46,74,0.14)'}">
        <Menu ref="leftMenu" accordion :active-name="activeName" theme="light" width="auto" :open-names="[openNames]" on-select="selectMenu">
          <Submenu :name="Route.linkname" v-for="(Route,i) in Routers" :key="i">
            <template slot="title" >
              <Icon :type="Route.icon"></Icon>
              {{ Route.name }}
            </template>
            <MenuItem :name="RouteItem.linkname" class="menu-border" v-for="RouteItem in Route.list" :key="RouteItem.id">
              <router-link exact class="router-link" :to="{path:RouteItem.url}" >
                {{RouteItem.name}}
              </router-link>
            </MenuItem>
          </Submenu>
        </Menu>
      </Sider>
      
      <Layout :style="{padding: '0 0 0 0', marginLeft: '200px',paddingTop:'80px'}">
        <div style="padding: 0 30px; min-width: 1110px;">
          <router-view />
        </div>
      </Layout>
    </Layout>
    
  </div>
</template>

<script>
import { Layout, Sider, Menu, MenuItem, Submenu, Icon } from "iview";
import Routes from '@/router/Route';
import TopTip from '@/components/Tips';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "vmenu",
  components: {
    Layout,
    Sider,
    Menu,
    MenuItem,
    Submenu,
    Icon,
    Routes,
    TopTip
  },
  data(){
    return {
      // Routes: Routes.RouteDatas
      openNames: '',
      activeName:'',
      open: '',
      active: '',
      urlTo: '',
      icon:[
        'ios-home',
        'ios-paper',
        'ios-bookmarks',
        'ios-person',
        'ios-list',
        'ios-pricetag',
        'ios-gear',
        'ios-locked'
      ],
      Routers: []
    }
  },
  created() {
    // 组件初始化后执行
    //this.menuList();
    //this.checkRight();
    this.getMenuTree();
  },
  mounted() {
    //do something after mounting vue instance
    this.menuList()
  },
 
  methods: {
    menuList() {
      
      // 这个方法里定义好，高亮和路由
      
      let path = this.$route.path; // 获取到地址拦上#号后面的url
      let from = this.$route.query.from;
      this.openNames = from ? from : this.open;
      this.activeName = path ? path : this.active;
      
      // $nextTick 在下次 DOM 更新循环结束之后执行延迟回调。在修改数据之后立即使用这个方法，获取更新后的 DOM。
      // updateOpened: 手动更新展开的子目录，注意要在 $nextTick 里调用
      // updateActiveName: 手动更新当前选择项，注意要在 $nextTick 里调用
      this.updateMenu();
    },
    updateMenu(msg) {
      if(msg) {
        this.getMenuTree(msg);
      }
      this.$nextTick(function() {
        this.$refs.leftMenu.updateOpened();
        this.$refs.leftMenu.updateActiveName();
      });
    },
    // checkRight(){
    //   var organType = JSON.parse(window.localStorage.getItem('userinfo')).restype;
    //   if(organType==1){
    //     Routes.RouteDatas[4].MenuItem.splice(1, 1);
    //   }
    // },
    //登录成功－权限组配置
    getMenuTree(msg) {
      var menulist = window.localStorage.getItem("menuData"); 
      //如果没有存储且不存在参数，重新请求
      if(menulist && !msg){
        this.Routers = JSON.parse(localStorage.getItem('menuData'))
        return;
      }
      Server.post(Path.getMenuTree,{
      }, res => {
        if(res.code == 0){
          this.Routers = res.data;
          //console.log(1, JSON.stringify(this.Routers));
          let openname = res.data[0].linkname;
          this.open = openname;
          let actname = res.data[0].list[0].linkname;
          this.urlTo = res.data[0].list[0].url;
          this.active = actname;
          this.menuList();
          this.$router.push(this.urlTo);
          //this.$Message.success(res.info);
          if(res.data){
              window.localStorage.setItem('menuData',JSON.stringify(res.data));
            }
        }else {
          this.$Message.error(res.info);
        }
      })
    }
  },
  // watch:{
  //   $route(){
  //     this.menuList();
  //   }
  // }
};
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
/*清楚默认样式*/
.ivu-layout {
  background: none;
}

a {
  color: #7A8599;
}

.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  border-right: none;
}

.ivu-menu-item a {
  padding: 0 0 0 26px;
}

/*子菜单伪类*/
.ivu-menu .ivu-menu-item {
  position: relative;
}

/*路由当前选中状态*/
.menu .router-link-active {
  color: @color1;
}

.menu a:after {
  display: block;
  content: ".";
  overflow: hidden;
  position: absolute;
  left: 40px;
  top: 22px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  // background: rgba(142, 157, 170, 1);
  background: #282B33;
}
.menu a.router-link-exact-active:after {
  display: block;
  content: ".";
  overflow: hidden;
  position: absolute;
  left: 40px;
  top: 22px;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: @color1;
}

.ivu-menu-vertical.ivu-menu-light:after {
  background: none;
}

.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title{
  padding: 13px 30px;
  color:#8F9BB3;
  font-size: 14px;
}
.ivu-menu-vertical .ivu-menu-submenu-title-icon{
  top:6px
}

/*侧边栏背景颜色*/
.ivu-layout-sider,
.ivu-layout-sider-children,
.ivu-menu-light {
  background: #383B42 !important;
  color: #7A8599;
  font-size: 14px;
}


.menu .ivu-menu-opened{
  color: @color1;
  // background: #ecf6fe !important;
  background:rgba(33,36,43,1) !important;
}
.menu .ivu-menu-submenu {
  // border-bottom: 1px solid #f0faff;
  box-sizing: border-box;
  color: #7A8599;
  //color: #fff;
}

.menu .ivu-layout-sider .ivu-menu-submenu .ivu-menu-submenu-title:hover {
  background-color: #33465c;
  color: #8096af;
  background-color:rgba(33,36,43,1);
  color: #fff;
}

.menu .ivu-menu-vertical .ivu-menu-item {
  background: #fefefe;
}
.menu .ivu-menu-vertical .ivu-menu-item:hover a{
  color: @color1;
}

.ivu-menu-submenu-title:hover {
  background-image: initial;
  background-position-x: initial;
  background-position-y: initial;
  background-size: initial;
  background-repeat-x: initial;
  background-repeat-y: initial;
  background-attachment: initial;
  background-origin: initial;
  background-clip: initial;
  background-color: red !important;
}
.guide{
  position:fixed;
  bottom: 0;
  left: 0;
  background:#f4faff;
  z-index: 99;
  width: 200px;
  font-size: 14px;
  a{
    display: block;
    height: 56px;
    line-height: 56px;
  }
  i{
    margin-left: 24px;
  }
  span{
    display: inline-block;
    color: #7A8599;
    margin-left:26px;
  }
  &:hover{
    background: #ecf6fe !important;
  }
}
/* 左侧导航栏 */
.ivu-icon {
  color: @color1;
  width: 20px;
  height: 20px;
  font-size: 14px;
  // border:1px solid red;
  vertical-align: middle;
  margin-top: -2px;
  text-align: center;
  &::before{
    display: none;
  }
}
//<Icon type="md-create" />
.ivu-icon-ios-home{
  background: url('./assets/images/menu_icon_home.png') no-repeat center;
}
.ivu-icon-ios-paper{
  background: url('./assets/images/menu_icon_course.png') no-repeat center;
}
.ivu-icon-ios-bookmarks{
  background: url('./assets/images/menu_icon_affair.png') no-repeat center;
}
.ivu-icon-ios-person{
  background: url('./assets/images/meun_icon_user.png') no-repeat center;
}
.ivu-icon-ios-list{
  background: url('./assets/images/menu_icon_price.png') no-repeat center;
}
.ivu-icon-md-create{
  background: url('./assets/images/menu_icon_essay.png') no-repeat center;
}
.ivu-icon-ios-pricetag{
  background: url('./assets/images/menu_icon_money.png') no-repeat center;
}
.ivu-icon-ios-gear{
  background: url('./assets/images/menu_icon_set.png') no-repeat center;
}
.ivu-icon-ios-locked{
  background: url('./assets/images/menu_icon_lock.png') no-repeat center;
}
.ivu-menu-opened{
   .ivu-icon-ios-home{
      background: url('./assets/images/menu_icon_home_sel.png') no-repeat center;
    }
    .ivu-icon-ios-paper{
      background: url('./assets/images/menu_icon_course_sel.png') no-repeat center;
    }
    .ivu-icon-ios-bookmarks{
      background: url('./assets/images/menu_icon_affair_sel.png') no-repeat center;
    }
    .ivu-icon-ios-person{
      background: url('./assets/images/meun_icon_user_sel.png') no-repeat center;
    }
    .ivu-icon-ios-list{
      background: url('./assets/images/menu_icon_price_sel.png') no-repeat center;
    }
    .ivu-icon-md-create{
      background: url('./assets/images/menu_icon_essay_sel.png') no-repeat center;
    }
    .ivu-icon-ios-pricetag{
      background: url('./assets/images/menu_icon_money_sel.png') no-repeat center;
    }
    .ivu-icon-ios-gear{
      background: url('./assets/images/menu_icon_set_sel.png') no-repeat center;
    }
    .ivu-icon-ios-locked{
      background: url('./assets/images/menu_icon_lock_sel.png') no-repeat center;
    }
  }
.ivu-menu-submenu-title > i, .ivu-menu-submenu-title span > i{
  margin-right: 21px;
}
.ivu-layout-sider{
  width: 200px !important;
  min-width: 200px !important;
}
.ivu-menu-vertical .ivu-menu-item, .ivu-menu-vertical .ivu-menu-submenu-title{
  padding: 13.5px 24px;
}

</style>



