<template>
  <div>
    <panel-title :crumbs="[{name:'免费领课',route:''}]" />
    <Card dis-hover style="padding:31px 63px 61px;">
       <div class="mytheme-mg-top-20">
        <!--添加时间-->
        <span>添加时间：</span>
        <!--时间选择框-->
        <DatePicker size="large" type="daterange" @on-change="handleChange" format="yyyy-MM-dd" placeholder="请选择时间范围" style="width: 300px"></DatePicker>
        <Button  class="mytheme-mg-left-70" type="primary" @click="FreeCollectionList(current)">查询</Button>
      </div>
      <!--年级题库列表-->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!--分页-->
      <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
      </pagination>
    </Card>
  </div>
</template>
<script>
import { Card, DatePicker, Table, Button} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "QuestionBank",
  components: {panelTitle,Card,DatePicker,"i-table": Table,Button,pagination},
   data(){
    return {
     start_time:'',
     end_time:'',
     colData:[{
              title: '序号',
              key: 'id',
             align: 'center'
          },
          {
              title: '昵称',
              key: 'name',
             align: 'center'
          },
          {
            title: '手机号',
              key: 'mobile',
              align: 'center',
              render:(h,params)=>{
                  return h('div',{},
                  '+'+params.row.prphone+' '+params.row.mobile
                  )
              }
          },{
            title: '添加时间',
            key: 'receivetime',
            align: 'center' 
          }
        //   ,{
        //     title: '操作',
        //       key: 'handle',
        //       align: 'center',
        //       render: (h, params, index) => {
        //       return h('div',[
        //         h('router-link',{
        //           props:{
        //             to:'/periodList?from=TeachingAffairs&id='+params.row.id
        //           },
        //           style:{
        //             cursor:'pointer',
        //             color: '#3F9CE8',
        //             display: 'inline-block'
        //           },
        //           attrs:{
        //             class: 'tablebtn yellow'
        //           }
        //         },this.$t('Common.watch'))//查看
        //         ])
        //        }
        //   }
          ],
     tabData:[],
     total:0,
     display:20,
     current:1
    }
  },
  methods: {
    stamp(times) {//时间处理器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
    //获取时间范围
     handleChange(daterange) {
        this.start_time=(new Date(daterange[0])).getTime()/1000||'';
        this.end_time=(new Date(daterange[1])).getTime()/1000||'';

        this.FreeCollectionList(this.current);
    },
    //领课列表
     FreeCollectionList(current = 1) {
      Server.post(Path.FreeCollectionList,{
            start_time:this.start_time,
            end_time:this.end_time,
            page_num:current
        },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.freelist;
             this.tabData.forEach(item => {
              item.receivetime=this.stamp(item.receivetime)
            });
            this.total = res.data.pageinfo.total;
            this.display = Number(res.data.pageinfo.pagesize);
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
    //分页
     pagechange(currentPage) {
      this.FreeCollectionList(currentPage);
    },
  },
  created() {
    this.FreeCollectionList(this.current)
  },
 mounted(){},
 computed:{},
 watch:{
   
 }
};
</script>
<style scoped lang='less'>

</style>



