<template>
  <ul id="StudentInfo" class='cardP0'>
    <Card  dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30 postr">
      <div class="header-img-box fl">
        <div class="header-img">
          <DefaultImg :sourceimg="data.imageurl" style='height:100%'/>
        </div>
        <div class="header-user">
          <span class="userName">
            {{data.nickname}}  
          </span>
          <span class='sex'>
            <!-- 1男 2女 其他未知 -->
            <img :src='data.sex == 1? male : (data.sex == 2? female : unknow)' alt='' />
            <!-- <img :src='data.sex == 1? male : female' alt='' /> -->
          </span>
          <p class="login-time">
            <!-- 最近登录时间 -->
            {{$t('studentDetails.logintime')}}{{$t('Common.colon')}}
            <br>{{data.logintime}}
          </p>
          <!-- <router-link :to="{ name: 'AddStudent',query:{type:'edit',id:data.id,from:'MainUserManagement'}}">
            <Button style="float:right">编辑</Button>
          </router-link> -->
        </div>
      </div>
      <ul class="user-msg-box">
        <ul class="LabelBigBox">
          <ul class="LableBox">
            <!-- 用户ID： -->
            <li><span class="LableName">{{$t('teacherDetails.userID')}}{{$t('Common.colon')}}</span><span>{{data.id}}</span>  </li>
            <!-- 手机号： -->
            <li><span class="LableName">{{$t('rest.phone')}}{{$t('Common.colon')}}</span><span>+{{data.prphone}} {{data.mobile}}</span>  </li>
            <!-- 生日： -->
            <li><span class="LableName">{{$t('teacherDetails.birth')}}{{$t('Common.colon')}}</span><span>{{data.birthday}}</span>  </li>
          </ul>
          <ul class="LableBox">
            <!-- <li><span class="LableName">真实姓名：</span><span>{{data.username}}</span>  </li> -->
            <!-- 账户状态： -->
            <li><span class="LableName">{{$t('teacherDetails.accountstatus')}}{{$t('Common.colon')}}</span><span>{{data.status?this.$t('teacherDetails.Disable'):this.$t('teacherDetails.Enable')}}</span>  </li>
            <!-- 注册时间： -->
            <li><span class="LableName">注册时间{{$t('Common.colon')}}</span><span>{{data.addtime}}</span>  </li>
            <!-- 所在地： -->
            <li><span class="LableName">{{$t('teacherDetails.area')}}{{$t('Common.colon')}}</span>
              <span> {{changeCountry(data.country)}} </span>
              <span> {{changeProvince(data.province)}} </span>
              <span> {{changeCity(data.city)}}</span>
            </li>
          </ul>
          
          <ul class="LableBox">
            <li>
              <!-- 个人简介： -->
              <span class="LableName">{{$t('teacherDetails.profile')}}{{$t('Common.colon')}}</span>
              <span class="personDesc" style='word-wrap: break-word;'>{{data.profile}}</span>  
            </li>
          </ul>
        </ul>
      </ul>
    </Card>
  </ul>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import city from "@/Api/city.json";
import {
  Card,
  Button,
} from "iview";
import DefaultImg from '@/components/defaultHeader';
import male from './assets/images/male.png';
import female from './assets/images/female.png';
import unknow from './assets/images/unknow.png';
export default {
  name: "StudentInfo",
  props:['data'],
  components: {
    panelTitle,
    Card,
    Button,
    DefaultImg,
  },
  data(){
    return {
      male,
      female,
      unknow,
    }
  },
  methods:{
    //地址转换
    changeCountry(val){
      if(val == 0 || val == null){
        return this.$t('teacherDetails.unknow2'); //未知
      }else{
        // return city[val]
        city.map((el,index) => {
          if(el.id == val){
            if(this.$i18n.locale == 'zh-cn'){
              val =el.name
            }else{
              val =el.en_name
            }
          }
        })
        return val;
      }
    },
    changeProvince(val){
      // return city[val]
      if(val == 0 || val == null){
        return
      }else{
        city.map((el,index) => {
          if(el.id == val){
            if(this.$i18n.locale == 'zh-cn'){
              val =el.name
            }else{
              val =el.en_name
            }
          }
        })
        return val;
      }
    },
    changeCity(val){
      // return city[val]
      if(val == 0 || val == null){
        return
      }else{
        city.map((el,index) => {
          if(el.id == val){
            if(this.$i18n.locale == 'zh-cn'){
              val =el.name
            }else{
              val =el.en_name
            }
          }
        })
        return val;
      }
    },
  }
}
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.postr{
  position: relative;
}
.header-img-box{
  width: 270px;
  text-align: center;
  position: relative;
  .header-img {
    width: 93px;
    height: 93px;
    margin: 70px auto 15px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 3px 12px #d0d0d0;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .header-user{
    overflow: hidden;
  }
  .userName{
    font-size: 18px;
    color: #5E6266;
    margin-right: 7px;
  }
  .sex{
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    margin-top: -5px;
    img{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .login-time{
    font-size: 16px;
    line-height: 24px;
    color: #F89240;
    margin-top: 24px;
  }
}
.user-msg-box{
  padding-left: 270px;
  .LabelBigBox{
    overflow: hidden;
    border-left:1px solid @line2;
    font-size: 16px;
    .LableBox{
      width: 100%;
      padding: 45px 20px 45px 53px;
      border-bottom: 1px solid @line2;
      li{
        height: 20px;
        line-height: 20px;
        width: 31%;
        display: inline-block;
      }
      @media screen and (max-width: 1440px) {
        li {
          display: table-cell;
          padding: 0 5px 0 5px;
          font-size: 14px;
        }
      }
      &:last-child{
        border-bottom: none;
        li{
          width: 100%;
        }
      }
    }
    @media screen and (max-width: 1440px) {
      .LableBox {
        padding: 45px 20px 45px 30px;
        display: table;
      }
    }
  }
}
</style>



