<template>
  <div>
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
          <!--学生姓名-->
          <span >学生姓名：</span>
          <!--输入关键字-->
          <Input  class="input-normal-width input-max-height"
                @on-enter="TransferLessonList(current)"
                v-model="studentName" placeholder="学生姓名"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="TransferLessonList(current)">搜索</Button>

          </div>
        <!--年级题库列表-->
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
        <!--分页-->
        <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
        </pagination>
    </Card>
  </div>
</template>
<script>
import { Card, Input, Table, Button,Select,Modal} from "iview";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "processedCourse",
  components: {Card,Input,"i-table": Table,Button,pagination,Select,Modal},
   data(){
    return {
       studentName:'',
     colData:[{
             title: '序号',
              key: 'id',
             align: 'center'
          },{
            title: '学生名称',
              key: 'student_name',
              align: 'center'
          },{
            title: '课程/原班级',
              // key: 'courseOldClass',
              align: 'center',
              render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.ondlesson),
                  h("p",{},params.row.old_class)
                ]);
              }
          },{
            title: '课时/原授课老师',
              key: 'periodOldTeacher',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.oldsort),
                  h("p",{},params.row.old_teacher)
                ]);
              }
          },{
            title: '课程/新班级',
              key: 'courseNewClass',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.newlesson),
                  h("p",{},params.row.new_class)
                ]);
              }
          },{
            title: '课时/新授课老师',
              key: 'periodNewTeacher',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.newsort),
                  h("p",{},params.row.new_teacher)
                ]);
              }
          },{
            title: '申请时间',
              key: 'applytime',
              align: 'center'
          },{
            title: '处理结果',
              key: 'results',
              align: 'center',
               render: (h, params) => {
                  let results="已批准";
                  let color='#999999';
                  if(params.row.status=='1'){
                      results="已批准";
                  }else{
                    results="已拒绝";
                    color='#E81717';
                  }
                 return h("div",{
                    style:{color: color}
                 },results)}
          },{
            title: '处理时间',
              key: 'updatetime',
              align: 'center'
          }],
     tabData:[],
        total:0,
        display:20,
        current:1,
    }
  },
  methods: {
     stamp(times) {//时间处理器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
       //调班列表
     TransferLessonList(current = 1) {
      Server.post(Path.TransferLessonList,{
       status:'1',
       studentname:this.studentName,
       pagenum:current
      },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.lesslist;
             this.tabData.forEach(item => {
              item.applytime=this.stamp(item.applytime)
              item.updatetime=this.stamp(item.updatetime)
            });
            this.total = res.data.pageinfo.total;
            this.display = Number(res.data.pageinfo.pagesize);
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
  //分页
    pagechange(currentPage) {
      this.TransferLessonList(currentPage);
    }
  },
  created() {
   this.TransferLessonList(this.current);
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";
.testModel .ivu-modal-footer{
      display:none!important;
    }
 .testModel {
    .type-content dt{
      text-indent:1rem;
    }
    .type-content dd{
      text-indent:2rem;
    }
}
</style>



