<template>
  <div id="wait-order-list">
    <Table :loading="loading" :columns="colData" :data="tabData" border></Table>
    <pagination :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
  </div>
</template>

<script>
  import {Table, Card, Input, Icon} from 'iview';
  import pagination from '@/components/common/pagination';
  import Server from '@/service/request'
  import Path from '@/service/Path'

  export default {
    name: "has-order-list",
    components: {
      Table,
      Card,
      Input,
      pagination,
      Icon,
      Server,
    },
    data() {
      return {
        colData: [
          {title: this.$t('orderManagepage.student'), key: 'studentname', align: 'center'}, //学生
          {title:  this.$t('courseManagepage.coursetitle'), key: 'coursename', align: 'center'}, //课程名称
          {title:  this.$t('orderManagepage.lecturer'), key: 'teachername', align: 'center'}, //授课老师
          {title:  this.$t('orderManagepage.ordertime'), key: 'ordertime', align: 'center'},//下单时间
          {title:  this.$t('orderManagepage.reference'), key: 'ordernum', align: 'center'},//订单编号
          {title:  this.$t('orderManagepage.amount'), key: 'amount', align: 'center'},//订单金额
        ],
        tabData: [],
        loading:true,
        current: 1,
        total: 0,
        display: 20,
      }
    },
    created() {
      this.getOrderList();
    },
    methods: {
      // 查询订单列表
      getOrderList(page=1,type=0) {
        this.loading = true;
        Server.post(Path.getAccount,{
          pagenum:page,
          type
        },res=>{
          let {code,data}=res;
          this.tabData = [];
          if(code===0){
            var datas = data.data;
            for (var i = 0; i < datas.length; i++) {
              var obj = {};
              obj.ordernum = datas[i].ordernum;
              obj.coursename = datas[i].coursename;
              obj.teachername = datas[i].teachername;
              obj.ordertime = datas[i].addtimestr;
              obj.studentname = datas[i].studentname;
              obj.amount = datas[i].amount;
              obj.paytype = datas[i].paytype || this.$t('Common.NO'); //暂无
              obj.orderstatus = datas[i].orderstatus;
              this.tabData.push(obj);
            }
            this.current = Number(data.pageinfo.pagenum);
            this.total = data.pageinfo.total;
            this.display = data.pageinfo.pagesize;
          }
          this.loading = false;
        })
      },
      // 筛选
      Screen(index) {
        this.activeItem = index;
        this.getOrderList(1,index?0:1);
      },
      // 查看详情
      showDetails(id) {
        this.$router.push({path: '/OrderDetails?from=MainOrderManagement', query: {orderid: id}});
      },
      // 分页请求
      pageChange(page) {
        this.getOrderList(page);
      }
    },
    computed: {
    }
  }
</script>

<style scoped>
</style>



