<template>
  <div id="StuLessonInfo" class='br8 mg-bottom-20'>
    <Card dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30">
      <div class="header">
        <img :src='icon2' alt='' />
        <!-- 已购课程 -->
        {{$t('studentDetails.title')}}
      </div>
      <ul class="LessonBox" v-if='data.length > 0'>
        <li class="lesson-item" v-for="course in data" :key="course.id">
          <div class="lesson-item-page">
            <div class="lesson-item-header" style="position: relative;">
              <img :src="course.imageurl" alt="">
              <div class="classType">
                <span class="mediaType">
                    {{changeType(course.coursetype)}}
                </span>
                <span v-if="course.classname" class="mediaType">
                    {{course.classname}}
                </span>
              </div>
            </div>
            <div class="lesson-item-content">
              <div class="lesson-title">
                {{course.coursename}}
              </div>
              <div class="lesson-time-price">
                <div class="lesson-price">
                  <span class="big-text fr">
                    {{$t('Common.symbol')}}{{course.amount}}/课时
                  </span>
                  <span class="lesson-time fl">
                    {{course.nickname}}
                  </span>
                  <!-- / -->
                  <!-- 课时 -->
                  <!-- <span>{{$t('courseManagepage.time')}}</span> -->
                </div>
              </div>
            </div>
          </div>
        </li>
      </ul>
      <!-- 暂无数据 -->
      <defaultShow :text="$t('Common.nodata')" v-else/>
    </Card>
  </div>
</template>
<script>
import { Card } from "iview";
import icon2 from './assets/images/icon2.png';
import defaultShow from "@/components/defaultShow";
export default {
  name: "StuLessonInfo",
  components: {
    Card,
    defaultShow
  },
  props:[
    'data'
  ],
  data() {
    return {
      icon2,
    };
  },
  methods:{
    //转换班型
    changeType(value) {
      if (value == 1) {
        return (value = '录播课');//1对1
      } else if (value == 2) {
        return (value = '直播课');//小班课
      }
		},
  }
};
</script>
<style scoped lang='less'>
.header{
  font-size: 18px;
  color:#5E6266;
  padding: 18px 0 34px 28px;
  img{
    display: inline-block;
    widows: 26px;
    height: 26px;
    vertical-align: middle;
    margin: -4px 10px 0 0;
  }
}
.LessonBox {
  overflow: hidden;
  padding: 0 0 10px 67px;
}
.lesson-item {
  padding: 0 53px 30px 0;
  float: left;
  .lesson-item-page{
    width:280px;
    box-sizing: border-box;
    border: 1px solid #fefefe;
    box-shadow:0px 9px 16px rgba(102,164,234,0.18);
    border-radius: 4px;
    &:hover{
      border:1px solid #469af8;
      box-shadow:0px 9px 16px rgba(102,164,234,0.18)
    }
  }
  .lesson-item-header{
    background-color: #eee;
    width: 100%;
    height: 147px;
    overflow: hidden;
    border-radius:4px 4px 0 0;
    img{
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .lesson-item-content{
    height: 77px;
    padding:0 20px;
    box-sizing: border-box;
    .lesson-title{
      line-height: 40px;
      height: 40px;
      // font-weight: bold;
      font-size: 16px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #404040;
      margin: 10px 0 2px;
    }
    .lesson-time-price{
      overflow: hidden;
      line-height: 18px;
      .lesson-price{
        // font-weight: bold;
        font-size: 14px;
        color: #F04747;
      }
      .lesson-time{
        font-size: 14px;
        color:#999;
      }
    }
  }
}
.classType{
  position: absolute;
  left: 10px;
  bottom: 6px;
}
.mediaType {
  display: inline-block;
  height: 20px;
  padding: 0 8px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  text-align: center;
  font-size: 12px;
  color: #fff;
  line-height: 20px;
}
</style>



