<template>
  <div>
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
          <!--学生姓名-->
          <span>学生姓名：</span>
          <Input class="input-normal-width input-max-height"
                @on-enter="TransferLessonList(current)"
                v-model="studentName" placeholder="学生姓名"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="TransferLessonList(current)">搜索</Button>

        </div>
      <!--列表-->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!--分页-->
      <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
      </pagination>
    </Card>
    <!--提示弹框-->
     <Modal class='testModel'
            v-model="hintModel"
               :closable="false">
        <div slot="header">温馨提示</div>
        <div class="type-content">
             <p>{{textcontent}}</p>
        </div>
         <div slot="footer">
          <!--确认-->
          <Button type="primary" @click="TransferLessApply()">确认</Button>
          <!--取消-->
          <Button @click="hintModel = false;">取消</Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import { Card, Input, Table, Button,Modal} from "iview";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "pendingCourse",
  components: {Card,Input,"i-table": Table,Button,pagination,Modal},
   data(){
    return {
      studentName:'',
     colData:[{
              title: '序号',
              key: 'id',
             align: 'center'
          },{
            title: '学生名称',
              key: 'student_name',
              align: 'center'
          },{
            title: '课程/原班级',
              key: 'courseOldClass',
              align: 'center',
              render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.ondlesson),
                  h("p",{},params.row.old_class)
                ]);
              }
          },{
            title: '课时/原授课老师',
              key: 'periodOldTeacher',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.oldsort),
                  h("p",{},params.row.old_teacher)
                ]);
              }
          },{
            title: '课程/新班级',
              key: 'courseNewClass',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.newlesson),
                  h("p",{},params.row.new_class)
                ]);
              }
          },{
            title: '课时/新授课老师',
              key: 'periodNewTeacher',
              align: 'center' ,
               render: (h, params) => {
                return h("div", [
                  h("p",{},params.row.newsort),
                  h("p",{},params.row.new_teacher)
                ]);
              }
          },{
            title: '申请时间',
              key: 'applytime',
              align: 'center' 
          },{
            title: '操作',
              key: 'handle',
              align: 'center',
              render: (h, params, index) => {
              return h('div',[
                 h('div',{
                on: {
                  'click': () => {
                    Server.post(Path.testingLessApply,{
                      tranid:params.row.id,
                    },res=>{
                      let {code,info}=res;
                      if(code==0){
                        this.isAgree = true;
                        this.hintModel = true;
                        this.TsID=params.row.id;
                        this.status=1;
                        this.textcontent='您确定同意'+params.row.student_name+'学生的调课申请吗？'
                      }else{
                        this.isAgree = true;
                        this.hintModel = true;
                        this.TsID=params.row.id;
                        this.status=1;
                        this.textcontent=info;
                      }
                    })
                  }
                },
                  style:{
                    cursor:'pointer',
                     color:'#3F9CE8',
                    display: 'inline-block',
                    marginRight:'35px'
                  },
                  attrs:{
                   class: 'tablebtn blue',
                  }
                },'同意'),//同意
                 h('div',{
                on: {
                  'click': () => {
                   this.isAgree = false;
                    this.hintModel = true;
                    this.TsID=params.row.id;
                    this.status=2;
                    this.textcontent='您确定要拒绝'+params.row.student_name+'学生的调课申请吗？'
                  }
                },
                  style:{
                    cursor:'pointer',
                    color: '#ED3825',
                    display: 'inline-block'
                  },
                  attrs:{
                    class: 'tablebtn yellow'
                  }
                },'拒绝')//拒绝
                ])
               }
          }],
      tabData:[],
        total:0,
        display:20,
        current:1,
     isAgree: false, //同意
     hintModel: false, //提示弹框
      TsID:'',
      status:'1',
      textcontent:''
    }
  },
  methods: {
     stamp(times) {//时间处理器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      let h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
      let m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
      let s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
      return Y+M+D+h+m+s;
    },
       //调班列表
     TransferLessonList(current = 1) {
      Server.post(Path.TransferLessonList,{
       status:'0',
       studentname:this.studentName,
       pagenum:current
      },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.lesslist;
             this.tabData.forEach(item => {
              item.applytime=this.stamp(item.applytime)
            });
            this.total = res.data.pageinfo.total;
            this.display = Number(res.data.pageinfo.pagesize);
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
  //分页
    pagechange(currentPage) {
      this.TransferLessonList(currentPage);
    },
    //同意拒绝
    TransferLessApply(){
      Server.post(Path.TransferLessApply,{
      status:this.status,
      tranid:this.TsID
      },
        res => {
          if (res.code === 0) {
             this.hintModel = false;
             this.$Message.success(res.info)
              this.TransferLessonList(this.current)
            }else if(res.code==60000){
               this.$Message.error(res.data.errmsg)
            }else{
            this.$Message.error(res.info)
          }
        }
      );
    }
  },
  created() {
  this.TransferLessonList(this.current)
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";
.testModel .ivu-modal-footer{
      display:none!important;
    }
 .testModel {
    .type-content dt{
      text-indent:1rem;
    }
    .type-content dd{
      text-indent:2rem;
    }
}
</style>



