<template>
  <div id="tips" v-if="false">
    <div class="tips-wrapper">
      <div>
        <img :src="icon_tip" class="tips-icon tips-icon-error inline-block">
        <div class="tips-text inline-block">
          您当前购买的是试用套餐，还可以试用 {{usettime}} 天，开通正式版本可获得更多权限。
          <router-link tag="a" to="/Buyer" class="red">马上开通过正式版</router-link>
        </div>
        <img :src="icon_close" class="tip-icon tips-icon-close inline-block" @click="flag=false">
      </div>
    </div>
  </div>
</template>

<script>
  import icon_tip from '@/assets/images/icon_tip_error.png';
  import icon_close from '@/assets/images/icon_close.png';
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "tips",
    data() {
      return {
        icon_tip,
        icon_close,
        flag: false,
        usettime: 7
      }
    },
    created() {
      //do something after creating vue instance
      this.getOrganTrialBillLastTime();
    },
    mounted() {
      //do something after creating vue instance
      this.getOrganTrialBillLastTime();
    },
    computed: {
      getRoute() {
        return this.$route;
      }
    },
    watch: {
      getRoute(val) {
        this.getOrganTrialBillLastTime();
      }
    },
    methods: {
      //查询体验套餐剩余天数
      getOrganTrialBillLastTime() {
        Server.post(Path.getOrganTrialBillLastTime, {}, res => {
          let {code, data} = res;
          if (code === 0 && this.$route.fullPath === '/webAdminIndex') {
            this.flag = true;
            this.usettime = data.lastdays;
          } else {
            this.flag = false;
          }
        });
      }
    }
  }
</script>

<style scoped>
  #tips {
    width: 100%;
    height: 46px;
    line-height: 46px;
    background-color: #fff;
    -webkit-box-shadow: 2px 0 0 rgba(230, 230, 230, 0.09);
    -moz-box-shadow: 2px 0 0 rgba(230, 230, 230, 0.09);
    box-shadow: 2px 0 0 rgba(230, 230, 230, 0.09);
    position: relative;
  }

  .tips-wrapper {
    padding: 0 50px;
    font-size: 14px;
  }

  .inline-block {
    display: inline-block;
  }

  .tips-icon {
    vertical-align: text-bottom;
  }

  .tips-text {
    padding-left: 10px;
    color: #666666;
  }

  .tips-icon-close {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 50px;
    margin: auto;
    cursor: pointer;
  }
</style>



