<template>
  <div>
   <panel-title :crumbs="[
      {
        name:'题库管理',
        route:{path:'/QuestionBank',query:{from:'TeachingAffairs'}}
      },
      {
        name:'查看详情',
        route:''
      },
    ]" />
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
        <!--班级/课程名称-->
        <span>习题名称：</span>
        <!--输入关键字-->
        <Input
            class="input-normal-width input-max-height"
            @on-enter="getExerciseList(current)"
            v-model.trim="periodName" placeholder="习题名称"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getExerciseList(current)">搜索</Button>

         </div>
          <!--年级题库列表-->
          <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>

          <!--分页-->
        <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
          </pagination>
    </Card>

    <!--查看弹框-->
     <Modal class='testModel'
            v-model="seeTestModel"
            :closable="false">
        <div slot="header">{{choiceQues.periodname}}</div>
        <div class="type-content">
         <div v-for='(item,k,index) in getQuesNum(choiceQues.subject)' :key="item.id">
             <h4>
                <span>{{index=='0'?'一':(index=='1'?'二':(index=='2'?'三':'四'))}}、</span>
                <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
                <span>({{totalScore(item)}}分)</span>
            </h4>
            <dl v-for='(i,indexs) in item' :key='i.id'>
                <dt style="width:480px;word-break:break-all;">{{indexs+1}}、{{i.name}}</dt>
                <dd  v-if="i.imageurl" style="padding-left:30px;text-indent: 0rem;">
                      <img :src="'http://'+img" v-for="img in transfer(i.imageurl)" :key="img.id" style="width:150px;height:100px;margin-right:15px;border:solid 1px #eee;" />
                </dd>
                <dd v-if="k==1||k==2">
                    <p v-for="option in i.options" :key="option.id">{{option}}</p>
                </dd>
                <!--<dd v-if="i.analysis">解析：{{i.analysis}}</dd>-->
                <dd style="width:480px;word-break:break-all;">
                  <span v-if="i.correctanswer">答案：{{i.correctanswer}}</span>
                  <span v-if="i.analysis">解析：{{i.analysis}}</span>
                </dd>
            </dl>
        </div>
        </div>
        <div slot="footer" style="text-align: center;">
          <!--确认-->
          <Button type="primary" @click="seeTestModel = false;">确认</Button>
          <!--取消-->
          <Button @click="seeTestModel = false;">取消</Button>
        </div>
  </Modal>
    <!--删除弹框-->
     <Modal
    v-model="LessonTypeDeleteModel"
    :closable="false">
    <div slot="header">温馨提示</div>
    <div class="type-content">{{delText}}</div>
    <div slot="footer">
      <!--确认-->
      <Button type="primary" @click="deleteExercise()">确认</Button>
      <!--取消-->
      <Button @click="LessonTypeDeleteModel = false;">取消</Button>
    </div>
  </Modal>
  </div>
</template>
<script>
import { Card, Input, Table, Button,Modal} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "periodList",
  components: {panelTitle,Card,Input,"i-table": Table,Button,pagination,Modal},
   data(){
    return {
      periodName:'',
     colData:[{
              title: '编号',
              key: 'id',
             align: 'center'
          },{
            title: '习题名称',
              key: 'periodname',
              align: 'center'
          },{
            title: '修改时间',
              key: 'updatetime',
              align: 'center' 
          },{
            title: '题目总数',
              key: 'subjectcount',
              align: 'center' 
          },{
            title: '操作',
              key: 'handle',
              align: 'center',
             render: (h, params, index) => {
              return h('div',[
              h('div',{
                on: {
                  'click': () => {
                    this.seeTestModel = true;
                    Server.post(Path.getExerciseinfo,{
                      id: params.row.id
                    },
                    res => {
                      if (res.code === 0) {
                        this.choiceQues = res.data;
                      }
                    }
                  )
                  }
                },
                style:{
                  cursor:'pointer',
                  color: '#FFA80D',
                  display: 'inline-block',
                  marginRight:'35px'
                },
                attrs:{
                  class: 'tablebtn yellow'
                }
              },'查看'),//查看
              h('div',{
                // props:{
                //     to:'/editQues/2?from=TeachingAffairs&id='+this.$route.query.id+'&exeId='+params.row.id
                // },
                 on: {
                  'click': () => {
                    Server.post(
                          Path.checkHaveSchedulingLesson,
                          {
                           id:params.row.id
                          },
                          res => {
                            if(res.code===0){
                              this.$router.push({path:'/editQues/2',query:{from:'TeachingAffairs',id:this.$route.query.id,exeId:params.row.id}})
                            }else{
                               this.$Message.error("有班级正在使用该习题，无法编辑！");
                            }
                     });
                  }
                },
                style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block',
                  marginRight:'35px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'编辑'),//编辑
              h('div',{
                on: {
                  'click': () => {
                    this.LessonTypeDeleteModel = true;
                    this.deleteId= params.row.id;
                    Server.post(
                          Path.checkHaveSchedulingLesson,
                          {
                           id:this.deleteId
                          },
                          res => {
                            if(res.code===0){
                              this.isShowSendTip = false;
                              this.delText="您确定要删除该习题吗？";
                            }else{
                              this.LessonTypeDeleteModel = true;
                              this.delText=res.info;
                            }
                     });
                  }
                },
                style: {
                  cursor:'pointer',
                  color: '#ED3825',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn red'
                }
              },'删除')//删除
            ])
          }
          }],
    tabData:[],
     total:0,
     display:20,
     current:1,
      seeTestModel: false, //查看
      LessonTypeDeleteModel: false, //删除
      delText:'您确定要删除该习题吗？',
       choiceQues:{
        courseid:'',
        periodid:'',
        periodname:'',
        subject:{},
        deleteId:'',
        },
    }
  },
  methods: {
    //习题列表
     getExerciseList(current = 1) {
      Server.post(Path.getExerciseList,{
          courseid: this.$route.query.id,
          periodname: this.periodName,
          pagenum: current
        },
        res => {
          if (res.code === 0||res.code ===70400) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.pagesize = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }
        }
      );
    },
    transfer(img) {//图片格式的转换
          return img.split(',');
      },
    //删除习题
     deleteExercise() {
      Server.post(Path.deleteExercise,{
          id:this.deleteId
        },
        res => {
          if (res.code ==0) {
            this.$Message.success(res.info);
            this.getExerciseList(this.current);
          }
        }
      );
      this.LessonTypeDeleteModel = false;
    },
    //分页
     pagechange(currentPage) {
       this.current=currentPage
      this.getExerciseList(this.current);
    },
    //获取题号
     getQuesNum(obj){
         for( let k in obj){
           if(obj[k].length==0) {
             delete obj[k]
           }
         }
         return obj;
     },
     //计算满分
     totalScore(arr){
         let sum = 0;
         arr.forEach(item=>{
             sum += parseInt(item.score);
         })
         return sum;
     }
  },
  created() {
    this.getExerciseList(this.current);
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
.testModel {
    .ivu-modal-footer{
      display:none;
    }
    .type-content dt{
      text-indent:1rem;
    }
    .type-content dd{
      text-indent:2rem;
    }
}
</style>



