<template>
  <div id="cardTitle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "CardTitle"
}
</script>

<style scoped>
#cardTitle {
  width: 188px;
  height: 43px;
  line-height: 43px;
  font-size: 18px;
  text-align: center;
  background: url(./assets/cardtitle.png) no-repeat;
  background-size: 100% 100%;
  color: #fff;
}
</style>



