import http from "axios";
import qs from "qs";
import sha1 from "sha1";
import md5 from 'md5';
import { Message } from "iview";

function getStr(arr, key, starttime, token) {
  var tostr = arr ? getStrings(arr, "") : "";
  return md5(sha1(md5(tostr) + md5(key) + starttime) + token);
}

function getStrings111(data, str) {
  for (var x in data) {
    if (isJson(data[x]) || isArray(data[x])) {
      str += x;
      str = getStrings(data[x], str);
    } else {
      str += x + data[x];
    }
  }
  return str;
}

function getStrings(data, str) {
  for (var x in data) {
    if (isJson(data[x])) {
      var i = 0;
      for (var k in data[x]) {
        i++;
        break;
      }
      if (i > 0) {
        str += x;
        str = getStrings(data[x], str);
      }
    } else if (isArray(data[x])) {
      if (data[x].length != 0) {
        str += x;
        str = getStrings(data[x], str);
      }
    } else {
      if (
        data[x] !== undefined &&
        data[x] !== null &&
        data[x] !== "" &&
        data[x] !== false &&
        data[x] !== true
      ) {
        str += x.toString() + data[x].toString();
      }
    }
  }
  return str;
}

function isArray(o) {
  return Object.prototype.toString.call(o) == "[object Array]";
}

function isJson(obj) {
  return (
    typeof obj == "object" &&
    Object.prototype.toString.call(obj).toLowerCase() == "[object object]" &&
    !obj.length
  );
}

function queryString(data) {
  var str = "";
  for (var d in data) {
    if (data.hasOwnProperty(d)) {
      str += d + "=" + data[d] + "&";
    }
  }
  return str;
}
// config http
let baseUrl = "";
if (process.env.NODE_ENV === "development") {
 // baseUrl = "http://www.51menke.net/";
  // baseUrl = "http://test.menke.com/";
   baseUrl = "http://www.lemonedu.com/";
} else if (process.env.NODE_ENV === "production") {
  baseUrl = "/";
}
http.defaults.baseURL = baseUrl;
// http.defaults.timeout = 2500
http.defaults.headers.post["Content-Type"] = "application/x-www-form-urlencoded";
