// Add a request interceptor 解决ie下url带中文参数乱码问题

// 自动设置语言
// const navLang = (navigator.language || navigator.browserLanguage).toLowerCase();
// const lang = navLang.indexOf('zh') != -1 ? 'zh-cn' : 'en-us';
http.interceptors.request.use(function (config) {
    // Do something before request is sent
    var starttime = Date.parse(new Date());
    if (config.method == "get") {
      config.url = encodeURI(config.url);
    }
    // config.headers.common["lang"] = lang;
    // return config;
    if (sessionStorage.getItem("userinfo") == null || sessionStorage.getItem("userinfo") == "" ||
      sessionStorage.getItem("key") == null || sessionStorage.getItem("key") == "") {
      window.sessionStorage.removeItem("userinfo");
      window.sessionStorage.removeItem("key");
      window.localStorage.removeItem("menuData");
      // window.location.href = location.origin + "/admin/login/login.html";
    } else {
      var token = JSON.parse(window.sessionStorage.getItem("userinfo")).token;
      var data = qs.parse(config.data);

    config.headers.common["sign"] = getStr(data, sessionStorage.getItem("key"), starttime, token);
    config.headers.common["token"] = String(token);
    config.headers.common["starttime"] = String(starttime);

      // window.localStorage.setItem('organ_sign', SignStr);
      // window.localStorage.setItem('organ_token', String(token));
      // window.localStorage.setItem('organ_starttime', String(starttime));

    }
    return config;
  },
  function (error) {
    // Do something with request error
    return Promise.reject(error);
  },
);

// Add a response interceptor
var count = 1
http.interceptors.response.use(
  function (response) {
    // Do something with response data
    if (response.data.code == -40666) {
      count++
      if(count == 2){
        Message.error(response.data.info)
      }
      window.sessionStorage.removeItem("userinfo");
      window.sessionStorage.removeItem("key");
      window.localStorage.removeItem("menuData");
      window.location.href = location.origin + "/admin/#/login";
      // setTimeout(function () {
      //   window.localStorage.removeItem("userinfo");
      //   window.localStorage.removeItem("key");
      //   // window.location.href = location.origin + "/admin/login/login.html";
      //   window.location.href = location.origin + "/admin/#/login";
      // }, 1500);
    } else {
      return response.data;
    }
  },
  function (error) {
    // Do something with response error
    if (error.response) {
      if (error.response.status === 400) {
        Message.error("数据错误！");
      } else if (error.response.status === 401) {
        // define window.app=vm in main.js
        // window.app.$router.push({name: 'Login'});
      } else if (error.response.status === 403) {
        Message.error("您没有操作权限哦！");
      } else if (error.response.status === 404) {
        Message.error("对象不存在！");
      } else if (error.response.status === 500) {
        Message.error("服务器发生了一些错误哦！");
      }
    }
    return Promise.reject(error);
  }
);

export default http;



