<template>
   <div id="wangeditor">
        <div ref="editorElem"></div>
    </div> 
</template>
<script>   
    import E from 'wangeditor'
    import Server from "@/service/request";
    import Path from "@/service/Path";
    export default {
        name: 'editorElem',
        data () {
            return {
                editorContent: '',
                uploadid:[1,1,1],
                imgUploadUrl: window.location.origin === 'http://127.0.0.1:8080' ? "http://www.lemonedu.com/teacher/Uploadfile/Upload" : "/teacher/Uploadfile/Upload"
            }
        },
        props:['catchData','content'],    //接收父组件的方法
        mounted() {
            var editor = new E(this.$refs.editorElem)        //创建富文本实例
            editor.customConfig.onchange = (html) => {
                this.editorContent = html
                this.catchData(html)  //把这个html通过catchData的方法传入父组件
            }
            editor.customConfig.uploadFileName = 'uploadFile'
            // 隐藏“网络图片”tab
            editor.customConfig.showLinkImg = false

            editor.customConfig.menus = [          //菜单配置
                'head',
                //'list',  // 列表
                'justify',  // 对齐方式
                'strikethrough',
                'bold',
                'fontSize',  // 字号
                'italic',
                'underline',
                'image',  // 插入图片
                'foreColor',  // 文字颜色
                'bgcolor', //背景色
                'undo',  // 撤销
                'redo',  // 重复
            ] 

            editor.customConfig.uploadImgServer = this.imgUploadUrl
            editor.customConfig.uploadImgHooks = {
                customInsert: function (insertImg, result, editor) {
                    insertImg(`http://${result.data}`)
                },
            }
            editor.customConfig.uploadImgParams = {
                allpathnode: [1,1,1],
            }
            editor.create()  
            editor.txt.html(this.content)
        },
    }
</script>

<style scoped lang='less'>
// 此段样式需要去全局commom.css中拷贝 /* 20180926 hyq wangedit编辑器 */
#wangeditor{
    width: 100%;
    .w-e-toolbar{
        display: block;
        border: 1px solid #CFDCE7 !important;
        .w-e-menu{
            display: inline-block;
            z-index: 1 !important;
        }
    }
    .w-e-text-container{
        border: 1px solid #CFDCE7 !important;
        border-top: none !important;
        z-index: 0 !important;
    }
}
</style>





