<template>
  <div id="ReaOver">
    <panel-title :crumbs="[{name:'批阅管理', route:'/ReaOver'},{name:'老师作业批阅', route:''}]"/>
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
         <!--按学生姓名查询-->
        <span>学生姓名{{$t('symbol.colon')}}</span>
        <Input
           placeholder="请输入学生姓名"
          class="input-normal-width input-max-height"
          v-model.trim="searchNickName"
          @on-enter="getTeacherReviewList(current)"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getTeacherReviewList(current)">搜索</Button>
      </div>

      <!-- 学生作文列表 -->
      <Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :columns="colData" :data="tabData" no-data-text="暂无数据"></Table>
   
      <!-- 分页 -->
      <pagination :total="total" :currentPage="current" :display="display" @pagechange="pageChange" class="mg-top-20" v-if='total>0'></pagination>
    </Card>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Button
} from "iview";

export default {
  name: "ReaOver",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    Table,
    Button
  },
  data() {
    return {
      searchNickName: "", //昵称查询关键字
      colData: [
        {
          title:"编号",
          key: "compositionid",
          align: "center"
        }, //编号
        {
          title: "学生姓名",
          key: "studentname",
          align: "center"
        }, //学生姓名
        {
          title: "作文标题",
          key: "title",
          align: "center"
        }, //作文标题
         {
          title: "批阅时间",
          key: "reviewtime",
          align: "center"
        },//批阅时间
        {
          title:"操作", //操作
          render: (h, params) => {
            return h("div", {
                  style: {
                     color: "#3F9CE8",
                    cursor:'pointer'
                  },
                  on: {
                    click: () => {
                      this.$router.push({
                        path: '/EssayDetail',
                        query: {
                          from:"EssayManagement",
                          beFrom:'ReaOver',
                          teacherid:this.$route.query.teacherid,
                          type: 1,
                          compositionid:params.row.compositionid
                          }
                      });
                    }
                  }
            },"查看");
          },
          align: "center"
        }
      ],
      tabData: [],
      current: 1,
      total: 0,
      display: 20,

    };
  },
  created() {
    this.getTeacherReviewList(this.current);
  },
  watch: {
  },
  computed: {},
  methods: {
    // 搜索学生作文列表
    getTeacherReviewList(current = 1) {
      Server.post(
        Path.getTeacherReviewList,
        {
          teacherid: this.$route.query.teacherid,
          nickname: this.searchNickName,
          pagenum: current
        },
        res => {
          let {code,data}=res
          if (code === 0) {
            this.tabData = data.data;
            this.total =data.pageinfo.total;
            this.display = data.pageinfo.pagesize;
            this.current = Number(data.pageinfo.pagenum);
          }else{
            this.tabData = [];
          }
        }
      );
    },
    //分页
    pageChange(index) {
      this.current = index;
      this.getTeacherReviewList(this.current);
    }
    
  }
};
</script>

<style scoped lang='less'>
</style>



