<template>
  <div id="MainAdmin">
    <!--首页-->
    <panel-title :crumbs="crumbs" />
    <template >
       <Row :gutter="20" style='margin-bottom:10px'>
          <Col :sm="12" :md="12" :lg="6" class='grid-bg'>
              <Card dis-hover>
                  <div class="grid-content grid-content-wrapper">
                      <i class="grid-con-icon grid-con-icon1" ></i>
                      <div class="grid-cont-right">
                        <!--交易总额-->
                        <span class="grid-text">{{$t('homepage.amountTrade')}}</span>
                        <span class="grid-num">¥ {{statistics.totalflowcash}}</span>
                      </div>
                  </div>
              </Card>
          </Col>
          <Col :sm="12" :md="12" :lg="6" class='grid-bg'>
              <Card dis-hover>
                  <div class="grid-content grid-content-wrapper">
                      <i class="grid-con-icon grid-con-icon2" ></i>
                      <div class="grid-cont-right">
                        <!--成交订单数-->
                        <span class="grid-text">{{$t('homepage.transactionOrders')}}</span>
                        <span class="grid-num">{{statistics.totalorder}}笔</span>
                      </div>
                  </div>
              </Card>
          </Col>
          <Col :sm="12" :md="12" :lg="6" class='grid-bg'>
              <Card dis-hover>
                  <div class="grid-content grid-content-wrapper">
                      <i class="grid-con-icon grid-con-icon3" ></i>
                      <div class="grid-cont-right">
                        <!--学生总数-->
                        <span class="grid-text">{{$t('homepage.totalStudents')}}</span>
                        <span class="grid-num">{{statistics.studentarr.studenttotal}}人</span>
                      </div>
                  </div>
              </Card>
          </Col>
          <Col :sm="12" :md="12" :lg="6" class='grid-bg'>
              <Card dis-hover>
                  <div class="grid-content grid-content-wrapper">
                      <i class="grid-con-icon grid-con-icon4" ></i>
                      <div class="grid-cont-right">
                        <!--老师总数-->
                        <span class="grid-text">{{$t('homepage.totalTeachers')}}</span>
                        <span class="grid-num">{{statistics.teachtotal}}人</span>
                      </div>
                  </div>
              </Card>
          </Col>
      </Row>
    </template>
    <template>
       <Row class="mg-bottom-20" :gutter="20">
          <Col :sm="24" :md="24" :lg="12">
            <Card>
              <!--机构总览-->
                <p class='title' ><img src="./assets/images/index_icon5.png" />{{$t('homepage.institutionsOverview')}}</p>
                <div class="grid-wrapper">
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.classarr.classtodaynum}}</div>
                    <!--今日新增-->
                    <div class="text">{{$t('homepage.todayNew')}}</div>
                  </div>
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.classarr.classyesnum}}</div>
                    <!--昨日新增-->
                    <div class="text">{{$t('homepage.yesterdayNew')}}</div>
                  </div>
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.classarr.monthclassnum}}</div>
                    <!--本月增加-->
                    <div class="text">{{$t('homepage.monthincrease')}}</div>
                  </div>
                  <div class="grid-box">
                    <div class="num">{{statistics.classarr.classingnum}}</div>
                    <!--在招生班级-->
                    <div class="text">{{$t('homepage.admissionclass')}}</div>
                  </div>
                </div>
            </Card>
          </Col>
          <Col :sm="24" :md="24" :lg="12">
            <Card>
              <!--学生总览-->
                <p class='title' ><img src="./assets/images/index_icon6.png" />{{$t('homepage.studentsoverview')}}</p>
                <div class="grid-wrapper">
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.studentarr.stutodaynum}}</div>
                    <!--今日新增-->
                    <div class="text">{{$t('homepage.todayNew')}}</div>
                  </div>
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.studentarr.stuyesnum}}</div>
                    <!--昨日新增-->
                    <div class="text">{{$t('homepage.yesterdayNew')}}</div>
                  </div>
                  <div class="grid-box grid-box-border">
                    <div class="num">{{statistics.studentarr.monthnum}}</div>
                    <!--本月增加-->
                    <div class="text">{{$t('homepage.monthincrease')}}</div>
                  </div>
                  <div class="grid-box">
                    <div class="num">{{statistics.studentarr.studenttotal}}</div>
                    <!--学生总数-->
                    <div class="text">{{$t('homepage.totalStudents')}}</div>
                  </div>
                </div>
            </Card>
          </Col>
      </Row>
    </template>
    <template>
       <Row class="mg-bottom-20">
          <Col span="24">
            <Card>
                <div>
                  <!--课时数及学生数统计-->
                  <p class='title' ><img src="./assets/images/index_icon7.png" />{{$t('homepage.statisticsofClassHoursAndStudents')}}</p>
                  <ul class="btnTab">
                    <li v-for="(item,index) in items" @click="selectStyle(1,index)" :class="{'active':activeItem == index}" :key="index" >{{item.select}}</li>
                  </ul>
                  <div style="padding:0 20px;">
                    <!--各时间阶段课时数统计-->
                    <p class='tip' >{{LineClassText}}{{$t('homepage.statisticsOfClassHoursinDifferentTimePeriodsToday')}}</p>
                    <div ref="chartOne" :style="{width: '100%', height: '300px'}" >
                    </div>
                  </div>
                </div>
            </Card>
          </Col>
      </Row>
      <Row class="mg-bottom-20">
          <Col span="24">
            <Card>
              <div>
                <!--交易流水统计-->
                <p class='title' ><img src="./assets/images/index_icon8.png" />{{$t('homepage.Transactionflowstatistics')}}</p>
                <ul class="btnTab">
                  <li v-for="(item,index) in items2" @click="selectStyle(2,index)" :class="{'active':activeItem2 == index}" :key="index" >{{item.select}}</li>
                </ul>
                <div style="padding:0 20px;">
                  <!--销售统计-->
                  <p class='tip' >{{LineOrderText}}{{$t('homepage.salesstatistics')}}</p>
                  <div ref="chartTwo" :style="{width: '100%', height: '300px'}" >
                  </div>
                </div>
              </div>
            </Card>
          </Col>
      </Row>
      <Row class="mg-bottom-20">
        <Col span="24">
          <Card>
            <div>
              <!-- 各端交易流水统计 -->
              <p class='title' ><img src="./assets/images/index_icon8.png" />各端交易流水统计</p>
              <ul class="btnTab">
                <li v-for="(item,index) in items3" @click="selectStyle(3,index)" :class="{'active':activeItem3 == index}" :key="index" >{{item.select}}</li>
              </ul>
              <div style="padding:0 20px;">
                <!-- 销售统计 -->
                <p class='tip' >{{LinePayText}}各端交易流水统计</p>
                <div ref="chartThree" :style="{width: '100%', height: '300px'}" >
                </div>
              </div>
            </div>
          </Card>
        </Col>
      </Row>
    </template>
  </div>
</template>
<script>
import Vue from "vue";
import panelTitle from "@/components/panelTitle";
import { Row, Card, Col, Tabs, TabPane } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import moment from "moment";
//引入Echarts主模块
let echarts = require("echarts/lib/echarts");
//引入柱状图
require("echarts/lib/chart/line");
//引入所需组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");

export default {
  name: "MainAdmin",
  components: {
    panelTitle,
    Row,
    Col,
    Card,
    Tabs,
    TabPane
  },
  data() {
    return {
      crumbs: [{
       name: this.$t('homepage.indexpage'),
        route:''
      }],
      activeItem: 0,
      activeItem2: 0,
      activeItem3: 0,
      LineClassText: this.$t('homepage.today'),    // 今日
      LineOrderText: this.$t('homepage.Nearly7days'),    // 近7天
      LinePayText: this.$t('homepage.Nearly7days'),    // 近7天
      lineOrderthreeText: "各终端交易流水统计",
      lineChartOrder: null,
      lineChartClass: null,
      lineChartPay: null,
      statistics: {
        totalflowcash: "0.00",
        totalorder: 0,
        classarr: {
          classingnum: 0,
          classtodaynum: 0,
          classyesnum: 0,
          monthclassnum: 0
        },
        studentarr: {
          studenttotal: 0,
          stutodaynum: 0,
          stuyesnum: 0,
          monthnum: 0
        },
        teachtotal: 0
      },
      orgTotal: {},
      items: [
        {
          select: this.$t('homepage.today'), //今日
          active: true
        },
        {
          select:this.$t('homepage.week'),   //本周
          active: false
        },
        {
          select: this.$t('homepage.month') ,  //本月
          active: false
        }
      ],
      items2: [
        {
          select: this.$t('homepage.Nearly7days'), //近7天
          active: true
        },
        {
          select: this.$t('homepage.Nearly30days'), //近30天
          active: false
        }
      ],
      items3: [
        {
          select: this.$t('homepage.Nearly7days'), //近7天
          active: true
        },
        {
          select: this.$t('homepage.Nearly30days'), //近30天
          active: false
        }
      ]
    };
  },
  created() {
    //do something after mounting vue instance

    this.getInitData();
  },
  mounted() {
    //do something after mounting vue instance
    this.createChartOne();
    this.createChartTwo();
    this.getClassLineData();
    this.getOrderLineData();
    this.createChartThree();
    this.getPayLineData();

    window.addEventListener('resize', ()=> {
      this.lineChartClass.resize();
      this.lineChartOrder.resize();
      this.lineChartPay.resize();
    })
  },
  methods: {
    getInitData() {
      Server.post(Path.getOrganAnalysis, {}, res => {
        // console.log(res)
        let { code, data } = res;
        if (code === 0) {
          this.statistics = data;
        }
      });
    },
    selectStyle(type, index) {
      if (type == 1) {
        this.activeItem = index;

        switch (index) {
          case 0:
            this.LineClassText = this.$t('homepage.today'); //今日
            this.getClassLineData("day");
            break;
          case 1:
            this.LineClassText = this.$t('homepage.week'); //本周
            this.getClassLineData("week");
            break;
          case 2:
            this.LineClassText = this.$t('homepage.month');   //本月
            this.getClassLineData("month");
            break;
          default:
        }
      } else if(type == 2) {
        this.activeItem2 = index;
        switch (index) {
          case 0:
            this.LineOrderText = this.$t('homepage.Nearly7days'); //近7天
            this.getOrderLineData("week");
            break;
          case 1:
            this.LineOrderText = this.$t('homepage.Nearly30days'); //近30天
            this.getOrderLineData("month");
            break;
          default:
        }
      } else {
        this.activeItem3 = index;
        switch (index) {
          case 0:
            this.LinePayText = this.$t('homepage.Nearly7days'); //近7天
            this.getPayLineData("week");
            break;
          case 1:
            this.LinePayText = this.$t('homepage.Nearly30days'); //近30天
            this.getPayLineData("month");
            break;
          default:
        }
      }
    },
    /**
     * 获取当前周
     * @return {[date:'2018-03-23',week:'周一']} [description]
     */
    initWeekData() {
      var week = [this.$t('courseTablepage.Monday'), this.$t('courseTablepage.Tuesday'), this.$t('courseTablepage.Wednesday'),this.$t('courseTablepage.Thursday'), this.$t('courseTablepage.Friday'), this.$t('courseTablepage.Saturday'), this.$t('courseTablepage.Weekday')];
      var weekArray = [];
      for (var i = 1; i <= week.length; i++) {
        var obj = {};
        obj.date = moment()
          .day(i)
          .format("YYYY-MM-DD");
        obj.week = week[i - 1];
        weekArray.push(obj);
      }
      return weekArray;
    },
    //
    /**
     * 获取当天小时数，半小时为准
     * @return {[time:'08:00',key:'16']} [description]
     */
    initHoursData() {
      var date = [];
      var h = ["00", "30"];
      var k = 16;
      for (var i = 8; i <= 24; i++) {
        for (var j = 0; j < h.length; j++) {
          var obj = {};
          var str = "";
          if (i < 10) {
            obj.time = "0" + i + ":" + h[j];
            obj.key = k++;
            date.push(obj);
          } else if (i == 24 && j == 1) {
          } else {
            obj.time = i + ":" + h[j];
            obj.key = k++;
            date.push(obj);
          }
        }
      }
      return date;
    },
    /**
     * 获取当月所有天数
     * @return {['2018-02-03']} [description]
     */
    initMonthDate() {
      var toMonth = moment().format("YYYY-MM");
      var lastDay = moment(toMonth, "YYYY-MM").daysInMonth(); //最后一天
      var dayArray = [];
      for (var i = 1; i <= lastDay; i++) {
        var str = "";
        if (i < 10) {
          str = toMonth + "-0" + i;
        } else {
          str = toMonth + "-" + i;
        }
        dayArray.push(str);
      }
      return dayArray;
    },
    getClassLineData(type = "day") {
      Server.post(
        Path.getOrganAnaCourse,
        {
          courseline: type
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            let daySumData = [];
            let dayClassSum = [];
            let time = [];
            if (type == "day") {
              data.coursearr = data.coursearr.slice(16,48)
              time = this.initHoursData().map((el, index) => {
                return el.time;
              });
              data.coursearr.forEach((el, index) => {
                daySumData.push(el.realnum);
                dayClassSum.push(el.num);
              });
            }
            if (type == "week") {
              time = this.initWeekData().map((el, index) => {
                return el.week;
              });
              data.coursearr.forEach((el, index) => {
                daySumData.push(el.realnum);
                dayClassSum.push(el.num);
              });
            }
            if (type == "month") {
              time = this.initMonthDate().map((el, index) => {
                return el.substring(el.indexOf("-") + 1);
              });
              data.coursearr.forEach((el, index) => {
                daySumData.push(el.realnum);
                dayClassSum.push(el.num);
              });
            }
            this.lineChartClass.setOption({
              xAxis: {
                data: time
              },
              series: [
                {
                  smooth: true,
                  name: this.$t('homepage.classhour'), //课时数
                  data: dayClassSum
                },
                {
                  smooth: true,
                  name: this.$t('homepage.Numberstudents'), //学生数
                  data: daySumData
                }
              ]
            });
          }
        }
      );
    },
    /*创建图表一*/
    createChartOne() {
      this.lineChartClass = echarts.init(this.$refs.chartOne);
      this.lineChartClass.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          data: ["666"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: []
        },
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: this.$t('homepage.classhour') , //课时数
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                color: "#6e7efe",
                width: 2
              }
            },
            areaStyle: {
              normal: {}
            },
            itemStyle: {
              normal: {
                color: "#eff7fe"
              }
            },
            data: []
          },
          {
            name: this.$t('homepage.Numberstudents'), //学生数
            type: "line",
            symbol: 'none',
            lineStyle: {
              normal: {
                color: "transparent",
                width: 0
              }
            },
            areaStyle: {
              normal: {
                color: "transparent"
              }
            },
            itemStyle: {
              normal: {
                color: "transparent",
                label: {
                  show: false
                }
              }
            },
            data: []
          }
        ]
      });
    },
    getOrderLineData(type = "week") {
      Server.post(
        Path.getOrganAnaFlow,
        {
          flowline: type
        },
        res => {
          let { code, data } = res;
          // console.log(2, res);
          if (code === 0) {
            let daySumData = [];
            let dayClassSum = [];
            let time = [];
            data.flowarr.forEach((el, index) => {
              daySumData.push(el.totalpay);
              dayClassSum.push(el.num);
              time.push(el.datestr.substring(el.datestr.indexOf("-") + 1));
            });
            // console.log(21, time);
            this.lineChartOrder.setOption({
              xAxis: {
                data: time
              },
              series: [
                {
                  smooth: true,
                  name: this.$t('homepage.order'), //订单数
                  data: dayClassSum
                },
                {
                  smooth: true,
                  name: this.$t('homepage.total'), //总金额
                  data: daySumData
                }
              ]
            });
          }
        }
      );
    },
    /*创建图表二*/
    createChartTwo() {
      this.lineChartOrder = echarts.init(this.$refs.chartTwo);
      this.lineChartOrder.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          data: ["666"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: []
        },
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: this.$t('homepage.order'), //订单数
            type: "line",
            smooth: true,
            lineStyle: {
              normal: {
                color: "#6e7efe",
                width: 2
              }
            },
            areaStyle: {
              normal: {}
            },
            itemStyle: {
              normal: {
                color: "#eff7fe"
              }
            },
            data: []
          },
          {
            name:  this.$t('homepage.total'), //总金额
            type: "line",
            symbol: 'none',
            lineStyle: {
              normal: {
                color: "transparent",
                width: 0
              }
            },
            areaStyle: {
              normal: {
                color: "transparent"
              }
            },
            itemStyle: {
              normal: {
                color: "transparent",
                label: {
                  show: false
                }
              }
            },
            data: []
          }
        ]
      });
    },
    getPayLineData(type = 'week'){
      Server.post(Path.getOrganPayAnaFlow,{
        flowline: type
      }, res => {
        let { code, data} = res;
       // console.log(1, res);
        if(code == 0){
          
          let pcPaydata = [];
          let appPaydata = [];
          let wxPaydata = [];
          let day = [];
          data.forEach((el,i) => {
            pcPaydata.push(el.pcprice);
            appPaydata.push(el.appprice);
            wxPaydata.push(el.wxprice);
            day.push(el.datestr.substring(el.datestr.indexOf("-") + 1));
          });
          // console.log(pcPaydata);
          this.lineChartPay.setOption({
            xAxis: {
              data: day
            },
            series: [
              {
                name: 'PC端交易额', 
                data: pcPaydata
              },
              {
                name: 'App端交易额', 
                data: appPaydata
              },
              {
                name: '微信端交易额', 
                data: wxPaydata
              }
            ]
          });
        }
      })
    },
    // 创建图表三
    createChartThree() {
      this.lineChartPay = echarts.init(this.$refs.chartThree);
      this.lineChartPay.setOption({
        tooltip: {
          trigger: "axis",
        },
        color: ['#9dffce', '#5598fd', '#ffcfcf'],
        legend: {
         // data: ["PC端交易额", "App端交易额", "微信端交易额"],
          data: [{
               name:'PC端交易额',
               icon : 'rect',
               textStyle: {
                  color: '#525866'    
               }
           },{
               name:'App端交易额',
               icon : 'rect',
               textStyle: {
                  color: '#525866'    
               }
           },{
               name:'微信端交易额',
               icon : 'rect',
               textStyle: {
                  color: '#525866'    
               }
           }]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: 'PC端交易额' ,
            type: "line",
            smooth: false,
            lineStyle: {
              normal: {
                color: "#9dffce",
                width: 2
              }
            },
            data: []
          },
          {
            name: 'App端交易额' , 
            type: "line",
            smooth: false,
            lineStyle: {
              normal: {
                color: "#5598fd",
                width: 2
              }
            },
            data: []
          },
          {
            name: '微信端交易额' ,
            type: "line",
            smooth: false,
            lineStyle: {
              normal: {
                color: "#ffcfcf",
                width: 2
              }
            },
            data: []
          },
          
        ]
      })
    }
  }
};
</script>
<style scoped lang = "less">
@import "~@/assets/base.less";
.tip {
  font-size: 16px;
  color: #525866;
}
.title {
  display: inline-block;
  width: 100%;
  line-height: 20px;
  font-size: 16px;
  font-family: PingFangSC-Medium;
  color: #666;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 0 16px 0;
  border-bottom: 1px solid #E6E6E6;
  img {
    width: 18px;
    height: 18px;
    vertical-align: middle;
    margin: -2px 5px 0 0;
  }
}
/* .ivu-card {
    border-radius: 8px;
} */
.btnTab {
  position: absolute;
  right: 20px;
  width: 50%;
  text-align: right;
  li {
    display: inline-block;
    margin-left: 20px;
    cursor: pointer;
    font-size: 16px;
    color: #8F9BB3;
    &.active {
      color: #3F9CE8;
      border-bottom: 1px solid @color1;
    }
  }
}
.grid-content-wrapper {
  display: flex;
  align-items: center;
  height: 88px;
  /* margin: 22px 0 0 0; */
  .grid-con-icon {
    font-size: 50px;
    width: 64px;
    height: 64px;
    text-align: left;
    line-height: 64px;
    color: #fff;
    &.grid-con-icon1 {
      background: url(assets/images/index_icon1.png) no-repeat left;
      background-size: 100%;
    }
    &.grid-con-icon2 {
      background: url(assets/images/index_icon2.png) no-repeat left;
      background-size: 100%;
    }
    &.grid-con-icon3 {
      background: url(assets/images/index_icon3.png) no-repeat left;
      background-size: 100%;
    }
    &.grid-con-icon4 {
      background: url(assets/images/index_icon4.png) no-repeat left;
      background-size: 100%;
    }
  }
  .grid-cont-right {
    flex: 1;
    margin-left: 15px;
    line-height: 36px;
    .grid-text {
      font-size: 16px;
      color: #666;
      /* margin-bottom: 3px; */
    }
    .grid-num {
      font-size: 20px;
      color: #4D4D4D;
      float: right;
    }
  }
}
.grid-bg{
  .ivu-card-bordered{
    height: 120px;
  }
}
.grid-wrapper {
  display: flex;
  justify-content: space-around;
  padding: 16px 0;
  .grid-box {
    flex: 1;
    text-align: center;
    .num {
      font-size: 22px;
      color: #66B4FF;
      margin-bottom: 20px;
    }
    .text {
      font-size: 16px;
      color: #666;
      padding-bottom: 16px;
    }
    
    /* &:last-child{
      .num,.text{
        color:@color1;
      }
    } */
  }
  .grid-box-border{
    position: relative;
    &:after{
      position: absolute;
      right: 0;
      top: 16px;
      content: " ";
      display: block;
      width: 1px;
      height: 54px;
      border-left: 1px solid rgba(235,235,235,1);
    }
  }
}
</style>



