<template>
  <div id="Course">
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
          <!--学生账号-->
          <span>学生账号{{$t('Common.colon')}}</span>
          <!--学生账号-->
          <Input
            v-model.trim="InputText"
            placeholder="请输入学生账号"
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>
          <!--学生昵称-->
          <span class="mytheme-mg-left-70">学生昵称{{$t('Common.colon')}}</span>
          <!--学生昵称-->
          <Input
            v-model.trim="nickname"
            placeholder="请输入学生昵称"
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getStuList(InputText, nickname, current)">搜索</Button>

          <!--推送消息\发送短信 -->
          <div class="fg">
            <!-- <Button type="primary"  @click="addStudentModel">发送短信</Button> -->
            <Button type="primary" @click="showPush">推送消息</Button>
         </div>
      </div>

      <!-- 学生列表 -->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :loading="loading" :columns="columns1" :data="data1" @on-selection-change="chageSelect"></i-table>
      
       <!-- 分页 -->
      <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
   </Card>

     <!-- 推送消息的弹框 -->
    <Modal v-model="isPush" width="560" :closable="false">
      <div slot="header">推送消息</div>
       <Form :label-width="127" ref="pushInfo" :model="pushInfo" :rules="ruleValidate">
          <Row>
            <Col>
              <FormItem label="推送标题：" prop="title">
                  <Input type="text" v-model.trim="pushInfo.title" placeholder="请输入标题名称，长度不能超过14个字" :maxlength="14" style="width: 340px;"/>
              </FormItem>
            </Col>
          </Row>
           <Row>
            <Col>
              <FormItem label="推送内容：" prop="content">
                  <Input type="textarea" v-model.trim="pushInfo.content" placeholder="请输入内容，长度不能超过50个字" :maxlength="50"  style="width: 340px;" :rows="5"/>
              </FormItem>
            </Col>
          </Row>
      </Form>
      <div slot="footer" style="text-align: center;">
          <!--确认 -->
          <Button type="primary" @click="sendStudentMessage('pushInfo')">{{$t('Common.sure')}}</Button>
          <!--取消 -->
          <Button @click="handleReset('pushInfo')">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Switch,
  Button,
  Form,
  Row,
  Col,
  FormItem,
  Checkbox,
  Modal
} from "iview";
export default {
  name: "",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    "i-table": Table,
    "i-switch": Switch,
    Button,
    Form,
    Row,
    Col,
    FormItem,
    Checkbox,
    Modal
  },
  created() {
    this.getStuList(this.mobile, this.nickname, this.current);
  },
  watch: {
  },
  methods: {
    //获取学生列表
    getStuList(mobile, nickname, page = 1) {
      Server.post(
        Path.getStudentUserList,
        {
          mobile: mobile,
          nickname: nickname,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            // this.data1 = [];
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total = total;
            this.display = pagesize;
            this.current = Number(pagenum);
            this.data1 = data.data;
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        }
      );
    },
    //分页
    pagechange(currentPage) {
      this.getStuList(this.mobile, this.nickname, currentPage);
    },
    chageSelect(val) {//获取选中的学生id
    let arr=[]
      val.forEach(el => {
        arr.push(el.id)
      });
      this.studentids=arr
    },
    handleSelectAll(status) {//改变选中状态
      this.$refs.selection.selectAll(status);
    },
    sendStudentMessage(name) {//推送消息
     this.$refs[name].validate(valid => {
        if (valid) {
          Server.post(
            Path.sendStudentMessage,
            {
              studentids: this.studentids,
              title: this.pushInfo.title,
              content: this.pushInfo.content
            },res=>{
              if(res.code==0){
                this.isPush=false;
                this.studentids==[]
                this.handleSelectAll(false)
              this.$Message.success(res.info);
              }
            })
        }
     })
    },
     handleReset(name) {
      //取消按钮
      this.$refs[name].resetFields();
      this.$refs.selection.selectAll(false);
      this.isPush=false;
      this.studentids=[];
    },
    //初始化变量
    showPush(){
      if(this.studentids.length==0){
        this.$Message.error("请选择要推送的学生");
        return false;
        }else{
        this.isPush=true;
      }
      this.pushInfo.title="";
      this.pushInfo.content="";
    },
    // handleCheckAllhandleCheckAll() {
    //   if (this.indeterminate) {
    //     this.AllCheck = false;
    //   } else {
    //     this.AllCheck = !this.AllCheck;
    //   }
    //   this.indeterminate = false;
    //   if (this.AllCheck) {
    //     this.handleSelectAll(true);
    //   } else {
    //     this.handleSelectAll(false);
    //   }
    // },
    
    // addStudentModel() {
    //   //判断审核状态
    //   let id = JSON.parse(window.localStorage.getItem("userinfo")).organid;
    //   let { store } = this.$store;
    //   store.dispatch({
    //     type: "getStatus",
    //     id,
    //     newtype: "-1"
    //   });
    // },
  },
  data() {
    return {
      // mobile: "",
      nickname: "",//学生昵称
      InputText: "",//学生账号
      loading: true,//加载中
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      isPush:false,//是否推送消息
      studentids:[],//选中的学生ID
      pushInfo:{
        title:'',//推送标题
        content:''//推送内容
      },
      ruleValidate: {
        title: [{ required: true, message: '标题不能为空', trigger: 'blur' }],
        content: [{ required: true, message: '内容不能为空', trigger: 'blur' }]
      },
      columns1: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: "学生ID", //学生ID
          align: "center",
          key: "id"
        },
        {
          title: "学生账号", //学生账号
          key: "mobile",
          align: "center"
        },
        {
          title: "学生昵称", //用户昵称
          align: "center",
          key: "nickname"
        },
        {
          title: this.$t("studentManagepage.sex"), //性别
          align: "center",
          key: "sex",
          render: (h, params, index) => {
            let Sex = params.row.sex;
            switch (Sex) {
              case 0:
                Sex = this.$t("teacherDetails.unknow"); //保密
                break;
              case 1:
                Sex = this.$t("teacherDetails.male"); //男
                break;
              case 2:
                Sex = this.$t("teacherDetails.female"); //女
                break;
            }
            return h("div", {}, Sex);
          }
        },
        {
          title: "分类", //分类
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.categoryname || "---");
          }
        },
        {
          title: this.$t("studentManagepage.Recently"), //最近登录
          align: "center",
          key: "logintime"
        },
        {
          title: "备注", //备注
          // width: 300,
          align: "center",
          render: (h, params) => {
            let remark = "---";
            if (params.row.tagname && params.row.childtagname) {
              remark = params.row.tagname + "：" + params.row.childtagname;
            }
            return h("div", remark);
          }
        },
        {
          title: "帐户启用状态",
          align: "center",
          key: "status",
          render: (h, params, index) => {
            return h("div", [
              h("i-switch", {
                on: {
                  "on-change": value => {
                    Server.post(
                      Path.changeUserStatus,
                      {
                        userid: params.row.id,
                        flag: !Number(value)
                      },
                      res => {
                        let { code, data } = res;
                        if (code === 0) {
                          this.$Message.info(res.info);
                        } else {
                          this.$Message.error(res.info);
                        }
                      }
                    );
                  }
                },
                props: {
                  value: !Boolean(params.row.status)
                }
              })
            ]);
          }
        },
        {
          title: this.$t("Common.mark"), //操作
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h(
              "router-link",
              {
                props: {
                  to:
                    "/StudentInfo/" + params.row.id + "?from=MainUserManagement"
                },
                style: {
                  cursor: "pointer",
                  color: "#3F9CE8",
                  display: "inline-block",
                },
                attrs: {
                  class: "tablebtn yellow"
                }
              },
              "查看详情"
            ); //查看
          }
        }
      ],
      data1: []
    };
  }
};
</script>
<style scoped lang='less'>
</style>



