<template>
  <div id="EditLesson">
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
            <!-- 基本信息  -->
            {{$t('courseManagepage.basicinformation')}}{{$t('Common.colon')}}
            <!-- 已购买次数：次 -->
            <!-- <span class="red tip">（{{$t('courseManagepage.buyNum')}}{{$t('Common.colon')}}{{data.payordernum}} {{$t('courseManagepage.unit')}}）</span> -->
        </p>
        <ul class="message">
          <!-- <li>
             课程封页：
            <span> {{$t('courseManagepage.Coursescoverpage')}}{{$t('Common.colon')}}</span>
            <img class="photo" :src="data.imageurl" alt="">
          </li> -->
          <li>
            <!-- 课程分类： -->
            <span>班级名称{{$t('Common.colon')}}</span>
            <p>{{data.gradename}}</p>
          </li>
          <li>
            <!-- 课程名称： -->
            <span style="vertical-align:middle">{{$t('courseManagepage.coursetitle')}}{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.curriculumname}}</p>
            </div>
          </li>
          <li>
            <!-- 课程价格 -->
            <span style="vertical-align:middle">课程价格{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.price}}</p>
            </div>
          </li>
          <li>
            <!-- 课程讲师： -->
            <span style="vertical-align:middle">{{$t('add.classTwacher')}}{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.teachername}}</p>
            </div>
          </li>
          <li>
            <!-- 添加时间： -->
            <span style="vertical-align:middle">班级人数上限{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.fullpeople}}</p>
            </div>
          </li>
          <!-- <li>
            上架状态：
            <span style="vertical-align:middle">{{$t('add.classStatus')}}{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.statusstr}}</p>
            </div>
          </li>
          <li>
           课程概述：
            <span>{{$t('courseManagepage.courseoverview')}}{{$t('Common.colon')}}</span>
            <div class="input-box">
              <p>{{data.generalize}}</p>
            </div>

          </li> -->
          
        </ul>
      </Card>
    </template>
  </div>
</template>
<script>
import panelTitle from '@/components/panelTitle';
import { Card  } from "iview";
export default {
  name: "classInfo",
  components: {
    panelTitle,
    Card
  },
  props:[
    'data'
  ]
}
</script>
<style scoped>
.tip{
  font-size:14px;
}
.title {
  color: #5e6266;
  font-size: 18px;
  font-weight: normal;
}
.photo {
  overflow: hidden;
  width: 180px;
  height: 135px;
}
.message {
  padding: 46px 30px 0 0px;
  font-size: 16px;
}
.message li {
  margin-bottom: 23px;
  line-height: 40px;
}
.message li > span {
    width: 200px;
    white-space:nowrap;
    text-align: right;
    line-height: 40px;
    color: #7A8599;
}
.message li * {
  display: inline-block;
  vertical-align: top;
}
.orange {
  display: inline-block;
  vertical-align: middle;
}
.blue {
  color: #469af8;
  font-size: 14px;
  display: block !important;
  padding-left: 80px;
}
.input-box {
  vertical-align: middle !important;
  overflow: hidden;
}
.input-box .label-box {
  float: left;
  width: 90px;
  height: 40px;
  border: 1px solid #469af8;
  text-align: center;
  line-height: 38px;
  margin-right: 20px;
  border-radius: 5px;
  font-size: 16px;
}
.btnbox {
  overflow: hidden;
  padding: 20px 0;
  margin-right: 100px;
}
.type-item{
  padding: 0 25px;
}
.type-item-title{
  position: relative;
  padding-left: 15px;
}
.type-item-title::before{
  content:'';
  width: 6px;
  height: 6px;
  background: #f5496d;
  position: absolute;
  left: 0;
  top:50%;
  margin-top:-3px;
  border-radius: 50%;
}
.type-item-title::after{
  content:'';
  width: 78%;
  position: absolute;
  right: 0;
  height: 1px;
  background-color: #d3dfe9;
  top:50%;
}
.type-item-content ul{
  overflow: hidden;
  padding: 10px 15px 10px;
}
.type-item-content ul li{
  width: 73px;
  height: 25px;
  text-align: center;
  line-height: 20px;
  color: #757a80;
  font-size: 12px;
  line-height: 25px;
  border:1px solid #469af8;
  border-radius: 4px;
  margin-right: 10px;
  margin-bottom: 18px;
  float: left;
  cursor: pointer;
}
.type-item-content ul li.active{
  color:#fff;
  background-color: #469af8;
}
.type-item-content ul li:nth-child(5n){
  margin-right: 0;
}
</style>



