<template>
  <div id="content">
    <v-header @updateMenuFn="updateMenuFn"></v-header>
    <v-menu ref="vMenu" style="z-index:98"></v-menu>
  </div>
</template>

<script>
import Header from '../components/header';
import Menu from '../components/menu';
export default {
  name: 'index',
  components: {
    'v-header': Header,
    'v-menu': Menu,
  },
  methods: {
    updateMenuFn(msg) {
      this.$refs.vMenu.updateMenu(msg);
    },
  },
}
</script>
<style scoped>
</style>



