<template>
  <div id="arranging-small">
    <panel-title :crumbs="crumbs"></panel-title>
    <template>
      <Card dis-hover  class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
        <!--课程名称-->
        <span>{{$t('LessonDetail.coursetitle')}}{{$t('Common.colon')}}</span>
        <!--输入关键字-->
        <Input style="width:200px;"
               v-model="coursename"
               :placeholder="$t('courseManagepage.coursetitle')"
               icon="ios-search" 
               @on-click="getCurricukumList(current, coursename, coursetype)"
               @on-enter="getCurricukumList(current, coursename, coursetype)"/>
        <!-- 课程类型 -->
        <span style="margin-left: 15px;">课程类型{{$t('Common.colon')}}</span>
          <i-select v-model="coursetype" 
            style="width: 200px; border:1px solid #dddee1; border-radius: 4px;"
            @on-change="getCurricukumList(current, coursename, coursetype)">
            <i-option value="">全部</i-option>
            <i-option value="1">录播课</i-option>
            <i-option value="2">直播课</i-option>
          </i-select>
        <!--添加课程-->
        <Button style="margin-left: 15px;" type="primary" @click="addScheduling()">{{$t('courseManagepage.Addcourse')}}</Button>
        <!-- <Button type="primary" shape="circle" class='fr h-search-btn' @click="getCurricukumList(current, coursename, coursetype)">查询</Button> -->
        <template>
          <Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></Table>
        </template>
        <template>
          <pagination :total="total" :display="display" :current-page="current" @pagechange="pageChange" v-if='total>0'></pagination>
        </template>
      </Card>
    </template>
    
    <Modal v-model="showModal" 
    :closable="false">
      <div slot="header">
        <!--温馨提示-->
        <span>{{$t('Common.kindlyreminder')}}</span>
      </div>
      <div v-if="this.isDelCourse == false" style="">
        <p>确认删除该课程么?</p>
      </div>
      <div v-if="this.isDelCourse == true">
        <div class="input-box">
          该课程下有开设班级，请先将班级删除，在进行此操作！
        </div>
        <template>
          <Table :columns="columnscur" :data="delCourse"></Table>
        </template>
        <div class="input-box" style="overflow: hidden">
          <pagination :total="totalpage" :display="displaypage" :current-page='curpage' @pagechange="pagechangemodal"  v-if='total>0'></pagination>
        </div>
      </div>
      <div slot="footer" v-if="this.isDelCourse == true" >
        <!--知道了-->
        <Button  type="warning" @click="showModal = false">{{$t('arrangManage.know')}}</Button>
      </div>
      <div slot="footer" v-if="this.isDelCourse == false">
        <!--确定-->
        <Button v-if="!delModalBtn"  type="primary" @click="deleteClass(delCourseId)">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button v-if="!delModalBtn"  @click="showModal = false">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle';
  import {
    ButtonGroup,
    Button,
    Card,
    Input,
    Table,
    Switch,
    Modal,
    Select,
    Option
  } from 'iview'
  import pagination from '@/components/common/pagination'
  import Server from '@/service/request'
  import requestPath from '@/service/Path'

  export default {
    name: "arranging-small",
    components: {
      ButtonGroup,
      Button,
      Card,
      Input,
      Table,
      'i-switch': Switch,
      pagination,
      Modal,
      Server,
      requestPath,
      panelTitle,
      "i-select": Select,
      "i-option": Option
    },
    data() {
      return {
        showDeleteModal:false,
        delCourseId: 0,
        activeId:0,
        status:0,
        coursetype: '',
        isshowtype:'inline-block',
        requestFlag: false,
        delModalBtn: false,
        crumbs:[{
          name: "课程列表",
          route:'/Small'
        }],
        colData: [
          {
            title:  this.$t('arrangManage.serial'), //编号
            key: 'id',
            align: 'center'
          },
          {
            //课程封页
            title: this.$t('courseManagepage.Coursescoverpage'),
            key: 'imageurl',
            align: 'center',
            render: (h, params) => {
              return h('img', {
                attrs: {
                  src: params.row.imageurl
                },
                style: {
                  width: '72px',
                  height: '54px',
                  margin: '10px 0 5px 0',
                  borderRadius: '4px'
                }
              })
            }
          },
          {
            title: this.$t('courseManagepage.coursetitle'),//课程名称
            key: 'coursename',
            align: 'center'
          },
          {
            title: "添加时间", //添加时间
            key: 'addtime',
            align: 'center'
          },
          {
            title: "课程类型", //课程类型
            key: 'classtypesstr',
            align: 'center'
          },
          {
          //上/下架"
          title: this.$t("courseManagepage.Onofftheshelves") ,
          align: "center",
          key: "status",
          render: (h, params, index) => {
            return h('div',[
              h("i-switch",
                  {
                    on: {
                      'on-change': (value) => {
                        // console.log("value:" +value);
                        this.switchChange(params.row.id, value);
                      }
                    },
                    props: {
                      value: params.row.status === 1,
                    }
                  })
            ])
          }
        },
        {
          //操作
          title: this.$t("courseManagepage.operation") ,
          align: "center",
          key: "Opreate",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                  to:'/CourseInfo/'+params.row.id+'/'+params.row.curriculumid+'?from='+ this.$route.query.from + '&classType='+ params.row.classtypes
                  //to:'/CourseInfo/'+params.row.id+'?from='+ this.$route.query.from
                },
                style:{
                  cursor:'pointer',
                  color: '#ffb932',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn yellow'
                }
              },this.$t('Common.watch')),
              h('router-link',{
                props:{
                    to:'/EditLesson/'+params.row.id+'?type=edit&from='+ this.$route.query.from + '&classType='+ params.row.classtypes
                },
                style: {
                  cursor:'pointer',
                  color: '#2693ff',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              }, this.$t('Common.edit')),
              h('div',{
                on: {
                  'click': () => {
                    this.activeId = params.row.id;
                    this.ShowModalDel();
                  }
                },
                style: {
                  cursor:'pointer',
                  color: 'red',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn red'
                }
              },this.$t('Common.delete'))
            ])
          }
        }
        ],
        tabData: [],
        total: 0,
        showModal: false,
        coursename: '',
        current: 1,
        display: 20,
        isStop:false,
        isStopText: '',
        isStopHead:'',
        isDelCourse: false,
        delCourse: [],
        totalpage: 0,
        curpage: 1,
        displaypage: 20,
        columnscur: [{
          title: "编号",
          align: "center",
          key: "id"
        },{
          title: "班级名称",
          align: "center",
          key: "gradename"
        }]
      }
    },
    created() {
      this.getCurricukumList( this.current, this.coursename, this.coursetype);
    },
    methods: {
      //更改启用状态
      switchChange(id, status) {
        Server.post(requestPath.editCurricukum, {
          id: id,
          status: status ? 1:0
        }, (res) => {
          if (res.code == 0) {
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
          this.getCurricukumList( this.current, this.coursename, this.coursetype);
        });
      },
      //删除班级
      deleteClass(){
        let {activeId}= this;
        console.log(this.activeId);
        Server.post(requestPath.editCurricukum, {
          id: activeId,
          // status: status,
          delflag: 0
        }, (res) => {
          if (res.code == 0) {
            this.$Message.success(res.info);
            this.getCurricukumList( this.current, this.coursename, this.coursetype);
            this.showModal = false;
          }else{
            this.$Message.error(res.info);
          }

        });
      },
      //删除课程时查询到的列表信息
      getCurricuClass(activeId, curpage) {
        console.log("del",this.activeId);
        Server.post(requestPath.getCurricuClass,{
          id: this.activeId,
          limit: this.curpage
        }, res => {
          if(res.code == 0){
            this.delCourse = res.data.data;
            this.isDelCourse = true;
            this.curpage = Number(res.data.pageinfo.pagenum);
            this.displaypage = res.data.pageinfo.pagesize;
            this.totalpage = res.data.pageinfo.total;
          }else {
            this.isDelCourse = false;
            this.delCourse = [];
          }
        })
      },
      pagechangemodal(currentPage) {
        this.getCurricuClass( this.activeId, currentPage);
      },
      // 查询课程列表
      getCurricukumList( current=1, coursename, classtypes) {
        Server.post(requestPath.getCurricukumList, {
          pagenum: current,
          coursename: this.coursename,
          classtypes: this.coursetype
        }, (res) => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
            this.tabData = [];
          }
        });
      },
      pageChange(currentPage) {
        this.loading = true;
        this.getCurricukumList( currentPage, this.coursename, this.coursetype);
      },
      Route(path, index) {
        this.$router.push({
          path: path,
          query: index
        })
      },
      Switch(index) {

      },
      Edit(id, classtypes) {
        this.$router.push({
          path: '/LargeEdit?from=MainLessonManagement',
          query: {
            id: id,
            classtypes: classtypes
          }
        });
      },
      ShowModalDel() {
        this.showModal = true;
        this.getCurricuClass();
      },
      // 去选择班型
      addScheduling() {
        this.$router.push('/ChooseClassType?from=MainLessonManagement')
      }
    }
  }
</script>

<style scoped lang='less'>
@import "~@/assets/less/tab.less";
</style>



