<template>
  <div id="Main">
    <!--机构课表-->
    <panel-title :crumbs="crumbs" />
    <Card dis-hover class="mytheme-container-mg-30 mytheme-pd-bottom-30 noradius-card mytheme-noborder" style="margin-top: -30px;">
      <div class="CalendarBox">
        <div class="Calendar-content">
          <div class="Cal-table-head">
            <Button class="today-btn" @click="getToDayData">{{$t('homepage.today')}}</Button>
            <div class="Cal-table-head-prev Cal-table-head-btn" @click="getPrevData()"></div>
            <div class="Cal-table-head-currentMonth">{{y}}年{{Zn[m > 12 ? 0 : m - 1]}}</div>
            <div class="Cal-table-head-next Cal-table-head-btn" @click="getNextData()"></div>
          </div>
          <div class="Col-table-content">
            <table class="Cal-table" border="1" cellspacing="0" cellpadding="0">
              <thead>
                <tr>
                  <th>一</th>
                  <th>二</th>
                  <th>三</th>
                  <th>四</th>
                  <th>五</th>
                  <th>六</th>
                  <th>日</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="da,index in data" :key="index">
                  <td v-for="d,i in da" :key="i">
                    <div
                      class="cal-item-box"
                      :class="{
                        'activeMonth':d.activeMonth,
                        'pre-month-active':d.prevMonth || d.nextMonth,
                        'active': d.active,
                        'had-class-avtive':d.prevDay&&d.num>0,
                        'next-class-avtive':d.nextDay&&d.num>0,
                        'today-prev-active':getNowTime(`${d.timestr}`, 1),
                        'today-next-active':getNowTime(`${d.timestr}`, 0),
                        'nextmonth-class-active':getNowTime(`${d.year}-${d.month}`, 2),
                        }" v-if="d.num == 0"
                        style='cursor: default;'>
                      <span class="cal-item-date" v-if="!d.active">{{d.day}}</span>
                      <span class="cal-item-text" v-if='d.active && $i18n.locale == "zh-cn"'>
                        共{{d.num}}节课
                      </span>
                    </div>
                    <div class="cal-item-box"  :class="{
                      'activeMonth':d.activeMonth,
                      'pre-month-active':d.prevMonth || d.nextMonth,
                      'active': d.active,
                      'had-class-avtive':d.prevDay&&d.num>0,
                      'next-class-avtive':d.nextDay&&d.num>0,
                      'today-prev-active':getNowTime(`${d.timestr}`, 1),
                      'today-next-active':getNowTime(`${d.timestr}`, 0),
                      'nextmonth-class-active':getNowTime(`${d.year}-${d.month}`, 2),
                      }" v-else
                      @click="getDataList(d.year,d.month,d.day)"
                    >
                      <span class="cal-item-date" v-if="!d.active">{{d.day}}</span>
                      <span class="cal-item-text"  v-if='$i18n.locale == "zh-cn"'>共{{d.num}}节课</span>
                      <span class="cal-item-text" v-else>
                        {{d.num}} {{$t('courseTablepage.Atotalofclass')}}
                      </span>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- <div class="Cal-table-tip" v-if="todaylist">
          <div class="table-tip-triangle"></div>
          <div class="table-tip-content">
            <div class="table-tip-content-title">课程安排：</div>
            <div class="table-tip-content-coursetime" v-for="(el, i) in todaylist" :key="i">
              <span class="time">{{el.todayStartTime}}~{{el.todayEndTime}}</span>
              <span class="course">{{el.periodname}}</span>
            </div>
          </div>
        </div> -->
      </div>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import { Card, Button ,Row, Col} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";

import left from "./assets/left.png";
import right from "./assets/right.png";
export default {
  name: "Main",
  components: {
    Card,
    Button,
    panelTitle,
    Row,
    Col,
  },
  data() {
    return {
      crumbs:[{
        name: this.$t('courseTablepage.courseTablepage'),
        route:''
      }],
      left,
      right,
      data:[],
      y:0,
      m:0,
      day:0,
      En:[
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ],
      Zn:[
        '1月',
        '2月',
        '3月',
        '4月',
        '5月',
        '6月',
        '7月',
        '8月',
        '9月',
        '10月',
        '11月',
        '12月',
      ],
      todaylist: [],
      nowDate: this.getNowTime()
      
    };
  },
  created() {
    //do something after creating vue instance
    this.getToDayData();
    //获取今日课表
    this.getTodayList();
  },
  methods: {
    getNowTime(d2, status) {
      let date = new Date(),
        year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        minute = date.getMinutes();
      let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
      let YearMDStr = `${year}-${month}-${day}`;
      let YearMStr = `${year}-0${month}`;
      let YearMSD2Str = `${new Date(d2).getFullYear()}-${new Date(d2).getMonth() + 1}`;

      /**
        * 如果存在参数，返回当前时间是否大于目标时间
        */
      if (d2) {
        if (status == 1) {
          return (
            new Date(YearMDStr.replace(/-/g, "/")) > new Date(d2.replace(/-/g, "/"))
          );
        } else if (status == 0) {
          if (this.$Utils.isBroswer().name == 'mozilla') {
            if (new Date(YearMStr).getTime() < new Date(YearMSD2Str).getTime()) {
              return false;
            }
          }
          //判断是否是当月，如果大于本月，返回false
          if (new Date(YearMStr.replace(/-/g, "/")) < new Date(YearMSD2Str.replace(/-/g, "/"))) {
            return false;
          }
          return (
            new Date(YearMDStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
          );
        } else if (status == 2) {
          if (this.$Utils.isBroswer().name == 'mozilla') {
            return new Date(YearMStr).getTime() < new Date(d2).getTime();
          }
          return (
            new Date(YearMStr.replace(/-/g, "/")) < new Date(d2.replace(/-/g, "/"))
          );
        }
      } else {
        return dateStr;
      }
    },
    getTodayList() {
      let date = new Date(),
        year = date.getFullYear(),
        month = (date.getMonth() + 1) > 10 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`,
        day = date.getDate() > 10 ? date.getDate() : `0${date.getDate()}`;
      let newDate = `${year}-${month}-${day}`;
      Server.post(Path.getLessonsByDate, {
        date: newDate
      }, res => {
        if(res.code == 0){
          this.todaylist = res.data.data;
          this.todaylist.forEach(el => {
            this.$set(el, 'todayStartTime', this.$Utils.formatDateStr(new Date(el.starttime), 'HH:mm'));
            this.$set(el, 'todayEndTime', this.$Utils.formatDateStr(new Date(el.endtime), 'HH:mm'));
          })
        }else{
          this.todaylist = [];
        }
      })
    },
    getDataList(y,m,d){
      this.$router.push(
        {
          path:'/MainLessonList',
          query:{ from:'MainIndex',date:`${y}-${m}-${d}` }
        }
      )
    },
    getNextData(){
      let { m } = this;
      this.m += 1;
      if(this.m > 12){
        this.y += 1;
        this.m = 1;
      }
      this.organCourseList(this.y,this.m,this.day);
    },
    getPrevData(){
      let { m } = this;
      this.m -= 1;
      if(this.m < 1){
        this.m = 12;
        this.y -= 1;
      }
      this.organCourseList(this.y,this.m,this.day);
    },
    getToDayData(){
      var date = new Date();
      this.y = date.getFullYear();
      this.m = date.getMonth()+1;
      this.day = date.getDate();
      this.organCourseList(this.y,this.m,this.day);
    },
    organCourseList(year,month,day) {
      // var date = new Date(year,month,1);
      var y = year;
      var m = month;
      var day = day;
      Server.post(Path.organCourseList,{
        date:`${year}-${month}-1`
      },res=>{
        let {code,data}= res;
        if(code===0){
          this.data = data.map((el,index)=>{
            var d = el.map((e,i)=>{
                if(e.month < m){
                  e.prevMonth = true;
                }else{
                  e.prevMonth = false;
                }
                if(e.month > m){
                  e.nextMonth = true;
                }else{
                  e.nextMonth = false;
                }

                if(e.month == m){
                  e.activeMonth = true;
                }

                if(e.day == day && e.year == new Date().getFullYear() && e.month == new Date().getMonth()+1){
                  e.active = true;
                }else{
                  e.active = false;
                }


                //判断有课状态下，是待上课还是已结课
                if(e.day < day && e.num >0){
                  e.prevDay = true;
                }else{
                  e.prevDay = false;
                }
                if(e.day > day && e.num >0){
                  if(Number(e.month)< new Date().getMonth()+1){
                    e.prevDay = true;
                  }else{
                    e.nextDay = true;
                  }
                }else{
                  e.nextDay = false;
                }
                return e;
                return e;
            })
            return d;
          });
        }
      })
    }
  },
};
</script>
<style scoped lang='less'>
 @import "~@/assets/less/variable";
.CalendarBox {
    max-width: 1250px;
    margin: 20px auto;
    .Calendar-content {
      margin-top: 10px;
      .Cal-table-head {
        position: relative;
        height: 55px;
        line-height: 55px;
        background: @color-theme;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        text-align: center;
        color: @color-white;
        .today-btn {
          position: absolute;
          left: 60px;
          top: 16px;
          border: none;
          font-size: 14px;
          background: @color-highlight-blue;
          color: @color-white;
          width: 72px;
          height: 26px;
          line-height: 16px;
          padding: 0;
        }
        .Cal-table-head-btn,
        .Cal-table-head-currentMonth {
          display: inline-block;
          cursor: pointer;
        }
        .Cal-table-head-currentMonth {
          font-size: @font-size-large-s;
          // margin-left: 14%;
        }
        .Cal-table-head-btn {
          vertical-align: middle;
          line-height: 22px;
          width: 22px;
          height: 22px;
          margin: 10px;
          &.Cal-table-head-prev {
            position: absolute;
            left: 260px;
            top: 8px;
            background: url(~@/assets/images/icon/icon_paging_l.png) no-repeat center;
            background-size: 100% 100%;
          }
          &.Cal-table-head-next {
            position: absolute;
            right: 260px;
            top: 8px;
            background: url(~@/assets/images/icon/icon_paging_r.png) no-repeat center;
            background-size: 100% 100%;
          }
        }
      }
      .Col-table-content {
        .Cal-table {
          width: 100%;
          text-align: center;
          border-collapse: collapse;
          border: 1px solid transparent;
          box-sizing: border-box;
          tr:first-child th {
            color: @color-theme;
            background: #ECF6FF;
          }
          tr:nth-child(1) {
            td {
              border-top: 1px solid #ECF6FF;
            }
          }
          tr:first-child th:last-child {
            border-right: none;
          }
          tr td:first-child {
            border-left: none;
          }
          tr td:last-child {
            border-right: none;
          }
          tr:last-child td {
            border-bottom: 1px solid #CCCCCC;
          }
          thead tr {
            height: 50px;
            font-size: 18px;
            color: @color-gray;
            background: #FCFCFC;
          }
          tbody tr td {
            position: relative;
            height: 85px;
            color: #637e95;
            vertical-align: middle;
            text-align: center;
            position: relative;
            width: 100px;
            border: 1px solid #CCCCCC;
            .cal-item-box {
              width: 100%;
              height: 100%;
              // 
              padding-top: 14px;
              &.had-class-avtive {
                cursor: pointer;
              }
              .cal-item-date {
                // position: absolute;
                // left: 0;
                // right: 0;
                // top: 10px;
                text-align: center;
                box-sizing: border-box;
                font-size: 42px;
                font-family: MicrosoftYaHei-Bold;
                color: #B8B8B8;
              }
            }
          }
        }
      }
    }
    .Cal-table-tip {
      position: relative;
      margin-top: 30px;
      padding: 0 30px;
      border: 1px dashed #BBBBBB;
      border-radius: 10px;
      .table-tip-content {
        .table-tip-content-title {
          line-height: 40px;
          font-size: @font-size-normal;
          color: #666666;
        }
        .table-tip-content-coursetime {
          line-height: 40px;
          span {
            font-size: @font-size-small;
            &.time {
              display: table-cell;
              width: 100px;
              color: @color-theme;
            }
            &.course {
              display: table-cell;
            }
          }
        }
      }
    }
  }

  .had-class-avtive .cal-item-date,
  .next-class-avtive .cal-item-date {
    color: #B8B8B8;
  }

  .had-class-avtive .cal-item-text,
  .next-class-avtive .cal-item-text,
  .active .cal-item-text {
    font-size: 14px;
  }

  /** 将来的课程 **/
  .next-class-avtive {
    pointer-events: auto;
    cursor: pointer;
    .cal-item-text {
      width: 66px;
      height: 26px;
      line-height: 26px;
      background: @color-orange;
      color: #fff;
    }
  }

  .nextmonth-class-active {
    background: #F4F8FC;
    .cal-item-date {
      color: #DFE3E6 !important;
    }
  }

  /** 大于今天的日期颜色 **/
  .today-next-active {
    .cal-item-date {
      color: @color-highlight-blue !important;
    }
  }

  /** 今天 **/
  .active {
    position: absolute;
    z-index: 3;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    border: 2px solid @color-orange;
    cursor: pointer;
    box-sizing: content-box;
    padding-top: 0px!important;
    .cal-item-text {
      width: 100%;
      height: 100%;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 85px;
      font-size: 30px;
      color: @color-orange;
      background: none;
    }
  }

  /** 存在课程的课程文字说明 **/
  .cal-item-text {
    width: 65px;
    height: 25px;
    line-height: 25px;
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    font-size: @font-size-normal;
    background: #CCCCCC;
    color: #666565;
  }

  /** 已过去的课程 **/
  .pre-month-active {
    color: #a0abb4;
    pointer-events: none;
    cursor: pointer;
  }

  /** 今天以前 **/
  .today-prev-active {
    background: #F4F8FC;
  }

  /** 今天以前且上个月的 **/
  .pre-month-active.today-prev-active {
    .cal-item-date {
      color: #DFE3E6!important;
    }
  }
</style>



