<template>
  <div class="steps" id="step">
    <div :class="['step-item','active']">
      1
      <div class="desc">
        {{desc[0]}}
      </div>
    </div>
    <div :class="['line',{'line-active':step == 2 || step == 3}]"></div>
    <div :class="['step-item',{'active':step == 2 || step == 3}]">
      2
      <div class="desc">
        {{desc[1]}}
      </div>
    </div>
    <div :class="['line',{'line-active':step == 3}]"></div>
    <div :class="['step-item',{'active':step == 3}]">
      3
      <div class="desc">
        {{desc[2]}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "step",
  data: () => ({

  }),
  props: {
    step: {  // 必须提供字段
      type: Number,
      default: 3,
      required: true
    },
    desc: {
      type:Array,
      default: [
        '填写课程信息', //..
        '填写课时信息',//..
        '填写课时信息'//
      ]
    }
  }
}
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
  .steps{
    max-width: 800px;
    margin: 0 auto 45px;
    /* overflow: hidden; */
    position: relative;
    height: 100px;
  }
  .steps *{
    display: inline-block;
    vertical-align: middle;
  }
  .step-item{
    width: 86px;
    height: 86px;
    border-radius: 50%;
    border:3px solid #d9e3ec;
    text-align: center;
    font-size: 26px;
    line-height: 81px;
    color: #cfdce7;
    position: absolute;
    z-index: 2;
    background-color: #eee;
  }
  .steps .step-item:nth-of-type(1){
    left: 0;
  }
  .steps .line:nth-of-type(2){
    top:50%;
    margin-top:-1px;
    left: 80px;
  }
  .steps .step-item:nth-of-type(3){
    left: 50%;
    margin-left: -46px;
  }
  .steps .line:nth-of-type(4){
    top:50%;
    margin-top:-1px;
    right: 80px;
  }
  .steps .step-item:nth-of-type(5){
    right: 0;
  }
  .desc{
    position: absolute;
    top: 100px;
    left: -20px;
    width:120px;
    display: block!important;
    font-size: 16px;
    color: #5e6266!important;
    line-height: 18px!important;
  }
  .line{
    position: absolute;
    z-index: 1;
    width: 300px;
    height: 2px;
    background:#62BAFE;
  }
  .line-active{
    background: #62BAFE;
  }
  .active{
    border-radius: 50%;
    width: 90px;
    height: 90px;
    background: -webkit-linear-gradient(left, #63BAFE, #2A9CF4);
    color:#fff;
    line-height: 80px;
    border: 5px solid #e0f2fe;
    text-align: center;
  }
  .active .desc{
    color:#64BFF8 !important;
    font-size: 14px;
  }

</style>



