<template>
  <div id="order-list">
    <!--帐户明细-->
    <panel-title :crumbs="[
      {
        name:$t('orderManagepage.orderManagepage'),
        route:''
      },
    ]" />

    <template>
    	
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
        <div class="Money-box clearfloat">
          <div class="fl Money-item">
            <!--可用余额-->
              <span>总交易额：</span>
              <span class="red Money-font">￥{{income}}</span>
          </div>
          <!--<div class="fr">-->
            <!--提现-->
            <!--<Button type="primary" shape="circle" @click="openModal">{{$t("orderManagepage.withdrawdeposit")}}</Button>-->
          <!--</div>-->
        </div>
        <div class="mytheme-pd-top-20">
          <ul style="line-height:35px;margin-left:-15px;">
            <li>1. 本列表只展示所有付款成功的订单，已下单但还未支付的暂不展示；</li>
            <li>2. 订单金额为实际支付的费用，不包含优惠券或积分抵扣的部分；</li>
            <li>3. 该列表展示的订单按照付款时间倒叙排序；</li>
            <li>4. 您可以输入起始时间或学生姓名搜索对应的订单。</li>
          </ul>
        </div>
      </Card>
    </template>
    <!--<template>
      <ButtonGroup class="mg-bottom-20 h-new-ButtonGroup">
        <Button :type="index==activeItem?'primary':'default'" size="large" v-for="(btn, index) in btnList" :key="btn.id" @click="Screen(index)" >
          {{btn.Name}}
        </Button>
      </ButtonGroup>
    </template>-->
    <Modal v-model="isShowAddModal">
      <p slot="header">
        <!--提现-->
        <span>{{$t('orderManagepage.withdrawdeposit')}}</span>
      </p>
      <div>
        <Form :label-width="130" class='form-confrol'>
          <Row>
            <Col span="24">
              <FormItem :label="$t('orderManagepage.Withdrawalamount')+$t('Common.colon')">
                  <!--&lt;!&ndash;可提现金额&ndash;&gt;   请输入提现金额-->
                  <Input :placeholder="$t('orderManagepage.enterWithdrawalamount')" v-model="ShenData.price"/>
                  <br> 
                  <div class='tip2'>*{{$t('orderManagepage.Withdrawalamount')}}<span class="red">{{$t('Common.symbol')}}{{hasMoney}}</span></div>
              </FormItem>

              <!--提现到-->
              <FormItem :label="$t('orderManagepage.Withdrawalto')+$t('Common.colon')">
                  <div class="payImg">
                    <img src="./assets/images/zhifubao.png" alt="" v-if='wxFlat != true' @click="wxFlat=true;palFlat=false;ShenData.paytype=3">
                    <img src="./assets/images/zhifubao_checked.png" alt="" v-else>
                    <img src="./assets/images/paypal.png" alt="" v-if='palFlat != true'  @click="wxFlat=false;palFlat=true;ShenData.paytype=5">
                    <img src="./assets/images/paypal_checked.png" alt="" v-else >
                  </div>
              </FormItem>

              <template v-if='ShenData.paytype == 3'>
                <!--收款姓名：-->
                <FormItem :label="$t('orderManagepage.payname')+$t('Common.colon')">
                    <span class='tip'>{{name}}</span>
                </FormItem>
                <!--支付宝帐户：-->
                <FormItem :label="$t('orderManagepage.Paytreasurename')+$t('Common.colon')">
                    <!--请输入支付宝帐户-->
                    <Input :placeholder="$t('orderManagepage.enterPaytreasurename')" v-model="ShenData.cashaccount"/>
                </FormItem>
              </template>

              <template v-if='ShenData.paytype == 5'>
                <!--PayPal账户：-->                
                <FormItem :label="$t('orderManagepage.Paytreasurename2')+$t('Common.colon')">
                    <Input :placeholder="$t('orderManagepage.enterPaytreasurename2')"  v-model="ShenData.cashaccount"/>
                </FormItem>
              </template>
            </Col>
          </Row>
        </Form>
      </div>
      <div slot="footer">
        <!--取消-->
        <Button type="text" shape="circle" @click="isShowAddModal = false;">{{$t('Common.cancel')}}</Button>
        <!--确认-->
        <Button type="warning" shape="circle" @click="sendShenqing">{{$t('Common.sure')}}
        </Button>
      </div>
    </Modal>
    <router-view></router-view>
  </div>
</template>

<script>
import {
  Table,
  Button,
  ButtonGroup,
  Card,
  Input,
  Icon,
  Form,
  FormItem,
  Col,
  Row,
  Modal,
  RadioGroup,
  Radio
} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "order-list",
  components: {
    RadioGroup,
    Radio,
    Table,
    Button,
    ButtonGroup,
    Card,
    panelTitle,
    Input,
    pagination,
    Icon,
    Server,
    Form,
    FormItem,
    Col,
    Row,
    Modal
  },
  data() {
    return {
      wxFlat:true,
      palFlat:false,
      ShenData:{
        price:'',
        paytype:'3',  // 2微信，3 支付宝，4 银联，5 paypal
        cashaccount:'',
      },
      income:'',
      name:'',
      
      isShowAddModal: false,
      hasMoney: 0,
      waitMoney: 0,
      activeItem: 0
//    btnList: [
//      {
//        Name: this.$t('orderManagepage.settled'), //已结算
//        Type: 1,
//        id: 1
//      },
//      {
//        Name: this.$t('orderManagepage.Forthe'), //待结算
//        Type: 2,
//        id: 2
//      }
//    ]
    };
  },
  created() {
    this.getAccountCount();
  },
  // 监听,当路由发生变化的时候执行
//watch: {
//  $route: {
//    handler: function(val, oldVal){
//      let url = val.fullPath;
//      if(url.indexOf('wait') < 0){
//        this.activeItem = 0;
//      }
//    },
//    // 深度观察监听
//    deep: true
//  }
//},
  methods: {
    openModal(){
      this.ShenData.price = '';
      this.ShenData.cashaccount='';
      this.isShowAddModal = true;
    },
    sendShenqing(){
      Server.post(Path.addWithdraw,{
        ...this.ShenData
      },res=>{
        let {code,data}=res;
        if(code===0){
          this.$Message.success(res.info);
          this.getAccountCount(); //重新
          this.isShowAddModal = false;
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    getAccountCount() {
      Server.post( Path.getAccountInfo,{
          'type':1
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.income = data.usablemoney;
          }
        }
      );
    }
    // 筛选
//  Screen(index) {
//    this.activeItem = index;
//    this.$router.push({
//      path: `${index == 0 ? "/AccountDetails" : "/AccountDetails/wait"}`,
//      query: { from: "MainOrderManagement" }
//    });
//    this.getAccountCount(index ? 0 : 1);
//  }
  },
  computed: {}
};
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.Money-box {
  // padding: 20px 60px 20px 87px;
  border-bottom: 1px dashed #e6e6e6;
  margin: 0 -16px;
  .ivu-btn{
    padding: 6px 33px;
    font-size: 16px;
    margin-top: 19px;
    box-shadow: 4px 0px 6px rgba(73,125,246,1);
  }
}
// .tip-box {
//   padding: 20px 70px;
// }
// .tip-box ul li {
//   line-height: 30px;
//   position: relative;
//   font-size: 14px;
//   color: #8E9DAA;
// }
// .tip-box ul li::after {
//   content: "";
//   width: 5px;
//   height: 5px;
//   border-radius: 50%;
//   background-color: @color1;
//   left: -15px;
//   top: 11px;
//   position: absolute;
// }
.Money-item {
  // margin-right: 150px;
  // color: #474E5D;
  font-size: 16px;
}
.Money-font {
  font-size: 36px;
  color:#F03611;
  margin: 10px 0 5px;
}
.tip{
  font-size: 14px;
  display: inline-block;
  margin-top: 8px;
  vertical-align: middle;
  line-height: 20px;
}
.tip2{
  color: #A4ABB3;
  font-size: 12px;
  display: inline-block;
  margin-top: 8px;
  vertical-align: middle;
  line-height: 20px;
}
.payImg {
  img{
    width: 162px;
    height: 60px;
    vertical-align: top;
    cursor: pointer;
  }
}
</style>



