<template>
  <div id="ClassType">
     <!--素材管理 -->
    <Card class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" dis-hover>
      <div class="mytheme-mg-top-20">
        <template>
          <!--  文件夹 -->
          <!-- <span>{{$t('materialManagepage.folder')}}</span> -->
          <!--  新建文件夹 -->
          <!-- <Button type="primary" @click="addMaterModel">{{$t('materialManagepage.newfolder')}}</Button> -->
        </template>
        <ul class="mater-box">
          <li  @click="addMaterModel">
            <div class="mater-item">
              <div class="mater-img">
                <img src="./assets/img/icon_addfolder@2x.png"/>
                <div></div>
              </div>
              <div class="mater-name">新建文件夹</div>
            </div>
          </li>
          <li
          v-for="el,index in materData"
          :key="el.fileid"
          @mouseover="el.active = true"
          @mouseout="el.active = false"
          >
            <div class="mater-item">
              <div class="mater-delete" v-if="el.active" @click="deleteModal(el.fileid,el.juniorcount)">
                <img src="./assets/img/delete.png" alt="">
              </div>
              <router-link :to="{ path: '/MaterialInfo/'+el.fileid, query: {fatherid:el.fatherid,fileid: el.fileid,filename:el.showname,from:'MainLessonManagement'} }">
                <div class="mater-img">
                  <img :src="MaterIcon" alt="">
                  <!-- 个 -->
                  <div class="origin" v-if='$i18n.locale == "zh-cn"'>
                    ({{el.juniorcount}}{{$t('materialManagepage.unit')}})
                  </div>
                  <div class="origin" v-else>
                    ({{$t('materialManagepage.unit')}} {{el.juniorcount}})
                  </div>
                </div>
                <div class="mater-name" :title="el.showname">
                    {{el.showname}}
                </div>
              </router-link>
            </div>
          </li>
        </ul>
        <!-- 暂无文件夹 -->
        <!-- <defaultShow v-else :text="$t('Common.nodata')" /> -->
      </div>
    </Card>
   <!--  新建文件夹 -->
    <Modal
      v-model="MaterModal"
      :closable="false" >
      <div slot="header">{{$t('materialManagepage.newfolder')}}</div>
      <div class="type-content">
        <Form :label-width="100" class='form-confrol mg-top-20' ref="formValidate" :model="formData" :rules="ruleValidate">
          <Form-item :label="$t('materialManagepage.name')+$t('Common.colon')" prop="materName">
            <Input v-model="formData.materName" :maxlength="20" placeholder="" @keyup.enter.native="ok()" />
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
       <!--  确认 -->
        <Button type="primary" @click="ok('formValidate')">{{$t('Common.sure')}}</Button>
        <!--  取消 -->
        <Button  @click="MaterModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
  </Modal>
  <!-- 温馨提示 -->
  <Modal
    v-model="DeleteModal"
    :closable="false" >
    <div slot="header">{{$t('Common.kindlyreminder')}}</div>
    <div>{{deleteFileText}}</div>
    <div slot="footer">
      <Button type="primary" @click="deleteFile()">{{$t('Common.sure')}}</Button>
      <Button @click="DeleteModal = false">{{$t('Common.cancel')}}</Button>
    </div>
  </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import MaterIcon from "./assets/img/MaterIcon.png";
import { Card, Input, Button, Modal, Form, Row, Col, FormItem } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";
import icon_addfolder from './assets/img/icon_addfolder@2x.png';
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    Modal,
    Form,
    "Form-item": FormItem,
    Col,
    Row,
    defaultShow,
    icon_addfolder
  },
  watch: {
    getstate: function(val) {
      if (val == 3) {
        this.MaterModal = true;
      }
    }
  },
  computed: {
    getstate() {
      return this.$store.store.state.status;
    }
  },
  methods: {
    deleteModal(id, num) {
      if (num > 0) {
        //该文件夹下有多个文件，删除文件夹会导致文件夹下的文件也被删除，您确定要删除吗？
        this.deleteFileText = this.$t("materialManagepage.remind1");
      } else {
        //您确定要删除该文件夹吗？
        this.deleteFileText = this.$t("materialManagepage.remind2");
      }
      this.fileid = id;
      this.DeleteModal = true;
    },
    //删除课件
    deleteFile() {
      let { fileid } = this;
      Server.post(
        Path.deleteFile,
        {
          fileid: fileid
        },
        res => {
          this.$Message.info(res.info);
          if (res.code == 0) {
            this.page = 1;
            this.DeleteModal = false;
            this.materData = this.materData.filter((el, index) => {
              return el.fileid != fileid;
            });
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    addMaterModel() {
      this.MaterModal = true;
    },
    ok(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let { materName } = this.formData;
          Server.post(
            Path.fileAdd,
            {
              showname: materName
            },
            res => {
              let { code, data } = res;
              if (code === 0) {
                this.$Message.success(res.info);
                this.materData = [];
                this.getMaterData();
              } else {
                this.$Message.error(res.info);
              }
              this.MaterModal = false;
              this.formData.materName = "";
            }
          );
        }
      })
    },
    cancel() {
      this.formData.materName = "";
    },

	getMaterData(page = 1){
	      Server.post(
	        Path.getFileList,
	        {
	          showname: "",
	          pagenum: page
	        },
	        res => {
	          let { code, data } = res;
	          if (code === 0) {
	            var mater = data.data.map((el, index) => {
	              el.active = false;
	              return el;
	            });
	            if (page == 1) {
	              this.materData = [...mater];
	            }
	            this.busy = false;
	          } else {
	            this.busy = true;
	          }
	          if (this.materData.length > 0) {
	            this.isshowDefault = true;
	          } else {
	            this.isshowDefault = true;
	          }
	        }
	      );
	    }
  },
  created() {
    //do something after creating vue instance
    this.getMaterData();
  },
  mounted() {},
  data() {
    return {
      page: 0,
      busy: false,
      MaterIcon,
      MaterModal: false,
      formData: {
        materName: "",
      },
      ruleValidate: {
        materName: [
          {required: true, message: "文件夹名称不能为空", trigger: 'blur' } 
        ]
      },
      materData: [],
      loading: true,

      DeleteModal: false,
      fileid: 0,
      deleteFileText: "",
      isshowDefault: true
    };
  }
};
</script>
<style scoped lang='less'>
.mater-box {
    padding: 20px;
    overflow: hidden;
    li {
      position: relative;
      width: 198px;
      height: 194px;
      float: left;
      &:hover {
        background: #ECF1FF;
      }
      .mater-item {
        position: relative;
        cursor: pointer;
        .mater-name {
          padding: 0 10px 0 10px;
          height: 48px;
          line-height: 48px;
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 14px;
          color: #666666;
        }
        .mater-img {
          width: 100%;
          height: 120px;
          text-align: center;
          font-size: 16px;
          img {
            margin: 32px auto 0 auto;
            width: 66px;
            height: 66px;
          }
          div {
            margin: 12px auto 0 auto;
            font-size: 12px;
            color: #808080;
          }
        }
      }
      .mater-delete {
        position: absolute;
        cursor: pointer;
        right: 5px;
        top: 5px;
        z-index: 3;
      }
    }
  }

  .ivu-card-head {
    span {
      font-size: 16px;
    }
  }
</style>



