<template>
<div>
  <panel-title :crumbs="crumbs" />
  <!-- 展示页面 -->
  <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
    <div class="mytheme-mg-top-20 title">
      <!--关于我们-->
      <span>下载配置</span>
      <!--编辑-->
      <div class="fg" v-if="!isShowEdit">
        <Button type="primary" @click="downloadEdit()">编辑</Button>
      </div>
    </div>
    <div class="content" v-if="!isShowEdit">
        <Row>
            <Col span="20" offset="2" class="item mb20">
                <div class="fl">柠檬树下大语文：</div>
                <div class="fr">
                    <div class="imgcode">
                        <div class="imgWrap"><img :src="datalist.qrcodeandroid || ''" alt="" @error="getCodeImg"></div>
                        <span>android下载</span>
                    </div>
                    <div class="imgcode">
                        <div class="imgWrap"><img :src="datalist.qrcodeios || ''" alt=""  @error="getCodeiosImg"></div>
                        <span>ios下载</span>
                    </div>
                </div>
            </Col>
            <Col span="20" offset="2" class="item">
                <div class="fl">Cloudclass插件：</div>
                <div class="fr">
                    <div> <span class="fr-item1">Windows系统</span>{{datalist.cloudclasswindows}} </div>
                    <div> <span class="fr-item1">Mac系统</span>{{datalist.cloudclassmac}}</div>
                </div>
            </Col>
            <Col span="20" offset="2" class="item">
                <div class="fl">TeamView：</div>
                <div class="fr">
                    <div> <span class="fr-item1">Windows系统</span>{{datalist.teamviewwindows}} </div>
                    <div> <span class="fr-item1">Mac系统</span>{{datalist.teamviewmac}}</div>
                </div>
            </Col>
            <Col span="20" offset="2" class="item">
                <div class="fl">Chrome浏览器：</div>
                <div class="fr">
                    <div> <span class="fr-item1">Windows系统</span>{{datalist.chromewindows}} </div>
                    <div> <span class="fr-item1">Mac系统</span>{{datalist.chromemac}}</div>
                </div>
            </Col>
            <Col span="20" offset="2" class="item">
                <div class="fl">360极速浏览器：</div>
                <div class="fr">
                    <div><span class="fr-item1">XP系统</span>{{datalist.browser360xp}}</div>
                </div>
            </Col>
        </Row>
    </div>
    <div class="content" v-if="isShowEdit">
        <Form ref="formData" :model="formData" :rules="ruleValidate">
            <Row>
                <Col span="20" offset="2" class="item mb20">
                    <div class="fl">柠檬树下大语文：</div>
                    <div class="fr">
                        <div class="imgcode">
                            <Upload
                            :data="uploadid"
                            @success="getCodeAndUrl">
                            <div class="loadImg">
                                <img class="photo" :src="formData.qrcodeandroid" alt="">
                            </div>
                            </Upload>
                            <span>android下载</span>
                        </div>
                        <div class="imgcode">
                            <Upload
                            :data="uploadid"
                            @success="getCodeIosUrl">
                            <div class="loadImg">
                                <img class="photo" :src="formData.qrcodeios" alt="">
                            </div>
                            </Upload>
                            <span>ios下载</span>
                        </div>
                    </div>
                </Col>
                <Col span="20" offset="2" class="item">
                    <div class="fl">Cloudclass插件：</div>
                    <div class="fr">
                        <div><span class="fr-item">Windows系统</span><FormItem class="formItem" prop="cloudclasswindows"> <Input class="form-control" v-model="formData.cloudclasswindows"/></FormItem></div>
                        <div><span class="fr-item">Mac系统</span><FormItem class="formItem" prop="cloudclassmac"> <Input class="form-control" v-model="formData.cloudclassmac"/></FormItem></div>
                    </div>
                </Col>
                <Col span="20" offset="2" class="item">
                    <div class="fl">TeamView：</div>
                    <div class="fr">
                        <div><span class="fr-item">Windows系统</span><FormItem class="formItem" prop="teamviewwindows"> <Input class="form-control" v-model="formData.teamviewwindows"/></FormItem></div>
                        <div><span class="fr-item">Mac系统</span><FormItem class="formItem" prop="teamviewmac"> <Input class="form-control" v-model="formData.teamviewmac"/></FormItem></div>
                    </div>
                </Col>
                <Col span="20" offset="2" class="item">
                    <div class="fl">Chrome浏览器：</div>
                    <div class="fr">
                        <div><span class="fr-item">Windows系统</span><FormItem class="formItem" prop="chromewindows"> <Input class="form-control" v-model="formData.chromewindows"/></FormItem></div>
                        <div><span class="fr-item">Mac系统</span><FormItem class="formItem" prop="chromemac"> <Input class="form-control" v-model="formData.chromemac"/></FormItem></div>
                    </div>
                </Col>
                <Col span="20" offset="2" class="item">
                    <div class="fl">360极速浏览器：</div>
                    <div class="fr">
                        <div><span class="fr-item">XP系统</span><FormItem class="formItem" prop="browser360xp"><Input class="form-control" v-model="formData.browser360xp"/></FormItem></div>
                    </div>
                </Col>
                <Col span="20" offset="2">
                    <div style="text-align: center; margin-top: 20px;">
                        <!--确认-->
                        <Button type="primary" @click="saveData('formData')">确认</Button>
                        <!--取消-->
                        <Button @click="concel" style="margin-left: 20px;">取消</Button>
                    </div>
                </Col>
            </Row>
        </Form>
    </div>
  </Card>
  
</div>
</template>
<script>
import { Card, Button, Row, Col, Form, FormItem, Input } from "iview";
import panelTitle from "@/components/panelTitle";
import Upload from "@/components/UploadPicture";
import codeImg from "./assets/icon_addfolder@2x.png";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "DownloadSetting",
  components: { panelTitle, Card, Button, Row, Col, Form, FormItem, Input, Upload },
  data() {
    // const validHttp = (rule, value, callback) => {
    //   let httpRegex = /^http(s)?:\/\/.+/;
    //   if (value === '') {
    //     callback(new Error('请输入网址!'));
    //   } else {
    //     if (!httpRegex.test(value)) {
    //       callback(new Error('请编辑http(s)://协议'))
    //     }else {
    //       callback();
    //     }
    //   }
    // };  //validator: validHttp
    return {
      crumbs: [{ name: "下载配置", route: "" }],
      datalist: {},
      noData: '---',
      isShowEdit: false,
      uploadid:[10,1,1],
      formData: {
        qrcodeandroid: codeImg, //android 二维码
        qrcodeios: codeImg, //ios二维码
        cloudclasswindows: "", // cloudclass window系统
        cloudclassmac: "", // cloudclass mac系统
        teamviewwindows: "", //teamview window系统
        teamviewmac: "", //teamview mac系统
        chromewindows: "", //chrome window系统
        chromemac: "", // chrome mac 系统
        browser360xp: "" //360极速xp系统
      },
      ruleValidate: {
        cloudclasswindows: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        cloudclassmac: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        teamviewwindows: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        teamviewmac: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        chromewindows: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        chromemac: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
        browser360xp: [
          {required: true, message:"请输入网址", trigger: 'blur'}
        ],
      }
    };
  },
  created() {
    this.getOrganDownloadJson();
  },
  methods: {
    //获取默认二维码数据
    getCodeImg(e) {
        e.target.src = codeImg;
    },
    getCodeiosImg(e) {
        e.target.src = codeImg;
    },
    //获取下载配置项
    getOrganDownloadJson() {
        Server.post(Path.getOrganDownloadJson, {},
        res => {
            if(res.code === 0){
               this.datalist = res.data||{};
            } else{
                this.$Message.error(res.info);
                this.datalist = {};
            }
        })
    },
    //编辑页面
    downloadEdit() {
        this.isShowEdit = true;
        
        Server.post(Path.getOrganDownloadJson, {},
        res => {
            if(res.code === 0){
               res.data = res.data||{}
               this.formData.qrcodeandroid = res.data.qrcodeandroid||codeImg;
               this.formData.qrcodeios = res.data.qrcodeios||codeImg;
               this.formData.cloudclasswindows = res.data.cloudclasswindows;
               this.formData.cloudclassmac = res.data.cloudclassmac;
               this.formData.teamviewwindows = res.data.teamviewwindows;
               this.formData.teamviewmac = res.data.teamviewmac;
               this.formData.chromewindows = res.data.chromewindows;
               this.formData.chromemac = res.data.chromemac;
               this.formData.browser360xp = res.data.browser360xp;
            } else{
                this.$Message.error(res.info);
            }
        })
    },
    //android 二维码
    getCodeAndUrl(imgdata) {
        this.formData.qrcodeandroid = imgdata;
    },
    //ios 二维码
    getCodeIosUrl(imgdata) {
        this.formData.qrcodeios = imgdata;
    },
    //取消
    concel() {
        this.isShowEdit = false;
    },
    //判断是否加http(s)协议,否则默认加上http://
    setHtp(url){
        return url = url.substr(0,7).toLowerCase() == "http://" || url.substr(0,8).toLowerCase() == "https://" ? url : "http://" + url;
    },
    //确定
    saveData(name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                let {qrcodeandroid, qrcodeios, cloudclasswindows, cloudclassmac, teamviewwindows, teamviewmac, chromewindows, chromemac, browser360xp } = this.formData;
                //判断是否加了http://协议
                cloudclasswindows = this.setHtp(cloudclasswindows);
                cloudclassmac = this.setHtp(cloudclassmac);
                teamviewwindows = this.setHtp(teamviewwindows);
                teamviewmac = this.setHtp(teamviewmac);
                chromewindows = this.setHtp(chromewindows);
                chromemac = this.setHtp(chromemac);
                browser360xp = this.setHtp(browser360xp);
                
                Server.post(Path.setOrganDownloadJson, {
                    'qrcodeandroid': qrcodeandroid,
                    'qrcodeios': qrcodeios,
                    'cloudclasswindows': cloudclasswindows,
                    'cloudclassmac': cloudclassmac,
                    'teamviewwindows': teamviewwindows,
                    'teamviewmac': teamviewmac,
                    'chromewindows': chromewindows,
                    'chromemac': chromemac,
                    'browser360xp': browser360xp
                }, res => {
                    let { code, data, info} = res;
                    if(code == 0){
                       this.isShowEdit = false;
                       this.getOrganDownloadJson();
                    }else{
                        this.$Message.error(info);
                    }
                })
            }
        })
        
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/less/table.less";
.title{
  color: #666;
  font-size:16px;
  font-family:MicrosoftYaHei-Bold;
  font-weight:bold;
  line-height: 36px;
  overflow: hidden;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
}
.content{
  font-size: 16px;
  .ivu-form-item{
      margin-bottom: 0;
  }
  .item{
      &.mb20{
          margin-bottom: 20px;
      }
      .fl{
          width: 150px;
          text-align: right;
      }
      .fr{
          float: left;
        .fr-item{
            text-align: right;
            width: 120px;
            display: inline-block;
            margin-right: 12px;
            margin-bottom: 32px;
        }
        .fr-item1{
            text-align: right;
            width: 120px;
            display: inline-block;
            margin-right: 12px;
            margin-bottom: 16px;
        }
        .formItem{
            display: inline-block;
            vertical-align: middle;
        }
        .form-control{
            width: 280px;
        }
        .imgcode{
            display: inline-block;
            margin-right: 80px;
            width: 120px;
            .imgWrap, .loadImg{
                width: 120px;
                height: 120px;
                cursor: pointer;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            span{
                width: 120px;
                line-height: 32px;
                text-align: center;
                display: inline-block;
            }
            &:last-child{
                margin-right: 0;
            }
        }
      }
  }
}

</style>



