<template>
  <div id="default-img">
    <img class="defaultimg" :src="imgUrl" alt="" ref="img"  id='p2' />
  </div>
</template>
<script>
import defaultimg from './assets/img/default.png'

export default {
  name: "default-img",
  props:{
    sourceimg:{
      default:defaultimg,
      type:String
    }
  },
  created() {
    //do something after creating vue instance
    // document.getElementById("p2").style.display="block !important";
  },
  mounted() {
    //do something after mounting vue instance
    this.$refs.img.onerror = () => {
      this.imgUrl = defaultimg;
    }
  },
  data(){
    return {
      // showStyle:'block !important',
      imgUrl: this.sourceimg==''?defaultimg:this.sourceimg
    }
  },
  watch:{
    sourceimg:function(newVal,oldVal){
      this.imgUrl = newVal;
    }
  }
}
</script>
<style scoped>
#default-img {
  width: 100%;
  height: 100%;
}
.defaultimg{
  width: 100%;
  height: 100%;
}
</style>



