<template>
  <div id="refund">
    <panel-title :crumbs="[
      {
        name:'首页轮播图',
        route:''
      },
    ]" />
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
          <!-- <h4 style="">广告说明</h4> -->
          <div class="head">
            <img :src="explain">
            <span>广告说明</span>
          </div>
          <p style="margin:40px 0;">您可以根据需要在官网添加首页广告页，添加完成后，将在首页以轮播的方式显示</p>
      </Card>
      
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
        <div class="mytheme-mg-top-20">
          <router-link :to="{ path: '/AdvertIndex', query: {from:'MainBuyManagement'}}">
            <Button  type="primary">添加广告</Button>
          </router-link>
        </div>
        <Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :loading="loading" :columns="colData" :data="tabData"></Table>
       </Card>
      <Modal v-model="isShowDelModal" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除该广告页？</div>
      <div slot="footer">
        <Button type="primary" @click="deldata()">确认</Button>
        <Button @click="isShowDelModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { Table, Card, Button, ButtonGroup, Input, Icon, Modal } from "iview";
import panelTitle from "@/components/panelTitle";
import explain from "./img/explain@2x.png";

import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "refund",
  components: {
    panelTitle,
    Table,
    Card,
    Button,
    ButtonGroup,
    Input,
    Icon,
    Modal
  },
  created() {
    //do something after creating vue instance
    this.getData();
  },
  data() {
    return {
      explain,
    	isShowDelModal: false,
      loading: true,
      id:0,
      pageName:'',
      colData: [
        { title: "编号", key: "id", align: "center" },
        { title: "广告名称", key: "remark", align: "center" },
        { title: "跳转页面",
          key: "imagepath",
          align: "center" ,
          render:(h,params)=>{
            let pageName='';
            let urltype = params.row.urltype;
            switch(urltype){
            case 1:
                pageName=params.row.coursename;break;
            case 2:
                pageName=params.row.teachername;break;
            default:
                pageName=params.row.url;break;
            }
            return h('div',{},pageName)
          }
        },
        {
          title: "设置",
          key: "orderEdit",
          render: (h, params, index) => {
              let pageName='';
              let urltype = params.row.urltype;
              switch(urltype){
              case 1:
                  pageName=params.row.coursename;break;
              case 2:
                  pageName=params.row.teachername;break;
              default:
                  pageName=params.row.url;break;
              }
            return h('div',[
              h(
                "router-link",
                {
                  style: {
                    color: "#3F9CE8"
                  },
                  props: {
                    //  + '&dec=' + params.row.remark + "&pageName=" + pageName
                    to: "/AdvertIndex?from=MainBuyManagement&id="+params.row.id+ "&pageName=" + pageName
                  }
                },
                "编辑"
              ),
              h(
                "span",
                {
                  style: {
                    color: "#ED3825",
                    marginLeft:'35px',
                    cursor:'pointer'
                  },
                  on: {
                    'click': () => {
                    	this.isShowDelModal= true,
                     		this.id = params.row.id;
                    }
                  },
                },
                "删除"
              ),
            ])

          },
          align: "center"
        }
      ],
      tabData: []
    };
  },
  methods: {
    AddAdver() {},
    getData() {//获取首页广告列表
      Server.post(Path.HomepageAdsList, {}, res => {
        let { code, data } = res;
        if (code === 0) {
          this.tabData = data;
        }
        this.loading = false;
      });
    },
    // 查看详情
    ShowDetails() {
      this.$router.push("/OrderDetails");
      // this.$router.push({name: 'OrderDetails'});
    },
    // 筛选数据
    btnChange(val) {
      this.DataType = val.type;
    },
    deldata (id) {///删除首页广告
      Server.post(Path.HomepageAdsDel,{
        adsid:this.id
      },res=>{
        let {code,data}=res;
        if(code == 0){
        	this.isShowDelModal= false,
          this.$Message.success(res.info);
          this.getData();
        }else{
          this.$Message.error(res.info);
        }
      })
    }
  }
};
</script>

<style scoped lang='less'>
 .head{
    display: flex;
    align-items: center;
    padding-bottom:20px;
     border-bottom:dashed 1px #E6E6E6;
    img{
      width: 18px;
      height: 18px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
</style>



