<template>
  <div>
    <panel-title :crumbs="pathArr" />
     <Card dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30">
      <div slot="title" class="head">
        <img :src="fillIn">
        <span>录入习题</span>
      </div>
         <Form>
            <Row>
                <Col>
                    <FormItem label="课程名称：">
                        <Select v-model="courseId" filterable :disabled='notSelect' @on-change="getPeriodList(courseId)" placeholder="请选择课程" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
                            <Option v-for="item in classifyOne" :value="item.id" :key="item.id">{{item.coursename}}</Option>
                        </Select>
                    </FormItem>
                </Col>
            </Row>
            <Row>
                <Col>
                    <FormItem label="课时内容：">
                        <Select v-model="periodId" filterable :disabled='notSelect' placeholder="请选择课时"  @on-change="checkHaveExercise()" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
                            <Option v-for="item in classifyTow" :value="item.id" :key="item.id">{{item.periodname}}</Option>
                        </Select>
                    </FormItem>
                </Col>
            </Row>
            <!-- <Row v-if="$route.params.enterType==2">
                <Col>
                    <FormItem>
                        <h3 style="width:870px;text-align: center;">{{choiceQues.periodname}}</h3>
                    </FormItem>
                </Col>
            </Row> -->
            <Row>
                <Col>
                    <FormItem>
                        <!-- :disabled="courseId=='' || periodId==''" -->
                        <Button type="warning" :disabled='replaceBol' @click="initAddBut()">添加题目</Button>
                    </FormItem>
                </Col>
            </Row>
            <Row>
            <Col>
                <FormItem label="作业："> </FormItem>
                    <section class="homework mytheme-mg-bottom-30">
                        <div v-for='(item,k,index) in getQuesNum(choiceQues.subject)' :key="item.index">
                            <h4>
                                <span>{{index=='0'?'一':(index=='1'?'二':(index=='2'?'三':'四'))}}、</span>
                                <span>{{k=='1'?'单选题':(k=='2'?'多选题':(k=='3'?'填空题':'作文题'))}}。</span>
                                <span>({{totalScore(item)}}分)</span>
                            </h4>
                            <dl v-for='(i,indexs) in item' :key="i.indexs">
                                <dt style="width:586px;word-break:break-all;">{{indexs+1}}、{{i.name}}</dt>
                                <dd  v-if="i.imageurl" >
                                     <img :src="'http://'+img" v-for="img in transfer(i.imageurl)" :key="img.id" style="width:150px;height:100px;margin-right:15px;border:solid 1px #eee;" />
                                </dd>
                                <dd v-if="k==1||k==2">
                                    <p v-for="option in i.options" :key="option.id">{{option}}</p>
                                </dd>
                                <!--<dd v-if="i.analysis">解析：{{i.analysis}}</dd>-->
                                <dd>
                                    <span v-if="i.correctanswer" style="word-break:break-all;">答案：{{i.correctanswer}}</span>
                                    <span v-if="i.analysis" style="word-break:break-all;">解析：{{i.analysis}}</span>
                                </dd>
                                <dd class="delStyle">
                                    <span @click="showEditModal(indexs,k,i)">修改</span>
                                    <span @click="showDelModal(indexs,k)">删除</span>
                                </dd>
                            </dl>
                        </div>
                    </section>
            </Col>
            </Row>
             <Row style="width:870px;text-align: center;">
                <Col>
                    <!-- 保存 -->
                    <Button type="primary"  @click="addExercise()">完成</Button>
                    <!-- 取消 -->
                    <Button @click="$router.push({path:'/QuestionBank',query:{from:'TeachingAffairs'}})">取消</Button>
                </Col>
            </Row>
        </Form>
      </Card>
       <!--删除提示弹框-->
      <Modal
            v-model="hintModel"
            :closable="false">
        <div slot="header">温馨提示</div>
        <div class="type-content">
           <p>您确定要删除该题目吗？</p>
        </div>
        <div slot="footer">
            <!--确认-->
            <Button type="primary" @click="delSubject()">确认</Button>
            <!--取消-->
            <Button @click="hintModel = false;">取消</Button>
        </div>
    </Modal>
   <!--编辑题目弹框-->
    <Modal
        width="715"
        :closable="false"
        v-model="addQuesModel">
        <div slot="header">{{addEditText}}</div>
        <div style="padding:0 120px;">
            <Row class="mytheme-pd-bottom-20 ">
                <Col>
                    <RadioGroup v-model="quesType" class="radioGroup">
                        <Radio :disabled="changeType" v-for="i in radios" :label='i.label' :key='i.lable'>{{i.value}}</Radio>
                    </RadioGroup>
                </Col>
            </Row>
            <Form ref="topic" :model="topic" :rules="ruleValidate" :label-width="110">
            <!-- 题干-->
                <Row>
                    <Col>
                        <FormItem prop="stem" label="题干：">
                            <Input v-model.trim="topic.stem" placeholder="请输入题干名称,长度不超过100个汉字" :maxlength="100" style="width: 288px;"/>
                        </FormItem>
                    </Col>
                </Row>
            <!--图片-->
                <Row>
                    <Col>
                        <FormItem label="上传图片：">
                            <div style="display:flex;">
                                <!-- 展示区 -->
                                <div v-if="topic.imagesArr[0]!=''" class="demo-upload-list" v-for="(item,index) in topic.imagesArr" :key="index">
                                    <img  :src="'http://'+item">
                                    <div class="demo-upload-list-cover">
                                        <Icon type="ios-eye-outline" @click.native="handleView(item)"></Icon>
                                        <Icon type="ios-trash-outline" @click.native="handleRemove(index)"></Icon>
                                    </div>
                                </div>
                                <!-- 上传区-->
                                <Upload
                                    v-if="topic.imagesArr.length<4"
                                    :action="imgDate"
                                    ref="upload"
                                    name="uploadFile"
                                    :data="{allpathnode:'11,1,1',filetype:'1'}"
                                    :show-upload-list="false"
                                    :on-success="handleSuccess"
                                    >
                                    <div class="demo-upload-list">
                                        <img class="photo" src="./assets/images/uploadIcon@2x.png" alt="">
                                    </div>
                                </Upload>
                            </div>
                            <!-- 图片双击放大区 -->
                            <Modal v-model="visible" style="position:fixed;z-index:2000;">
                                <div slot="header">图片预览</div>
                                <img :src="'http://'+topic.imgName" v-if="visible" style="width: 100%">
                                 <div slot="footer">
                                    <!--确认-->
                                    <!-- <Button type="primary" @click="visible=false">确认</Button> -->
                                    <!--取消-->
                                    <!-- <Button @click="visible=false">取消</Button> -->
                                </div>
                            </Modal>
                        </FormItem>
                    </Col>
                </Row>
                <!-- 选项-->
                <Row  v-if="quesType=='1' || quesType=='2'">
                    <Col>
                        <FormItem label="选项："></FormItem>
                        <div>
                            <Row>
                                <Col>
                                    <FormItem prop="optionA" label="A">
                                        <Input v-model.trim="topic.optionA" placeholder="请输入选项A,长度不超过50个汉字" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionB" label="B">
                                        <Input v-model.trim="topic.optionB" placeholder="请输入选项B,长度不超过50个汉字" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionC" label="C">
                                        <Input v-model.trim="topic.optionC" placeholder="请输入选项C,长度不超过50个汉字" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <FormItem prop="optionD" label="D">
                                        <Input v-model.trim="topic.optionD" placeholder="请输入选项D,长度不超过50个汉字" :maxlength="50" style="width: 288px;"/>
                                    </FormItem>
                                </Col>
                            </Row>
                        </div>
                    </Col>
                </Row>
                <!-- 单选框-->
                <Row v-if="quesType =='1'">
                    <Col>
                        <FormItem prop="" label="">
                            <RadioGroup v-model="topic.answer" class="radioGroup">
                                <Radio v-for="i in radioOpt" :label='i' :key='i'>{{i}}</Radio>
                            </RadioGroup>
                        </FormItem>
                    </Col>
                </Row>
                <!-- 多选框-->
                <Row v-if="quesType =='2'">
                    <Col>
                        <FormItem prop="" label="">
                            <CheckboxGroup class="radioGroup" v-model='multiSelect' @on-change="handleCheck">
                                <Checkbox  v-for="(item,index) in radioCheck" :key="index"  :label="item.label">
                                    {{item.name}}
                                </Checkbox>
                            </CheckboxGroup>
                        </FormItem>
                    </Col>
                </Row>
                <!-- 答案-->
                <Row v-if="quesType =='3'">
                    <Col>
                        <FormItem prop="answer" label="答案：">
                            <!-- <span v-if="quesType ==1||2">{{topic.answer}}</span>  -->
                            <Input v-model.trim="topic.answer" placeholder="请输入答案,长度不超过100个汉字" :maxlength="100" style="width: 288px;"/>
                        </FormItem>
                    </Col>
                </Row>
                <!-- 解析-->
                <Row v-if="quesType =='4'">
                    <Col>
                        <FormItem prop="analytic" label="解析：">
                            <Input v-model.trim="topic.analytic" type="textarea" :rows="5" placeholder="请输入解析,长度不超过300个汉字" :maxlength="300" style="width: 288px;resize:none" />
                        </FormItem>
                    </Col>
                </Row>
                <!-- 分值-->
                <Row>
                    <Col>
                        <FormItem prop="score" label="分值：">
                            <!-- onkeyup="this.value=this.value.replace(/\D/g,'')" onafterpaste="this.value=this.value.replace(/\D/g,'')" -->
                            <Input v-model.trim="topic.score" :maxlength="3" placeholder="请输入分值" style="width: 210px;margin-right:10px;"/>分
                        </FormItem>
                    </Col>
                </Row>
            </Form>
        </div>
        <div slot="footer">
            <!--确认-->
            <Button type="primary" @click="saveSubject('topic')">确认</Button>
            <!--取消-->
            <Button @click="handleReset('topic')">取消</Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import {
  Card,
  Col,
  Input,
  Select,
  Option,
  Button,
  Modal,
  Form,
  FormItem,
  Row,
  RadioGroup,
  Radio,
  Upload,
  Icon,
  CheckboxGroup,
  Checkbox
} from "iview";
import panelTitle from "@/components/panelTitle";
import fillIn from "./assets/images/fillIn@2x.png";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "editQues",
  components: {
    panelTitle,
    Card,
    Col,
    Input,
    Select,
    Option,
    Button,
    Modal,
    Form,
    FormItem,
    Row,
    RadioGroup,
    Radio,
    Upload,
    Icon,
    CheckboxGroup,
    Checkbox
  },
  data() {
    const emptyRegex = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("内容不能为空"));
      }
      callback();
    };
    const optionRegex = (rule, value, callback) => {
      if (this.quesType == 1 || this.quesType == 2) {
        if (value === "") {
          callback(new Error("内容不能为空"));
        }
      }
      callback();
    };
    const analyRegex = (rule, value, callback) => {
      if (this.quesType == 4) {
        if (value === "") {
          callback(new Error("内容不能为空"));
        }
      }
      callback();
    };
    const ScoreRegex = (rule, value, callback) => {
      let scoreRegex = /^100$|^([1-9]|[1-9]\d)$/;
      // let scoreRegex = /^[1-9]\d*$/;
      if (value === "") {
        callback(new Error("内容不能为空"));
      } else if (!scoreRegex.test(value)) {
        callback(new Error("请输入大于0小于等于100的整数"));
      } else {
        callback();
      }
    };
    return {
      fillIn,
      imgDate:
        window.location.origin === "http://127.0.0.1:8080" ||
        window.location.origin === "http://127.0.0.1:8081"
          ? "http://www.lemonedu.com/teacher/Uploadfile/Upload"
          : "/teacher/Uploadfile/Upload",
      pathArr: [], //面包屑路由数组
      classifyOne: [], //课程列表
      courseId: "", //课程ID
      classifyTow: [], //课时列表
      periodId: "", //课时ID
      notSelect: false, //题目录入时课程及课时的选择是否可用
      replaceBol: true, //重复课时时禁用添加按钮
      addQuesModel: false, //题目录入框的显示隐藏
      quesType: "1", //题型
      topic: {
        stem: "", //题干
        imagesArr: [], //图组
        imgName: "", //图片名称
        optionA: "", //四个选项
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //答案
        analytic: "", //解析
        score: "" //分值
      },
      visible: false, //图片是否放大展示
      ruleValidate: {
        stem: [{ required: true, validator: emptyRegex, trigger: "blur" }],
        optionA: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionB: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionC: [{ required: true, validator: optionRegex, trigger: "blur" }],
        optionD: [{ required: true, validator: optionRegex, trigger: "blur" }],
        answer: [{ required: true, validator: emptyRegex, trigger: "blur" }],
        analytic: [{ required: true, validator: analyRegex, trigger: "blur" }],
        score: [{ required: true, validator: ScoreRegex, trigger: "blur" }]
      },
      choiceQues: {
        //一套试题
        courseid: "",
        periodid: "",
        periodname: "",
        subject: { "1": [], "2": [], "3": [], "4": [] }
      },
      single: [], //单选题组
      multiple: [], //多选题组
      gapFilling: [], //填空题组
      composition: [], //作文题组
      radios: [
        {
          value: "单选题",
          label: "1"
        },
        {
          value: "多选题",
          label: "2"
        },
        {
          value: "填空题",
          label: "3"
        },
        {
          value: "作文题",
          label: "4"
        }
      ],
      radioOpt: ["A", "B", "C", "D"], //单选
      radioCheck: [
        { label: "A", name: "A" },
        { label: "B", name: " B" },
        { label: "C", name: "C" },
        { label: "D", name: "D" }
      ], //多选
      saveAnswer: [], //储存多选题的答案
      hintModel: false, //提示框（删除）
      delIndex: "",
      delKey: "",
      isHandle: "add",
      addEditText: "录入试题",
      multiSelect: [],
      changeType: false
    };
  },
  methods: {
    transfer(img) {
      //图片格式的转换
      return img.split(",");
    },
    //添加按钮
    initAddBut() {
      if (this.courseId == "" || this.periodId == "") {
        this.$Message.error("请先选择相应的课程及课时"); //请输入题干
      } else {
        this.addQuesModel = true;
        this.isHandle = "add";
        this.$refs["topic"].resetFields();
      }

      this.quesType = "1";
      this.topic = {
        stem: "", //题干
        imagesArr: [], //图组
        imgName: "", //图片名称
        optionA: "", //四个选项
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //答案
        analytic: "", //解析
        score: "" //分值
      };
    },
    //获取所有课程
    getAllCurriculum() {
      Server.post(
        Path.getAllCurriculum,
        {
          coursename: ""
        },
        res => {
          if (res.code === 0) {
            this.classifyOne = res.data;
          }
        }
      );
    },
    //获取课程下课时列表
    getPeriodList(id) {
      Server.post(
        Path.getPeriodList,
        {
          curriculumid: id,
          periodname: ""
        },
        res => {
          if (res.code === 0) {
            this.classifyTow = res.data;
          }
        }
      );
    },
    //获取课程下课时列表时判断是否该课时下已有试题
    checkHaveExercise() {
      if (this.$route.params.enterType == 2) {
        return;
      }
      Server.post(
        Path.checkHaveExercise,
        {
          courseid: this.courseId,
          periodid: this.periodId
        },
        res => {
          if (res.code === 70412) {
            if (this.$route.params.enterType == 1) {
              this.$Message.error(res.info);
            }
            this.replaceBol = true;
          } else {
            this.replaceBol = false;
          }
        }
      );
    },
    handleCheck(nData) {
      //多选题时获取所有选中的选项
      if (nData == null || nData == "") {
        this.topic.answer = "";
      } else {
        this.topic.answer = nData.join().toUpperCase();
      }
    },
    //is添加 or 编辑 ？
    saveSubject(name) {
      this.changeType = false;
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.isHandle == "add") {
            this.addSubject();
          } else {
            this.editSubject();
          }
          this.addQuesModel = false;
        }
      });
      // this.$refs[name].resetFields();
    },
    handleReset(name) {
      this.changeType = false;
      //录题的取消按钮
      this.$refs[name].resetFields();
      this.addQuesModel = false;
      this.topic.analytic = "";
      this.multiSelect = [];
    },
    //添加题目
    addSubject() {
      let imgStr = this.topic.imagesArr.join(",");

      let item = {
        type: this.quesType,
        name: this.topic.stem,
        imageurl: imgStr,
        options: [
          "A." + this.topic.optionA,
          "B." + this.topic.optionB,
          "C." + this.topic.optionC,
          "D." + this.topic.optionD
        ],
        analysis: this.topic.analytic,
        correctanswer: this.topic.answer,
        score: this.topic.score
      };

      if (this.quesType == 1) {
        this.single.push(item);
      } else if (this.quesType == 2) {
        this.multiple.push(item);
      } else if (this.quesType == 3) {
        item.options = [];
        this.gapFilling.push(item);
      } else if (this.quesType == 4) {
        this.composition.push(item);
      }

      this.choiceQues.courseid = this.courseId;
      this.choiceQues.periodid = this.periodId;
      this.choiceQues.subject["1"] = this.single;
      this.choiceQues.subject["2"] = this.multiple;
      this.choiceQues.subject["3"] = this.gapFilling;
      this.choiceQues.subject["4"] = this.composition;
    },
    //获取题号
    getQuesNum(obj) {
      for (let k in obj) {
        if (obj[k].length == 0) {
          delete obj[k];
        }
      }
      return obj;
    },
    //计算满分
    totalScore(arr) {
      let sum = 0;
      arr.forEach(item => {
        sum += parseInt(item.score);
      });
      return sum;
    },
    //回显到修改框
    showEditModal(index, k, item) {
      //  this.getExerciseinfo()
      this.changeType = true;
      this.isHandle = "edit";

      this.addQuesModel = true;
      this.addEditText = "修改试题";
      this.delIndex = index;
      this.delKey = k;

      this.quesType = k;
      this.topic.stem = item.name;
      if (item.imageurl != null) {
        this.topic.imagesArr = item.imageurl.split(",");
      }
      // console.log(item.imageurl)
      if (item.options != "") {
        this.topic.optionA = item.options[0].substring(2);
        this.topic.optionB = item.options[1].substring(2);
        this.topic.optionC = item.options[2].substring(2);
        this.topic.optionD = item.options[3].substring(2);
      }
      this.topic.answer = item.correctanswer;
      this.multiSelect = this.transfer(item.correctanswer);
      this.topic.analytic = item.analysis;
      this.topic.score = item.score;
      // console.log(this.topic)
    },
    //编辑题目
    editSubject() {
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].name = this.topic.stem;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].name = this.topic.stem;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].imageurl = this.topic.imagesArr.join(",");
      this.choiceQues.subject[this.delKey][this.delIndex].type = this.quesType;
      this.choiceQues.subject[this.delKey][this.delIndex].options = [
        "A." + this.topic.optionA,
        "B." + this.topic.optionB,
        "C." + this.topic.optionC,
        "D." + this.topic.optionD
      ];
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].correctanswer = this.topic.answer;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].analysis = this.topic.analytic;
      this.choiceQues.subject[this.delKey][
        this.delIndex
      ].score = this.topic.score;
    },
    //显示删除弹框
    showDelModal(index, k) {
      this.hintModel = true;
      this.delIndex = index;
      this.delKey = k;
    },
    //删除题目
    delSubject() {
      this.choiceQues.subject[this.delKey].splice(this.delIndex, 1);
      this.hintModel = false;
    },

    //查看图片
    handleView(name) {
      this.topic.imgName = name;
      this.visible = true;
    },
    // 删除图片
    handleRemove(index) {
      this.topic.imagesArr.splice(index, 1);
    },
    // 图片的成功回调
    handleSuccess(res) {
      if (res.code == 0) {
        this.topic.imagesArr.push(res.data);
      }
    },
    //保存整套试题
    addExercise() {
      if (this.$route.params.enterType == 1) {
        Server.post(
          Path.addExercise,
          {
            courseid: this.courseId,
            periodid: this.periodId,
            subject: this.choiceQues.subject
          },
          res => {
            if (res.code === 0) {
              this.$router.push("/QuestionBank?from=TeachingAffairs");
              this.$Message.success(res.info);
            }
          }
        );
      } else if (this.$route.params.enterType == 2) {
        Server.post(
          Path.updateExercise,
          {
            id: this.$route.query.exeId,
            courseid: this.courseId,
            periodid: this.periodId,
            subject: this.choiceQues.subject
          },
          res => {
            if (res.code === 0) {
              this.$router.push("/QuestionBank?from=TeachingAffairs");
              // this.$router.push({path:'/QuestionBank',query:{from:'TeachingAffairs'}})
              this.$Message.success(res.info);
            } else {
              this.$Message.error(res.info);
            }
          }
        );
      }
    },
    //编辑入库后的整套试题
    getExerciseinfo() {
      Server.post(
        Path.getExerciseinfo,
        {
          id: this.$route.query.exeId
        },
        res => {
          if (res.code === 0) {
            this.replaceBol = false;
            this.notSelect = true;
            this.choiceQues = res.data;
            this.courseId = res.data.courseid;
            this.getPeriodList(this.courseId);
            this.periodId = res.data.periodid;
            this.single = this.choiceQues.subject["1"];
            this.multiple = this.choiceQues.subject["2"];
            this.gapFilling = this.choiceQues.subject["3"];
            this.composition = this.choiceQues.subject["4"];
          }
        }
      );
    }
  },
  created() {
    this.getAllCurriculum();
    if (this.$route.params.enterType == 2) {
      this.pathArr = [
        {
          name: "题库管理",
          route: { path: "/QuestionBank", query: { from: "TeachingAffairs" } }
        },
        {
          name: "查看详情",
          route: {
            path: "/periodList",
            query: { from: "TeachingAffairs", id: this.$route.query.id }
          }
        },
        {
          name: "编辑试题",
          route: ""
        }
      ];
      this.getExerciseinfo();
    } else {
      this.pathArr = [
        {
          name: "题库管理",
          route: { path: "/QuestionBank", query: { from: "TeachingAffairs" } }
        },
        {
          name: "录入试题",
          route: ""
        }
      ];
    }
  },
  mounted() {},
  computed: {},
  watch: {
    quesType(news) {
      if (this.$route.query.id && this.isHandle=='edit') {
        return;
      }
      this.$refs.topic.resetFields();
      this.topic = {
        stem: "", //题干
        imagesArr: [], //图组
        imgName: "", //图片名称
        optionA: "", //四个选项
        optionB: "",
        optionC: "",
        optionD: "",
        answer: "", //答案
        analytic: "", //解析
        score: "" //分值
      };
      this.multiSelect=[];
    }
  }
};
</script>
<style scoped lang='less'>
.head {
  display: flex;
  align-items: center;
  // padding-bottom:20px;
  // border-bottom: solid 1px #E6E6E6;
  img {
    width: 18px;
    height: 18px;
    margin-right: 20px;
  }
  span {
    font-size: 16px;
    font-family: MicrosoftYaHei-Bold;
    font-weight: bold;
    color: #666;
  }
}
.homework {
  width: 870px;
  height: auto;
  background: #fafbfc;
  border: 1px solid #cccccc;
  padding: 0 80px;
  // padding-right:200px;
  font-size: 14px;
  color: #7a8599;
  > div {
    padding: 15px 0;
  }
  dl {
    position: relative;
    text-indent: 1rem;
    border-bottom: solid 1px #eee;
    dt {
      line-height: 45px;
    }
    dd {
      text-indent: 2rem;
      line-height: 40px;
    }
  }
}
.delStyle {
  position: absolute;
  right: 0;
  top: 0;
  span {
    margin-right: 15px;
    color: #3f9ce8;
    cursor: pointer;
  }
  span:last-child {
    color: #ed3825;
  }
}
.radioGroup {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
//题目图片
.demo-upload-list {
  display: inline-block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  margin-right: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.demo-upload-list img {
  width: 100%;
  height: 100%;
}
.demo-upload-list-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.demo-upload-list:hover .demo-upload-list-cover {
  display: block;
}
.demo-upload-list-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  margin: 0 2px;
}
</style>



