<template>
  <div id="AddLessonView">
  <!--   课程管理 --><!-- 编辑课程信息 -->
    <panel-title
      :crumbs="[
        {
          name:$t('courseManagepage.courseManage'),
          route:{
            path:'/Small',
            query:{
              from:'MainLessonManagement'
            }
          }
        },
        {
          name:$t('courseManagepage.editcourse'),
          route:'',
        },
      ]"
    />
    <step :step='step' :desc='desc' class='aaaaa'/>
    <router-view></router-view>
  </div>
</template>
<script>
import panelTitle from '@/components/panelTitle';
import step from '@/components/step';
export default {
  name: "AddLessonView",
  components: {
    step,
    panelTitle,
  },
  data(){
    return {
      step:1,
      desc:[
        this.$t('courseManagepage.addclassmeg'),//填写课程信息
        this.$t('courseManagepage.addclasstime'),//填写课时信息
        this.$t('Common.set'),//设置
      ],
    }
  },
  created(){
    this.stepActive()
  },
  watch:{
    "$route": "stepActive"
  },
  methods: {
    stepActive(){
      this.step = Number(this.$route.query.step);
    }
  }
}
</script>
<style scoped lang='less'>
</style>



