<template>
  <!--学生标签-->
  <div id="ClassType">
    <panel-title :crumbs="crumbs" />
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
          <!--添加分类 -->
          <Button type="primary" @click="showTypeEditModal('add')">添加标签</Button>

          <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1"></i-table>

          <pagination  v-if='total>0' :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange"></pagination>
      </Card>
    <Modal
      v-model="LessonTypeEditModel"
      :closable="false">
      <div slot="header">{{LessonTypeText}}</div>
        <Form class="mg-top-20" :label-width="140" ref="label" :model="label" :rules="ruleValidate">
          <Row class="mg-bottom-10">
            <Col>
              <!--标签名称-->
              <FormItem label="标签名称：" prop="tagName">
                <Input style="width:240px;" v-model.trim="label.tagName" placeholder="最多可输入15个汉字" :maxlength="15"/>
               </FormItem>
            </Col>
          </Row>
          <Row class="mg-bottom-10">
            <Col>
              <!--子标签名称 -->
              <FormItem style=" position:relative;" label="子标签名称：" prop="childtag">
                <Input style="width:240px;" v-model.trim="label.childtag" placeholder="最多可输入5个汉字" :maxlength="5" @keyup.enter.native="saveChildtag" />
                <a @click="saveChildtag">保存</a>
              </FormItem>
            </Col>
          </Row>
          <Row class="mg-bottom-10">
            <Col span="24">
              <div>
                  <Tag closable v-for="i in label.childtagArr" v-if="tagBol"  @on-close="handleClose" :key="i.index">{{i}}</Tag>
                </div>
            </Col>
          </Row>
        </Form>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="ok('label')">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button @click="LessonTypeEditModel = false;"> {{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    <Modal
      v-model="LessonTypeDeleteModel"
       :closable="false">
      <div slot="header">温馨提示</div>
      <div class="type-content"> 确认删除该标签吗？</div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="okDelete"> 确认</Button>
        <!--取消-->
        <Button @click="LessonTypeDeleteModel = false;"> 取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/common/pagination";
  import { Card, Input, Table, Switch, Button, Modal, Form,FormItem, Row, Col, Tag } from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name: "StudentLabel",
    components: {
      panelTitle,
      Card,
      Input,
      Button,
      "i-table": Table,
      pagination,
      "i-switch": Switch,
      Modal,
      Form,
      FormItem,
      Col,
      Row,
      Tag
    },
    created() {
      this.getClassData(this.current);
    },
    methods: {
      //保存子标签
      saveChildtag(){
        if(this.label.childtag==""){
            this.$Message.error("请输入子标签");
            return false;
          }
        // if (!/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(this.childtag)){
        //     this.$Message.error("标签名只可以是数字、字母或者汉字");
        //     return false;
        //     this.childtag = "";
        //   }
          this.label.childtagArr.push(this.label.childtag)
          this.label.childtag=''
      },
      deleteItem(id=0){
        Server.post(Path.deleteTag,{
          id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.getClassData(this.current);
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
        })
      },
      handleClose (event, name) {
        const index = this.label.childtagArr.indexOf(name);
        this.label.childtagArr.splice(index, 1);
      },
      addClassName(id=0){
        let { tagName,childtagArr} = this.label;
        let str=childtagArr.join()//子标签转字符串
        // if (!/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(tagName)) {
        //   this.$Message.error("标签名只可以是数字、字母或者汉字");
        //   return false;
        //   tagName = "";
        // }
        Server.post(Path.addTag,{
          name: tagName,
          childname: str,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.getClassData(1);
            this.label.tagName="";
             this.label.childtag = "";
            this.label.childtagArr=[];
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
          this.LessonTypeEditModel = false;
        })
      },
      //编辑分类
      editClassName(id=0){
        let { tagName } = this.label;
        let str=this.label.childtagArr.join()//子标签转字符串
        // if (!/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(tagName)) {
        //   this.$Message.error("标签名只可以是数字、字母或者汉字");
        //   return false;
        //   tagName = "";
        // }
          Server.post(Path.updateTag,{
            id,
            name:tagName,
            childname:str
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.getClassData(this.current);
              this.$Message.success(res.info);
              // this.label.childtagArr=[]
            }else{
              this.$Message.error(res.info);
            }
            this.LessonTypeEditModel = false;
          })
      },
      isShowClass(id=0,type){
        Server.post(Path.editCategoryId,{
          id,
          status: Number(type),
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
            this.getClassData(this.current);
            //location.reload()
          }
        })
      },
      pagechange: function(currentPage) {
        this.loading = true;
        this.current = currentPage;
        this.getClassData(currentPage);
      },
      getClassData(page=1){
        Server.post(Path.getTagList,{
          name: '',
	    		pagenum: page
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.data1 = data.data;
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total =total;
            this.display=pagesize;
            this.current=Number(pagenum);
           
            this.loading = false;
          }else{
            this.data1 = [];
            this.loading = false;
          }
        })
      },
      ok(name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            let { edtiType } = this;
            if(edtiType=='add'){
              this.addClassName();
            }else{
              this.editClassName(this.editId);
            }
          }
         })
      },
      okDelete() {
        this.deleteItem(this.editId);
        this.LessonTypeDeleteModel = false;
      },
      showTypeEditModal(type) {
        this.LessonTypeEditModel = true;
        if(type=='add'){
           this.label.tagName="";
          this.label.childtag = "";
          this.label.childtagArr=[];
          this.edtiType = 'add'
          // 添加标签
          this.LessonTypeText = '添加标签'
        }else{
          //编辑标签
          this.edtiType = ''
          this.LessonTypeText = '编辑标签'
           this.label.childtag = "";
        }
      },
      showTypeDeleteModal() {
        this.LessonTypeDeleteModel = true;
      }
    },
    computed:{
      getstate(){
        return this.$store.store.state.status;
      }
    },
    watch:{
      LessonTypeEditModel(val){
        if(val == false){
          this.$refs.label.resetFields()
        }
      }
    },
    data() {
      const tagRegex = (rule, value, callback) => {
        if (this.label.childtagArr.length=='0') {
          callback(new Error("子标签名不能为空"));
        }
          callback();
      };
      return {
        LessonTypeText:this.$t("classifyManagepage.Addclassification"), //添加分类
        MsgModal:false,
        total: 0, // 记录总条数
        display: 10, // 每页显示条数
        current: 1, // 当前的页数
        label:{
          tagName:'', //标签名称
          childtag:'', //子标签名称
          childtagArr: [],
        },
        ruleValidate: {
          tagName: [{ required: true, message: '标签名不能为空', trigger: 'blur' }],
         childtag: [{validator: tagRegex}]
        },
        currentIndex:0,
        editId:0, //分类编辑ID
        loading:true,
        InputText: "",
        LessonTypeEditModel: false, //编辑分类弹
        LessonTypeDeleteModel: false, //删除分类弹
        edtiType:'',
        disabled: '',
        crumbs:[{name: "学生标签", route:''}],
        columns1: [
          {
            //编号
            title: '编号',
            align: "center",
            key: "id"
          },
          {
            //分类名称
            title: "标签名称",
            align: "center",
            key: "name"
          },
          {
            //分类名称
            title: "子标签名称",
            align: "center",
            key: "childtag"
          },
          {
            //是否显示
            title:this.$t("classifyManagepage.Whethershown") ,
            align: "center",
            key: "status",
            render: (h, params, index) => {
              return h("div", [
                h(
                  "i-switch",
                  {
                    on: {
                      "on-change": (value) => {
                      Server.post(Path.SwitchTagStatus,{
                            id:params.row.id,
                            status:Number(value)
                        },res=>{
                          let { code, data } = res;
                          if(code===0){
                            this.$Message.info(res.info);
                            this.getClassData(this.current);
                            let n = 0;
                            this.data1.forEach(item =>{
                              if(params.row.status==1){
                                n++;
                                if(n==1){
                                  this.$Message.error("如果不显示标签，学生注册时将无法选择标签");  
                                }
                              }
                            })
                          }else{
                            this.$Message.error(res.info);
                          }
                        })
                      }
                    },
                    props: {
                      value: Boolean(params.row.status),
                      // disabled:Boolean(params.row.TypeNumber)
                    }
                  }
                )
              ]);
            }
          },
          {
            //操作
            title: this.$t("classifyManagepage.operation"),
            align: "center",
            key: "Make",
            render: (h, params, index) => {
              return h("div", [
                h(
                  "a",
                  {
                    on: {
                      click: value => {
                        this.label.tagName = params.row.name;
                        this.editId = params.row.id;
                        this.label.childtagArr=params.row.childtag.split(",");
                        this.showTypeEditModal();
                      }
                    },
                    style: {
                      cursor: "pointer",
                      color: "#3F9CE8",
                      display: "inline-block",
                      marginRight: "35px"
                    },
                    attrs: {
                      class: "tablebtn blue"
                    }
                  },
                  "编辑"
                ),
                h(
                  "a",
                  {
                    on: {
                      click: value => {
                        this.editId = params.row.id;
                        this.showTypeDeleteModal();
                      }
                    },
                    style: {
                      cursor: "pointer",
                      color: "#ED3825",
                      display: "inline-block"
                    },
                    attrs: {
                      class: "tablebtn red"
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ],
        data1: [],
        tagBol:true,
        // childtagArr: [],
        childtagStr:'',
        nameRE:true
      };
    }
  };
</script>
<style lang="less" scoped>
a{
  position: absolute;
  left: 280px;
  top:0;
  width:30px;
}
</style>



