<template>
  <div>
     <panel-title :crumbs="routeArr" />
    <Tabs class='mytheme-tab' type="card" :animated="false" @on-click='msg' >
          <TabPane label="待处理" name='待处理'>
              <pending-class></pending-class>
          </TabPane>
          <TabPane label="已处理" name='已处理'>
            <processed-class></processed-class>
          </TabPane>
      </Tabs>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Tabs, TabPane} from "iview";
import pendingClass from "./pendingClass";
import processedClass from "./processedClass";
export default {
  name: "ChangeShift",
  components: {panelTitle,Tabs,
    TabPane,pendingClass,processedClass},
   data(){
    return {
      routeArr:[{name:'调班管理',route:''},{name:'待处理',route:''}]
    }
  },
  methods: {
      msg(name){
     this.routeArr=[{name:'调班管理',route:''},{name:name,route:''}];
    }
  },
  created() {
  
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>

</style>



