<template>
  <div id="Course">
    <panel-title :crumbs="[
      {
        name:'老师推荐',
        route:''
      },
    ]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
     <div class="mytheme-mg-top-20">
	        <span>老师姓名：</span>
	        <Input
          class="input-normal-width input-max-height"
	        v-model="InputText"
	        @keydown.native.enter="SearchCourse"
	        placeholder="老师名称" />
          <Button type="primary" class="mytheme-mg-left-70" @click="SearchCourse">搜索</Button>
	        <Button type="primary" @click="getCourseData2(InputText2,current2)" style="float: right;">添加推荐</Button>
        </div>

		    <i-table ref="selection" class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1" ></i-table>
        
        <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='total>0'></pagination>
   </Card>
   <Modal v-model="isShaowdown" class="crlvvv" width="700" :closable="false">
	      <div slot="header">老师推荐</div>
			 <Card dis-hover class=" mytheme-noborder">
          <div style="margin-bottom:20px;">
	        <span style="margin-bottom:20px;color: #7a8599;">老师名称：</span>
	        <Input
          class="input-normal-width input-max-height"
	        v-model="InputText2"
	        placeholder="老师名称"
	        @on-enter="getCourseData2(InputText2,current2)"/>
          <Button type="primary" class="mytheme-mg-left-70" @click="getCourseData2(InputText2,current2)">搜索</Button>
        </div>
        <i-table ref="selection" class="mytheme-mg-top-30 mytheme-table noborder-table" stripe :columns="columns" :data="data" @on-selection-change="selectSuccess"></i-table>
        <pagination :total="tota2" class="mg-top-20" :current-page='current2' @pagechange="pagechange2" :display='display2' v-if='tota2>0'></pagination>
		  	</Card>
        <div slot="footer">
              <Button type="primary" @click="addSelect">{{$t('Common.sure')}}</Button>
              <Button @click="isShaowdown = false">取消</Button>
			  </div>
		</Modal>
    <Modal v-model="isShaowdown2" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除该老师吗？</div>
      <div slot="footer">
        <Button @click="delList()" type="primary">确认</Button>
        <Button @click="isShaowdown2 = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, ButtonGroup, Button, Modal } from "iview";
import Server from '@/service/request';
import Path from '@/service/Path';
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    ButtonGroup,
    Button,
    Modal
  },
  created() {
    this.getCourseData(this.current);
  },
  methods: {
    pagechange(currentPage) {//页面分页逻辑
      this.current = currentPage;
      this.loading = true;
      this.getCourseData(this.current);
    },
    pagechange2(currentPage) {//弹框分页逻辑
      this.current2 = currentPage;
      this.getCourseData2(this.InputText2,this.current2);
    },
    getCourseData(page=1){//获取页面数据
      Server.post(Path.teacherList,{
        'teachername': this.InputText,
        'pagenum': page,
        "recommend": 1
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data1 =[];
           for (let i = 0 ; i < res.data.teacherlist.length; i++) {
          	let data = {};
          	data.teacherid = res.data.teacherlist[i].teacherid;
          	data.teachername = res.data.teacherlist[i].teachername == null ? this.tips : res.data.teacherlist[i].teachername;
          	this.data1.push(data); 
          }
          this.total = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
        }else{
          this.$Message.error(res.info);
          this.data1 = [];
        }
      })
    },
    getCourseData2(name,page=1){//获取弹框数据
    	this.isShaowdown = true
      Server.post(Path.teacherList,{
					'teachername':name,
					'pagenum':page,
					'recommend':0
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data = [];
          this.tota2 = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
          for (let i = 0 ; i < res.data.teacherlist.length; i++) {
          	let data = {};
          	data.teacherid = res.data.teacherlist[i].teacherid;
          	data.teachername = res.data.teacherlist[i].teachername == null ? this.tips : res.data.teacherlist[i].teachername;
          	this.data.push(data); 
          }
        }else{
          this.$Message.error(res.info);
          this.data = [];
      }
      })
    },
    moveItem(id1,id2){//上移 下移
      Server.post(Path.tercherUp,{
        'teacherid1': id1,
        'teacherid2': id2
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
          this.getCourseData(this.current);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    selectSuccess(selection) {//弹框复选框 选中数据  处理
    					let arr = [];
				    	for( let i = 0;  i < selection.length; i++) {
				    	
              	arr.push(selection[i].teacherid);
    					}

    					this.spilt=arr.join();

    },
    addSelect () { //选中推荐课程返回后台
    	// this.isShaowdown = false
        if(this.spilt === ""){
          this.$Message.error("请先选择老师");
          return false;
        }
    	Server.post(Path.Addtercher,{
        'teacherid': this.spilt
     },(res)=>{
        let { code, data } = res;
        if(code===0){
        	this.getCourseData(this.current);
          this.$Message.success("添加老师成功");
        }else {
        	this.$Message.error(res.info)
        }
      })
    },
    delList(){//删除推荐课程
    	Server.post(Path.tercherDel,{
        'teacherid': this.idd
      },(res)=>{
        let { code, data } = res;
        if(code===0){
        	this.isShaowdown2 = false
          this.$Message.success(res.info);
          this.getCourseData(this.current);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    isRecommendCourse(id,state){
      Server.post(Path.switchCourseStatus_c,{
        'courseid': id,
        'status': state
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    SearchCourse(){
      this.getCourseData(this.current);
    }
  },
  data(){
    return{
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      tota2: 0, // 记录总条数
      display2: 20, // 每页显示条数
      current2: 1, // 当前的页数
      InputText: "",
      InputText2: "",
      mSelection: [],
      tips:'无',
      spilt:"",
      idd:'',
      isShaowdown:false,
      isShaowdown2:false,
      columns1: [
        {
          title: "编号",
          align: "center",
          key: "teacherid"
        },
        {
          title: " 老师名称",
          align: "center",
          key: "teachername"
        },
        {
          title: "排序",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "a",
                {
                  on: {
                    'click': ()=>{
                      if(params.index == 0){
                        this.$Message.warning('已移至第一个');
                      }else{
                        this.moveItem(params.row.teacherid,this.data1[params.index-1].teacherid);
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn yellow"
                  }
                },
                "上移"
              ),
              h(
                "a",
                {
                  on: {
                    "click": value => {
                      if(params.index == this.data1.length - 1){
                        this.$Message.warning('已移至最后一个');
                      }else{
                        this.moveItem(params.row.teacherid,this.data1[params.index+1].teacherid);
                        
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn blue"
                  }
                },
                "下移"
              )
            ]);
          }
        },
        {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',{
                on: {
                	click: () => {										
              		this.idd = params.row.teacherid;
              		this.isShaowdown2=true;
									
                	}
                }
                ,style: {
                  cursor:'pointer',
                  color: '#ED3825',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'删除')
          }
        }
      ],
       columns: [
       {
	        type: 'selection',
	        width: 60,
	        align: 'center'
        },
        {
          title: "编号",
          align: "center",
          key: "teacherid"
        },
        {
          title: " 老师名称",
          align: "center",
          key: "teachername"
        }
      ],    
      data: [
      ],         
      data1: [
      ]
    }
  }
};
</script>
<style scoped>
	.btn {
  width: 120px;
  line-height: 20px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px 0;
  border:none ;
}
.footer{
	height: 60px;
	margin-top: 10px;
	position: relative;
}
.enter{
	position: absolute;
	right: 20px;
	top: 20px;
}
.del{
	position: absolute;
	top: 20px;
	right: 140px;
}
</style>



