<template>
  <div id="ClassType">
     <!--素材管理 -->
      <panel-title :crumbs="crumbs"></panel-title>

    <Card dis-hover class="h-new-search">
      <template>
       <!--  文件夹 -->
        <span class='fl-text2'>{{$t('materialManagepage.folder')}}</span>
       <!--  新建文件夹 -->
        <!-- <Button type="primary" shape="circle" class='fr h-search-btn' @click="addMaterModel">{{$t('materialManagepage.newfolder')}}</Button> -->
      </template>
    </Card>
    <Card class="mg-bottom-20" dis-hover style='border-radius:0 0 8px 8px'>
      <ul class="mater-box" v-if="isshowDefault">
        <li
        v-for="el,index in materData"
        :key="el.fileid"
        @mouseover="el.active = true"
        @mouseout="el.active = false"
        >
          <div class="mater-item">
            <div class="mater-delete" v-if="el.active" @click="deleteModal(el.fileid,el.juniorcount)">
              <img src="./assets/img/delete.png" alt="">
            </div>
            <router-link :to="{ path: '/MaterialInfo/'+el.fileid, query: {fatherid:el.fatherid,fileid: el.fileid,filename:el.showname,filetype: $route.query.filetype,from:'MainLessonManagement'} }">
              <div class="mater-img">
                <img :src="MaterIcon" alt="">
                <!-- 个 -->
                <div class="origin" v-if='$i18n.locale == "zh-cn"'>
                  ({{el.juniorcount}}{{$t('materialManagepage.unit')}})
                </div>
                <div class="origin" v-else>
                  ({{$t('materialManagepage.unit')}} {{el.juniorcount}})
                </div>
              </div>
              <div class="mater-name" :title="el.showname">
                  {{el.showname}}
              </div>
            </router-link>
          </div>
        </li>
      </ul>
      <!-- 暂无文件夹 -->
      <defaultShow v-else :text="$t('Common.nodata')" />
      <!-- 下拉加载 先注释 20180719 huang -->
      <!-- <div class="infiniteLoad"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="busy"
      infinite-scroll-distance="10">
      </div> -->
    </Card>
   <!--  新建文件夹 -->
    <Modal
      v-model="MaterModal"
      :title="$t('materialManagepage.newfolder')"
      :mask-closable="false">
      <div class="type-content">
        <Form class="form-confrol mg-top-20" :label-width="80">
          <Form-item :label="$t('materialManagepage.name')+$t('Common.colon')">
            <Input v-model="materName" :maxlength="20" placeholder="" @keyup.enter.native="ok()" />
          </Form-item>
        </Form>
      </div>
      <div slot="footer">
       <!--  确认 -->
        <Button type="primary" @click="ok()">{{$t('Common.sure')}}</Button>
        <!--  取消 -->
        <Button @click="MaterModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
  </Modal>
  <!-- 温馨提示 -->
  <Modal
    v-model="DeleteModal"
    :closable="false">
    <div slot="header">{{$t('Common.kindlyreminder')}}</div>
    <div>{{deleteFileText}}</div>
    <div slot="footer">
      <Button type="primary" @click="deleteFile()">{{$t('Common.sure')}}</Button>
      <Button @click="DeleteModal = false">{{$t('Common.cancel')}}</Button>
    </div>
  </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import MaterIcon from "./assets/img/MaterIcon.png";
import { Card, Input, Button, Modal, Form, Row, Col, FormItem } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from "@/components/defaultShow";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    Modal,
    Form,
    "Form-item": FormItem,
    Col,
    Row,
    defaultShow
  },
  watch: {
    getstate: function(val) {
      if (val == 3) {
        this.MaterModal = true;
      }
    }
  },
  computed: {
    getstate() {
      return this.$store.store.state.status;
    }
  },
  methods: {
    
    deleteModal(id, num) {
      if (num > 0) {
        //该文件夹下有多个文件，删除文件夹会导致文件夹下的文件也被删除，您确定要删除吗？
        this.deleteFileText = this.$t("materialManagepage.remind1");
      } else {
        //您确定要删除该文件夹吗？
        this.deleteFileText = this.$t("materialManagepage.remind2");
      }
      this.fileid = id;
      this.DeleteModal = true;
    },
    //删除课件
    deleteFile() {
      let { fileid } = this;
      Server.post(
        Path.deleteFile,
        {
          fileid: fileid
          // teacherid: ''
        },
        res => {
          this.$Message.info(res.info);
          if (res.code == 0) {
            this.page = 1;
            this.DeleteModal = false;
            this.materData = this.materData.filter((el, index) => {
              return el.fileid != fileid;
            });
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    addMaterModel() {
      //判断审核状态
      // let id = JSON.parse(window.localStorage.getItem('userinfo')).organid;
      // let { store } = this.$store;
      // store.dispatch({
      //   type:'getStatus',
      //   id,
      //   newtype:'-1'
      // });
      this.MaterModal = true;
    },
    ok() {
      let { materName } = this;
      if (materName == "") {
        this.$Message.error(this.$t("showBasicInfo.tip")); //请输入内容
        return false;
      }
      Server.post(
        Path.fileAdd,
        {
          showname: materName
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$Message.success(res.info);
            this.materData = [];
            this.getMaterData();
          } else {
            this.$Message.error(res.info);
          }
          this.MaterModal = false;
          this.materName = "";
        }
      );
    },
    cancel() {
      this.materName = "";
    },

	getMaterData(page = 1){
	      Server.post(
	        Path.getFileList,
	        {
	          showname: "",
	          pagenum: page,
	          fatherid:0,
	          filetype:1,
	          teacherid:this.$route.query.teacherid
	        },
	        res => {
	        	//console.log(res)
	          let { code, data } = res;
	          if (code === 0) {
	            var mater = data.data.map((el, index) => {
	              el.active = false;
	              return el;
	            });
	            if (page == 1) {
	              this.materData = [...mater];
	            }
	            this.busy = false;
	          } else {
	            this.busy = true;
	          }
	          if (this.materData.length > 0) {
	            this.isshowDefault = true;
	          } else {
	            this.isshowDefault = false;
	          }
	        }
	      );
	    }
  },
  created() {
    //do something after creating vue instance
    this.getMaterData();
   // console.log(this.$route.query.teacherid)
  },
  mounted() {},
  data() {
    return {
      page: 0,
      busy: false,
      MaterIcon,
      MaterModal: false,
      materName: "",
      materData: [],
      loading: true,
      DeleteModal: false,
      fileid: 0,
      deleteFileText: "",
      isshowDefault: true,
      crumbs: [{
          name: "个人资源",
          route:{
            path:'/PersonList',
            query:{
              from:'MainLessonManagement',
            }
          }
        }, {
            name:"私人文件夹",
            route:'',
        }]
    };
  }
};
</script>
<style scoped lang='less'>
.mater-box {
    padding: 20px;
    overflow: hidden;
    li {
      position: relative;
      width: 198px;
      height: 194px;
      float: left;
      &:hover {
        background: #ECF1FF;
      }
      .mater-item {
        position: relative;
        cursor: pointer;
        .mater-name {
          padding: 0 10px 0 10px;
          height: 48px;
          line-height: 48px;
          text-align: center;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 14px;
          color: #666666;
        }
        .mater-img {
          width: 100%;
          height: 120px;
          text-align: center;
          font-size: 16px;
          img {
            margin: 32px auto 0 auto;
            width: 66px;
            height: 66px;
          }
          div {
            margin: 12px auto 0 auto;
            font-size: 12px;
            color: #808080;
          }
        }
      }
      .mater-delete {
        position: absolute;
        cursor: pointer;
        right: 5px;
        top: 5px;
        z-index: 3;
      }
    }
  }

  .ivu-card-head {
    span {
      font-size: 16px;
    }
  }

</style>



