<template>
<!-- http://test.menke.com -->
  <div id="upload-picture">
    <Upload
      name="uploadFile"
      :action="imgDate"
      :data="{allpathnode:data}"
      :format="['jpg','jpeg','png']"
      :max-size="20480"
      :on-success="onsuccess"
      :show-upload-list="false"
      :on-format-error="handleFormatError"
      :on-exceeded-size="handleMaxSize"
    >
      <slot></slot>
    </Upload>
  </div>
</template>

<script>
  import {Upload} from 'iview';
  export default {
  	data() {
  		return {
        imgDate: window.origin === 'http://127.0.0.1:8080'? "http://www.lemonedu.com/teacher/Uploadfile/Upload" : "/teacher/Uploadfile/Upload"
  			
  		}
  	},
    name: "upload-picture",
    props: {
      // 分类值
      data: {
        type: Array,
        default (){
        	return [1,0,2]
        }
      },
      head:{
        type:Object,
        default:function(){
          return { sign:'',token:'',starttime:'' }
        }
      }
    },
    created() {
      //do something after creating vue instance
    },
    components: {
      Upload
    },
    methods: {
      // 返回上传成功的图片url
      onsuccess(file) {
        this.$emit('success', `http://${file.data}`);
      },
      handleFormatError (res,file) {
       this.$Message.warning(this.$t('materialManagepage.remind5'));//文件格式不正确
     },
     handleMaxSize (file) {
       this.$Message.warning(this.$t('materialManagepage.remind6')); //文件过大
     },
    }
  }
</script>

<style scoped>

</style>



