<template>
  <div>
    <panel-title :crumbs="[{name:'精彩瞬间',route:''}]" />
      <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
        <!--课程名称-->
        <span class='fl-text'>课程名称：</span>
        <!--课程名称 @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="courseKeyWord" placeholder="课程名称"/>
        <!--班级名称-->
        <span class='fl-text'>班级名称：</span>
        <!--班级名称 @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="classKeyWord" placeholder="班级名称"/>
        <!--授课老师-->
        <span class='fl-text'>授课老师：</span>
        <!--授课老师 @on-click="getCourseList(classtypes, current, keyWords)"-->
        <Input style="width:200px;" icon="ios-search"
               @on-enter="getCourseList(classtypes, current, keyWords)"
               v-model="teacherKeyWord" placeholder="授课老师"/>
       </div>
      <!--年级题库列表-->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!--分页-->
      <pagination  class="mg-top-20">
          <!-- :total="total" :currentPage="current" :display="display" @pagechange="pageChange" v-if='total>0'-->
      </pagination>
    </Card>
     <!--删除弹框-->
     <Modal class='testModel'
            v-model="delModel"
            title="温馨提示">
        <div class="type-content">删除后将不可恢复，您确定要删除吗？</div>
         <div slot="footer">
          <!--取消-->
          <Button type="text" shape="circle" @click="delModel = false;">取消</Button>
          <!--确认 @click="okDelete"-->
          <Button type="warning" shape="circle" @click="delModel = false;">确认</Button>
        </div>
  </Modal>
  </div>
</template>
<script>
import { Card, Input, Table, Modal,Button} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from '@/components/common/pagination'
export default {
  name: "SplendidMoment",
  components: {panelTitle,Card,Input,"i-table": Table,Modal,Button,pagination},
  data(){
    return {
      courseKeyWord:'',
      classKeyWord:'',
      teacherKeyWord:'',
     colData:[{
              title: '序号',
              key: 'number',
             align: 'center'
          },{
            title: '课程名称',
              key: 'courseName',
              align: 'center'
          },{
            title: '班级名称',
              key: 'className',
              align: 'center' 
          },{
            title: '课时名称',
              key: 'periodName',
              align: 'center' 
          },{
            title: '授课老师',
              key: 'teacher',
              align: 'center' 
          },{
            title: '时长',
              key: 'duration',
              align: 'center' 
          },{
            title: '录制时间',
              key: 'durarecordTimetion',
              align: 'center' 
          },{
            title: '操作',
              key: 'handle',
              align: 'center',
               render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{//'+params.row.id+'/'+params.row.curriculumid+'
                  to:'/splendidDetail/?from='+ this.$route.query.from
                },
                style:{
                  cursor:'pointer',
                  color: '#ffb932',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn yellow'
                }
              },'查看'),//查看
               h('div',{
                on: {
                  'click': () => {
                    this.delModel = true;
                  }
                },
                  style:{
                    cursor:'pointer',
                    color: 'red',
                    display: 'inline-block'
                  },
                  attrs:{
                    class: 'tablebtn red'
                  }
                },'删除')//删除
            ])
          }
          }],
     tabData:[{
        number: 1,
        courseName: '小学三年级语文',
        className:'考前冲刺班1',
        periodName: '课时3：古诗词阅读',
        teacher:'李二蛋',
        duration:'12分8秒',
        durarecordTimetion:'2018-06-20 18:00:03',
        handle: ''
     }],
     delModel:false//删除弹框
    }
  },
  methods: {

  },
  created() {
  
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
  @import "~@/assets/less/tab.less";

</style>



