<template>
  <div id="EditLessonTime">
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
            <!--课时信息-->
            <span class='fl'>{{$t('courseManagepage.coursetime')}}</span>
            <Button  @click="addLessonItem" class="btn-add-box fr">
            <!--新增课程单元-->
            {{$t('courseManagepage.addcoursetime')}}
            </Button>
        </p>
        <Form class="mg-top-20">
          
          <Row>
            <Col span="20" offset="2" v-for="lesson,index in lessonData.inperiod" :key="index">
            <div class="class-time-box-item">
              <dl>
                <dt class="clearfloat" style='border:1px solid #e6e6e6'>
                  <div class="time-title-icon">
                    <div class="time-title-icon-q "></div>
                  </div>
                  <div class="class-text-title fl">
                    <span v-if="!lesson.OutisInput">第{{index+1}}单元：{{lesson.unitname}}</span>
                    <Form style="width: 400px;" v-if="lesson.OutisInput">
                      <Row>
                        <Col span="16">
                        <!--请输入单元名称-->
                        <i-input v-model="lesson._inputText" @keyup.enter.native="lesson.unitname = lesson._inputText;lesson.OutisInput = false" autofocus class="inputWidth" :maxlength='40' :placeholder="$t('courseManagepage.prompt')"> </i-input>
                        </Col>
                        <Col span="4" offset="2">
                        <!--保存-->
                        <Button type="primary" @click.native="saveOutEdit(lesson)">{{$t('courseManagepage.save')}}</Button>
                        </Col>
                      </Row>
                    </Form>
                  </div>
                  <div class="edit-btn-box">
                    <Dropdown trigger="click">
                      <a href="javascript:void(0)" class="green more-btn">
                        <!-- <img :src="select" alt=""/> -->
                      </a>
                      <DropdownMenu slot="list">
                        <DropdownItem @click.native="outEdit(lesson)">
                          <img :src="editIcon" alt="" />
                          <!--编辑-->
                          {{$t('Common.edit')}}
                        </DropdownItem>
                        <DropdownItem @click.native="deleteParentItem(index)">
                          <img :src="deleteIcon" alt="" />
                          <!--删除-->
                          {{$t('Common.delete')}}
                        </DropdownItem>
                        <DropdownItem @click.native="addLessonTimeItem(lesson,index)">
                          <img :src="addIcon" alt="" />
                          <!--增加课时-->
                          {{$t('Common.addlesson')}}
                        </DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </div>
                </dt>
                <dd class="clearfloat" v-for="el,i in lesson.list" :key="i">
                  <div class="clearfloat lessontimeitem" style='border:1px solid #e6e6e6'>
                    <div class="fl">
                      <!--课时-->
                      <span v-if="!el.isInput">{{$t('courseManagepage.time')}}{{i+1}}：{{el.periodname}}</span>
                      <Form style="width: 400px;" v-if="el.isInput">
                        <Row>
                          <Col span="16">
                          <i-input v-model="el._inputText" @keyup.enter.native="el.periodname = el._inputText;el.isInput = false" autofocus class="inputWidth" :maxlength='40' placeholder='请输入课时名称'></i-input>
                          </Col>
                          <Col span="4" offset="2">
                          <!--保存-->
                          <Button type="primary" @click.native="saveInnerEdit(el,lesson,index,i)">{{$t('courseManagepage.save')}}</Button>
                          </Col>
                        </Row>
                      </Form>
                    </div>
                    <!-- <span class="showTime" v-if="ifTimeShow"> -->
                    <span class="showTime" v-if="el.timestr">
                      <img :src="ClockIcon"> {{el.timestr}}
                    </span>
                    <div class="edit-btn-box">
                      <Dropdown trigger="click">
                        <a href="javascript:void(0)" class="green more-btn">
                          <!-- <img :src="select" alt=""/> -->
                        </a>
                        <DropdownMenu slot="list">
                          <!-- <DropdownItem  @click.native="el.isInput = false"> -->
                          <DropdownItem @click.native="innerEdit(el)">
                            <img :src="editIcon" alt="" />
                            <!--编辑-->
                            {{$t('Common.edit')}}
                          </DropdownItem>
                          <DropdownItem @click.native="deleteChildernItem(i,index)">
                            <img :src="deleteIcon" alt="" />
                            <!--删除-->
                            {{$t('Common.delete')}}
                          </DropdownItem>
                          
                          <DropdownItem @click.native="openAbout(index,i)">
                            <img :src="aboutIcon" alt="" />
                            <!--关联文件-->
                            {{$t('Common.addfile')}}
                          </DropdownItem>
                          
                        </DropdownMenu>
                      </Dropdown>
                    </div>
                  </div>
                  <!-- {{el.courseware}} -->
                  <div class="materBox">
                    <Tag type="border" closable color="#387BFF" @on-close="deleteTag(index,i,dindex)" checkable v-for="d,dindex in el.courseware" :key="d.fileid">{{ d.name }}</Tag>
                  </div>
                </dd>
              </dl>
            </div>
            </Col>
          </Row>
          <Row>
            <Col span="20" offset="2">
            <div class="btnbox">
              <!--下一步-->
              <!-- <next-btn @click.native="saveData" >{{$t('courseManagepage.nextstep')}}</next-btn> -->
              <Button type="primary" @click.native="saveData" >下一步</Button>
              <!-- </router-link> -->
            </div>
            </Col>
          </Row>
        </Form>
      </Card>
    </template>
    <Modal v-model="AboutMater" width="820" :closable="false">
      <p slot="header">
        <Icon @click="backMater" type="chevron-left" v-if="isShowMaterList"></Icon>
        <span style="cursor:pointer">{{materTitle}}</span>
      </p>
      <div class="type-content" ref="MaterScroll">
        <Form>
          <Row>
            <Col span="24">
            <ul class="mater-box" v-if="!isShowMaterList">
                <template v-if="materData.length>0">
                    <li v-for="el,index in materData" :key="el.fileid">
                        <div class="mater-item" @click="openMaterList(el.fileid,el.showname)">
                        <div class="mater-img">
                            <img :src="MaterIcon" alt="" />
                            <div class="origin">{{el.juniorcount}}个</div>
                        </div>
                        <div class="mater-name green">
                            {{el.showname}}
                        </div>
                        </div>
                    </li>
              </template>
              <defaultShow v-else :text="$t('Common.nodata')" style='margin:-40px 0'/>
            </ul>
            <ul class="class-box" v-if="isShowMaterList">
                <template v-if="materListData.length>0">
                    <li v-for="(mater, i) in materListData" :key="mater.fileid">
                        <label>
                        <Checkbox class="class-check" v-model="mater.isSelect"></Checkbox>
                        <div class="checkText">
                            {{mater.showname}}
                        </div>
                        </label>
                    </li>
                </template>
                <template v-else>
                     <li>
                        <label>
                        <div class="checkText">
                            {{$t('Common.nodata')}}
                        </div>
                        </label>
                    </li>
                </template>
            </ul>
            </Col>
          </Row>
        </Form>
      </div>
      <div slot="footer">
        <!--确定-->
        <Button type="primary" v-if="isShowMaterList" @click="saveMater">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button v-if="isShowMaterList" @click="isShowMaterList=false; materTitle=$t('courseManagepage.Associatedtext')">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import NextBtn from "@/components/NextBtn/NextBtn";
import {
    Card,
    Input,
    Form,
    Row,
    Col,
    Dropdown,
    DropdownMenu,
    DropdownItem,
    Button,
    Modal,
    Icon,
    FormItem,
    Checkbox,
    Tag,
    DatePicker,
    Option,
    Select
} from "iview";
import ClockIcon from "@/container/ArrangingManagement/assets/icon_clock@2x.png";
import editIcon from "./assets/img/icon_edit.png";
import addIcon from "./assets/img/icon_add.png";
import deleteIcon from "./assets/img/icon_delete.png";
import aboutIcon from "./assets/img/icon_about.png";
import moreIcon from "./assets/img/icon_more.png";
import MaterIcon from "./assets/img/MaterIcon.png";
import select from "./assets/img/select.png";
import addtime from "./assets/img/icon_time@2x.png"
import Server from "@/service/request";
import Path from "@/service/Path";
import defaultShow from '@/components/defaultShow';
export default {
    name: "EditLesson",
    components: {
        Card,
        "i-input": Input,
        NextBtn,
        Form,
        Row,
        Col,
        Dropdown,
        DropdownMenu,
        DropdownItem,
        Button,
        Modal,
        Icon,
        Checkbox,
        FormItem,
        Tag,
        DatePicker,
        Option,
        Select,
        defaultShow,
    },
    data() {
        return {
           
            ifTimeShow: false,
            isShowEdit: true,
            isShowEditModal: false,
            defaultDate: new Date(),
            MaterIcon,
            AboutMater: false,
            value: "",
            ClockIcon,
            editIcon,
            addIcon,
            deleteIcon,
            aboutIcon,
            moreIcon,
            select,
            addtime,
            materData: [],
            materListData: [],
            activeMaterId: 0,
            activeListMaterId: [],
            lessonData: {
                id: this.$route.query.id,
                inperiod: [{
                   "unitname" : "",
                   "isInput": true,
                    "_inputText": "",
                    "unitsort": 1,
                    "id": "",
                    "list": [{
                        "id": "",
                        "periodname": "",
                        "isInput": true,
                        "periodsort": 1,
                        "courseware": "",
                        "_inputText": ""
                    }],
                    "OutisInput": true }
                ]
            },
            childrenId: 0,
            parentId: 0,
            materTitle: this.$t("courseManagepage.Associatedtext"),
            isShowMaterList: false,
            LessonTimeNumber: 0,
            fileData: [],
            filePage: 1,
            falg: true,
            activeIndex: -1,
            listData: {
                intime: "",
                timekey: 16,
                id: ""
            },
            unitsort: 0,
            options2: {
                disabledDate(date) {
                    return date && date.valueOf() <= Date.now() - 86400000; //可以选今天
                    // return date && date.valueOf() <=Date.now()   //不可以选择今天
                }
            },
           
        };
    },
    filters: {
        fileName: function(id) {
            let { fileData } = this;
            for (var i = 0; i < fileData.length; i++) {
                if (id == fileData[i].id) {
                    return fileData[i].name;
                }
            }
        },
        deleteOne: function(data) {
            var d = data;
            d.shift();
            return d;
        }
    },
    created() {
        //do something after creating vue instance
        if (window.localStorage.getItem("inperiod")) {
            this.lessonData.inperiod = JSON.parse(
                window.localStorage.getItem("inperiod")
            );
            //console.log(this.lessonData.inperiod);
            if(this.lessonData.inperiod.length === 0){
                this.lessonData.inperiod = [{
                   "unitname" : "",
                   "isInput": true,
                    "_inputText": "",
                    "unitsort": 1,
                    "id": "",
                    "list": [{
                        "id": "",
                        "periodname": "",
                        "isInput": true,
                        "periodsort": 1,
                        "courseware": "",
                        "_inputText": ""
                    }],
                    "OutisInput": true }
                ]
            }else {
                this.lessonData.inperiod.forEach((el, index) => {
                    el.list.forEach((e, i) => {
                        this.LessonTimeNumber += 1;
                    });
                });
            }
        }
    },
    watch: {
        lessonData: {
            handler: function(val, oldVal) {},
            deep: true
        }
    },
    mounted() {
        //元素内滚动加载
        let nScrollHight = 0; //滚动距离总长(注意不是滚动条的长度)
        let nScrollTop = 0; //滚动到的当前位置
        let nDivHight = 500; //元素高度
        let that = this;
        this.$refs.MaterScroll.onscroll = function() {
            nScrollHight = that.$refs.MaterScroll.scrollHeight;
            nScrollTop = that.$refs.MaterScroll.scrollTop;
            if (nScrollTop + nDivHight >= nScrollHight) {
                if (that.falg) {
                    that.filePage++;
                    that.getMaterData(that.filePage);
                }
            }
        };
    },
    methods: {
        /**
         * 删除课件
         * @param  {[type]} parentIndex   [单元]
         * @param  {[type]} childrenIndex [课时]
         * @param  {[type]} tagIndex      [课件索引]
         * @return {[type]}               [description]
         */
        deleteTag(parentIndex, childrenIndex, tagIndex) {
            let { inperiod } = this.lessonData;
            inperiod[parentIndex].list[childrenIndex].courseware.splice(
                tagIndex,
                1
            );
           
        },
        aaa(val){
         // console.log(val)
        },
        //编辑课时名称
        innerEdit(val) {
            val.isInput = true;
            val._inputText = val.periodname;
            this.getMaterData();
        },
        // 日期格式化
        formatDate(time) {
            var date = new Date(time);
            var year = date.getFullYear(),
                month = date.getMonth() + 1, //月份是从0开始的
                day = date.getDate(),
                hour = date.getHours(),
                min = date.getMinutes(),
                sec = date.getSeconds();
            var newTime =
                year +
                "-" +
                (month < 10 ? "0" + month : month) +
                "-" +
                (day < 10 ? "0" + day : day);
            return newTime;
        },
        
        //编辑单元名称
        outEdit(val) {
            val.OutisInput = true;
            val._inputText = val.unitname;
            this.getMaterData();
        },
        //保存单元名称
        saveOutEdit(val) {
            val.unitname = val._inputText;
            if (val.unitname == "") {
                this.$Message.error(this.$t('courseManagepage.tip1'));//单元名称不能为空
                return false;
            }
            val.OutisInput = false;
            this.getMaterData();
        },
        //保存课时名称
        saveInnerEdit(val, lesson, index, i) {
            val.periodname = val._inputText;
            if (val.periodname == "") {
                this.$Message.error(this.$t('courseManagepage.tip2'));//课时名称不能为空
                return false;
            }
            val.isInput = false;
            this.getMaterData();
        },
        
        //下一步
        saveData() {
            let { lessonData } = this;
           
            Server.post(
                Path.addTwoCurricukum,
                {
                    id: this.$route.query.id,
                    inperiod: [...lessonData.inperiod]
                },
                res => {
                    let { code, data } = res;
                    if (code === 0) {
                        this.$router.push({
                            path: "/AddLesson/Edit",
                            query: {
                                step: "3",
                                classType: this.$route.query.classtypes,
                                id: this.$route.query.id,
                                from:'MainLessonManagement'
                            }
                        });
                        let Storage = JSON.parse(
                            localStorage.getItem("addLessonMsesage")
                        );
                        Storage.UnitNumber = lessonData.inperiod.length;
                        Storage.LessonTime = this.LessonTimeNumber;
                        localStorage.setItem(
                            "addLessonMsesage",
                            JSON.stringify(Storage)
                        );
                    } else {
                        this.$Message.error(res.info);
                    }
                }
            );
        },
        saveMater(id) {
            //保存选中的文件
            let _strMater = this.materListData
                .filter((el, index) => {
                    return el.isSelect == true;
                })
                .map((e, i) => {
                    return {
                        id: e.fileid,
                        name: e.showname
                    };
                });
            //录播课只能关联一个课件判断
            if(this.$route.query.classtypes == 1 && _strMater.length >1){
                _strMater = [];
                this.$Message.error("录播课只能关联一个视频课件");
                return false;
            }
            this.activeListMaterId = [..._strMater];
            //console.log(this.activeListMaterId);
            let { parentId, childrenId } = this;
            let courseware = this.lessonData.inperiod[parentId].list[childrenId]
                .courseware;
            let fileListData = [...courseware, ...this.activeListMaterId]; //contact
            this.lessonData.inperiod[parentId].list[
                childrenId
            ].courseware = this.unique(fileListData);
            var len = fileListData.length;
        
            this.AboutMater = false; //关闭窗口
           
        },
        //去重
        unique(arr) {
            var unique = {};
            arr.forEach(function(a) {
                unique[JSON.stringify(a)] = 1;
            });
            arr = Object.keys(unique).map(function(u) {
                return JSON.parse(u);
            });
            return arr;
        },
        backMater() {
            this.activeMaterId = 0; //初始化父级课件
            this.isShowMaterList = false; //打开课件例表
            this.materTitle = this.$t('courseManagepage.Associatedtext'); //标题 关联课本
        },
        openMaterList(id, title) {
            this.activeMaterId = id;
            this.isShowMaterList = true; //打开课件例表
            this.materTitle = title; //标题
            
            Server.post(
                Path.getFileList,
                {
                    showname: "",
                    pagenum: 1,
                    fatherid: id,
                    usetype: this.$route.query.classtypes
                },
                res => {
                    let { code, data } = res;
                    if (code === 0) {
                        //增加选中字段
                        this.materListData = data.data.map((e, index) => {
                            e.isSelect = false; //
                            return e;
                        });

                        /**
                         * 打开资源文件列表时，已经存在的课件，默认选中状态
                         * @date 2018 06 26 15:04
                         * @authors qyh
                         */
                        this.materListData.forEach(el => {
                            this.lessonData.inperiod[this.parentId].list[
                                this.childrenId
                            ].courseware.forEach(item => {
                                if (el.fileid == item.id) {
                                    el.isSelect = true;
                                }
                            });
                        });
                    }else if(code === 10082){
                        this.materListData = [];//没有数据的文件夹清空处理
                    }
                }
            );
        },
        openAbout(parentId, childrenId) {
            this.parentId = parentId;
            this.childrenId = childrenId;
            this.getMaterData();
            this.AboutMater = true;
            //初始化弹框
            this.backMater();
        },
        getMaterData(page = 1, id = 0) {
            Server.post(
                Path.getFileList,
                {
                    showname: "",
                    pagenum: page,
                    fatherid: id,
                    usetype: this.$route.query.classtypes
                },
                res => {
                    let { code, data } = res;
                    if (code === 0) {
                        if (page == 1) {
                            this.materData = [...data.data];
                        } else {
                            this.materData = [...this.materData, ...data.data];
                        }
                        if (data.data.length < data.pageinfo.pagesize) {
                            this.falg = false; //控制滚动条无限加载
                        }
                    } else {
                        this.falg = false;
                    }
                }
            );
        },
        saveLocalStorage() {},
        addLessonTimeItem(lesson, index) {
            var item = {
                id: "",
                periodname: "",
                isInput: true,
                periodsort: 1,
                courseware: "",
                _inputText: ""
                // intime: 'df',
                // timekey: 'fd'
            };
            let { inperiod } = this.lessonData;
            inperiod.forEach((el, i) => {
                if (index == i) {
                    item.periodsort = el.list.length + 1;
                    el.list.push(item);
                    this.LessonTimeNumber += 1;
                }
            });
        },
        addLessonItem() {
            var objtemp = {
                unitname: "",
                isInput: true,
                _inputText: "",
                unitsort: 1,
                id: "",
                list: [],
                OutisInput: true
            };
            let { inperiod } = this.lessonData;
            objtemp.unitsort = inperiod.length + 1;
            inperiod.push(objtemp);
        },
        deleteParentItem(index) {
            let { inperiod } = this.lessonData;
            inperiod.splice(index, 1);
            //重新排序
            this.lessonData.inperiod = inperiod.map((e, i) => {
                e.unitsort = i + 1;
                return e;
            });
        },
        deleteChildernItem(i, index) {
            let { inperiod } = this.lessonData;
            inperiod.forEach((el, d) => {
                if (index == d) {
                    el.list.splice(i, 1);
                    //重新排序
                    this.lessonData.inperiod[d].list = inperiod[d].list.map(
                        (e, i) => {
                            e.periodsort = i + 1;
                            return e;
                        }
                    );
                }
            });
            this.LessonTimeNumber -= 1;
        }
        
    }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.ivu-icon{
    float: left;
}
.inputWidth {
}
.title {
    color: #5e6266;
    font-size: 18px;
    font-weight: normal;
}
.btn-add-box {
    padding: 18px auto;
    overflow: hidden;
    margin-bottom: 20px;
}
.btn-add-lesson {
    float: right;
    width: 160px;
    height: 50px;
    text-align: center;
    font-size: 20px;
    color: #fff;
    line-height: 50px;
    background-color: #ffb932;
    cursor: pointer;
}
.class-time-box-item {
    border-left: 1px solid #62BAFE;
    padding-left: 60px;
    margin-left: 42px;
}
.class-time-box-item dl {
    padding: 0 0 10px 0;
}
.class-time-box-item dt,
.class-time-box-item dd {
    /* border: 1px solid #469af8; */
    margin-bottom: 20px;
    position: relative;
    line-height: 40px;
    border-radius: 4px;
    text-indent: 10px;
}
.class-time-box-item dt .time-title-icon {
    position: absolute;
    width: 28px;
    height: 28px;
    background-color: #fff;
    border: 1px solid #62BAFE;;
    left: -75px;
    top: -1px;
    border-radius: 50%;
}
.class-time-box-item dt .time-title-icon .time-title-icon-q {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: #62BAFE;
    position: absolute;
    left: 5px;
    top: 5px;
}
.class-time-box-item dd {
    margin-left: 60px;
}
.edit-btn-box {
    float: right;
    /* padding-top: 6px; */
    height: 42px;
}
.edit-btn-box a {
    display: block;
    overflow: hidden;
    text-align: center;
    margin-right: 10px;
}
.lessontimeitem {
    border-radius: 4px;
    position: relative;
}
/* .edit-btn-box .time-edit-item {
  float: left;
  cursor: pointer;
  margin-right: 10px;
}
.edit-btn-box .time-edit-item img {
  width: 55%;
} */
.ivu-dropdown {
    height: 42px;
    overflow: hidden;
}
.ivu-dropdown-menu li * {
    display: inline-block;
    vertical-align: middle;
}
.mater-box {
    overflow: hidden;
}
.mater-box li {
    width: 164px;
    float: left;
    margin: 0 24px 17px 0;
}
.mater-item {
    border: 1px solid #cfdce7;
    border-radius: 4px;
    cursor: pointer;
}
.mater-img {
    text-align: center;
    height: 129px;
    padding-top: 32px;
    overflow: hidden;
}
.mater-img img {
    width: 64px;
    height: 54px;
    display: block;
    margin: 0 auto 5px;
}
.mater-name {
    line-height: 44px;
    text-align: center;
    background: rgba(88,157,253,.1);
    font-size: 15px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.type-content {
    height: 380px;
    overflow-x: hidden;
}
/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
.type-content::-webkit-scrollbar {
    width: 5px;
    height: 16px;
    background-color: #f5f5f5;
}

/*定义滚动条轨道 内阴影+圆角*/
.type-content::-webkit-scrollbar-track {
    /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); */
    border-radius: 10px;
    background-color: #f5f5f5;
}

/*定义滑块 内阴影+圆角*/
.type-content::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: rgba(0, 0, 0, 0.3);
}
.bottom-border {
    border-bottom: 1px solid rgba(211, 223, 233, 1);
}
.class-box {
    margin: 0 60px;
}
.class-box li * {
    display: inline-block;
}
.class-box li label {
    width: 100%;
    height: 100%;
}
.class-box li {
    height: 40px;
    border-bottom: 1px solid rgba(232, 237, 240, 1);
    line-height: 40px;
    position: relative;
    font-size: 16px;
    color: #2F3331;
}
.class-box .class-check {
    position: absolute;
    left: -30px;
    top: 0;
}
.overflow {
    overflow: hidden;
}
.more-btn {
    width: 50px;
    height: 42px;
    background: url("./assets/img/select.png") no-repeat center;
    background-size: 24px;
}
.time-sheet {
    display: table-cell;
    font-size: 0;
}

.time-sheet li {
    display: inline-block;
    padding: 0 18px;
    margin: 0 16px 20px 0;
    font-size: 14px;
    text-align: center;
    border: 1px solid #B3B3B3;
    border-radius: 4px;
    cursor: pointer;
    color: @fz-Color1;
    &.activeTime {
        background: @color1;
        color: #fff;
        border: 1px solid @color1;
    }
}

.showTime {
    position: absolute;
    right: 168px;
}
.showTime img {
    position: relative;
    top: 8px;
}
.ivu-card-head p{
    height: 38px;
    line-height: 38px;
}
.ivu-dropdown-item{
    color: #86898C;
    font-size: 14px !important;
}
.btnbox{
    margin: 20px 0 81px 104px;
}
</style>



