<template>
  <div >
    <panel-title :crumbs="[{name:'套餐列表',route:'/SetMealList?from=MainBuyManagement'},{name:'添加套餐',route:''}]" />
   	<Card  dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30" style="padding-bottom: 200px;">
   		<Form :label-width="180" ref='formItem' :model="formItem" class='form-confrol cardp1' :rules="ruleCustom" >   			
   			<Row>
   					<Col span="20" style="height: 140px;">
              <!-- 基本信息 -->
              <card-title style='margin-top:55px'>{{$t('showBasicInfo.BasicInfo')}}</card-title>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1">
            <!-- 套餐配置：-->
              <FormItem label="套餐配置："  prop="classTime">
              	购买<Input class="mealTime" v-model.trim="formItem.classTime" style="width: 10%;" :maxlength="4"/>课时
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1">
            <!-- 套餐名称：-->
              <FormItem label="套餐名称：" prop="setName">
                <Input placeholder="直播送录播" style="width: 80%;"  :maxlength="20"  v-model.trim="formItem.setName"/>
                <!-- (套餐名称长度不超过20个汉字，支持中英文输入) -->
                <div class='headimg-des'> (套餐名称长度不超过20个汉字，支持中英文输入)</div>
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="18" offset="1" >
            	<!--套餐封面-->
              <FormItem label="套餐封面：" prop="images">
                <UploadPic
                  :data="upload"
                  @success="getLogoImg"
                 style="width:204px;height:68px;position: relative;">
                  <div class="logo-box">
                      <img v-if="!formItem.images" :src="uploadIcon" style="position: absolute;left:50%;top:50%; transform: translate(-50%,-50%)"/>
                    	<img :src="formItem.images" class='logo' v-model="formItem.images" />
                  </div>
                </UploadPic>
                <div class='headimg-des'> (为保证显示效果，图片尺寸为1920*1080，图片格式支持jpg、png)</div>
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1" >
            <!-- 套餐价格： -->
              <FormItem label="请输入套餐价格：" prop="packagePrice">
                <Input placeholder="请输入套餐价格" style="width:30%;margin-right: 10px;" v-model="formItem.packagePrice" />元
              </FormItem>
            </Col>
        	</Row>   
        	
   			<Row>
   					<Col span="20" style="height: 140px;">
              <!-- 使用信息 -->
              <card-title style='margin-top:55px'>使用信息</card-title>
            </Col>
          </Row>        	
          <Row>
            <Col span="12" offset="1">
            <!-- 每人限购： -->
              <FormItem label="每人限购：" prop="classNum" :show-message="maleBol">
		        		<RadioGroup v-model="radipurchaseo">
		                <Radio label="male">无限制</Radio>
                    <div>
                      <Radio label="female">限购买</Radio>
                      <!-- 限购次数 -->
                      <Input style="width: 10%;margin-right: 50px;" v-model="formItem.classNum" :disabled="radipurchaseo == 'female' ? false : true "/>
                      <span	class='headimg-des'>（限购次数为整数）</span>
                    </div>
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row> 
          
          <Row>
            <Col span="12" offset="1">
            <!-- 使用门槛： -->
              <FormItem label="使用门槛：" prop="classPrice" :show-message="maleBol1">
		        		<RadioGroup v-model="threshold">
		                <Radio label="male" >无限制</Radio>
                    <div>
                      <Radio label="female">订单满</Radio>		            
                      <Input  style="width: 14%;margin-right: 10px;" v-model="formItem.classPrice" :disabled="this.threshold == 'female' ? false : true "/>元可用
                    </div>
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row>          

          <Row>
            <Col span="12" offset="1">
            <!-- 有效期： -->
              <FormItem label="有效期：" prop="numDays" :show-message="maleBol2">
		        		<RadioGroup v-model="term" class="term" style="margin-top: 3px;">
		                <Radio label="female" >日期范围</Radio>
                    <DatePicker type="daterange" placement="bottom-end" :value="valueTime" :editable="false" placeholder="开始时间 - 结束时间" @on-change="handleChange"
                    	:disabled="this.term == 'female' ? false : true " style="margin-bottom: 20px;" :options="optionss"></DatePicker> </br>
                    <Radio label="days">固定天数</Radio>
		            		<Input class="terminpt" :disabled="this.term == 'days' ? false : true " :maxlength="3" v-model="formItem.numDays" />
		            		<!--<span slot="append" >天</span>-->
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row> 
        	
           <Row>
            <Col span="12" offset="1">
            <!-- 可使用课程：-->
              <FormItem label="可使用课程：" >
									<Button class="classBtn" :class= "{btnActive : allClass}" @click="active ('allClass',0)">全部课程</Button>
									<Button class="classBtn" :class= "{btnActive : Appoint}"  @click="active ('Appoint',1)">指定分类</Button>
									<Button class="classBtn" :class= "{btnActive : fication}" @click="active ('fication',2)">指定课程</Button>
              </FormItem>
            </Col>
        	</Row>  
        	
        	<Row>
            <Col span="12" offset="1" v-if="(edit && classifyNum!=0) || (edit && strage.length!=0)">
            <!-- 选择分类/课程：-->
              <FormItem :label="activeIdex==2?'选择分类：':'选择课程：'" prop ="treeData" >
              		<div class="pfication">
              			<!-- <span class="classification" v-model="treeData" v-for="item of treeData">{{item.title}}</span> -->
              			<span v-if="activeIdex==2" class="classification edit" @click = "isShowDelModal = true ">共选择{{classifyNum}}分类</span>
              			<span v-if="activeIdex==3" class="classification edit" @click = "isShowDel = true ">共选择{{strage.length}}门课程</span>
              		</div>
              </FormItem>
            </Col>
        	</Row>
       	
          <Row>
            <Col span="12" offset="1">
              <!-- 使用说明： 请输入内容-->
              <FormItem label="使用说明：" prop="summary">
                <Input :placeholder="$t('showBasicInfo.tip')" type="textarea"  :rows="6" :autosize="{minRows: 2,maxRows: 5}" v-model.trim="formItem.summary" :maxlength = '100'/>
                <!-- 提示 -->
                <div class='headimg-des'>（简要介绍自己课堂的优势、特色信息，方便用户获得直观印象，100字内）</div>
              </FormItem>
            </Col>
          </Row>

          <Row>
            <Col span="12" offset="1">
              <!-- 是否赠送课时： 请输入内容-->
              <FormItem label="是否赠送课时：">
		        		<RadioGroup v-model="check" style="margin: 4px;">
		                <Radio label="male" style="margin-right: 100px;">是</Radio>
		                <Radio label="female">否</Radio>		            
		            </RadioGroup>
              </FormItem>
            </Col>
          </Row>

          <!-- 非赠送部分————指定课程弹框 -->
          <Modal v-model="isShowDel" width="900":closable="false">
              <div slot="header">指定课程</div>
               <Card dis-hover class="mytheme-noborder">
              <div style=" height: 300px;">
                <ul class="classUl">
                  <CheckboxGroup  class="checkboxGroup" v-model="cheecck"  @on-change="getDate">
                    <li v-for="(itmes,index) in dataM" class="claasLi">
                        <img :src="itmes.imageurl" alt="" />
                        <!-- <span class="classSpan">{{itmes.coursename}}</span> -->
                        <Checkbox class="classBox" :label="itmes.id" v-model="itmes.focus" size="large" >{{itmes.coursename}}</Checkbox>
                    </li>
                  </CheckboxGroup>
                </ul>
              </div>
              <pagination :total="total"  :current-page='current' @pagechange="pagechange"  :display= "display" v-if='total>0'></pagination>
             	</Card>	
              <div slot="footer" >
                <Button type="primary" @click="showSelectItem(isShowDel=false,edit=true)">确认</Button>
                <Button @click="isShowDel=false">取消</Button>
              </div>
					</Modal>

					<!--赠送部分————指定课程弹框-->
          <Modal v-model="isShowDell" width="900" :closable="false">
              <p slot="header">指定课程</p>
               <Card dis-hover class="mytheme-noborder">
              <div style=" height: 300px;">
                <ul class="classUl">
                    <CheckboxGroup  class="checkboxGroup" v-model="cheeecck"  @on-change="getDatePlus">
                    <li v-for="(itmes,index) in dataw" class="claasLi">
                        <img :src="itmes.imageurl" alt="" />
                        <!-- <span class="classSpan">{{itmes.coursename}}</span> -->
                        <Checkbox class="classBox" :label="itmes.id" v-model="itmes.focus" size="large" >{{itmes.coursename}}</Checkbox>
                    </li>
                    </CheckboxGroup>
                </ul>
              </div>
              <pagination :total="totall"  :current-page='currentl' @pagechange="pagechangePlus"  :display= "displayl" v-if='totall>0'></pagination>
                	</Card>	  
              <div slot="footer" >
                <Button type="primary" @click="showSelectItem(isShowDell=false,edit1=true)">确认</Button>
                <Button @click="isShowDell= false">取消</Button>
              </div>
					</Modal>
					
				  <!--非赠送部分————指定分类弹框-->
			    <Modal v-model="isShowDelModal" width="500" :closable="false">
			    	    <p slot="header">选择课程分类</p>
            	  <Tree :data="data1" ref="mTree" multiple show-checkbox v-model="formItem.tree" @on-check-change= "setTree"></Tree>
                <div slot="footer">
                  <Button type="primary" @click="showSelectItem(isShowDelModal=false,edit=true)">确认</Button>
                  <Button @click="moisShow('mian')">取消</Button>
                </div>
			    </Modal>
			    
					<!--赠送部分————指定分类弹框-->
			    <Modal v-model="isShowDelModall" width="500":closable="false">
			    	    <p slot="header">选择课程分类</p>
            	  <Tree :data="data2" ref="mTreePlus" multiple show-checkbox v-model="formItem.treePlus" @on-check-change= "setTreePlus"></Tree>
                <div slot="footer">
                  <Button type="primary" @click="showSelectItem(isShowDelModall=false,edit1=true)">确认</Button>
                  <Button @click="moisShow('plus')">取消</Button>
                </div>
			    </Modal>

        <!--赠送课时部分-->  
				<div v-if="this.check == 'female' ? false : true">
			  		  <Row>
		            <Col span="12" offset="1">
		            <!-- 赠送————有效期： 请输入内容 -->
		              <FormItem label="赠送课时：" >
				        		<RadioGroup v-model="give" class="term" style="margin-top: 3px;">
		                    <FormItem label="" prop="broadcast" :show-message="maleBol4">
		                    	<Radio label="broadcast" style="margin-bottom: 7px;">赠送</Radio>
				            			<Input  style="width: 18%;margin: 0 10px 6px 0" :disabled="this.give == 'broadcast' ? false : true" v-model="broadcast"/>&nbsp;课时录播课
				            		</FormItem>
				            		</br>
				            		 <FormItem label="" prop="broadcasting" :show-message="maleBol5">
		                      	<Radio label="broadcasting" style="margin-top: 4px;">赠送</Radio>
				            				<Input  style="width: 18%;margin:0px 10px 0  0; display: inline-block;" :disabled="this.give == 'broadcasting' ? false : true " v-model="broadcasting"/>&nbsp;课时直播课
				           			 </FormItem>
				        		</RadioGroup>
				        		</br>
										<span  class="classTime" v-if="bradShow">请输入课时</span>
		              </FormItem>
		            </Col>
		        	</Row> 
		        	
		        	<Row>
		            <Col span="12" offset="1">
		            <!--  赠送————使用门槛： 请输入内容 -->
		              <FormItem label="使用门槛：" prop="classPrice1" :show-message="maleBol6">
				        		<RadioGroup v-model="thresholdPlus" style="width: 60%;">
				                <Radio label="male" >无限制</Radio>
                        <div>
                          <Radio label="female">订单满</Radio>		            
                          <Input  style="width: 14%;margin-right: 10px;" :disabled="this.thresholdPlus == 'female' ? false : true " v-model="formItem.classPrice1" :maxlength="4"/>
                            元可用
                        </div>
				            </RadioGroup>
		              </FormItem>
		            </Col>
		        	</Row>          
		
		          <Row>
		            <Col span="12" offset="1">
		            <!-- 赠送————有效期： 请输入内容 -->
		              <FormItem label="有效期：" prop="Daysnum" :show-message="maleBol7">
				        		<RadioGroup v-model="termPlus" class="term" style="margin-top: 3px;">
				                <Radio label="female" >日期范围</Radio>
                    		<DatePicker type="daterange"  placement="bottom-end" :value="timeValue" :editable="false"  placeholder=" 选择截止日期" @on-change="handleChangePlus"
                    		:disabled="this.termPlus == 'female' ? false : true " :options="optionss" style="margin-bottom: 20px;" >
                    		</DatePicker></br>                    
		                    <Radio label="days">固定天数</Radio>
				            		<Input class="terminpt" :disabled="this.termPlus == 'days' ? false : true " v-model="formItem.Daysnum" :maxlength="3" />
				            </RadioGroup>
		              </FormItem>
		            </Col>
		        	</Row> 
		        	
		           <Row>
		            <Col span="12" offset="1">
		            <!-- 赠送————可使用课程： 请输入内容 -->
		              <FormItem label="可使用课程：" >
											<Button class="classBtn" :class= "{btnActive : allClass1}" @click="active1 ('allClass1',0)">全部课程</Button>
											<Button class="classBtn" :class= "{btnActive : Appoint1}"  @click="active1 ('Appoint1',1)">指定分类</Button>
											<Button class="classBtn" :class= "{btnActive : fication1}" @click="active1 ('fication1',2)">指定课程</Button>
		              </FormItem>
		            </Col>
		        	</Row>  

		        	<Row>
		            <Col span="12" offset="1" v-if="(edit1&&classifyNum1!=0)||(edit1&&stragePlus.length!=0)">
		            <!-- 赠送————选择分类： 请输入内容 -->
		              <FormItem :label="activeIdex==2?'选择分类：':'选择课程：'"  prop ="treeDataPlus" >
		              		<div class="pfication">
		              			<!-- <span class="classification" v-for="item of treeDataPlus">{{item.title}}</span> -->
		              			<span v-if="activePlus==2" class="classification edit" @click = "isShowDelModall = true ">共选择{{classifyNum1}}分类</span>
		              			<span v-if="activePlus==3" class="classification edit" @click = "isShowDell = true ">共选择{{stragePlus.length}}门课程</span>
		              		</div>
		              </FormItem>
		            </Col>
		        	</Row>		        			    			    
					</div>		
					
				<Row>
		      <Col span="21" offset="1">
          	 <FormItem label="" class="btnPad">
          	 	<!-- <div class="btn custom-btn-cancel br21" style="margin-right: 20px;" @click="backRou">取消</div> -->
            	<Button type="primary" @click="handleSubmit('formItem')">提交</Button>
            </FormItem>
          </Col>
			</Row>	
   		</Form>
   	</Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import cardTitle from "@/components/common/CardTitle/CardTitle";
import {
  Card,
  Form,
  FormItem,
  Input,
  Upload,
  Button,
  Col,
  Row,
  RadioGroup,
  Radio,
  DatePicker,
  Modal,
  Select,
  Option,
  Tree,
  CheckboxGroup,
  Checkbox
} from "iview";
import Server from "@/service/request";
import UploadPic from "@/components/UploadPicture";
import Path from "@/service/Path";
import uploadIcon from '@/assets/images/icon_addfolder@2x.png';
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Form,
    FormItem,
    UploadPic,
    Input,
    Upload,
    Button,
    Col,
    Row,
    pagination,
    cardTitle,
    RadioGroup,
    Radio,
    DatePicker,
    Modal,
    Select,
    Option,
    Tree,
    CheckboxGroup,
    Checkbox
  },
  data() {
    const PriceMeal = (rule, value, callback) => {//套餐价格
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (value === "") {
        callback(new Error("请输入套餐价格"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确套餐价格"));
      } else {
        callback();
      }
    };
    const mealPrice = (rule, value, callback) => {//使用门槛的满多少钱
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (this.threshold === "male") {
        callback();
      } else {
        if (value === "") {
          callback(new Error("请输入金额"));
        } else if (!PriceRegex.test(value)) {
          callback(new Error("请按正确金额格式输入"));
        }
        callback();
      }
    };
    const classPrice = (rule, value, callback) => {//赠送门槛的满多少钱
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      value = this.formItem.classPrice1;
      if (this.thresholdPlus === "female" && value === "") {
        callback(new Error("请输入金额"));
      } else if (this.thresholdPlus === "male") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请按正确金额格式输入"));
      } else {
        callback();
      }
    };
    const classMeal = (rule, value, callback) => {//课时正则
      let PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (value === "") {
        callback(new Error("请输入课时"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确课时"));
      } else {
        callback();
      }
    };
    // const images = (rule, value, callback) => {//图片正则
    //   if (value === "") {
    //     callback(new Error("图片log不能为空"));
    //   }
    //   callback();
    // };
    const classNum = (rule, value, callback) => {//每人限购次数正则
      let PriceRegex = /^[1-9][0-9]{0,4}$/;
      if (this.radipurchaseo === "female" && value === "") {
        callback(new Error("请输入限购次数"));
      } else if (this.radipurchaseo != "female") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确限购次数"));
      } else {
        callback();
      }
    };
    const numDays = (rule, value, callback) => {//有效期的固定天数
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.term === "days" && value === "") {
        callback(new Error("请输入天数"));
      } else if (this.term != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    // const treeData = (rule, value, callback) => {//指定分类
    // 			value = this.treeData;
    //       if (this.activeIdex === 2 && value.length == 0) {
    //         callback(new Error('指定课程分类不能为空'));
    //       }else {
    //         callback();
    //       }
    // };
    // const treeDataPlus = (rule, value, callback) => {//赠送————指定分类
    // 			value = this.treeDataPlus;
    //       if (this.activePlus === 2 && value.length == 0) {
    //         callback(new Error('指定课程分类不能为空'));
    //       }else {
    //         callback();
    //       }
    // };
    const Daysnum = (rule, value, callback) => {//赠送有效日期————固定天数
      value = this.formItem.Daysnum;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.termPlus === "days" && value === "") {
        callback(new Error("请输入天数"));
      } else if (this.termPlus != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    const broadcasting = (rule, value, callback) => {//赠送课时正则--直播课
      value = this.broadcasting;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcast") {
        this.broadcasting = "";
        callback();
      } else if (this.give === "broadcasting" && value === "") {
        callback(new Error("请输入课时数"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    const broadcast = (rule, value, callback) => {//赠送课时正则--录播课
      value = this.broadcast;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcasting") {
        this.broadcast = "";
        callback();
      }
      if (this.give === "broadcast" && value === "") {
        callback(new Error("请输入课时数"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    return {
      uploadIcon,
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      allClass: true, //全部课程
      Appoint: false, //指定分类
      fication: false, //指定课程
      allClass1: true, //全部课程
      Appoint1: false, //指定分类
      fication1: false, //指定课程
      isShowDelModal: false, //分类弹框
      isShowDelModall: false, //赠送弹框
      isShowDel: false, //指定课程弹框
      isShowDell: false, //指定课程弹框赠送
      edit: false, //课程分类编辑按钮
      totall: 0, // 记录总条数
      displayl: 20, // 每页显示条数
      currentl: 1, // 当前的页数
      edit1: false, //课程分类编辑按钮 赠送
      bradShow: false,
      activeIdex: 1, //全部课程 指定课程 制定分类 选中状态
      activePlus: 1, //全部课程 指定课程 制定分类 选中状态 赠送
      upload: [], //图片
      cheecck: [], //指定课程 复选框  选定返回数组 必须为空 否则只会单一返回
      cheeecck: [], //指定课程 复选框  选定返回数组 必须为空 否则只会单一返回 赠送
      valueTime: [], //'2018-01-01', '2020-02-15'
      timeValue: [], //'2018-01-01', '2020-02-15'
      radipurchaseo: "male", //每人限购radio
      threshold: "male", //使用门槛radio
      term: "female", //截止日期插件
      termPlus: "female", //赠送课时 使用日期插件
      give: "broadcast", //赠送
      thresholdPlus: "male", //赠送课时使用门槛radio
      broadcast: "", //录播课
      broadcasting: "", //直播课
      check: "female", //是否赠送选课
      starTime: "", // 时间插件
      endTime: "", // 时间插件
      starTimePlus: "", // 时间插件
      endTimePlus: "", // 时间插件
      formdate: "",
      classtypess: "1",
      strage: "", //指定课程strage存储选定数据
      stragePlus: "", //指定课程strage存储选定数据
      treeData: [], //课程分类
      treeDataPlus: [], //课程分类赠送
      data1: [], //指定分类数据
      data2: [], //指定分类数据赠送
      dataM: [], //指定课程渲染数据
      dataw: [], //指定课程渲染数据
      categoryids: "", //课程分类ID
      categoryidsPlus: "", //课程分类ID 赠送
      curriculumids: "", //课程id
      curriculumidsPlus: "", //课程id赠送
      classifyNum: 0, //选择了几个分类
      classifyNum1: 0, //赠送了几个分类
      optionss: {//限定时间从当前开始
        disabledDate(date) {
          return date && date.valueOf() < Date.now() - 86400000;
        }
      },
      formItem: {
        classTime: "", //购买课时数
        setName: "", //套餐名称
        images: "", //上传腾讯云之后图片url
        packagePrice: "", //套餐价格
        classNum: "", //限购次数
        classPrice: "", //课程满足价格
        numDays: "", //输入框天数
        tree: "", //课程分类结构
        treePlus: "", //赠送课时 课程分类
        Daysnum: "", //赠送课时 固定天数
        summary: "", //文本框
        classPrice1: "" //使用门槛 input
      },
      ruleCustom: {
        // images: [{ required: true, validator: images,trigger: 'change' }], //图片的非空验证
        classTime: [{ required: true, validator: classMeal,trigger: 'blur'}], //购买课时
        setName: [{ required: true, message: "套餐名称不能为空",trigger: 'blur'}], //套餐名称的非空验证
        broadcasting: [{ required: true, validator: broadcasting,trigger: 'blur'}], //赠送课时正则--直播课
        broadcast: [{ required: true, validator: broadcast,trigger: 'blur'}],  //赠送课时正则--录播课
        packagePrice: [{ required: true, validator: PriceMeal,trigger: 'blur'}], //套餐价格
        classNum: [{ required: true, validator: classNum,trigger: 'blur'}],//每人限购次数
        classPrice: [{ required: true, validator: mealPrice,trigger: 'blur'}],//使用门槛的满多少钱
        classPrice1: [{ required: true, validator: classPrice,trigger: 'blur'}], //赠送门槛的满多少钱
        numDays: [{ required: true, validator: numDays,trigger: 'blur'}],//有效期的固定天数
        Daysnum: [{ required: true, validator: Daysnum,trigger: 'blur'}],//赠送有效日期————固定天数
        // treeData: [{ required: true, validator: treeData}],//指定分类
        // treeDataPlus: [{ required: true,validator: treeDataPlus}],//赠送————指定分类
        summary: [{ required: true, message: "使用说明不能为空" }] //赠送 固定天数
      },
      store:0,//改变所选分类或课程的状态
      storePlus:0,//改变所选分类或课程的状态
      maleBol:true,//是否做正则验证
      maleBol1:true,
      maleBol2:true,
      maleBol3:true,
      maleBol4:true,
      maleBol5:false,
      maleBol6:true,
      maleBol7:true,
    };
  },
  created() {
    this.upload = [3, 1, 1]; //上传图片机构设定
    this.getNewDate()
  },
  methods: {
    setTree() {
      //获取指定分类选择的数据
      this.treeData = this.$refs.mTree.getCheckedNodes();
      let arrId = [];
      for (let i = 0; i < this.treeData.length; i++) {
        arrId.push(this.treeData[i].id);
      }
      this.categoryids = arrId.join();
    },
    setTreePlus() {
      //赠送——获取指定分类选择的数据
      this.treeDataPlus = this.$refs.mTreePlus.getCheckedNodes();
      let arrId = [];
      for (let i = 0; i < this.treeDataPlus.length; i++) {
        arrId.push(this.treeDataPlus[i].id);
      }
      this.categoryidsPlus = arrId.join();
    },
    showSelectItem(bol1, bol2) {//选中分类及课程的展示
      bol1;
      bol2;
      this.classifyNum = 0;
      this.treeData.forEach(item => {
          this.classifyNum++;
      });
      this.classifyNum1 = 0;
      this.treeDataPlus.forEach(item => {
          this.classifyNum1++;
      });
    },
    handleSubmit(name) {//提交表单
      this.$refs[name].validate(valid => {
        if (valid) {
          this.SaveData();
        } else {
          // this.$Message.error('表单验证失败!');
        }
      });
    },
    //保存信息
    SaveData() {
      //套餐封面是否为空
      if(this.formItem.images === '') {
        this.$Message.error('请选择套餐封面');
      	return false;
      }
      //获取有效期范围
      if(this.term === 'female' && this.starTime === '') {
      	this.handleChange(this.valueTime)
      }
      //赠送——获取有效期范围
      if(this.termPlus === 'female' && this.starTimePlus === '') {
      	this.handleChangePlus(this.timeValue)
      }
      Server.post(
        Path.addMeal,
        {
          bughour: this.formItem.classTime, //课时
          setmeal: this.formItem.setName, //套餐名称
          setprice: this.formItem.packagePrice, //套餐价格
          limitbuy:this.radipurchaseo === "male" ? "0" : this.formItem.classNum, //限购次数(0:无限制 其他:限制次数)
          threshold: this.threshold === "male" ? "0" : this.formItem.classPrice, //使用门槛(0:无限制 其他:满金额使用)
          efftype: {
            status: this.term === "female" ? 1 : 2, //有效期状态（1：时间范围 2：固定天数）",
            effstarttime: this.starTime, //开始时间(时间戳)
            effendtime: this.endTime, //结束时间(时间戳)",
            efftime: this.formItem.numDays //固定天数"
          },
          trialtype: {
            status: this.activeIdex, //可使用课程（1：全部课程，2：指定分类，3：指定课程）
            categoryids: this.categoryids, //"课程分类ids"
            curriculumids: this.curriculumids //指定课程ids"
          },
          content: this.formItem.summary, //使用说明",
          setimgpath: this.formItem.images, //"图片地址",
          givestatus: this.check === "male" ? 1 : 0, // "是否赠送课时（0：不赠送 1：赠送）",
          sendvideo: this.broadcast, //"赠送课时（录播课）",
          sendlive: this.broadcasting, //"增送课时（直播课）",
          giftthreshold:this.thresholdPlus == "male" ? 0 : this.formItem.classPrice1, //"赠送——使用门槛(0:无限制 其他:满金额使用)",
          giftefftype: {
            status: this.termPlus === "female" ? 1 : 2, //有效期状态（1：时间范围 2：固定天数）,
            gifteffstarttime: this.starTimePlus, //"开始时间(时间戳)",
            gifteffendtime: this.endTimePlus, //"结束时间(时间戳)",
            giftefftime: this.formItem.Daysnum //"固定天数"
          },
          gifttrialtype: {
            status: this.activePlus, //"可使用课程（1：全部课程，2：指定分类，3：指定课程）",
            giftcategoryids: this.categoryidsPlus, //"课程分类ids",
            giftcurriculumids: this.curriculumidsPlus //"指定课程ids"
          }
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$Message.success(res.info);
            localStorage.removeItem('lv');
            localStorage.removeItem('lvv');
            this.$router.go(-1);
          } else if (code === 60016) {
            this.$Message.success(res.info);
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    moisShow(items) {//取消时清楚树形数据的选择
      if (items === "mian") {
        this.isShowDelModal = false;
        this.treeData = [];
      } else {
        this.isShowDelModall = false;
        this.treeDataPlus = [];
      }
    },
    getDate(value) {
      //指定课程返回选定数组
      this.strage = value;
      localStorage.lv = value;
      this.curriculumids = value.join();
    },
    getDatePlus(value) {
      //指定课程返回选定数组
      this.stragePlus = value;
      localStorage.lvv = value;
      this.curriculumidsPlus = value.join();
    },
    active(mun,num) {
      if(localStorage.getItem("store")!=num){
        this.store=num;
        localStorage.setItem("store",this.store)
      }

      if (mun == "allClass") {
        //全部课程逻辑
        this.allClass = true;
        this.Appoint = false;
        this.fication = false;
        this.edit = false;
        this.activeIdex = 1;
      }
      if (mun == "Appoint") {
        //制定分类逻辑
        this.allClass = false;
        this.Appoint = true;
        this.fication = false;
        this.data1.length==0?this.treeModale("main"):this.isShowDelModal = true;
        // this.treeModale("main");
        // this.edit = false;
        this.activeIdex = 2;
      }
      if (mun == "fication") {
        //指定课程逻辑
        this.allClass = false;
        this.Appoint = false;
        this.fication = true;
        this.classModale(null, "mian");
        // this.edit = false;
        this.activeIdex = 3;
      }
    },
    handleChange(items) {
      //生成时间戳
      this.starTime = items[0].split("-");
      this.endTime = items[1].split("-");
      this.starTime = new Date(this.starTime).getTime() / 1000;
      this.endTime = new Date(this.endTime).getTime() / 1000;
    },
    handleChangePlus(items) {
      //赠送生成时间戳
      this.starTimePlus = items[0].split("-");
      this.endTimePlus = items[1].split("-");
      this.starTimePlus = new Date(this.starTimePlus).getTime() / 1000;
      this.endTimePlus = new Date(this.endTimePlus).getTime() / 1000;
    },
    pagechange(currentPage) {
      //分页逻辑
      this.current = currentPage;
      this.loading = true;
      this.classModale(this.current, "mian");
    },
    pagechangePlus(currentPage) {
      //赠送分页逻辑
      this.currentl = currentPage;
      this.loading = true;
      this.classModale(this.currentl, "plus");
    },
    treeModale(Choice) {
      //渲染树形 数据
      Choice === "main"
        ? (this.isShowDelModal = true)
        : (this.isShowDelModall = true);
      Server.post(Path.meaTree, {}, res => {
        let { code, data } = res;
        this.data1 = [];
        this.data2 = [];
        for (let i = 0; i < res.data.length; i++) {
          let data = {};
          data.title = res.data[i].title;
          data.id = res.data[i].id;
          data.expand = false;
          data.checked = false;
          data.fatherid = res.data[i].fatherid;
          data.children = res.data[i].children;
          Choice === "main" ? this.data1.push(data) : this.data2.push(data);
        }
      });
    },
    classModale(page = 1, occ) {//指定课程数据 数据
      occ === "mian" ? (this.isShowDel = true) : (this.isShowDell = true);
      if (occ != "mian") {//获取不同的类型的指定课程
        this.give == "broadcasting"? (this.classtypess = "2"): (this.classtypess = "1");
      } else {
        this.classtypess = "";
      }
      Server.post(Path.mealClass,{
          coursename: "",
          classtypes: this.classtypess,
          pagenum: page
        },res => {
          let { code, data } = res;
          this.dataM = [];
          this.dataw = [];
          if (code == 0) {
            if (occ === "mian") {
                this.total = res.data.pageinfo.total;
                this.display = Number(res.data.pageinfo.pagesize);
              } else {
                this.totall = res.data.pageinfo.total;
                this.displayl = Number(res.data.pageinfo.pagesize);
              }
            for (let i in res.data.curriculumlist) {
              let data = {};
              data.id = res.data.curriculumlist[i].id;
              data.coursename = res.data.curriculumlist[i].coursename;
              data.imageurl = res.data.curriculumlist[i].imageurl;
              data.focus = false;
              //将获取回来的数据根据不同的情况分类赋值给买和送的
              occ === "mian" ? this.dataM.push(data) : this.dataw.push(data);
            }
            if (occ === "mian") {//数据的缓存展示
              for (var i = 0; i < this.dataM.length; i++) {
                //判断 缓存里面是否有相同的id
                for (var j = 0; j < this.strage.length; j++) {
                  if (this.dataM[i].id == this.strage[j]) {
                    this.dataM[i].focus = true;
                  }
                }
              }
            } else {
              for (var i = 0; i < this.dataw.length; i++) {
                //判断 缓存里面是否有相同的id 赠送
                for (var j = 0; j < this.stragePlus.length; j++) {
                  if (this.dataw[i].id == this.stragePlus[j]) {
                    this.dataw[i].focus = true;
                  }
                }
              }
            }
          } else {
            this.$Message.error(res.info);
          }
        }
      );
    },
    active1(mun,num) {
       if(localStorage.getItem("storePlus")!=num){
        this.storePlus=num;
        localStorage.setItem("storePlus",this.storePlus)
      }
      if (mun == "allClass1") {
        //全部课程逻辑赠送
        this.allClass1 = true;
        this.Appoint1 = false;
        this.fication1 = false;
        this.edit1 = false;
        this.activePlus = 1;
      }
      if (mun == "Appoint1") {
        //制定分类逻辑赠送
        this.allClass1 = false;
        this.Appoint1 = true;
        this.fication1 = false;
        this.data2.length==0?this.treeModale("plus"):this.isShowDelModall = true;
        // this.edit1 = false;
        this.activePlus = 2;
      }
      if (mun == "fication1") {
        //指定课程逻辑赠送
        this.allClass1 = false;
        this.Appoint1 = false;
        this.fication1 = true;
        this.classModale(null, "plus");
        // this.edit1 = false;
        this.activePlus = 3;
      }
    },
    backRou() {
      this.$router.go(-1);
    },
    getLogoImg(datas) {
      //图片上传后的url
      this.formItem.images = datas;
    },
    //获取当前的日期
    getNewDate(){
      let newDate=new Date()
      this.valueTime[0]=newDate.getFullYear()+'-'+(newDate.getMonth()*1+1)+'-'+newDate.getDate()
      this.valueTime[1]=(newDate.getFullYear()*1+1)+'-'+(newDate.getMonth()*1+1)+'-'+newDate.getDate()
      this.timeValue[0]=newDate.getFullYear()+'-'+(newDate.getMonth()*1+1)+'-'+newDate.getDate()
      this.timeValue[1]=(newDate.getFullYear()*1+1)+'-'+(newDate.getMonth()*1+1)+'-'+newDate.getDate()
    }
  },
  watch: {
    radipurchaseo(val){
      val=='male'?this.maleBol=false:this.maleBol=true
    },
    threshold(val){
      val=='male'?this.maleBol1=false:this.maleBol1=true
    },
    term(val){
      val=='female'?this.maleBol2=false:this.maleBol2=true
    },
    give(val){
      if (val=='broadcast') {
        this.maleBol5=false;
        this.maleBol4=true
      } else {
        this.maleBol4=false;
        this.maleBol5=true
      }
    },
    thresholdPlus(val){
      val=='male'?this.maleBol6=false:this.maleBol6=true
    },
    termPlus(val){
      val=='female'?this.maleBol7=false:this.maleBol7=true
    },
    //非赠送部分的指定分类、课程
    store(news){
      this.edit=false;
    },
    activeIdex(news){
        if (news==1) {
          this.data1=[];
          this.treeData=[];
          this.categoryids="";
          this.strage="";
          localStorage.removeItem('lv');;
          this.curriculumids="";
        } else if (news==2){
           this.strage="";
          localStorage.removeItem('lv');;
           this.curriculumids="";
        }else{
          this.data1=[];
          this.treeData=[];
          this.categoryids="";
        }
    },
    //赠送部分的指定分类、课程
     storePlus(news){
      this.edit1=false;
    },
     activePlus(news){
        if (news==1) {
          this.data2=[];
          this.treeDataPlus=[];
          this.categoryidsPlus="";
          this.stragePlus="";
          localStorage.removeItem('lvv');
          this.curriculumidsPlus="";
        } else if (news==2){
           this.stragePlus="";
           localStorage.removeItem('lvv');;
            this.curriculumidsPlus="";
        }else{
          this.data2=[];
          this.treeDataPlus=[];
          this.categoryidsPlus="";
        }
    }
  }
};
</script>
<style scoped>
/* @import "~assets/common.css"  */
.mealTime{
  width: 10%;
  margin: 0 5px;
}
.cardP0 .ivu-card-body {
  padding: 0;
}
.term {
  position: relative;
}
.checkboxGroup {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 15px;
  /* margin-right: 5px; */
  /* margin-bottom: 20px; */
  /* justify-content: space-between; */
}
.claasLi {
  width: 19%;
  margin-bottom: 20px;
  height: 130px;
  border: 1px solid #cfdce7;
  border-radius: 4px;
  position: relative;
  margin-left: 5px;
}
.claasLi img {
  border: none;
  margin-left: 7%;
  margin-top: 4%;
  border-radius: 4px;
  width: 86%;
  height: 76%;
}
.classBox {
 position: absolute;
  left: 7%;
  top: 80%;
  width: 134px;
  height: 30px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.classSpan {
  position: absolute;
  left: 7%;
  top: 80%;
  font-size: 12px;
}
.terminpt {
  width: 17%;
  position: absolute;
  top: 55px;
  left: 80px;
}
.classBtn {
  color: #515a6e;
  background-color: #fff;
  border-color: #CCCCCC;
  margin-right: 20px;
}
.btnActive {
  /* background: linear-gradient(left, #387bff, #22b2ee); */
  background: #3F9CE8;
  color: #ffffff;
}
/* .mgSelect {
  width: 90%;
  border: 1px solid #cfdce7;
  border-radius: 4px;
} */
.btn {
  width: 120px;
  line-height: 40px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px 0;
  border: none;
}
.btn:hover {
  color: none;
}
.pfication {
  width: 50%;
  height: 100%;
}
.classification {
  margin-left: 5px;
  line-height: 40px;
}

.edit {
  color: #387bff;
  cursor: pointer;
}
/* .ivu-modal-body {
  height: 500px;
  overflow: auto;
} */
.logo-box {
  height: 68px;
  display: block;
  /*background:rgba(238,246,245,1) url('./images/icon_upload.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size:40px; */
  /* border: 1px solid #cfdce7; */
  border-radius: 4px;
  cursor: pointer;
  width: 206px;
}
.logo-box {
  height: 100%;
}
.logo {
  /* width: 204px; */
  height: 68px;
  display: block;
  margin: 0 auto;
}
/* .borR {
  border: none;
  border: 1px solid #cfdce7;
} */

.classTime {
  color: #ed3f14;
  z-index: 888;
  position: absolute;
}
</style>



