<template>
  <div>
    <!--$t('classifyManagepage.classifyManagepage')-->
    <panel-title
      :crumbs="[
        {
          name:'奖励设置',
          route:{
            name:'RewardSet',
            query:{from:'TeachingAffairs'}
          }
        },
        {
          name:titleName, //编辑
          route:'',
        },
      ]"
    />
    <!-- 添加商品模块儿 -->
    <Card dis-hover class="h-new-search"  v-if="titleName=='添加商品'">
      <Form ref="formInline"   :model="formInline" :rules="ruleValidate" :label-width="180" class='form-confrol cardp1'>

        <!--奖品名称-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="奖品名称：" prop="rewardName">
              <Input v-model="formInline.rewardName" placeholder="请输入奖品名"  ></Input>
            </FormItem>
          </Col>
        </Row>
        <!--赠送条件-->
        <Row>
          <Col span="12" offset="2">
            <!--连续签到天数-->
            <FormItem label="赠送条件：" prop="dayTime">
              <span class="text">连续签到&nbsp;&nbsp;>=&nbsp;&nbsp;<Input v-model="formInline.dayTime"   style="width:80px;" ></Input>&nbsp;&nbsp;天</span>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12" offset="2">
            <!--总签到天数-->
            <FormItem  prop="allDayCurrent">
              <span class="text">总签到数&nbsp;&nbsp;>=&nbsp;&nbsp;<Input v-model="formInline.allDayCurrent"   style="width:80px;" ></Input>&nbsp;&nbsp;天</span>
              <div class='headimg-des'>（可不填写，不填写代表不做限制）</div>
            </FormItem>
          </Col>
        </Row>
        <!--优惠券类型-->
        <Row>
          <Col span="12" offset="2">
            <FormItem  label="优惠券类型：" prop="radioInfo">
              <RadioGroup v-model="formInline.radioInfo">
                <Radio label="1">折扣券</Radio>&nbsp;&nbsp;
                <Radio label="2" >现金券</Radio>&nbsp;&nbsp;
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!--优惠名称-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="优惠券名称：" prop="discountName">
              <Input v-model="formInline.discountName" placeholder="请输入优惠名称"  ></Input>
            </FormItem>
          </Col>
        </Row>
        <!--面额数量 v-if=''-->
         <!-- <Input v-else v-model="formInline.money" placeholder="请输入折扣数"></Input> -->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="面额：" prop="money">
              <Input v-model="formInline.money" :placeholder="formInline.radioInfo==2?'请输入金额数':'请输入折扣数'" >
                <span slot="append">{{formInline.radioInfo==2?'元':'折'}}</span>
              </Input>
              <div class='headimg-des'>（面值只能是数值，0.01-10000，限2位小数）</div>
            </FormItem>
          </Col>
        </Row>
        <!--使用门槛-->
        <Row>
          <Col span="12" offset="2">
            <FormItem  label="使用门槛："  prop="thresholdMoney">
              <RadioGroup v-model="formInline.threshold">
                <!-- 0为无限制，1为满减 -->
                <Radio label="0" >无限制</Radio>&nbsp;&nbsp;
                <Radio label="1">满</Radio>&nbsp;&nbsp;
              </RadioGroup>
              <Input  v-model="formInline.thresholdMoney"  :disabled="formInline.threshold=='0'"  style="width:80px;" ></Input>
              &nbsp;&nbsp;<span class="text">元可用</span>
            </FormItem>
          </Col>
        </Row>

        <!--有效期-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="有效期："   prop="dateNumberTime">
              <RadioGroup v-model="formInline.radioGroup">
                <Radio label="1">日期范围</Radio>{{formInline.dateRangeTime}}
                &nbsp;<DatePicker :disabled="formInline.radioGroup=='2'"  type="date" placeholder="请选择日期范围"  @on-change="getTime"></DatePicker>
                <br>
                <br>
                <Radio label="2">固定天数</Radio>
                &nbsp;<Input :disabled="formInline.radioGroup=='1'"   v-model="formInline.dateNumberTime" placeholder="" style="width:80px;" ></Input>
                <span class="text">
                  &nbsp;&nbsp;天
              </span>
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!--可用课程-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="可用课程：" prop="courseType">
              <RadioGroup v-model="formInline.courseType">
                <Radio label="全部课程" ></Radio>&nbsp;&nbsp;
                <Radio label="指定分类"></Radio>&nbsp;&nbsp;
                <Radio label="指定课程"></Radio>&nbsp;&nbsp;
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <!--选择的分类-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="选择的分类：" >
              <span class="text">
                语文、数学、英语    <span style="color:green;" @click="openModal" class="cursorChange">&nbsp;&nbsp;&nbsp;编辑</span>
              </span>
            </FormItem>
          </Col>
        </Row>
        <!--使用说明-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="使用说明：" prop="textareaInfo">
              <Input  v-model="formInline.textareaInfo" style="width:300px;" type="textarea" :rows="9" placeholder="请输入知识内容"></Input>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="20" offset="2" class="mg-bottom-20" style="margin-top:20px ;">
            <FormItem label="">
              <div class="custom-btn-warning savebtn" @click="formSubmit('formInline')">
                提交
              </div>
              <!-- <div class="btn savebtn" @click="SetBascInfo">保存</div> -->
            </FormItem>
          </Col>
        </Row>
      </Form>
      <!--可选择的课程模块框-->
      <Modal
        v-model="modalStatus"
        title="选择课程分类" >
        <div class="type-content">
           <!-- v-model="periodId" filterable -->
          <Select placeholder="请选择课时" style="width:300px;border: 1px solid #dddee1;border-radius: 4px;">
             <!-- v-for="item in classifyTow" :value="item.id" :key="item.id" -->
              <Option value="1">分类</Option>
          </Select>
          <Tree :data="data1" :getSelectedNodes="getSelectedNodes"  show-checkbox></Tree>
        </div>
        <div slot="footer">
          <!--取消-->
          <Button type="text" shape="circle" @click="modalStatus = false;">{{$t('Common.cancel')}}</Button>
          <!--确认-->
          <Button type="warning" shape="circle" >{{$t('Common.sure')}}</Button>
        </div>
      </Modal>
    </Card>

    <!-- 商品详情模块 -->
    <Card dis-hover class="h-new-search"  v-if="titleName=='奖品详情'">
      <Form :label-width="180" class='form-confrol'>
        <!-- 奖品名称： -->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="奖品名称：">
              <span class='text'>{{rewardInfo.name}}</span>
            </FormItem>
          </Col>
        </Row>
        <!--赠送条件-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="赠送条件：">
              <span class='text'>连续签到&nbsp;&nbsp;>=&nbsp;&nbsp;{{rewardInfo.condition1}}&nbsp;&nbsp;天</span>
            </FormItem>
          </Col>
        </Row>
        <Row>
          <Col span="12" offset="2">
            <FormItem >
              <span class='text'>总签到&nbsp;&nbsp;>=&nbsp;&nbsp;{{rewardInfo.condition2}}&nbsp;&nbsp;天</span>
            </FormItem>
          </Col>
        </Row>
        <!--优惠类型-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="优惠类型：">
              <span class='text'>{{rewardInfo.type=='1'?'折扣券':'现金券'}}</span>
            </FormItem>
          </Col>
        </Row>
        <!--优惠券名称-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="优惠券名称：">
              <span class='text'>{{rewardInfo.name}}</span>
            </FormItem>
          </Col>
        </Row>
        <!--面额-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="面额：">
              <span class='text'>{{rewardInfo.value}}&nbsp;&nbsp;元</span>
            </FormItem>
          </Col>
        </Row>
        <!--使用门槛-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="使用门槛：">
              <span class='text'>{{rewardInfo.mixamount}}</span>
            </FormItem>
          </Col>
        </Row>
        <!--有效期-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="有效期：">
              <span class='text'>截至日期 &nbsp;&nbsp;&nbsp;{{rewardInfo.expirevalue}}</span>
            </FormItem>
          </Col>
        </Row>
        <!--可使用课程分类-->
        <Row>
          <Col span="12" offset="2">
            <FormItem label="可使用课程分类：">
              <span class='text'>{{rewardInfo.forcourse}}</span>
            </FormItem>
          </Col>
        </Row>
      </Form>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card,  Table, Button,Modal,Form,Row,Col,Input,FormItem,RadioGroup,Radio,Icon,DatePicker,Tree,Select,Option} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {panelTitle,pagination,Card,'i-table':Table, Button,Modal,Form,Row,Col,Input,FormItem,RadioGroup,Radio,Icon,DatePicker,Tree,Select,Option},
   data(){
     const DayTimeRegex = (rule, value, callback) => {
       if(this.formInline.threshold!='1'){
         return;
       }
       let DayTimeRegex = /^[1-9]\d*$/;
       if (value === '') {
         callback(new Error('内容不能为空'));
       } else if (!DayTimeRegex.test(value)) {
         callback(new Error('请输入大于0的整数'))
       } else {
         callback();
       }
     };
     const DateNumberTimeRegex = (rule, value, callback) => {
       if(this.formInline.radioGroup!='2'){
         return;
       }
       let DayTimeRegex = /^[1-9]\d*$/;
       if (value === '') {
         callback(new Error('天数不能为空'));
       } else if (!DayTimeRegex.test(value)) {
         callback(new Error('请输入大于0的整数'))
       } else {
         callback();
       }
     };
     const EmptyRegex = (rule, value, callback) => {
       if (value === '') {
         callback(new Error('内容不能为空'));
       } else {
         callback();
       }
     };
     const AllDayCurrentRegex = (rule, value, callback) => {
       let AllDayCurrent = /^[1-9]\d*$/;
       if (value === '') {
         callback()
       } else if (!AllDayCurrent.test(value)) {
         callback(new Error('请输入大于0的整数'))
       }
     };
     const MoneyRegex = (rule, value, callback) => {
       let  MoneyRegex = /(^[1-9]\d{0,3}$)|(^[1][0]{4}$)|(^0\.\d{0,2}$)|(^[1-9]\d{0,3}\.\d{0,2}$)/;
       if (value === '') {
         callback(new Error('金额数不能为空'));
       } else if (!MoneyRegex.test(value)) {
         callback(new Error('请输入正确范围的数值'))
       }
     };
    return {
      value1: [],
      data1: [
        {
          title: 'parent 1',
          expand: true,
          children: [
            {
              title: 'parent 1-1',
              expand: true,
              children: [
                {
                  title: 'leaf 1-1-1'
                },
                {
                  title: 'leaf 1-1-2'
                }
              ]
            },
            {
              title: 'parent 1-2',
              expand: true,
              children: [
                {
                  title: 'leaf 1-2-1'
                },
                {
                  title: 'leaf 1-2-1'
                }
              ]
            }
          ]
        }],
      rewardInfo:{},
      titleName:'添加奖品',
      modalStatus:false,
      formInline:{
        rewardName:'', //奖品名称
        dayTime:'',     //连续签到天数
        allDayCurrent:"",   //总签到天数
        radioInfo:"1",  //优惠券的类型1为折扣券，2为现金券；
        discountName:'',    //优惠的名称
        money:'',       //优惠多少钱
        threshold:'1',    //使用门槛按钮 0为无限制，1为满减
        thresholdMoney:'',  //限制满多少元
        dateRange:false,  //选择日期单选按钮
        dateNumber:'固定天数',  //具体几天按钮
        dateRangeTime:'', //选择的具体时间
        dateNumberTime:'', //具体选择几天
        textareaInfo:'',   //使用说明
        courseType:'全部课程',  //课程分类
        radioGroup:'2'//1日期范围, 2固定天数
      },
      ruleValidate:{
        rewardName:[  { required: true, validator:EmptyRegex} ],
        dayTime:[  { required: true, validator:DayTimeRegex }],
        allDayCurrent:[  { validator:AllDayCurrentRegex} ],
        discountName:[  { required: true,validator:EmptyRegex } ],
        money:[  { required: true, validator:MoneyRegex }, ],
        thresholdMoney:[  { required: true, validator:DayTimeRegex } ],
        dateRangeTime:[  { required: true, validator:EmptyRegex }],
        dateNumberTime:[  { required: true, validator:DateNumberTimeRegex} ],
        textareaInfo:[  { required: true, validator:EmptyRegex } ],
      }
    }
  },
  methods: {
    getTime(timer) {//获取时间
      this.formInline.dateRangeTime = timer;
    },
    getSelectedNodes(value){  //树形数据获取
      console.log(value);
    },
    openModal(){    //打开添加课时模态框
      this.modalStatus = true;
    },
    formSubmit(name){  //表单提交
        Server.post(Path.addReward,{
            name:this.formInline.rewardName,
						type:this.formInline.radioInfo,
						condition1:this.formInline.dayTime,
						condition2:this.formInline.allDayCurrent,
						value:this.formInline.money,
						mixamount:this.formInline.threshold==0?'0':this.formInline.thresholdMoney,
						expiretype:this.formInline.radioGroup,
						expirevalue:this.formInline.dateRangeTime||this.formInline.dateNumberTime,
						forcourse:'语文',
						note:this.formInline.textareaInfo
        },
        res => {
          if (res.code === 0) {
            this.$Message.info(res.info);
          }
        }
      );
    },
    //获取奖品详情
      getRewardinfo(){
        Server.post(Path.getRewardinfo,{
          id:this.$route.params.id
        },
        res => {
          if (res.code === 0) {
            this.rewardInfo=res.data;
          }
        }
      );
      this.deleteItemModal = false;
    },
  },
  created() {
    let fromPage = this.$route.params.type;
    if(fromPage=='add'){
      this.titleName = '添加商品';
    }else{
      this.titleName = '奖品详情';
       this.getRewardinfo()
      let nowInfo = JSON.parse(this.$Local.getItem('rewardInfo'));
      this.rewardInfo = nowInfo;
    }
  },
 mounted(){},
 computed:{},
 watch:{}
 
};
</script>
<style scoped>
   /* @import "~@/assets/common.css"; */
 .cursorChange:hover
 {
   cursor:Pointer
 }

.cardp1 .ivu-form-item-validating .ivu-input-icon-validate {
  display: none;
}
/* .cardp1 .ivu-form-item-error .ivu-input-group-prepend, .ivu-form-item-error .ivu-input-group-append{
  border-color:#CFDCE7;
} */
.cardp1 .ivu-icon .ivu-icon-load-c .ivu-load-loop .ivu-input-icon .ivu-input-icon-validate{
  display: none!important;
}
.cardp1 .ivu-checkbox-wrapper + span, .ivu-checkbox + span{
	display: none;
}
</style>



