<template>
  <div id="Course">
    <panel-title :crumbs="[
      {
        name:'课程推荐',
        route:''
      },
    ]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
	        <span>推荐分类：</span>
	        <Input
          class="input-normal-width input-max-height"
	        v-model="InputText"
	        @keydown.native.enter="getCourseData(current)"
	        placeholder="推荐分类" />

          <Button type="primary" class="mytheme-mg-left-70" @click="getCourseData(current)">搜索</Button>

	        <Button type="primary" @click="showAddCate()" style="float: right;">添加推荐</Button>
      </div>
		  <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns" :data="data" ></i-table>
     
      <!-- <pagination :total="total" class="mg-top-20" :current-page='current' @pagechange="pagechange" :display='display' v-if='total>0'></pagination> -->
   </Card>
   
    <!-- 添加分类模块 -->
    <Modal v-model="isShowAddModal" :closable="false">
      <div slot="header">输入分类名称</div>
      <Form class="mg-top-20" :label-width="120" ref="classify" :model="classify" :rules="ruleValidate">
          <Row>
            <Col>
               <FormItem prop="categoryname" label="分类名称 ：">
                <!--分类名称-->
                  <Input style="width:245px;" :maxlength="4" placeholder="最多可输入4个汉字" v-model.trim="classify.categoryname"/>
                 </FormItem>
            </Col>
          </Row>
        </Form>
      <div slot="footer">
        <Button type="primary" @click="categoryAdd('classify')">确认</Button>
        <Button @click="isShowAddModal = false">取消</Button>
      </div>
    </Modal>

    <!-- 查看模块 -->
    <Modal v-model="lookModal" width="700" :closable="false">
      <div slot="header">课程推荐</div>
      <div>
         <i-table class="mytheme-table noborder-table" stripe :columns="columnSee" :data="dataSee"></i-table>
      </div>
      <div slot="footer">
        <Button @click="lookModal = false" type="primary">确认</Button>
        <Button @click="lookModal = false">取消</Button>
      </div>
    </Modal>


    <!-- 选择模块 -->
   <Modal v-model="isShaowdown" class="crlvvv" width="700" :closable="false">
	      <div slot="header">课程素材</div>
        <Card dis-hover class=" mytheme-noborder">
          <div style="margin-bottom:20px;">
            <span style="margin-bottom:20px;color: #7a8599;">课程名称：</span>
            <Input
            class="input-normal-width input-max-height"
            v-model="InputText2"
            placeholder="课程名称"
            @on-enter="getCourseData2(InputText2,current2)"/>

            <Button type="primary" class="mytheme-mg-left-70" @click="getCourseData2(InputText2,current2)">搜索</Button>

          </div>
          <i-table ref="selection" class="mytheme-table noborder-table" stripe :columns="columns2" :data="data2" @on-selection-change="selectSuccess"></i-table>
            <pagination :total="total2" class="mg-top-20" :current-page='current2' @pagechange="pagechange2" :display='display2' v-if='total2>0'></pagination>
         </Card>
        <div slot="footer" style="width: 100%;">
            <Button type="primary" @click="addSelect">{{$t('Common.sure')}}</Button>
            <Button @click="isShaowdown = false">取消</Button>
			  </div>
		</Modal>

    <!-- 删除模块 -->
    <Modal v-model="isShaowdown2" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除该推荐分类？</div>
      <div slot="footer">
        <Button @click="delList()" type="primary">确认</Button>
        <Button @click="isShaowdown2 = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, ButtonGroup, Button, Modal,Form,Row,Col,FormItem} from "iview";
import Server from '@/service/request';
import Path from '@/service/Path';
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    ButtonGroup,
    Button,
    Modal,Form,Row,Col,FormItem
  },
  created() {
    this.getCourseData(this.current);
  },
  methods: {
    pagechange(currentPage) {//页面分页逻辑
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      this.loading = true;
      this.getCourseData(currentPage);
    },
    getCourseData(page){//获取分类列表
      Server.post(Path.curriculumCategoryList,{
        categoryname: this.InputText,
        pagenum: page,
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data = res.data.categorylist;
          this.total = res.data.pageinfo.total;
          this.display = res.data.pageinfo.pagesize;
        }else{
          this.$Message.error(res.info);
          this.data = [];
        }
      })
    },
    showAddCate(){//显示添加分类框
      if(this.data.length>=6){
          this.$Message.error('分类不能多余6个哦');
          this.isShowAddModal = false;
      }else{
        this.isShowAddModal = true;
        this.classify.categoryname = '';
      }
    },
    categoryAdd(name){//添加推荐分类名称
      // if(this.classify.categoryname==''){
      //   this.$Message.error('请输入分类名称');
      //     return false;
      // }
       this.$refs[name].validate((valid) => {
          if (valid) {
            Server.post(Path.categoryAdd,{
              categoryname:this.classify.categoryname
            },(res)=>{
              let { code, data } = res;
              if(code===0){
              this.getCourseData(this.current);
              }else{
                this.$Message.error(res.data);
              }
            })
            this.isShowAddModal = false;
          }
       })
    },
    moveItem(id1,id2){//分类上移、下移
      Server.post(Path.CategoryRecoSort,{
        categoryid1: id1,
        categoryid2: id2
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
          this.getCourseData(this.current);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    CategoryRecoSee(){//查看课程
      Server.post(Path.CategoryRecoSee,{
        categoryid:this.addClassId
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.dataSee=data;
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    delList(){//删除分类
        Server.post(Path.categorydel,{
          categoryid: this.idd
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.isShaowdown2 = false
            this.$Message.success(res.info);
            this.getCourseData(this.current);
          }else{
            this.$Message.error(res.info);
          }
        })
      },
    pagechange2(currentPage) {//课程分页
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      this.getCourseData2(this.InputText2,currentPage);
    },
     getCourseData2(name,page=1){//课程列表
    	this.isShaowdown = true
      Server.post(Path.curriculumRecoList,{
					coursename:name,
					pagenum:page,
          recommend:0//默认0为所有未推荐的课程
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.data2 = res.data.curriculumlist;
          this.total2 = res.data.pageinfo.total;
          this.display2 = res.data.pageinfo.pagesize;
        }else{
          this.$Message.error(res.info);
          this.data2 = [];
      }
      })
    },
    selectSuccess(selection) {//弹框复选框 选中数据  处理
      let arr = [];
      for( let i = 0;  i < selection.length; i++) {
        arr.push(selection[i].id);
      }
      this.spilt=arr.join();
    },
    addSelect () { //向指定分类中添加推荐课程
    	Server.post(Path.courseAdd,{
        categoryid:this.addClassId,
        courseids:this.spilt
     },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.getCourseData(this.current);
          this.$Message.success(res.data);
        }else {
        	this.$Message.error('最多选择四门课程')
        }
      })
      this.isShaowdown = false;
    },
    moveItem2(id,id1){//课程上移、下移
      Server.post(Path.curriculumRecoSort,{
        curriculumid1: id,
        curriculumid2: id1
      },(res)=>{
        let { code, data } = res;
        if(code===0){
          this.$Message.success(res.info);
          this.CategoryRecoSee()
        }else{
          this.$Message.error(res.info);
        }
      })
    }
  },
  watch:{
    isShowAddModal(val){
      if(val==false){
        this.$refs.classify.resetFields()
      }
    }
  },
  data(){
    return{
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      total2: 0, // 记录总条数
      InputText: "",
      display2: 20, // 每页显示条数
      current2: 1, // 当前的页数
      InputText2: "",
      addClassId:'',//添加课程时的指定分类ID
      spilt:"",
      idd:'',
      isShaowdown:false,
      isShaowdown2:false,
      lookModal:false,
      isShowAddModal:false,
      classify:{
        categoryname:''//添加分类名称
      },
        ruleValidate:{categoryname:[{required: true, message: '请填写分类名称', trigger: 'blur' }]},
      columns: [
        {
          title: "序号",
          align: "center",
          key: "id"
        },
        {
          title: "推荐分类",
          align: "center",
          key: "categoryname"
        },
        {
          title: "排序",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "a",
                {
                  on: {
                    'click': ()=>{
                      if(params.index == 0){
                        this.$Message.warning('已移至第一个');
                      }else{
                        this.moveItem(params.row.id,this.data[params.index-1].id);
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    // color: "#2693ff",
                    display: "inline-block",
                    marginRight: "35px"
                  },
                  attrs: {
                    class: "tablebtn yellow"
                  }
                },
                "上移"
              ),
              h(
                "a",
                {
                  on: {
                    "click": value => {
                      if(params.index == this.data.length - 1){
                        this.$Message.warning('已移至最后一个');
                      }else{
                        this.moveItem(params.row.id,this.data[params.index+1].id);
                        
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    // color: "#2693ff",
                    display: "inline-block",
                  },
                  // attrs: {
                  //   class: "tablebtn blue"
                  // }
                },
                "下移"
              )
            ]);
          }
        },
        {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',[
               h('a', {
                  style: {
                    color: '#FFA80D',
                    marginRight: '35px'
                  },
                  on: {
                    'click': () => {
                      this.lookModal = true;
                      this.addClassId=params.row.id
                      this.CategoryRecoSee()
                    }
                  }
                }, '查看'),
                h('a', {
                  style: {
                    color: '#3F9CE8',
                    marginRight: '35px'
                  },
                  on: {
                    'click': () => {
                      this.addClassId=params.row.id;
                      this.getCourseData2(this.InputText2,this.current2)
                    }
                  }
                }, '选择'),
               h('a',{
                on: {
                	click: () => {										
                    this.idd = params.row.id;
                    this.isShaowdown2=true;
                	}
                }
                ,style: {
                  color: '#ED3825'
                }
              },'删除')
            ])
          }
        }
      ],
      data: [],   
      columnSee: [
        {
          title: "编号",
          align: "center",
          key: "id"
        },
        {
          title: " 课程名称",
          align: "center",
          key: "coursename"
        },
        {
          title: "排序",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h("div", [
              h(
                "a",
                {
                  on: {
                    'click': ()=>{
                      if(params.index == 0){
                        this.$Message.warning('已移至第一个');
                      }else{
                        this.moveItem2(params.row.id,this.dataSee[params.index-1].id);
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn yellow"
                  }
                },
                "上移"
              ),
              h(
                "a",
                {
                  on: {
                    "click": value => {
                      if(params.index == this.dataSee.length - 1){
                        this.$Message.warning('已移至最后一个');
                      }else{
                        this.moveItem2(params.row.id,this.dataSee[params.index+1].id);
                      }
                    }
                  },
                  style: {
                    cursor: "pointer",
                    color: "#2693ff",
                    display: "inline-block",
                    marginRight: "10px"
                  },
                  attrs: {
                    class: "tablebtn blue"
                  }
                },
                "下移"
              )
            ]);
          }
        },
         {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',{
                on: {
                	click: () => {										
                  this.idd = params.row.id;
                  Server.post(Path.courseDel,{
                      categoryid:this.addClassId,
                      courseids:params.row.id
                    },(res)=>{
                      let { code, data } = res;
                      if(code===0){
                        this.$Message.success(res.info);
                        this.CategoryRecoSee()
                      }else{
                        this.$Message.error(res.info);
                      }
                    })
                	}
                }
                ,style: {
                  color: '#ED3825',
                  cursor: "pointer"
                }
              },'删除')
          }
        }
      ],  
       dataSee: [],  
       columns2: [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: " 课程名称",
            align: "center",
            key: "coursename"
          }
      ],    
      data2: []
    }
  }
};
</script>
<style scoped>
/* .footer{
	height: 60px;
	margin-top: 20px;
	position: relative;
} */
</style>



