<template>
  <div id="ReaOver">
    <!--老师批阅列表-->
    <panel-title :crumbs="[{name:'批阅管理', route:''},]"/>
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
         <!--按老师姓名查询-->
        <span>老师名称{{$t('symbol.colon')}}</span>
        <Input
           placeholder="请输入老师名称"
          class="input-normal-width input-max-height"
          v-model.trim="searchNickName"
          @on-enter="getReviewList(current)"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getReviewList(current)">搜索</Button>
      </div>

      <!-- 老师列表 -->
      <Table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :columns="colData" :data="tabData" no-data-text="暂无数据"></Table>
   
      <!-- 分页 -->
      <pagination :total="total" :currentPage="current" :display="display" @pagechange="pageChange" class="mg-top-20" v-if='total>0'></pagination>
    </Card>
  </div>
</template>

<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Button
} from "iview";

export default {
  name: "ReaOver",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    Table,
    Button
  },
  data() {
    return {
      searchNickName: "", //昵称查询关键字
      colData: [
        {
          title:"编号",
          key: "teacherid",
          align: "center"
        }, //编号
        {
          title: "老师名称",
          key: "teachername",
          align: "center"
        }, //老师名称
        {
          title: "批阅数",
          key: "reviewcount",
          align: "center"
        }, //批阅数
        {
          title:"操作", //操作
          render: (h, params) => {
            return h("div", {
                  style: {
                    color: "#3F9CE8",
                    cursor:'pointer'
                  },
                  on: {
                    click: () => {
                      this.$router.push({
                        path: '/EssayList',
                        query: {
                          from:"EssayManagement",
                          teacherid:params.row.teacherid
                          }
                      });
                    }
                  }
            },"查看");
          },
          align: "center"
        }
      ],
      tabData: [],
      current: 1,
      total: 0,
      display: 20,

    };
  },
  created() {
    this.getReviewList(this.current);
  },
  watch: {
  },
  computed: {},
  methods: {
    // 搜索老师列表
    getReviewList(current = 1) {
      Server.post(
        Path.getReviewList,
        {
          teachername: this.searchNickName,
          pagenum: current
        },
        res => {
          let {code,data}=res;
          if (res.code === 0) {
            this.tabData = data.data;
            this.total = data.pageinfo.total;
            this.display = data.pageinfo.pagesize;
            this.current = Number(data.pageinfo.pagenum);
          }else{
            this.tabData = [];
          }
        }
      );
    },
    //分页
    pageChange(index) {
      this.current = index;
      this.getReviewList(this.current);
    }
    
  }
};
</script>

<style scoped lang='less'>
</style>



