<template>
  <div id="EditLesson">
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
            <!--设置-->
          {{$t('Common.set')}}
        </p>
        <Form :label-width="160" class='form-confrol mg-top-20' ref="formValidate" :model="formData" :rules="ruleValidate">
          <Row>
            <!--课程封面-->
            <Col span="20" offset="2">
              <FormItem label="课程封页：" prop='' class="input-box">
                <img class="photo" :src="getData.imageurl" alt="">
              </FormItem>
            </Col>
            <!--课程名称-->
            <Col span="20" offset="2">
              <FormItem label="课程名称：" prop='' class="input-box">
                <p class="p-txt">{{getData.coursename}}</p>
              </FormItem>
            </Col>
            <!--课程分类-->
            <Col span="20" offset="2">
              <FormItem label="课程分类：" prop='' class="input-box">
                <p class="p-txt">{{getData.categorystrname}}</p>
              </FormItem>
            </Col>
            <!--上架-->
            <Col span="20" offset="2">
              <FormItem label="上架：" prop='' class="input-box">
                 <i-switch @on-change="switchStatus" :value="Boolean(formData.status)"></i-switch>
              </FormItem>
            </Col>
            <!--课程价格-->
            <Col span="20" offset="2" v-if="$route.query.classType == 1" class="input-box">
              <FormItem label="课程价格：" prop='price'>
                 <Input autocomplete="off" placeholder="请输入价格" style="width:120px;" v-model="formData.price" /> 元
              </FormItem>
            </Col>
            <!--是否赠送教材：-->
            <Col span="20" offset="2" v-if="$route.query.classType == 2" class="input-box">
              <FormItem label="是否赠送教材：" prop=''>
                <RadioGroup v-model="formData.giftstatus">
                  <Radio :label="0" class="txt">是</Radio>
                  <Radio :label="1" class="txt">否</Radio>
                </RadioGroup>
              </FormItem>
            </Col>
            <!--赠送纸质资料：-->
            <Col span="20" offset="2" v-if="$route.query.classType == 2 && formData.giftstatus == 0 ? true : false" class="input-box">
              <FormItem label="赠送纸质资料：" prop=''>
                <checkbox-group v-model="material">
                <p style=" margin-bottom: 5px;" v-for="(gift,index) in formData.giftjson" :key="gift.id">
                  <Checkbox :label="gift.id" true-value="0" false-value="1" class="txt" v-model="gift.selected">{{gift.name}}</Checkbox >
                  <Input class="input-txt" :maxlength="3" v-model="gift.num" :disabled="gift.selected == 0 ? false : true "/><span class="txt">份</span>
                  <span class="tip">(最多可输入3位数字)</span>
                </p>
                </checkbox-group>
              </FormItem>
            </Col>
            <Col span="20" offset="2">
                <div class="btnbox">
                  <!-- <div class="btn-save" @click="saveData('formValidate')">
                    {{$t('courseManagepage.success')}}
                  </div> -->
                  <Button type="primary" @click.native="saveData('formValidate')" >完 成</Button>
              </div>
            </Col>
          </Row>
        </Form>
        
      </Card>
    </template>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import NextBtn from "@/components/NextBtn/NextBtn";
import { Card, Input, RadioGroup, Radio, Icon, Switch,CheckboxGroup,Checkbox, Form, FormItem, Row, Col, Button } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import Upload from "@/components/UploadPicture";

export default {
  name: "EditLesson",
  components: {
    panelTitle,
    Card,
    Input,
    NextBtn,
    Radio,
    RadioGroup,
    Icon,
    CheckboxGroup,
    Checkbox,
    Upload,
    "i-switch": Switch,
    Form,
    FormItem,
    Row,
    Col,
    Button
  },
  data() {
    const PriceCourse = (rule, value, callback) => {//价格正则
      let PriceRegex = /^[0-9]+(.[0-9]+)?$/;
      if (value === '') {
        callback(new Error('请输入课程价格'));
      } else if (!PriceRegex.test(value)) {
        callback(new Error('请输入正确课程价格'))
      } else {
        callback();
      }
    };
    const NumCourse = (rule, value, callback) => {//价格正则
      let NumRegex = /^[1-9]\d*$/;
       if (!NumRegex.test(this.gift.num)) {
        callback(new Error('请输入正确赠送数量'))
      } else {
        callback();
      }
    };
    return {
      all: true,
      isOffer: "",
      material: [],
      formData: {
        id: this.$route.query.id,
        classtypes: ['1', '2'],
        status: 1,
        giftstatus: 1,
        giftjson:[]
      },
      ruleValidate: {
        price: [
          {required: true, validator: PriceCourse, trigger: "blur" } 
        ],
      },
      getData: {},
      uploadid: [1, 1, 1],
      giftList: [],
      gift: [],
    };
  },
  created() {
    //do something after creating vue instance
    let LessonMessage = JSON.parse(localStorage.getItem("addLessonMsesage"));
    let LessonData = JSON.parse(localStorage.getItem("LessonData"));
    let {coursename, LessonTime, UnitNumber, categorystrname, imageurl} = LessonMessage;
    this.formData = Object.assign({}, this.formData, LessonData);
   // console.log(this.formData)
    this.getData = {
      coursename,
      LessonTime,
      UnitNumber,
      categorystrname,
      imageurl
    };
    //console.log(1,JSON.stringify(this.formData));
    let {classtypes} = this.formData;
    

    //var origin = JSON.parse(window.localStorage.getItem("userinfo"));
    //this.uploadid = [2, origin.organid, 1];
    if(this.$route.query.classType == 2){
      this.getListGfit();
    }
  },
  methods: {
    //上传图片
    getHeaderUrl(url) {
      this.getData.imageurl = url;
    },
    //　获取赠品列表
    getListGfit() {
      Server.post(Path.getListGfit,{
        id: this.$route.query.id
      }, res => {
        let {code, data} = res;
        if(code == 0){
          this.formData.giftjson = data;
          this.formData.giftjson.filter((el,index) => {
            return el.selected == 0;
          }).map((e,i) => {
            this.material.push(e.id);
           // console.log(13, JSON.stringify(this.material));
          })
        }
      })
    },
  //完成接口
  saveData(name) {
    this.$refs[name].validate((valid) => {
      if (valid) {

      }
    })
    if (this.$route.query.classType == 1) {
      if (this.formData.price == '') {
        this.$Message.error(this.$t('courseManagepage.tip5'));//基础价格不能为空
        return false;
      }
    }else{
      this.formData.price = '';
    }
    if(this.$route.query.classType == 2){
      //赠品提交校验
      let giftData = [];
      giftData = this.formData.giftjson.filter((el,index) => {
        return el.selected == 0;
      }).map((e,i) =>{
        return {
          num: e.num
        }
      })
      let NumRegex = /^[1-9]\d*$/;
      var flag = giftData.some((el,i)=>{
        return el.num == ''||!NumRegex.test(el.num)
      })
      if(flag){
        this.$Message.error("请正确填写赠送数量！");
        return false;
      }
      //console.log(3333)
      //赠品提交参数
      this.gift = this.formData.giftjson.filter((el,index) =>{
        return el.selected == 0;
      }).map((e,i) =>{
        return {
          id: e.id,
          num: parseInt(e.num)
        }
      })
    }
    Server.post(
      Path.addTriCurricukum,
      {
        id: this.formData.id,
        price: this.formData.price ,
        status: this.formData.status,
        giftstatus: this.formData.giftstatus,
        gift: this.gift
      },
      res => {
        let { code, data } = res;
        if (code === 0) {
          this.$Message.success(res.info);
          setTimeout(e=>{
            this.$router.push({
              path:'/Small',
              query:{ from: 'MainLessonManagement'}
            })
          },500)
        }else{
          this.$Message.error(res.info);
        }
      }
    )
  },
  //是否上架
  switchStatus(value) {
    this.formData.status = Number(value);
  }
}
}
</script>
<style scoped>
.title {
  color: #5e6266;
  font-size: 18px;
  font-weight: normal;
}
.photo {
  overflow: hidden;
  width: 180px;
  height: 135px;
}

.orange {
  display: inline-block;
  vertical-align: middle;
}
.blue {
  color: #469af8;
  font-size: 14px;
  display: block !important;
  padding-left: 80px;
}
.input-box {
  vertical-align: middle !important;
  overflow: hidden;
  font-size: 16px;
}

/* .btnbox {
  overflow: hidden;
  padding: 20px 0;
  margin-right: 100px;
} */
.btnbox{
    margin: 20px 0 81px 155px;
}
.btn-save {
  float: right;
  width: 170px;
  height: 50px;
  color: #fff;
  box-shadow: 0 0 5px #fdb44d;
  background-color: #fdb44d;
  font-size: 18px;
  text-align: center;
  line-height: 50px;
  border-radius: 50px;
  cursor: pointer;
}
.txt{
  font-size: 16px;
  width: 70px;
  vertical-align: middle;
}
.input-txt{
  width:60px;
  padding: 0 5px;
  margin-right:5px;
}
.p-txt{
  font-size: 16px;
  padding-top: 5px;
}
.tip{
  color: #BDC2C8 ;
  font-size: 16px;
}
</style>



