<template>
  <div id="EditLessonTime">
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
            <!-- 课时信息 -->
            {{$t('courseManagepage.coursetime')}}
        </p>
        <Form class="mg-top-20">
          <Row v-if='data.inperiod'>
            <Col span="20" offset="2" v-for="data,index in data.inperiod" :key="data.id">
              <div class="class-time-box-item">
                <dl>
                  <dt class="border-green">
                    <div class="time-title-icon">
                      <div class="time-title-icon-q">

                      </div>
                    </div>
                    <template v-if='$i18n.locale = "zh-cn"'>
                      第{{index+1}}单元：
                      <div class="edit-btn-box">共{{data.list.length}}个课时</div>
                    </template>
                    <template v-else>
                      {{$t('courseManagepage.tip6')}}{{index+1}}{{$t('Common.colon')}}
                      <div class="edit-btn-box">{{data.list.length}}{{$t('courseManagepage.tip7')}}</div>
                    </template>
                    {{data.unitname}}
                  </dt>
                  <dd class="border-green courseInfo" v-for="d,i in data.list" :key="d.id">
                    <!-- 课时 -->
                    {{$t('courseManagepage.time')}}{{i+1}}：{{d.periodname}}
                    <div class="edit-btn-box">
                      <!-- <a href="#" class="green">查看课件</a> -->
                    </div>
                    
                    <!--<span class="showTime" v-if="dataDetial.type!='1'">
                      <img :src="ClockIcon">{{d.timestr}}
                    </span>-->
                  </dd>
                  
                </dl>
              </div>
              
            </Col>
          </Row>
          <Row v-else>
            <p style='padding:0 0 30px 30px;'>{{$t('Common.NO')}}</p>
          </Row>
        </Form>

      </Card>
    </template>
  </div>
</template>
<script>
import { Card , Input, Modal, Form, Row, Col, Tag } from "iview";
export default {
  name: "EditLesson",
  components: {
    Card,
    Input,
    Modal,
    Form,
    Row,
    Col,
    Tag
  },
  props:[
    'data',
    'ClockIcon'
  ],
}
</script>
<style scoped>
.title {
  color: #5e6266;
  font-size: 18px;
  font-weight: normal;
}
.btn-add-box{
  padding: 18px auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.btn-add-lesson{
  float: right;
  width: 160px;
  height: 50px;
  text-align: center;
  font-size: 20px;
  color:#fff;
  line-height: 50px;
  background-color: #ffb932;
  cursor: pointer;
}
.class-time-box-item{
  border-left: 1px solid #62BAFE;
  padding-left: 60px;
  margin-left: 42px;
}
.class-time-box-item dl{
  padding: 0 0 10px 0;
}
.class-time-box-item dt,.class-time-box-item dd{
  border:1px solid #e6e6e6 !important;
  margin-bottom: 20px;
  position: relative;
  line-height: 40px;
  border-radius: 4px;
  text-indent: 10px;
}
.class-time-box-item dt .time-title-icon{
  position: absolute;
  width: 28px;
  height: 28px;
  background-color: #fff;
  border: 1px solid #62BAFE;
  left: -75px;
  top: -1PX;
  border-radius: 50%;
}
.class-time-box-item dt .time-title-icon .time-title-icon-q{
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #62BAFE;
  position: absolute;
  left: 5px;
  top: 5px;
}
.class-time-box-item dd{
  margin-left: 60px;
}
.edit-btn-box{
  float: right;
  margin-right: 15px;
}
.edit-btn-box .time-edit-item{
  float: left;
  cursor: pointer;
  margin-right: 10px;
}
.edit-btn-box .time-edit-item img{
  width: 55%;
}
.courseInfo{
  position: relative;
}
.showTime {
    position: absolute;
    right: 168px;
}
.showTime img {
    position: relative;
    top: 8px;
    margin-right: 8px;
}
</style>



