<template>
  <div>
    <panel-title :crumbs="routeArr"/>
      <Tabs class='mytheme-tab' type="card" :animated="false" @on-click='msg' >
          <TabPane label="未批阅" name='未批阅'>
              <not-read></not-read>
          </TabPane>
          <TabPane label="已批阅" name='已批阅'>
            <have-read></have-read>
          </TabPane>
      </Tabs>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Tabs, TabPane} from "iview";
import haveRead from "./haveRead";
import notRead from "./notRead";
export default {
  name: "homework",
  components: {
    panelTitle,
    Tabs,
    TabPane,
    notRead,
    haveRead
  },
  data() {
    return {
      routeArr:[{name:'作业管理',route:''},{name:'未批阅',route:''}]
    };
  },
  methods: {
    msg(name){
     this.routeArr=[{name:'作业管理',route:''},{name:name,route:''}];
    }
  },
  created() {},
  mounted() {},
  computed: {},
  watch: {}
};
</script>
<style scoped lang='less'>
</style>



