<template>
  <div >
    <panel-title :crumbs="crumbs" />
   	<Card  dis-hover class="mytheme-pd-left-20 mytheme-pd-right-20 mytheme-mg-bottom-30" style="padding-bottom: 200px;">
   		<Form :label-width="180" ref='formItem' :model="formItem" class='form-confrol cardp1' :rules="ruleCustom" >   			
   			<Row>
   					<Col span="20" style="height: 140px;">
              <!-- 基本信息 -->
              <card-title style='margin-top:55px'>{{$t('showBasicInfo.BasicInfo')}}</card-title>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1">
            <!-- 套餐配置：  -->
              <FormItem label="套餐配置："  prop="classTime">
              	购买
                <Input class="mealTime" v-model.trim="formItem.classTime" style="width: 10%;" :maxlength="4"/>
               	 课时
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1">
            <!-- 套餐名称： 请输入内容 -->
              <FormItem label="套餐名称：" prop="setName">
                <Input placeholder="直播送录播" style="width: 80%;"  :maxlength="20"  v-model.trim="formItem.setName"/>
                <!-- (套餐名称长度不超过20个汉字，支持中英文输入) -->
                <div class='headimg-des'> (套餐名称长度不超过20个汉字，支持中英文输入)</div>
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="18" offset="1" >
            	<!--套餐封面-->
              <FormItem label="套餐封面：" prop="images">
                <UploadPic
                  :data="upload"
                  @success="getLogoImg"
                   style="width:204px;height:68px;position: relative;">
                  <div class="logo-box">
                      <img v-if="!formItem.images" :src="uploadIcon" style="position: absolute;left:50%;top:50%; transform: translate(-50%,-50%)"/>
                    	<img v-else :src="formItem.images" class='logo' v-model="formItem.images" />
                  </div>
                </UploadPic>
                <div class='headimg-des'> (为保证显示效果，图片尺寸为1920*1080，图片格式支持jpg、png)</div>
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="1" >
            <!-- 套餐价格： 请输入内容 -->
              <FormItem label="请输入套餐价格：" prop="packagePrice">
                <Input placeholder="请输入套餐价格" style="width:30%;margin-right: 10px;"  :maxlength="8" v-model="formItem.packagePrice" />
                 	元
              </FormItem>
            </Col>
        	</Row>   
        	
   			<Row>
   					<Col span="20" style="height: 140px;">
              <!-- 使用信息 -->
              <card-title style='margin-top:55px'>使用信息</card-title>
            </Col>
          </Row>        	
        	
          <Row>
            <Col span="12" offset="1">
            <!-- 每人限购： 请输入内容 -->
              <FormItem label="每人限购：" prop="classNum" :show-message="maleBol">
		        		<RadioGroup v-model="radipurchaseo">
		                <Radio label="male">无限制</Radio>
                    <div>
		                 <Radio label="female">限购买</Radio>
                      <!-- 限购次数 -->
                      <Input  style="width: 10%;margin-right: 50px;" v-model="formItem.classNum"   :disabled="radipurchaseo == 'female' ? false : true "  :maxlength="4"/>
                        限购次数
                      <span	class='headimg-des'>（限购次数为整数）</span>
                    </div>
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row> 
          
          <Row>
            <Col span="12" offset="1">
            <!-- 使用门槛： 请输入内容 -->
              <FormItem label="使用门槛：" prop="classPrice" :show-message="maleBol1">
		        		<RadioGroup v-model="threshold">
		                <Radio label="male" >无限制</Radio>
                    <div>
                      <Radio label="female">订单满</Radio>		            
                      <Input  style="width: 14%;margin-right: 10px;" v-model="formItem.classPrice" :disabled="this.threshold == 'female' ? false : true " :maxlength="4"/>
                        元可用
                    </div>
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row>          

          <Row>
            <Col span="12" offset="1">
            <!-- 有效期： 请输入内容 -->
              <FormItem label="有效期：" prop="numDays" :show-message="maleBol2">
		        		<RadioGroup v-model="term" class="term" style="margin-top: 3px;">
		                <!--<Radio label="male" >无限制</Radio></br>-->
		                <Radio label="female" >日期范围</Radio>
                    <DatePicker type="daterange" :value="valueTime"   placement="bottom-end" :editable="false"  placeholder=" 选择截止日期" @on-change="handleChange"
                    	:disabled="this.term == 'female' ? false : true "  :options="optionss" style="margin-bottom: 20px;"></DatePicker> </br>
                    <Radio label="days">固定天数</Radio>
		            		<Input class="terminpt" :disabled="this.term == 'days' ? false : true "  :maxlength="3" v-model="formItem.numDays" />
		            		<!--<span slot="append" >天</span>-->
		            </RadioGroup>
              </FormItem>
            </Col>
        	</Row>

           <Row>
            <Col span="12" offset="1">
            <!-- 可使用课程： 请输入内容 -->
              <FormItem label="可使用课程：" >
									<Button class="classBtn" :class= "{btnActive : allClass}" @click="active ('allClass',0)">全部课程</Button>
									<Button class="classBtn" :class= "{btnActive : Appoint}"  @click="active ('Appoint',1); ">指定分类</Button>
									<Button class="classBtn" :class= "{btnActive : fication}" @click="active ('fication',2)">指定课程</Button>
              </FormItem>
            </Col>
        	</Row>  
        	
        	<Row>
            <Col span="12" offset="1" v-if="edit">
            <!-- 选择分类/课程： 请输入内容 -->
              <FormItem :label="activeIdex==2?'选择分类：':'选择课程：'" prop ="treeData" >
              		<div class="pfication">
              			<!-- <span class="classification" v-model="treeData" v-for="item of treeData">{{item.title}}</span> -->
											<span v-if="activeIdex==2" class="classification edit" @click = "isShowDelModal = true; active ('Appoint',1)">共选择{{classifyNum}}分类</span>
              			<span v-if="activeIdex==3" class="classification edit" @click = "isShowDel = true; classModale(null, 'mian');">共选择{{strage.length}}门课程</span>
              		</div>
              </FormItem>
            </Col>
        	</Row>
       	
          <Row>
            <Col span="12" offset="1">
              <!-- 使用说明： 请输入内容-->
              <FormItem label="使用说明：" prop="summary">
                <Input :placeholder="$t('showBasicInfo.tip')" type="textarea"  :rows="6" :autosize="{minRows: 2,maxRows: 5}" v-model.trim="formItem.summary" :maxlength = '100'/>
               <!-- 提示 -->
                <div class='headimg-des'>（简要介绍自己课堂的优势、特色信息，方便用户获得直观印象，100字内）</div>
              </FormItem>
            </Col>
          </Row>

          <Row>
            <Col span="12" offset="1">
              <!-- 是否赠送课时： 请输入内容-->
              <FormItem label="是否赠送课时：">
		        		<RadioGroup v-model="check" style="margin: 4px;">
		                <Radio label="male" style="margin-right: 100px;">是</Radio>
		                <Radio label="female">否</Radio>		            
		            </RadioGroup>
              </FormItem>
            </Col>
          </Row>

           <!-- 非赠送部分————指定课程弹框 -->
          <Modal v-model="isShowDel" width="900" :closable="false">
					    	    <div slot="header">指定课程	</div>
                    <Card dis-hover class="mytheme-noborder">
		        				<div style=" height: 300px;">
		        					<ul class="classUl">
		        						 <CheckboxGroup  class="checkboxGroup" v-model="cheecck"  @on-change="getDate">
			        						<li v-for="(itmes,index) in dataM" :key="index" class="claasLi">
				        							<img :src="itmes.imageurl" alt="" />
				        							<!-- <span class="classSpan">{{itmes.coursename}}</span> -->
				        								<Checkbox class="classBox" :label="itmes.id" v-model="itmes.focus" size="large" >{{itmes.coursename}}</Checkbox>
				        					</li>
		        						 </CheckboxGroup>
		        					</ul>
		        				</div>
       							 <pagination :total="total"  :current-page='current' @pagechange="pagechange"  :display= "display" v-if='total>0'></pagination>
		        			</Card>	
					      <div slot="footer" >
					    		<Button type="primary" @click="isShowDel=false;edit=true;">确认</Button>
					        <Button @click="isShowDel=false">取消</Button>
					      </div>
              
					</Modal>

						<!--赠送部分————指定课程弹框-->
          <Modal v-model="isShowDell" width="900" :closable="false">
                <div slot="header">指定课程 </div>
                 <Card dis-hover class="mytheme-noborder">
                <div style=" height: 300px;">
                  <ul class="classUl">
                      <CheckboxGroup  class="checkboxGroup" v-model="cheeecck"  @on-change="getDatePlus">
                      <li v-for="(itmes,index) in dataw" :key="index" class="claasLi">
                          <img :src="itmes.imageurl" alt="" />
                          <!-- <span class="classSpan">{{itmes.coursename}}</span> -->
                            <Checkbox class="classBox" :label="itmes.id" v-model="itmes.focus" size="large" >{{itmes.coursename}}</Checkbox>
                      </li>
                      </CheckboxGroup>
                  </ul>
                </div>
       					<pagination :total="totall"  :current-page='currentl' @pagechange="pagechangePlus"  :display= "displayl" v-if='totall>0'></pagination>
		        		</Card>			
					      <div slot="footer" >
					    		<Button type="primary" @click="isShowDell=false;edit1=true;">确认</Button>
					        <Button @click="isShowDell= false">取消</Button>
					      </div>
					</Modal>
					
					<!--非赠送部分————指定分类弹框-->
			    <Modal v-model="isShowDelModal" width="500" :closable="false">
			    	     <div slot="header">选择课程分类</div>
            	 <Tree :data="data1" ref="mTree" multiple show-checkbox v-model="formItem.tree" @on-check-change= "setTree();"></Tree>
				      <div slot="footer">
				    		<Button type="primary" @click="isShowDelModal=false; setTree()">确认</Button>
				        <Button @click="moisShow('mian')" >取消</Button>
				      </div>
			    </Modal>
			    
					<!--赠送部分————指定分类弹框-->
			    <Modal v-model="isShowDelModall" width="500" :closable="false">
			    	     <div slot="header">选择课程分类</div>
            	 <Tree :data="data2" ref="mTreePlus" multiple show-checkbox v-model="formItem.treePlus" @on-check-change= "setTreePlus();"></Tree>
				      <div slot="footer">
				    		<Button type="primary" @click="isShowDelModall=false; setTreePlus()">确认</Button>
				        <Button @click="moisShow('plus')">取消</Button>
				      </div>
			    </Modal>

        <!--赠送课时部分-->  
				<div v-if="this.check == 'female' ? false : true">  
			  		  <Row>
		            <Col span="12" offset="1">
		            <!-- 赠送————有效期： 请输入内容 -->
		              <FormItem label="赠送课时：" >
				        		<RadioGroup v-model="give" class="term" style="margin-top: 3px;">
		                    <FormItem label="" prop="broadcast" :show-message="maleBol4">
		                    	<Radio label="broadcast" style="margin-bottom: 7px;">赠送</Radio>
				            			<Input  style="width: 18%;margin: 0 10px 6px 0" :disabled="this.give == 'broadcast' ? false : true" v-model="broadcast"/>&nbsp;课时录播课
				            		</FormItem>
				            		</br>
				            		 <FormItem label="" prop="broadcasting" :show-message="maleBol5">
		                      	<Radio label="broadcasting" style="margin-top: 4px;">赠送</Radio>
				            				<Input  style="width: 18%;margin:0px 10px 0  0; display: inline-block;" :disabled="this.give == 'broadcasting' ? false : true " v-model="broadcasting"/>&nbsp;课时直播课
				           			 </FormItem>
				        		</RadioGroup>
				        		</br>
										<span  class="classTime" v-if="bradShow">请输入课时</span>
		              </FormItem>
		            </Col>
		        	</Row> 
		        	
		        	<Row>
		            <Col span="12" offset="1">
		             <!--  赠送————使用门槛： 请输入内容 -->
		              <FormItem label="使用门槛：" prop="classPrice1" :show-message="maleBol6">
				        		<RadioGroup v-model="thresholdPlus" style="width: 60%;">
				                <Radio label="male" >无限制</Radio>
                        <div>
                          <Radio label="female">订单满</Radio>		            
                          <Input  style="width: 14%;margin-right: 10px;" :disabled="this.thresholdPlus == 'female' ? false : true " v-model="formItem.classPrice1" :maxlength="4"/>
                            元可用
                        </div>
				            </RadioGroup>
		              </FormItem>
		            </Col>
		        	</Row>          
		
		          <Row>
		            <Col span="12" offset="1">
		            <!-- 赠送————有效期： 请输入内容 -->
		              <FormItem label="有效期：" prop="Daysnum" :show-message="maleBol7">
				        		<RadioGroup v-model="termPlus" class="term" style="margin-top: 3px;">
				                <Radio label="female" >日期范围</Radio>
                    		<DatePicker type="daterange" :value="timeValue" :editable="false"  placement="bottom-end" placeholder=" 选择截止日期" @on-change="handleChangePlus"
                    		:disabled="this.termPlus == 'female' ? false : true " :options="optionss" style="margin-bottom: 20px;">
                    		</DatePicker></br>                    
		                    <Radio label="days">固定天数</Radio>
				            		<Input class="terminpt" :disabled="this.termPlus == 'days' ? false : true " v-model="formItem.Daysnum" :maxlength="3" />
				            </RadioGroup>
		              </FormItem>
		            </Col>
		        	</Row> 
		        	
		           <Row>
		            <Col span="12" offset="1">
		             <!-- 赠送————可使用课程： 请输入内容 -->
		              <FormItem label="可使用课程：" >
											<Button class="classBtn" :class= "{btnActive : allClass1}" @click="active1 ('allClass1',0)">全部课程</Button>
											<Button class="classBtn" :class= "{btnActive : Appoint1}"  @click="active1 ('Appoint1',1)">指定分类</Button>
											<Button class="classBtn" :class= "{btnActive : fication1}" @click="active1 ('fication1',2)">指定课程</Button>
		              </FormItem>
		            </Col>
		        	</Row>  
		        	<Row>
		            <Col span="12" offset="1" v-if="edit1">
		            <!-- 选择分类： 请输入内容 -->
		              <FormItem :label="activeIdex==2?'选择分类：':'选择课程：'"  prop ="treeDataPlus" >
		              		<div class="pfication">
		              			<!-- <span class="classification" v-for="item of treeDataPlus">{{item.title}}</span> -->
		              				<span v-if="activePlus==2" class="classification edit" @click = "isShowDelModall = true;active1 ('Appoint1',1) ">共选择{{classifyNum1}}分类</span>
		              				<span v-if="activePlus==3" class="classification edit" @click = "isShowDell = true; active1 ('fication1',2) ">共选择{{stragePlus.length}}门课程</span>
		              		</div>
		              </FormItem>
		            </Col>
		        	</Row>		        			    			    
					</div>		
					
				<Row>
		      <Col span="21" offset="1">
          	 <FormItem label="" class="btnPad">
            	<!-- <div class="btn custom-btn-warning " style="margin-right: 20px;" @click="handleSubmit('formItem')">提交</div> -->
              <Button type="primary" @click.native="handleSubmit('formItem')">提交</Button>
               <!-- class="btn custom-btn-cancel br21" -->
          	 	<!-- <Button @click="backRou">取消</Button> -->
            </FormItem>

          </Col>
			</Row>	
   		</Form>
   	</Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import cardTitle from "@/components/common/CardTitle/CardTitle";
import {
  Card,
  Form,
  FormItem,
  Input,
  Upload,
  Button,
  Col,
  Row,
  RadioGroup,
  Radio,
  DatePicker,
  Modal,
  Select,
  Option,
  Tree,
  CheckboxGroup,
  Checkbox
} from "iview";
import Server from "@/service/request";
import UploadPic from "@/components/UploadPicture";
import Path from "@/service/Path";
import uploadIcon from "@/assets/images/icon_addfolder@2x.png";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Form,
    FormItem,
    UploadPic,
    Input,
    Upload,
    Button,
    Col,
    Row,
    pagination,
    cardTitle,
    RadioGroup,
    Radio,
    DatePicker,
    Modal,
    Select,
    Option,
    Tree,
    CheckboxGroup,
    Checkbox
  },
  data() {
    const PriceMeal = (rule, value, callback) => {
      //套餐价格
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (value === "") {
        callback(new Error("请输入套餐价格"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确套餐价格"));
      } else {
        callback();
      }
    };
    const mealPrice = (rule, value, callback) => {
      //使用门槛的满多少钱
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      if (this.threshold === "male") {
        callback();
      } else {
        if (value === "") {
          callback(new Error("请输入金额"));
        } else if (!PriceRegex.test(value)) {
          callback(new Error("请按正确金额格式输入"));
        }
        callback();
      }
    };
    const classPrice = (rule, value, callback) => {
      //赠送门槛的满多少钱
      let PriceRegex = /^(([1-9][0-9]*)|(([0]\.\d{1,2}|[1-9][0-9]*\.\d{1,2})))$/;
      value = this.formItem.classPrice1;
      if (this.thresholdPlus === "female" && value === "") {
        callback(new Error("请输入金额"));
      } else if (this.thresholdPlus === "male") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请按正确金额格式输入"));
      } else {
        callback();
      }
    };
    const classMeal = (rule, value, callback) => {
      //课时正则
      let PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (value === "") {
        callback(new Error("请输入课时"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确课时"));
      } else {
        callback();
      }
    };
    const images = (rule, value, callback) => {
      //图片正则
      if (value === "") {
        callback(new Error("图片log不能为空"));
      }
      callback();
    };
    const classNum = (rule, value, callback) => {
      //每人限购次数正则
      let PriceRegex = /^[1-9][0-9]{0,4}$/;
      if (this.radipurchaseo === "female" && value === "") {
        callback(new Error("请输入限购次数"));
      } else if (this.radipurchaseo != "female") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确限购次数"));
      } else {
        callback();
      }
    };
    const numDays = (rule, value, callback) => {
      //有效期的固定天数
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.term === "days" && value === "") {
        callback(new Error("请输入天数"));
      } else if (this.term != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    const treeData = (rule, value, callback) => {
      //指定分类
      value = this.treeData;
      if (this.activeIdex === 2 && value.length == 0) {
        callback(new Error("指定课程分类不能为空"));
      } else {
        callback();
      }
    };
    const treeDataPlus = (rule, value, callback) => {
      //赠送————指定分类
      value = this.treeDataPlus;
      if (this.activePlus === 2 && value.length == 0) {
        callback(new Error("指定课程分类不能为空"));
      } else {
        callback();
      }
    };
    const Daysnum = (rule, value, callback) => {
      //赠送有效日期————固定天数
      value = this.formItem.Daysnum;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.termPlus === "days" && value === "") {
        callback(new Error("请输入天数"));
      } else if (this.termPlus != "days") {
        callback();
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    const broadcasting = (rule, value, callback) => {
      //赠送课时日期正则--直播课
      value = this.broadcasting;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcast") {
        this.broadcasting = "";
        callback();
      } else if (this.give === "broadcasting" && value === "") {
        callback(new Error("请输入课时数"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    const broadcast = (rule, value, callback) => {
      //赠送课时正则--录播课
      value = this.broadcast;
      var PriceRegex = /^[1-9][0-9]{0,3}$/;
      if (this.give === "broadcasting") {
        this.broadcast = "";
        callback();
      }
      if (this.give === "broadcast" && value === "") {
        callback(new Error("请输入课时数"));
      } else if (!PriceRegex.test(value)) {
        callback(new Error("请输入正确格式"));
      } else {
        callback();
      }
    };
    return {
      uploadIcon,
      crumbs: [
        { name: "套餐列表", route: "/SetMealList?from=MainBuyManagement" },
        { name: "编辑套餐", route: "" }
      ],
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      allClass: true, //全部课程
      Appoint: false, //指定分类
      fication: false, //指定课程
      allClass1: true, //全部课程
      Appoint1: false, //指定分类
      fication1: false, //指定课程
      isShowDelModal: false, //分类弹框
      isShowDelModall: false, //赠送弹框
      isShowDel: false, //指定课程弹框
      isShowDell: false, //指定课程弹框赠送
