      editTreePlus: "", //获取应选中分类 赠送
      classifyNum: 0, //选择了几个分类
      classifyNum1: 0, //赠送了几个分类
      optionss: {
        //限定时间从当前开始
        disabledDate(date) {
          return date && date.valueOf() < Date.now() - 86400000;
        }
      },
      formItem: {
