        broadcast: [{ required: true, validator: broadcast,trigger: 'blur'}], //赠送课时正则--录播课
        packagePrice: [{ required: true, validator: PriceMeal,trigger: 'blur'}], //套餐价格
        classNum: [{ required: true, validator: classNum,trigger: 'blur'}], //每人限购次数
        classPrice: [{ required: true, validator: mealPrice,trigger: 'blur'}], //使用门槛的满多少钱
        classPrice1: [{ required: true, validator: classPrice,trigger: 'blur'}], //赠送门槛的满多少钱
        numDays: [{ required: true, validator: numDays,trigger: 'blur'}], //有效期的固定天数
        Daysnum: [{ required: true, validator: Daysnum,trigger: 'blur'}], //赠送有效日期————固定天数
        // treeData: [{ required: true, validator: treeData }], //指定分类
        // treeDataPlus: [{ required: true, validator: treeDataPlus }],//赠送————指定分类
        summary: [{ required: true, message: "使用说明不能为空",trigger: 'blur'}] //赠送 固定天数
      },
      store: 0, //改变所选分类或课程的状态
      storePlus: 0, //改变所选分类或课程的状态

      selectedCourse: [], //已选择课程（购买）

      classifyIds: "",
      maleBol:true,//是否做正则验证
      maleBol1:true,
