<template>
  <!--学生类型-->
  <div id="ClassType">
    <panel-title :crumbs="crumbs" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <!--添加分类 -->
      <Button type="primary"  @click="showTypeEditModal('add')">{{$t('classifyManagepage.Addclassification')}}</Button>

      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :loading="loading" :columns="columns1" :data="data1"></i-table>
     
      <pagination  v-if='total>0' :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange"></pagination>
    </Card>

  <!-- 添加/编辑分类的弹框 -->
    <Modal
      v-model="LessonTypeEditModel"
       :closable="false">
      <div slot="header">{{LessonTypeText}}</div>
        <Form class="mg-top-20" :label-width="120" ref="classify" :model="classify" :rules="ruleValidate">
          <Row>
            <Col>
               <FormItem prop="cateName" label="分类名称 ：">
                <!--分类名称-->
                  <Input style="width:245px;" :maxlength="4" placeholder="最多可输入4个汉字" v-model.trim="classify.cateName" @keyup.enter.native="ok('classify')" />
                 </FormItem>
            </Col>
          </Row>
        </Form>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="ok('classify')">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button @click="LessonTypeEditModel = false;"> {{$t('Common.cancel')}}</Button>
      </div>
    </Modal>

    <!-- 删除分类弹框 -->
    <Modal
      v-model="LessonTypeDeleteModel"
       :closable="false">
       <div slot="header">温馨提示</div>
      <div class="type-content"> 删除学生分类后，原来已经添加到该分类下的学生的分类信息及教务模块下知识管理的适用对象将被删除，您确定要删除吗？</div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="okDelete">确认</Button>
        <!--取消-->
        <Button @click="LessonTypeDeleteModel = false;">取消</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import panelTitle from "@/components/panelTitle";
  import pagination from "@/components/common/pagination";
  import { Card,FormItem, Input, Table, Switch, Button, Modal, Form, Row, Col } from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name: "StudentType",
    components: {
      panelTitle,
      Card,
      FormItem,
      Input,
      Button,
      "i-table": Table,
      pagination,
      "i-switch": Switch,
      Modal,
      Form,
      Col,
      Row,
    },
    created() {
      this.getClassData(this.current);
    },
    methods: {
      deleteItem(id){
        Server.post(Path.deleteStudentCategory,{
          id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.getClassData(this.current);
            this.$Message.success(res.info);
          }else{
            this.$Message.error(res.info);
          }
        })
      },
      

      getClassData(page=1){//获取分类列表
        Server.post(Path.getCategoryList,{
          name: '',
	    		pagenum: page,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // this.data1 = [];
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total =total;
            this.display=pagesize;
            this.current=Number(pagenum);
            this.data1 = data.data;
            this.loading = false;
          }else{
            this.data1 = [];
            this.loading = false;
          }
        })
      },
       pagechange: function(currentPage) {
        this.loading = true;
        this.current = currentPage;
        this.getClassData(currentPage);
        // ajax请求, 向后台发送 currentPage, 来获取对应的数据
      },
      showTypeEditModal(type) {//名称显示及数据回显
         this.LessonTypeEditModel = true;
        if(type=='add'){
          this.classify.cateName = '';
          this.edtiType = 'add'
          // 添加分类
          this.LessonTypeText = this.$t("classifyManagepage.Addclassification")
        }else{
          //编辑分类
          this.edtiType = ''
          this.LessonTypeText = this.$t("classifyManagepage.editclassification")
        }
      },
      ok(name) {//判断为添加还是编辑分类
       this.$refs[name].validate((valid) => {
          if (valid) {
            let { edtiType } = this;
            if(edtiType=='add'){
              this.addClassName();
            }else{
              this.editClassName(this.editId);
            }
          }
       })
      },
       addClassName(type,id){//添加分类
        let { cateName } = this.classify;
        // if (!/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(cateName)) {
        //   this.$Message.error("密码只可以是数字、字母或者汉字");
        //   return false;
        //   cateName = "";
        // }
          Server.post(Path.addStudentCategory,{
          name: cateName
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.$Message.success(res.info);
              this.getClassData(1);
            }else{
              this.$Message.error(res.info);
            }
            this.LessonTypeEditModel = false;
          })
      },
      // 编辑分类
      editClassName(id=0){
        let { cateName } = this.classify;
        // if (!/^[A-Za-z0-9\u4e00-\u9fa5]+$/.test(cateName)) {
        //   this.$Message.error("密码只可以是数字、字母或者汉字");
        //   return false;
        //   cateName = "";
        // }
        Server.post(Path.updateCategory,{
          id,
          name:cateName,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$Message.success(res.info);
            this.getClassData(1);
          }else{
            this.$Message.error(res.info);
          }
          this.LessonTypeEditModel = false;
        })
      },
      okDelete() {
        this.deleteItem(this.editId);
        this.LessonTypeDeleteModel = false;
      },
      
    },
    computed:{},
    watch:{},
    data() {
      return {
        LessonTypeText:this.$t("classifyManagepage.Addclassification"), //添加分类
        MsgModal:false,
        total: 0, // 记录总条数
        display: 10, // 每页显示条数
        current: 1, // 当前的页数
        classify:{
          cateName:''
        }, //分类名称
        ruleValidate:{cateName:[{required: true, message: '请填写分类名称', trigger: 'blur' }]},
        editId:0, //分类编辑ID
        loading:true,
        InputText: "",
        LessonTypeEditModel: false, //编辑分类弹
        LessonTypeDeleteModel: false, //删除分类弹
        edtiType:'',
        disabled: '',
        crumbs:[{name: "学生类型", route:''}],
        columns1: [
          {
            //编号
            title: '编号',
            align: "center",
            key: "id"
          },
          {
            //分类名称
            title: this.$t("classifyManagepage.systematicname"),
            align: "center",
            key: "name"
          },
          {
            //是否显示
            title:this.$t("classifyManagepage.Whethershown") ,
            align: "center",
            key: "status",
            render: (h, params, index) => {
              return h("div", [
                h("i-switch",
                  {
                    on: {
                      "on-change": (value) => {
                         Server.post(Path.SwitchCategoryStatus,{
                            id:params.row.id,
                            status:Number(value)
                        },res=>{
                          let { code, data } = res;
                          if(code===0){
                            this.$Message.info(res.info);
                          }else{
                            this.$Message.error(res.info);
                          }
                        })
                      }
                    },
                    props: {
                      value: Boolean(params.row.status)
                    }
                  }
                )
              ]);
            }
          },
          {
            //操作
            title: this.$t("classifyManagepage.operation"),
            align: "center",
            key: "Make",
            render: (h, params, index) => {
              return h("div", [
                h(
                  "a",
                  {
                    on: {
                      click: value => {
                        this.classify.cateName = params.row.name;
                        this.editId = params.row.id;
                        this.showTypeEditModal();
                      }
                    },
                    style: {
                      cursor: "pointer",
                      color: "#3F9CE8",
                      display: "inline-block",
                      marginRight: "35px"
                    },
                    attrs: {
                      class: "tablebtn blue"
                    }
                  },"编辑"),
                h(
                  "a",
                  {
                    on: {
                      click: value => {
                        this.editId = params.row.id;
                         this.LessonTypeDeleteModel = true;
                        // this.deleteItem(params.row.id);
                      }
                    },
                    style: {
                      cursor: "pointer",
                      color: "#ED3825",
                      display: "inline-block"
                    },
                    attrs: {
                      class: "tablebtn red"
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ],
        data1: []
      };
    }
  };
</script>
<style lang="less" scoped>
</style>



