<template>
  <div id="essayDetail">
    <panel-title :crumbs="crumbs"></panel-title>
    <Card style="padding:0 10% 20px" class="mytheme-container-mg-30 noradius-card mytheme-noborder" dis-hover>
        <Row>
            <p class="title">{{composition.title}}</p>
        </Row>
        <Row>
            <div class="right">
                <p>学生：{{composition.studentname}}</p>
                <p>日期：{{composition.addtime}}</p>
            </div>
        </Row>
        <Row class="between">
            <!-- <div style="flex:1">{{composition.content}}</div> -->
            <div style="flex:1">
                <p style="white-space: pre-line;">{{test}}</p>
            </div>
            <div style="margin-left: 20px;">
                <!-- v-model="value3" -->
                <Carousel
                    style="width:476px;"
                    :height="410"
                    :autoplay="setting.autoplay"
                    :autoplay-speed="setting.autoplaySpeed"
                    :dots="setting.dots"
                    :radius-dot="setting.radiusDot"
                    :trigger="setting.trigger"
                    :arrow="setting.arrow">
                    <CarouselItem v-for="(i,index) in  transfer(composition.imgurl||'')" :key="index" style="position: relative;" >
                        <img :src="'http://'+i" style="width:100%;height:100%;" alt="">
                        <div class="see" @click="handleView(i)">
                            <img :src="Zoom" alt="">
                        </div>
                    </CarouselItem>
                </Carousel>
            </div>
        </Row>
    </Card>
     <Card v-if="$route.query.type==1" class="mytheme-container-mg-30 noradius-card mytheme-noborder" style="margin-bottom:60px;" dis-hover>
            <fieldset class="formHead">
                <legend style="padding: 0 30px;">老师批阅</legend>
            </fieldset>
             <Form style="padding:0 205px" >
                <FormItem label='老师评语：' style="width:100%;">
                   <span style="display: inline-block;width: 120px;height:30px;line-height: 30px;padding:0 10px;border:solid 1px #ccc;">{{techercomment.reviewscore}}</span>
                </FormItem>
                <FormItem label='' style="width:100%;padding-left:70px;">
                   <p v-html="techercomment.commentcontent"></p>
                   <div style="text-align: right;color:#808080;font-size:16px;">
                       <p>批阅人：{{techercomment.teachername}}</p>
                       <p>{{techercomment.reviewtime}}</p>
                   </div>
                </FormItem>
             </Form>
            <fieldset class="formHead">
                <legend style="padding: 0 30px;">学生评价</legend>
            </fieldset>
         <Form v-if="ishave == true" style="padding:0 205px" >
            <FormItem label='评分：' style="width:100%;">
                 <Rate allow-half disabled class="rate" v-model="studentcomment.reviewscore"/>
                 <span style="color:#FF2F2F;margin:0 20px;" >{{studentcomment.hierarchy}}</span>
                 <span style="color:#808080;font-size:16px;">{{studentcomment.reviewtime}}</span>
            </FormItem>
            <!-- <FormItem label='' style="width:100%;padding-left:50px;" class="lable">
                <span>评价细心</span>
                <span>批改严谨</span>
                <span>评价很有帮助</span>
            </FormItem> -->
            <FormItem label='学生评价：' style="width:100%;"></FormItem>
            <FormItem label='' style="width:100%;padding-left:70px;">{{studentcomment.commentcontent}}</FormItem>
        </Form>
        <p v-if="ishave == false" style="text-align: center;color:#808080;font-size:16px;padding:30px 0;">暂无数据</p>
    </Card>
     <!-- 图片双击放大区 -->
    <Modal v-model="visible">
        <div slot="header">图片预览</div>
        <img :src="'http://'+imageEssay" style="width: 100%">
        <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
  import panelTitle from "@/components/panelTitle";
  import Zoom from "../img/fangdajing.png";
  import {Card,Button,Row,Col,Carousel,CarouselItem,Form,FormItem,Select,Option,Modal,Rate} from "iview";
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "",
    components: {panelTitle,Card,Button,Row,Col,Carousel,CarouselItem,Form,FormItem,Select,Option,Modal,Rate},
    watch: {
    },
    methods: {
         transfer(img) {
        //图片格式的转换
        return img.split("~");
        },
        //查看图片
        handleView(name) {
        this.imageEssay = name;
        this.visible = true;
        },
         getCompositioninfo(){//作文基本信息
         Server.post(Path.getCompositioninfo, {
             compositionid:this.$route.query.compositionid
         }, res => {
           let {code,data,info}=res
           if(code==0){
             this.composition=data.composition;
             this.test = `${data.composition.content.replace(/\\n/g, " \n ")}`;
             if(this.$route.query.type==1&&data.techercomment!=null){
                 this.techercomment=data.techercomment;
            }else{
                  this.techercomment={};
             }
             if(this.$route.query.type==1&&data.studentcomment!=null){
                 this.studentcomment=data.studentcomment;
                this.studentcomment.reviewscore=Number(this.studentcomment.reviewscore);
                 switch(this.studentcomment.reviewscore){
                    case 1:
                    this.studentcomment.hierarchy="非常不满意";
                    break;
                    case 2:
                    this.studentcomment.hierarchy="不满意，比较差";
                    break;
                    case 3:
                    this.studentcomment.hierarchy="一般";
                    break;
                    case 4:
                    this.studentcomment.hierarchy="比较满意";
                    break;
                    case 5:
                    this.studentcomment.hierarchy="非常满意，无可挑剔";
                    break;
                    }
                this.ishave = true;
            }else{
                  this.studentcomment={};
                   this.ishave = false;
             }
           }else{
               this.composition={};
             this.$Message.error(info);
           }
         })
      },
    },

    created() {
        if (this.$route.query.beFrom) {
        this.crumbs=[{
            name: "批阅管理",
            route: "/ReaOver?from=EssayManagement"
            },{
            name: "老师作业批阅",
            route: "/EssayList?from=EssayManagement&teacherid="+this.$route.query.teacherid
            },{
            name: "查看详情",
            route: ""
            }]
    } else {
        this.crumbs=[{name:'作文管理',
          route: '/EssayManagement?from=EssayManagement'
        },{
          name:this.$route.query.type==0?'未批阅':'已批阅',
          route: '/EssayManagement?from=EssayManagement'
        },{
          name:'查看详情',
          route: ''
        }]}
        this.getCompositioninfo()
    },
    data() {
      return {
        crumbs: [],
        Zoom,
        composition:{},//作文信息
        techercomment:{},//老师评语
        studentcomment:{},//学生评论信息
        value3: 0,
        setting: {//轮播图定义默认值
            autoplay: false,
            autoplaySpeed: 2000,
            dots: 'inside',
            radiusDot: true,
            trigger: 'click',
            arrow: 'never'
        },
        imageEssay:"",//放大图片地址
        visible: false, //图片是否放大展示
        model1:'',
        ishave: false,
        test: "" //作文内容
      };
    }
  };
</script>
<style scoped lang="less">
.title{
    margin-top: 45px;
    text-align: center;
    font-size:24px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:#333;
    line-height:42px;
}
.right{
    float: right;
    font-size:16px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:#333;
    line-height:42px;
}
.between{
    display: flex;
}
.see{
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 1000;
    width:43px;
    height:43px;
    text-align: center;
    background:rgba(128,128,128,1);
    opacity:0.5;
    img{
        margin-top: 5px;
        cursor: pointer;
        width: 33px;
        height: 33px;
    }
}
.sign{
    float: right;
    line-height:34px;
    font-size:16px;
    color:#808080;
}
.formHead{
    text-align: center;
    font-size:20px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:#4D4D4D;
    line-height:42px;
    border: 0;
    border-top: dashed 1px #CCCCCC;
}
.lable{
    span{
        
        padding: 2px 10px;
        margin-right: 20px;
        border:1px solid #ccc;
        border-radius:10px;
        font-family:MicrosoftYaHei;
        color:#808080;
        // line-height:30px;
    }
}
</style>



