<template>
  <div class="layout" id="header">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <!-- 51门课网 -->
          <div class="layout-logo"><img :src='imageurl' alt='' /><img :src='imageTxt' alt='' />教育业务管理系统</div>
          <div class="layout-nav">
            <ul class="layout-nav-list">
              <li class="list-item list-item-border">
                <Poptip trigger="hover" :transfer="true" width="300" style="position:relative; height:100%">
                  <div class="item-avatar">
                    <DefaultImg :sourceimg="adminInfo.userimage" />
                  </div>
                  <span>{{adminInfo.adminname}}</span>
                  <div slot="title" style="text-align: center;">
                    <!-- 账户信息 -->
                    <span class="green account-info-title">{{$t('homepage.accountinformation')}}</span>
                  </div>
                  <div slot="content">
                    <ul class="account-info">
                      <li class="account-info-item border-bottom">
                        <img src="./assets/images/icon.png">
                        <!-- 登录名： -->
                        <span>{{$t('rest.loginName')}}{{$t('Common.colon')}}{{adminInfo.adminname}}</span>
                      </li>
                      <li class="account-info-item border-bottom">
                        <img src="./assets/images/icon.png">
                        <!-- 手机号： -->
                        <span>{{$t('rest.phone')}}{{$t('Common.colon')}}
                          +{{adminInfo.prphone}} {{adminInfo.mobile}}</span>
                      </li>
                      <li class="account-info-item border-bottom">
                        <img src="./assets/images/icon.png">
                        <!-- 本次登录： -->
                        <span>{{$t('homepage.Thelogin')}}{{$t('Common.colon')}}{{adminInfo.logintime}}</span>
                      </li>
                    </ul>
                  </div>
                </Poptip>
              </li>
              <template>
                <!-- 返回首页 -->
                <li class="list-item list-item-border btnicon"  title='返回首页' v-if="hasIndex == true">
                  <!-- <router-link to="/"> -->
                    <a href="javascript:;" @click="gohome">
                      <img src="./assets/images/header_icon_home.png">
                      <span>首页</span>
                    </a>
                  <!-- </router-link> -->
                </li>
                <!-- 退出 -->
                <li class="list-item btnicon2" title='退出'>
                  <img src="./assets/images/header_icon_loginout.png"  @click="isShowCloseModal = true">
                  <span @click="isShowCloseModal = true">退出</span>
                </li>
              </template>
            </ul>
          </div>
        </Menu>
      </Header>
    </Layout>
    
    <Modal v-model="isShowCloseModal">
      <!-- 退出确认 -->
      <div slot="header">{{$t('rest.logout')}}</div>
      <!-- 确定要退出登录吗？ -->
      <div>{{$t('rest.logoutInfo')}}</div>
      <div slot="footer">
        <!-- 确定 -->
        <Button type="primary" @click="logout()">{{$t('Common.sure')}}</Button>
        <!-- 取消 -->
        <Button @click="isShowCloseModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    
  </div>
</template>

<script>
import { Layout, Menu, MenuItem, Icon, Poptip, Button, Modal } from "iview";
import SendCode from "@/components/SendCode";
import Server from "@/service/request";
import Path from "@/service/Path";
import Upload from "@/components/UploadPicture";
import DefaultImg from '@/components/defaultHeader';
import { mapState } from 'vuex'
import logo from './assets/images/defaultImg.png';
import logo_txt from './assets/images/logo_txt.png'
import areaCode from "@/components/areaCode";
import "@/assets/areaCode.css";
export default {
  name: "vheader",
  components: {
    Layout,
    Menu,
    MenuItem,
    Poptip,
    Button,
    Modal,
    Icon,
    SendCode,
    Path,
    Server,
    Upload,
    DefaultImg,
    'v-areaCode':areaCode,
  },
  data() {
    return {
      organInfo:{
        imageurl:logo,
        imageTxt: logo_txt,
        organname:this.$t('Common.web2'),
      },
      imageurl:logo,
      imageTxt: logo_txt,
      userinfo:{
          adminname: "",
          auditstatus: "",
          imageurl: "",
          logintime: "",
          organid: 0,
          organname: "",
          restype: "2",
          token: "",
          uid: 0,
          userimage: "",
          vip: "0",
          mobile:'',
          useraccount:"",
      },
      isShowCloseModal: false,
      MessageModal: false,
      pageIndex: 1,
      Path,
      Phone:'',
     
      cFormMessage:{
        userimg:'',
        username:'',
      },
      
      //uploadid:[2,JSON.parse(window.localStorage.getItem('userinfo')).organid,1],
      //adminName:JSON.parse(window.sessionStorage.getItem('userinfo')).useraccount,
      adminInfo:{
        adminname:"",
        mobile:"",
        useraccount:"",
        userimage:"",
      },
      menuData: [],
      hasIndex: false
    };
  },
  mounted() {

  },
 
  created() {
    this.userinfo = {...JSON.parse(window.sessionStorage.getItem('userinfo'))};
    //let id = JSON.parse(window.sessionStorage.getItem('userinfo')).organid;
    this.getAdminInfo();
    //this.menuData = JSON.parse(window.localStorage.getItem('menuData'))
    //this.getIndex();
    this.getMenuTree();
  },
  methods: {
    
    //获取机构信息
    // getOrganBaseInfo(origanid){
    //   Server.post(Path.getOrganBaseInfo,{
    //     organid:origanid,
    //   },res=>{
    //     let { code, data } = res;
    //     if(code===0){
    //       // 设置窗口标题与title
    //       if(data.organname != ''){
    //         this.organInfo.imageurl = data.imageurl;
    //         this.organInfo.organname = data.organname;
    //         document.title = data.organname;
    //         var link = document.querySelector("link[rel*='icon']") || document.createElement('link');
    //         link.type = 'image/x-icon';
    //         link.rel = 'shortcut icon';
    //         link.href = data.imageurl;
    //         document.getElementsByTagName('head')[0].appendChild(link);
    //       }
    //     }
    //   });
    // },
    //获取用户信息
    getAdminInfo(){
      Server.post(Path.getUserMeader,
        {},res=>{
          let {code,data}=res;
          if(code==0){
            this.adminInfo = {...data};
            this.cFormMessage.userimg = data.userimage;
            this.cFormMessage.username= data.adminname;
          }
        })
    },

  
   
    //返回首页
    gohome(){
      this.$emit('updateMenuFn', 1);
      this.$router.push("/webAdminIndex?from=MainIndex");
    },
    // 退出登录
    logout() {
      let {token} = this.userinfo;
      let {userinfo} = this;
      Server.post(Path.exitLogin,{
        token:token
      },res=>{
        let {code,data} = res;
        window.sessionStorage.removeItem('userinfo');
        window.sessionStorage.removeItem('key');
        window.localStorage.removeItem('menuData');
        this.$router.push('/login')
      })
    },
    // getIndex() {
    //   let idArr = [];
    //   this.menuData.forEach((el) => {
    //     el.list.forEach((e, i) => {
    //       idArr.push(e.id);
    //     })
    //   });
    //   if(idArr.includes(2) == true){
    //     this.hasIndex = true;
    //   }else {
    //     this.hasIndex = false;
    //   }
    // },
    //获取菜单配置
    getMenuTree() {
      Server.post(Path.getMenuTree,{
      }, res => {
        if(res.code == 0){
          if(res.data){
              window.localStorage.setItem('menuData',JSON.stringify(res.data));
            }
          let menuData = res.data;
          let idArr = [];
          menuData.forEach((el) => {
            el.list.forEach((e, i) => {
              idArr.push(e.id);
            })
          });
          if(idArr.includes(2) == true){
            this.hasIndex = true;
            //console.log(1111);
          }else {
            this.hasIndex = false;
            //console.log(222)
          }
        }
      })
    }
  }
};
</script>

<style scoped lang='less'>
@import "~@/assets/base.less";
.Edit {
  width: 70%;
  float: left;
  // overflow: hidden;
}
.SelectEditBtn {
  float: right;
  width: 29%;
  border-left: 1px solid @line;
  margin-top: 10px;
  height: 120px;
}
.SelectEditBtn ul li:nth-child(1){
  margin-bottom: 36px;
}
.SelectEditBtn ul li .editbtn {
  width: 96px;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  border: 1px solid #ffad65;
  border-radius: 4px;
  font-size: 14px;
  margin: 10px auto;
  color: #ffad65;
}
.SelectEditBtn ul li .editbtn.active {
  color: #fff;
  background: #ffad65;
}
.infoEdit ul li {
  line-height: 40px;
  font-size: 14px;
}
input {
  outline: none;
}
.infoEdit ul li span {
  width: 80px;
  text-align: right;
  color: #5E6266;
}
.infoEdit ul li input {
  border: none;
}
.infoEdit ul li .inputbox {
  border-bottom: 1px solid @line;
  min-width: 200px;
  line-height: 30px;
  position: relative;
  input{
    width: 100%;
  }
}
.infoEdit ul li * {
  display: inline-block;
}

.saveBtn {
  margin: 15px auto 0;
}
.head-img-box {
  color: #bdc2c8;
  text-align: center;
  font-size: 12px;
}
.head-img {
  width: 55px;
  height: 55px;
  overflow: hidden;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 auto 5px;
}
/* 帐户设置 */

.EditBtn {
  font-size: 28px;
  position: absolute;
  top: 22px;
  right: 10px;
  cursor: pointer;
}
#header {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 222;
  // box-shadow: 0px 10px 16px rgba(52, 109, 195, 0.14);
}

.layout {
  background: #f5f7f9;
  position: relative;
  overflow: hidden;
}

.layout-logo {
  float: left;
  position: relative;
  left: 20px;
  line-height: 80px;
  color: #fff;
  font-size: 20px;
}

.layout-logo  img{
  // width: 46px;
  height: 40px;
  display: inline-block;
  margin: -2px 10px 0 0;
  vertical-align: middle;
}

.layout-nav {
  float: right;
}

.ivu-menu-horizontal {
  height: 80px;
}

.ivu-menu-dark {
  background: #262F39;
}

.layout-nav-list {
  height: 80px;
  font-size: 0;
  line-height: 80px;
  
}

.layout-nav-list .list-item {
  display: inline-block;
  padding: 0 22px;
  color: #fff;
  cursor: pointer;
  span{
    font-size: 14px;
    vertical-align: middle;
    margin-left: 22px;
  }
}

.layout-nav-list .list-item-border:after{
  position: absolute;
  right: 0;
  top: 34px;
  content: " ";
  display: block;
  width: 1px;
  height: 16px;
  border-left: 1px dashed #fff;
}
.layout-nav-list .list-item img {
  vertical-align: middle;
}

.layout-nav-list .list-item.btnicon{
  padding: 0;
}
.layout-nav-list .list-item.btnicon a,
.layout-nav-list .list-item.btnicon2{
  display: inline-block;
  padding: 0 22px;
  text-align: center;
  color: #fff;
}
.list-item-border {
  position: relative;
}

.layout-nav-list .list-item .item-avatar {
  display: inline-block;
  // margin: 0px 22px 0 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  vertical-align: middle;
  overflow: hidden;
}

.layout-nav-list .list-item .item-avatar #default-img{
  width: 40px;
  height:40px;
}
.account-info-title {
  width: 100%;
  font-size: 16px;
  line-height: 42px;
}

.account-info .account-info-item {
  line-height: 42px;
}

.account-info .account-info-item img {
  vertical-align: middle;
}

.account-info .account-info-item.border-bottom {
  position: relative;
}

.account-info .account-info-item.border-bottom:after {
  content: " ";
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #f5f8fa;
}
.goVip {
  display: block;
  width: 101px;
  text-align: center;
  position: fixed;
  right: 50px;
  bottom: 50px;
}
#SendCode{
  position: absolute;
  top:0;
  right: 0;
}
</style>



