<template>
  <div id="StudentInfo">
    <panel-title
      :crumbs="[
        {
          name:$t('studentManagepage.studentManagepage'), //学生管理
          route:{
            path:'/StudentManagement',
            query:{
              from:'MainUserManagement',
            }
          }
        },
        {
          name:$t('studentDetails.studentDetails'),//学生详情
          route:'',
        },
      ]"
    />
    <stu-info class="mg-bottom-20" :data="data" />
    <stu-lesson-info :data="courselist" />
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import StuInfo from "./StuInfo";
import StuLessonInfo from "./StuLessonInfo";
import Server from '@/service/request';
import Path from '@/service/Path';
export default {
  name: "StudentInfo",
  components: {
    panelTitle,
    StuInfo,
    StuLessonInfo,
  },
  created() {
    //do something after creating vue instance
    let { id } = this.$route.params;
    Server.post(Path.getUserinfo,{
      userid:id
    },res=>{
      let { code, data } = res;
      if(code===0){
        this.data = data.data;
        if(this.data.profile == ''){
          this.data.profile = this.$t('Common.NO') //wu
        }
        this.courselist = [...data.courselist];
      }
    })
  },
  data(){
    return {
      data:{},
      courselist:[],
    }
  }
}
</script>
<style scoped>
.postr{
  padding: 10px 10px 20px;
  position: relative;
}
.header-img-box{
  width: 160px;
  background-color: #eee;
  position: relative;
}
.header-img{
  width: 110px;
  height: 100px;
  background: red;
  position: absolute;
  top:-55px;
}
.user-msg-box{
  padding-left: 140px;
}
.header-user{
  overflow: hidden;
}
.userName{
  font-size: 18px;
  margin-right: 20px;
}
.login-time{
  font-size: 12px;
}
.LabelBigBox{
  overflow: hidden;
}
.LableBox{
  width: 500px;
  margin-top: 20px;
}
.LableBox li{
  height: 40px;
}
.LableBox:nth-of-type(1){
  border-right:1px solid #cfdce7;
}
.LableBox:nth-of-type(2){
  padding-left: 60px;
}
</style>



