<template>
  <div class="panelTitle green">
    <!--<slot></slot>-->
    <ul class="bread-crumbs">
      <router-link tag="li" v-for="(el, i) in crumbs" :key="i" :to="el.route">{{el.name}}</router-link>
    </ul>
  </div>
</template>
<script>
  export default {
    name: "panelTitle",
    props: {
      crumbs:{
        default:function(){
          return [
            {
              name:'一级',
              route:''
            }
          ]
        },
        type:Array
      }
    }
  }
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
  .panelTitle {
    display: flex;
    align-items: center;
    height: 48px;
    // margin: 19px 0;
    padding-left: 30px;
    margin:0 -30px;
    margin-bottom: 31px;
    font-size: 14px;
    background-color: #ffffff;
  }

  .bread-crumbs li {
    position: relative;
    height: 22px;
    display: inline-block;
    // vertical-align: middle;
    color: #A4ABB3;
    cursor: pointer;
    //  border:solid 1px blue;
  }
.bread-crumbs li:first-child{
    padding-left: 20px;
    border-left:solid 4px #3F9CE8;
  }
  .bread-crumbs li:last-of-type {
    color: #525866;
    pointer-events: none;
  }

  .bread-crumbs li:after {
    margin: 0 5px;
    background-image: url('./panelTitle/images/icon_right.png');
    content: '';
    display: inline-block;
    color: #A4ABB3;
    font-weight: bold;
    width: 20px;
    height: 20px;
    background-size: 63%;
    background-repeat: no-repeat;
    background-position-y: 7px;
    // border:solid 1px red;
  }
  .bread-crumbs li:last-of-type::after{
    background-image: url('');

  }

  .bread-crumbs li:last-of-type:after {
    content: '';
  }
</style>



