<template>
  <div id="qyh-callout">
    <div :class="className">
      <div v-for="(item, index) in formData" :key="index" style="line-height: 2;">
        <label
          :style="{
              display:'inline-block',
              textAlign:'right',
              width:formStyle.labelWidth + 'px',
              fontSize: '16px'}">
          {{item.title}}：
        </label>
        <span style="padding: 0 0 0 30px; font-size: 16px; font-weight: lighter;">{{item.value}}</span>
      </div>
    </div>
  </div>
</template>

<script>
  import {Form, FormItem, Button, Col} from 'iview'

  export default {
    name: "qyh-callout",
    props: {
      // 组件的样式
      className: {
        type: String,
        default: 'callout callout-default'
      },
      // 组件的数据
      formData: {
        type: Array,
        required: true
      },
      // 表单样式设置
      formStyle: {
        type: Object,
        default: () => {
          return {
            labelWidth: 80
          }
        }
      }
    },
    components: {
      Form,
      FormItem,
      Button,
      Col
    }
  }
</script>

<style scoped>
  #qyh-callout {
  }
  #qyh-callout .callout {
    padding: 20px 0 20px 60px;
    margin: 20px 40px;
    border: 1px solid #eee;
    border-left-width: 5px;
    border-radius: 4px;
  }
  #qyh-callout .callout,
  #qyh-callout .callout-default {
    border-left-color: #999;
    background: #eee;
  }
  #qyh-callout .callout-info {
    border-left-color: #469af8;
    background: #f2f7fb;
  }
  #qyh-callout .callout-warning {
    border-left-color: #f89240;
    background: #fffbf8;
  }
  #qyh-callout .callout-danger {
    border-left-color: #f5496d;
    background: #f2dbe0;
  }
  #qyh-callout .callout-primary {
    border-left-color: #cae3ff;
    background: #fff;
  }
  #qyh-callout .callout-success {
    border-left-color: #3adfa7;
    background: #f7fffc;
  }
</style>



