<template>
  <div id="UserLessonList">
    <panel-title :crumbs="crumbs" />

    <Card class='br8 cardP0 mg-bottom-20' dis-hover>
      <CardTitle class="setprice-card-title">{{this.$route.query.date}}</CardTitle>
      <Row>
        <Col span="24" v-if="data.length > 0">
          <div class="evaluation-box">
            <div class="evaluation-item" v-for="d,index in data" :key="index">
              <div class="eval-header-img fl">
                <img :src="d.courseimage" alt="">
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <div class="fl eval-name">
                      {{d.periodname}}
                    </div>

                    <div class='fr'>
                      <!-- <Button  v-if="d.classstatus == 1" shape="circle" size="small" class="SearchButton SearchButton2"
                        @click="enterRoom(organid, d.id)">
                        <!-- 进教室 -->
                        <!-- {{$t('calendarSchedule.enclass')}}
                      </Button> --> 
                      <!-- <Button  v-if="d.classstatus == 0" shape="circle" size="small" class="SearchButton SearchButton4">
                        <!-- 未开始
                        
                      </Button> -->
                      <Button  v-if="d.classstatus == 1" shape="circle" size="small" class="SearchButton SearchButton2"
                        @click="enterRoom(d.forclassurl)">
                        <!-- 巡课 -->
                        巡课
                      </Button>
                      <!-- <Button  v-if="d.classstatus == 2" shape="circle" size="small" class="SearchButton SearchButton4">
                        <!-- 已结束
                        
                      </Button> -->
                      <router-link :to="{path:'LessonDetailsEdit',query: {
                        dateCur: $route.query.date,
                        toteachtimeid:d.id,
                        starttime:d.starttime,
                        endtime:d.endtime,
                        id:d.lessonsid,                       
                        curriculumid: d.curriculumid,
                        isChangeFile:d.classstatus,
                        teacherid: d.teacherid,
                       // type: d.type
                        }}"
                        class="SearchButton4">
                        <Button  shape="circle" size="small" class="SearchButton">
                         <!-- 查看 --> 
                         {{$t('Common.watch')}}{{type2}}
                        </Button>
                      </router-link>
                      <!-- <router-link :to="{path:'LessonDetailsEdit',query: {dateCur: $route.query.date,
                        type:'edit',
                        toteachtimeid:d.id,
                        starttime:d.starttime,
                        endtime:d.endtime,
                        id:d.lessonsid,
                        isChangeFile:d.classstatus,
                        type: d.type}}"  >
                        <Button  shape="circle" size="small" class="SearchButton">
                         查看{{type2}}
                        </Button>
                      </router-link> -->
                    </div>
                    <!-- <div class='fr' v-else>
                      <Button  v-if="d.classstatus == 1" shape="circle" size="small" class="SearchButton SearchButton2"
                      @click="enterRoom(organid, d.id)">
                      <!-- 巡课 -->
                      <!-- {{$t('calendarSchedule.enclass2')}}
                      </Button>
                    </div> -->
                  </li>
                  <li>
                    <div class="fl eval-time mr67">
                      <!-- 开始时间： -->
                      {{$t('calendarSchedule.starttime')}}{{$t('Common.colon')}}
                      {{d.starttime}}
                    </div>
                    <div class="fl eval-time">
                      <!-- 结束时间： -->
                      {{$t('calendarSchedule.endtime')}}{{$t('Common.colon')}}
                      {{d.endtime}}
                    </div>
                  </li>
                  <li>
                    <div class="fl mr67">
                      <!-- 课程名称 -->
                      课程名称{{$t('Common.colon')}}
                      {{d.coursename}}
                    </div>
                    <div class="fl">
                      <!-- 老师： -->
                      {{$t('calendarSchedule.teacher')}}{{$t('Common.colon')}}
                      {{d.nickname}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
           <pagination v-if="total > display" :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange"></pagination>
        </Col>
        <!-- <defaultShow v-else/> -->
      </Row>

    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
import bookImg from "./assets/bookImg.png";
import defaultShow from '@/components/defaultShow';
import { Card, Row, Col } from "iview";

import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "MainLessonList",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    bookImg,
    CardTitle,
    defaultShow,
  },
  created() {
    //do something after creating vue instance

  },
  data() {
    return {
      crumbs:[{
        name: this.$t('courseTablepage.courseTablepage'),//机构课表
        route:'/home',
      },{
        name: this.$t('calendarSchedule.subtitle'),//课程列表
        route:'',
      }],
      right,
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      bookImg,
      data:[],
      type2:'',
      // isrestype:false,
      organid:'',
    };
  },
  created() {
    //do something after creating vue instance
    this.getLessonsByDate();
    //1机构 2个体老师
    // this.isrestype = JSON.parse(window.localStorage.getItem('userinfo')).restype == '2' ? true:false;
    //this.enterRoom();
  },
  beforeCreate() {
    this.organid = JSON.parse(sessionStorage.getItem('userinfo')).organid;
  },
  methods: {
    //转换班型
    // changeType(value) {
    //   if (value == 1) {
    //     return (value = this.$t('Common.single'));//1对1
    //   } else if (value == 2) {
    //     return (value = this.$t('Common.samll'));//小班课
    //   } else if (value == 3) {
    //     return (value = this.$t('Common.large'));//直播课
    //   }
    // },
    changeType(value) {
      if (value == 1) {
        return "录播课";//录播课
      } else if (value == 2) {
        return "直播课";//直播课
      } 
		},
    //进教室
    enterRoom(url) {
     // let url = 'forclassurl' ;
      window.open(url)
     // console.log(url);
      // var a = document.createElement('a');
      // a.setAttribute(`target`, `_blank`);
      // a.setAttribute(`href`, `/admin/enterClassMiddle/index.html?toteachid=${toteachid}`);
      // // a.setAttribute(`href`, `http://127.0.0.1:8080/index.html?toteachid=${toteachid}`);
      // a.click();
    },
    clickFn(toteachtimeid,starttime,endtime,id,status){
      let atype = false;
      let  datacur= this.$route.query.date;
      if(status == 0){//结束
        atype=false
      }else if(status == 1){//进教室
        atype=true
      }else if(status == 2){//未开始
        atype=true
      };
      this.$router.push({
          name: 'LessonDetailsEdit',
          query: {
            dateCur: datacur,
            type:'edit',
            toteachtimeid:toteachtimeid,
            starttime:starttime,
            endtime:endtime,
            id:id,
            isChangeFile:atype,
          }
      })
    },
    getLessonsByDate(page=1){
      let { date } = this.$route.query;
      Server.post(Path.getLessonsByDate,{
        pagenum:page,
        date
      },res=>{
        let { code, data } = res;
        if(code == 0){
          let tmp  = (JSON.stringify(data) == "{}")
          if(tmp != true){
            this.data = data.data;
          }
          this.total = data.pageinfo.total;
          this.display = data.pageinfo.pagesize;
          this.current = Number(data.pageinfo.pagenum);
        }

      })
    },
    pagechange(currentPage) {
      this.getLessonsByDate(currentPage);
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.mr67{
  margin-right: 67px;
}
.evaluation-box {
  padding: 16px 80px 0;
}
.setprice-card-title{
  margin-top: 30px;
}
.evaluation-item {
  overflow: hidden;
  padding: 20px 0;
  border-bottom: 1px solid @line2;
  .eval-header-img {
    width: 198px;
    height: 108px;
    margin-right: 32px;
    border-radius: 8px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .eval-content li {
    overflow: hidden;
    margin-bottom: 13px;
    font-size: 16px;
    color: #7A8599;
    .eval-name {
      font-size: 18px;
      color: #525866;
      line-height: 36px;
    }
    .eval-time {
      color: #525866;
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
  &:last-child{
    border-bottom: none;
  }
}
.SearchButton{
  display: inline-block;
  border: none;
  color: #00DEB6;
  font-size: 14px;
  cursor: pointer;
  width: 84px;
  height: 36px;
  margin: 0 10px;
  background: rgba(221,254,248,1);
  border-radius: 18px;
  box-shadow: 2px 0px 5px rgba(0,222,182,0.2);
  &:focus{
    outline: none;
  }
}
.SearchButton2{
  background:rgba(255,244,231,1);
  box-shadow:2px 0px 5px rgba(245,139,13,0.2);
  color: #F58B0D;
}
.SearchButton3{
  background:rgba(255,226,231,1);
  box-shadow:2px 0px 5px rgba(255,24,64,0.2);
  color: #FF1840;
}
.SearchButton4{
  display: inline-block;
}
</style>



