<template>
  <div>
    <panel-title :crumbs="[{name:'题库管理',route:''}]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20" style="position: relative;">
          <!--课程名称-->
          <span>课程名称：</span>
          <!---课程名称-->
          <Input
              class="input-normal-width input-max-height"
              @on-enter="getCurricukumList(current)"
              v-model.trim="courseName" placeholder="课程名称"/>

          <Button type="primary" class="mytheme-mg-left-70" style="height:36px;" @click="getCurricukumList(current)">搜索</Button>
          
          <!--录入/导入题目-->
          <div class="butGroup">
            <Button type="primary" class="mytheme-mg-right-50" @click="$router.push('/editQues/1?from=TeachingAffairs')">录入题目</Button>
            <Upload 
              style="display: inline-block;"
              name="uploadFile"
              :action="upload.Path"
              :data='upload.data'
              :format="upload.format"
              :headers="head"
              @click.native="setHeader(upload.data)"
              :on-success="onsuccess">
              <Button type="warning" >导入试题</Button>
            </Upload>
          </div>
      </div>
      <!---课程名称/分类列表-->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
      <!--分页-->
      <pagination class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
      </pagination>
    </Card>
  </div>
</template>
<script>
import { Card, Input, Table, Button, Upload } from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "QuestionBank",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    Button,
    pagination,
    Upload
  },
  data() {
    return {
      upload:{//导入文件的参数
        Path: window.location.origin === 'http://127.0.0.1:8080' ? "http://www.lemonedu.com/admin/Importdata/Import" : "/admin/Importdata/Import",
        data:{importtype:1},
        format:['xlsx','xls','xlsm','xltx','xltm','xlsb','xlam']
      },
      head:{
        sign:'',
        token:'',
        starttime:'',
      },
      courseName: "",
      colData: [
        {
          title: "编号",
          key: "id",
          align: "center"
        },
        {
          title: "课程名称/分类",
          // key: 'courseAndType',
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "p",
                {
                  style: {
                    fontWeight: "bold"
                  }
                },
                params.row.coursename
              ),
              h("p", {}, "分类：" + params.row.categoryname)
            ]);
          }
        },
        {
          title: "试题数（套）",
          key: "exercise_count",
          align: "center"
        },
        {
          title: "操作",
          key: "handle",
          align: "center",
          render: (h, params, index) => {
            return h("div", [
              h(
                "router-link",
                {
                  props: {
                    to: "/periodList?from=TeachingAffairs&id=" + params.row.id
                  },
                  style: {
                    cursor: "pointer",
                    color: "#3F9CE8",
                    display: "inline-block"
                  },
                  attrs: {
                    class: "tablebtn yellow"
                  }
                },
                this.$t("Common.watch")
              ) //查看
            ]);
          }
        }
      ],
      tabData: [],
      total: 0,
      display: 20,
      current: 1
    };
  },
  methods: {
    //课程列表
    getCurricukumList(current = 1) {
      Server.post(
        Path.getQuestionBankCourse,
        {
          coursename: this.courseName,
          pagenum: current
        },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
            this.tabData =[]
          }
        }
      );
    },
    //分页
    pagechange(currentPage) {
      this.getCurricukumList(currentPage);
    },
     //设置请求头
    setHeader(data) {
      this.head = this.$Utils.setHeader(data);
    },
    onsuccess(res) {//导入成功的回调
        let {code,data,info}=res
        if(code==0){
          this.getCurricukumList(this.current);
          let text="成功导入"+data.num+"数据"
          this.$Message.success(text);
        }else{
          this.$Message.warning(info);
        }
    }
  },
  created() {
    this.getCurricukumList(this.current);
  },
  mounted() {},
  computed: {},
  watch: {}
};
</script>
<style scoped lang='less'>
.butGroup{
  position: absolute;
  top: 0;
  right:0;
  display:flex;
  align-items: flex-start;
}
</style>



