<template>
  <div id="">
    <Card class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30" dis-hover>
      <template>
        <Form :label-width="85" inline >
          <!--老师姓名-->
          <span class='fl-text' >老师姓名{{$t('Common.colon')}}</span>
          <Input
          	v-model="SearchName"
            style="width: 300px;"
            icon="ios-search"
            @on-enter="getAdminList(SearchName,current)"
            @on-click="getAdminList(SearchName,current)"
          />
        </Form>
      </template>
      <!-- 数据列表 -->
      <template>
        <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :columns="columns" :data="data1"></i-table>
      </template>
      <!-- 分页 -->
      <template>
        <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
      </template>
    </Card>
    <!-- 删除课件 -->
    <Modal
      v-model="isOverconfirm"
      :closable="false" >
      <div slot="header">温馨提示</div>
      <div class="type-content">
        <div class="input-box">
          你确定要删除该老师的所有课件吗？
        </div>
      </div>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="deleteFile()" > 确定</Button>
        <!--取消-->
        <Button @click="isOverconfirm = false;"> 取消 </Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  import {Card, Form, Input, Button, Table,Switch,FormItem, Modal, Row, Col} from 'iview';
  import pagination from "@/components/common/pagination";
  import Server from "@/service/request";
  import Path from "@/service/Path";
  export default {
    name: "PersonList",
    components: {
      Card,
      Form,
      FormItem,
      Input,
      Button,
      "i-table": Table,
      "i-switch": Switch,
      pagination,
      Server,
      Path,
      Modal,
      Col,
      Row
    },
    data() {
      return {
        total: 0, // 记录总条数
        display: 20, // 每页显示条数
        current: 1, // 当前的页数
        SearchName: '',
        InputText: "",
        DeleteModal:false,
        isOverconfirm: false,
        claID:'',
        columns: [
          {
            title: "编号",
            align: "center",
            key: 'teacherid'
          },
          {
            title: "老师姓名",
            align: "center",
            key: 'teachername'
          },
          {
            title: "操作",
            align: "center",
            render: (h, params, index) => {
              return h('div', [
                h('a', {
                  style: {
                    cursor: 'pointer',
                    color: 'rgb(255, 185, 50)',
                    display: 'inline-block',
                    marginRight: '10px'
                  },
                  on: {
                    'click': () => {
                      this.$router.push({
                        path: '/PersonListView',
                        query: {
                          filetype:1,
                          teacherid: params.row.teacherid,
                          from: 'MaterialManagement'
                        }
                      });
                    }
                  },
                  attrs: {
                    class: 'tablebtn blue',
                  }
                }, "查看"),
                // h('a', {
                //   style: {
                //     cursor: 'pointer',
                //     color: 'red',
                //     display: 'inline-block',
                //     marginRight: '10px'
                //   },
                //   on: {
                //     'click': () => {
                //         this.claID = params.row.fileid;
                //       	this.showOverModal();
                //     }
                //   },
                //   attrs: {
                //     class: 'tablebtn blue',
                //   }
                // }, "删除")
              ])
            }
          }
        ],
        data1: []
      }
    },
    created() {

       this.getAdminList(null,this.current);

    },
    methods: {
      showOverModal() {
        this.isOverconfirm = true;
      },
      //删除课件
      deleteFile() {
        Server.post(Path.deleteFile, {
          fileid: this.claID,
          // teacherid: ''
        }, (res) => {
        	//console.log(res)
          if(res.code === 0) {
            this.$Message.success(res.info);
            this.getAdminList(null,this.current,1);
            this.isOverconfirm = false;
          }else{
            this.$Message.error(res.info);
          }
        });
      },
     	getAdminList(showname, page = 1) {
        Server.post(Path.getFileTeacher, {
        "teachername": showname,
        "pagenum": page,
        }, (res) => {
        	//console.log(res)
          let {code, data} = res;
          if (code === 0) {
            this.data1 = [];
            let {pagenum, pagesize, total} = data.pageinfo;
            this.total = total;
            this.display = pagenum;
            this.current = pagesize;
            this.data1 = res.data.data;

            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        })
      },
      pagechange() {
     	this.current = currentPage;
      this.loading = true;
      this.getAdminList(null,this.current,1);
      }
    }
  }
</script>

<style lang="less" scoped>
  @import "~@/assets/less/tab.less";
</style>



