<template>
  <div>
    <panel-title :crumbs="crumbs" />
    <template>
        <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
        <template>
          <!--新建分组 -->
          <Button type="primary"  @click="addGroup()">新建分组</Button>
        </template>
        <!-- 权限组列表 -->
        <template>
          <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns" :data="datalist"></i-table>
        </template>
      </Card>
    </template>
    
    <Modal
    v-model="isShowDelModal"
    :closable="false" >
    <div slot="header">温馨提示</div>
    <div class="type-content">
      <div class="input-box">
        删除分组后，该分组下的所有账号将不可用，您确定要删除该分组吗？
      </div>
    </div>
    <div slot="footer">
      <!--确认-->
      <Button type="primary" @click="deleteGroup"> {{$t('Common.sure')}}</Button>
      <!--取消-->
      <Button @click="isShowDelModal = false"> {{$t('Common.cancel')}}</Button>
    </div>
  </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Table, Switch, Modal, Button, Row, Col } from "iview";
import Path from '@/service/Path';
import Server from '@/service/request';
export default {
  name: "",
  components: {
  	panelTitle,
  	Card, 
  	pagination,
  	"i-table": Table,
  	"i-switch": Switch,
  	Modal,
    Button,
    Row,
    Col
  },
  data(){
    return {
      crumbs:[{name:'权限组',route:''}],
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      loading:true,
      isShowDelModal: false,
      SearchName:'',
      InputText: "",
      editid: '',
      columns: [
        {
          title: "编号",
          align: "center",
          key: "id"
        },
        {
          title: "分组名",
          align: "center",
          key: "name"
        },
        {
          title: "添加日期",
          align: "center",
          key: "addtime"
        },
        {
          title: "可访问菜单",
          align: "center",
          key: "titleinfo"
        },
        {
          title: "操作",
          align: "center",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                    to:'/AddGroup?id='+params.row.id+'&from=RightsGroup&name='+params.row.name
                },
                style: {
                  cursor:'pointer',
                  color: '#2693ff',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'编辑'),
              h('div',{
                on: {
                  'click': () => {
                    this.isShowDelModal = true;
                    this.editid = params.row.id;
                  }
                },
                style: {
                  cursor:'pointer',
                  color: 'red',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn red'
                }
              },'删除')
            ])
          }
        }
      ],
      datalist:[]
    }
  },
  created() {
  	this.getUserGroup();
  },
  methods: {
    //权限组数据列表
    getUserGroup(){
      Server.post(Path.getUserGroup,{
        'pagenum': 1
      },(res) => {
        if(res.code == 0){
          this.datalist = res.data.data;
        }else{
          this.$Message.error(res.info);
        }  
      })
    },
    //添加权限组
    addGroup() {
      this.$router.push('/AddGroup?from=RightsGroup');
    },
    //删除权限组
    deleteGroup() {
      Server.post(Path.deleteGroup, {
        id: this.editid
      }, (res) => {
        if(res.code === 0) {
          this.getUserGroup();
          this.isShowDelModal = false;
        }else{
          this.$Message.error(res.info);
        }
      });
    }
  }
	
   
  
}
</script>
<style lang="less" scoped>
@import "~@/assets/less/tab.less";

</style>



