<template>
  <div id="Course">
     <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="mytheme-mg-top-20">
          <!--学生账号-->
          <span>学生账号{{$t('Common.colon')}}</span>
          <!--学生账号-->
          <Input
            v-model.trim="InputText"
            placeholder="请输入学生账号"
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>
          <!--学生昵称-->
          <span class="mytheme-mg-left-70">学生昵称{{$t('Common.colon')}}</span>
          <!--学生昵称-->
          <Input
            v-model.trim="nickname"
            placeholder="请输入学生昵称"
            @on-enter="getStuList(InputText, nickname, current)"
           class="input-normal-width input-max-height"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getStuList(InputText, nickname, current)">搜索</Button>
      </div>

      <!-- 超级作文的学生列表 -->
      <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe ref="selection" :loading="loading" :columns="columns1" :data="data1"></i-table>
      
       <!-- 分页 -->
      <pagination :total="total" :display="display" class="mg-top-20" :current-page='current' @pagechange="pagechange" v-if='total>0'></pagination>
   </Card>

  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import Server from "@/service/request";
import Path from "@/service/Path";
import {
  Card,
  Input,
  Table,
  Switch,
  Button,
  Form,
  Row,
  Col,
  FormItem,
  Checkbox,
  Modal
} from "iview";
export default {
  name: "",
  components: {
    panelTitle,
    pagination,
    Card,
    Input,
    "i-table": Table,
    "i-switch": Switch,
    Button,
    Form,
    Row,
    Col,
    FormItem,
    Checkbox,
    Modal
  },
  created() {
    this.getStuList(this.mobile, this.nickname, this.current);
  },
  watch: {
  },
  methods: {
    //获取学生列表
    getStuList(mobile, nickname, page = 1) {
      Server.post(
        Path.getHjxUserList,
        {
          mobile: mobile,
          nickname: nickname,
          pagenum: page
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            let { pagenum, pagesize, total } = data.pageinfo;
            this.total = total;
            this.display = pagesize;
            this.current = Number(pagenum);
            this.data1 = data.data;
            this.loading = false;
          } else {
            this.data1 = [];
            this.loading = false;
          }
        }
      );
    },
    //分页
    pagechange(currentPage) {
      this.getStuList(this.mobile, this.nickname, currentPage);
    },
  },
  data() {
    return {
      // mobile: "",
      nickname: "",//学生昵称
      InputText: "",//学生账号
      loading: true,//加载中
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      studentids:[],//选中的学生ID
      columns1: [
        {
          title: "学生ID", //学生ID
          align: "center",
          key: "id"
        },
        {
          title: "学生账号", //学生账号
          key: "mobile",
          align: "center"
        },
        {
          title: "学生昵称", //学生昵称
          align: "center",
          key: "nickname"
        },
        {
          title: this.$t("studentManagepage.sex"), //性别
          align: "center",
          key: "sex",
          render: (h, params, index) => {
            let Sex = params.row.sex;
            switch (Sex) {
              case 0:
                Sex = this.$t("teacherDetails.unknow"); //保密
                break;
              case 1:
                Sex = this.$t("teacherDetails.male"); //男
                break;
              case 2:
                Sex = this.$t("teacherDetails.female"); //女
                break;
            }
            return h("div", {}, Sex);
          }
        },
        {
          title: "学校", //学校
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.school || "---");
          }
        },
        {
          title: "年级", //年级
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.grade || "---");
          }
        },
        {
          title: "班级", //班级
          align: "center",
          key: "categoryname",
          render: (h, params) => {
            return h("div", params.row.class || "---");
          }
        },
        {
          title: "最后登录时间", 
          key: "logintime",
          align: "center"
        },
        {
          title: "帐户启用状态",
          align: "center",
          key: "status",
          render: (h, params, index) => {
            return h("div", [
              h("i-switch", {
                on: {
                  "on-change": value => {
                    Server.post(
                      Path.changeHjxUserStatus,
                      {
                        userid: params.row.id,
                        flag: !Number(value)
                      },
                      res => {
                        let { code, data } = res;
                        if (code === 0) {
                          this.$Message.info(res.info);
                        } else {
                          this.$Message.error(res.info);
                        }
                      }
                    );
                  }
                },
                props: {//0开启1关闭
                  value: !Boolean(params.row.status)
                }
              })
            ]);
          }
        }
      ],
      data1: []
    };
  }
};
</script>
<style scoped lang='less'>
</style>



