<template>
  <div>
    <panel-title :crumbs="[
      {
        name:'精彩瞬间',
        route:{path:'/SplendidMoment',query:{from:'TeachingAffairs'}}
      },
      {name:'精彩瞬间详情',route:''}
      ]" />
     <Card dis-hover class='br8 cardP0 mg-bottom-20'>
      <Form class="mg-top-20 type-content">
            <Row>
              <Col offset="2">
                  <!-- 课程名称 -->
                  <FormItem label="课程名称：">
                    <p class="textContent">小学三年级语文</p>
                  </FormItem>
              </Col>
            </Row>
            <Row>
              <Col offset="2">
                  <!-- 班级名称 -->
                  <FormItem label="班级名称：">
                    <p class="textContent">考前冲刺班</p>
                  </FormItem>
              </Col>
            </Row>
            <Row>
              <Col offset="2">
                  <!-- 课时名称 -->
                  <FormItem label="课程名称：">
                    <p class="textContent">课后习题精讲</p>
                  </FormItem>
              </Col>
            </Row>
            <Row>
              <Col offset="2">
                   <!-- 精彩瞬间 -->
                  <FormItem label="精彩瞬间：">
                  <div style="width:80%;">
                    <div class="playArea" v-if="playFlag">
                        <div class="nonPlay">
                            <img src="@/assets/images/replay@2x.png" alt="">
                        </div>
                        <div class="playText">课程回放制作中</div>
                    </div>
                    <div  v-else class="haveReplay">
                        <div style="display: inline-block;width: 100%;float:left;position:relative">
                            <img :src= stop class="stopimg" @click="play" v-show="imgflag">
                            <img :src="defaultCover" alt="" style="width:100%;height:99%;position:absolute" v-show="imgflag">
                            <iframe name ="video" :src="playpath" width="100%" height="433px" frameborder="0" style="border:1px #CFDCE7 solid"></iframe>
                        </div>
                      <!--<div class="menuList">
                            <div class="dash"></div>
                            <Menu :theme="theme3" active-name="1" :active-key="index" v-for="(item,index) in rePlayData.video" :key="index">
                                <span @click="videoShow(index)" class="listTest" :class="{'replaySpan': index===checkindex}">
                                    <span class="littleCircle" :class="{'replay-active': index ===checkindex }">●</span>
                                    片段
                                    <span style="margin-left: 10px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{$t('Main.LessonPlay.Part')}}{{index+1}}</span> 
                                    <span style="margin-left:20px;" class="playTitle" :class="{'replay-active': index ===checkindex }">{{item.duration}}</span>
                                </span>
                            </Menu>
                        </div>-->
                    </div>
                  </div>
                  </FormItem>
              </Col>
            </Row>
        </Form>
    </Card>
  </div>
</template>
<script>
import { Card,Form,Row,FormItem,Col} from "iview";
import panelTitle from "@/components/panelTitle";
import stop from '@/assets/images/iconstop.png';
import defaultCover from "@/assets/images/bg.png";
export default {
  name: "splendidDetail",
  components: {panelTitle,Card,Form,Row,FormItem,Col},
  data(){
    return {
     playFlag:false,
     imgflag: true,
     playpath:'',
     rePlayData:{},
     defaultCover,
     stop,
    }
  },
  methods: {
      play(){
      this.checkindex = 0;
      this.playpath =this.rePlayData.video[0].playpath
      this.imgflag =!this.imgflag
    },
  },
  created() {
  
  },
 mounted(){
     
 },
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
.textContent{
  margin-top:4px;
  font-size:16px;
  padding-left:104px;
  padding-right:40px;
  word-break:break-all;
}
.playArea{
  width: 900px;
  height: 354px;
  margin: 56px auto;
  background-color: #F3F3F3;
  line-height: 354px;
  position: relative;
}
.nonPlay{
  width: 116px;
  height: 180px;
  margin: 0 auto;
  padding-top: 52px;
}
.playText{
  position: absolute;
  left: 395px;
  top: 73px;
  color: #BFBFBF;
}
.haveReplay{
  width:900px;
  margin: 0 auto;
  margin-top: 33px;
  height: 433px;
}
.stopimg{
  position:  absolute;
  // top:144px;
  // left: 290px;
  z-index: 1;
  width: 72px !important;
  height: 72px !important;
  cursor: pointer;
  margin-left: 50%;
  margin-top: 25%;
}
.menuList{
  display: inline-block;
  float:left;
  width: 18%;
  height: 630px;
  background: #F3F3F3;
  border: 1px solid #CFDCE7;
  border-left: none;
  box-sizing: border-box;
  margin-top: 0px;
}
.dash{
  width: 1px;
  height: 626px;
  border-left: 1px dashed #CFDCE7;
  position: absolute;
  margin-left: 23px;
}
.listTest{
  height: 34px;
  line-height: 34px;
  display: inline-block;
  font-size:15px;
  width: 100%;
  border-left: 2px solid #F3F3F3;
}   
.listTest:hover{
  cursor: pointer;
}
.replaySpan{
  border-left: 2px solid #387BFF;
  background-color: #fff;
}
.littleCircle{
  margin-left: 15px;
  font-size: 19px;
  color: #CFDCE7;
  position: relative;
}
.replay-active {
  color: #387BFF;
  /* background-color: #fff; */
}
.playTitle{
  color:#8E9DAA;
}
</style>



