<template>
  <div id="">
    <panel-title :crumbs="crumbs"></panel-title>
    <Card dis-hover class="br8">
      <Form :label-width="130" ref='formData' :model="formData" :rules="ruleCustom"  class="mg-top-20">
        <Row>
          <i-Col span="21" offset="1">
            <FormItem label="用户昵称：" prop="UserName">
              <Input placeholder="请输入用户昵称,不超过30个字符" :maxlength="30" v-model.trim="formData.UserName" class="grInput"/>
            </FormItem>
          </i-Col>
          <i-Col span="21" offset="1">
            <FormItem label="用户名：" prop="useraccount">
              <Input placeholder="请输入用户名" v-model.trim="formData.useraccount" class="grInput"/>
            </FormItem>
          </i-Col>
          <i-Col span="21" offset="1">
            <FormItem label="角色：" prop="userGroup">
              <i-select v-model="formData.userGroup" style="width: 200px; border:1px solid #dddee1; border-radius: 4px;" class="grInput">
                <i-option v-for="(role, index) in userGroupList" :key="role.id" :value="role.id">{{role.name}}</i-option>
              </i-select>
            </FormItem>
          </i-Col>
          <i-Col span="21" offset="1">
            <FormItem label="手机号：" prop="mobile">
              <Input placeholder="请输入手机号" v-model="formData.mobile" class="grInput" :maxlength="11"/>
            </FormItem>
          </i-Col>
          <template v-if ='add'>
            <i-Col span="21" offset="1" >
              <FormItem label="登录密码：" prop="LoginPassWord">
                <Input placeholder="请输入登录密码" type="password" v-model="formData.LoginPassWord" class="grInput"/>
                <!-- <Col span="5" style="margin-left:5px; font-size:12px; color:#ccc">
                  默认不修改密码
                </i-Col> -->
              </FormItem>
            </i-Col>
            <i-Col span="21" offset="1" >
              <FormItem label="确认密码：" prop="ReLoginPassWord">
                <Input placeholder="请输入确认密码"  type="password" v-model="formData.ReLoginPassWord" class="grInput"/>
              </FormItem>
            </i-Col>
          </template>
          <template v-else>
            <i-Col span="21" offset="1" >
              <FormItem label="登录密码：">
                <Input placeholder="请重置登录密码" type="password" v-model="formData.LoginPassWord" class="grInput"/>
              </FormItem>
            </i-Col>
            <i-Col span="21" offset="1" >
              <FormItem label="确认密码：">
                <Input placeholder="请重置确认密码"  type="password" v-model="formData.ReLoginPassWord" class="grInput" />
              </FormItem>
            </i-Col>
          </template>
          <i-Col span="21" offset="1">
          	<FormItem label="" class="btnPad">
           	 	<Button style="margin-right: 20px;" @click="goback">取消</Button>
           	 	<Button type="primary" @click="handleSubmit('formData')">提交</Button>
            </FormItem>
          </i-Col>
        </Row>
      </Form>
    </Card>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import { Card, Input, Button, Icon, Form, Row, Col, FormItem, Select,Option} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    Button,
    Icon,
    Form,
    Row,
    "i-select": Select,
    "i-option": Option,
    'i-Col':Col,
    FormItem
  },
  created() {
    let  id  = this.$route.query.id;
    if(id){
      this.InitData(id);
      this.isEdit = false;
      this.msg = "编辑成员";
      this.crumbs[1].name = '编辑成员';
      this.add = false;
    }else{
      this.isEdit = true;
      this.msg = "添加成员";
      this.crumbs[1].name = '添加成员';
      this.add = true;
    }
    this.getAllUserGroup();
  },
  methods: {
    //获取成员组
    getAllUserGroup() {
      Server.post(Path.getAllUserGroup,{

      }, res => {
        if(res.code == 0) {
          console.log(res.data);
          this.userGroupList = res.data;
        } else{
          this.userGroupList = []
        }
      })
    },
    //查看单个管理员信息
    InitData(id){
      Server.post(Path.getAdminUser,{
        adminid:id
      },res=>{
        let {code,data}=res;
        if(code===0){
          this.formData.UserName = data.adminname;
          this.formData.useraccount = data.username ;
          this.formData.userGroup = data.groupids;
          this.formData.mobile = data.mobile;
          this.formData.Info = data.info;
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    //取消
    goback(){
      this.$router.push('/RightsManagement?from=MainRightManagement');
    },
    //表单验证
    handleSubmit (name) {
    //	console.log( this.$refs[name])
      this.$refs[name].validate((valid) => {
      	console.log(valid)
        if (valid) {
          this.SaveData();
        } else {
          // this.$Message.error('表单验证失败!');
        }
      })
    },
    //保存信息
    SaveData() {
      let {
        UserName,//登录名称
        useraccount,//用户名称
        mobile,
        LoginPassWord,
        ReLoginPassWord,
        Info,
        userGroup
      } = this.formData;
      let Log = this.$Message;
      let  id  = this.$route.query.id;
      //添加接口
      if(!id){
        Server.post(Path.addAdminUser,
          {
            username: UserName,
            useraccount:useraccount,
            mobile: mobile,
            password:LoginPassWord,
            repassword: ReLoginPassWord,
            info: Info,
            groupids: userGroup
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.$Message.success(res.info);
              this.formData.UserName = '';
              this.formData.userGroup = '';
              this.formData.useraccount = '';
              this.formData.mobile = '';
              this.formData.LoginPassWord = '';
              this.formData.ReLoginPassWord = '';
              this.formData.Info = '';
              this.$router.go(-1);
            } else {
              this.$Message.error(res.info);
            }
          }
        );
      }
      //编辑接口
      if(id){
        Server.post(Path.updateAdminUser,
        {
          	adminid:id,
            username: UserName,//登录名
            useraccount:useraccount,//用户名
            groupids: userGroup,
            mobile: mobile,
            password:LoginPassWord,
            repassword: ReLoginPassWord,
            info: Info
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.$Message.success(res.info);
            this.formData.UserName = '';
            this.formData.useraccount = '';
            this.formData.mobile = '';
            this.formData.LoginPassWord = '';
            this.formData.ReLoginPassWord = '';
            this.formData.Info = '';
            this.$router.go(-1);
          } else {
            this.$Message.error(res.info);
          }
        }
      );
     }
    }
  },
  data() {
    const validateuser = (rule, value, callback) => {
      let userRegex = /^[a-zA-Z0-9]*$/;
      if (value === '') {
        callback(new Error('请输入用户名'));
      } else {
        if (!userRegex.test(value)) {
          callback(new Error('请输入英文,字母,数字!'))
        }else {
          callback();
        }
      }
    };
    const validatePass = (rule, value, callback) => {
      let PasswordRegex = /^[^ ](\w){5,15}$/;
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.formData.ReLoginPassWord != '') {
          this.$refs.formData.validateField('ReLoginPassWord');
        }
        if (!PasswordRegex.test(value)) {
          callback(new Error('请输入6-16位新密码,字母、数字、下划线'))
        }else {
          callback();
        }
      }
    };
    const validatePassCheck = (rule, value, callback) => {
      let PasswordRegex = /^[^ ](\w){5,15}$/;
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.formData.LoginPassWord) {
        callback(new Error('两次输入密码不一致!'));
      }else if (!PasswordRegex.test(value)) {
        callback(new Error('请输入6-16位新密码,字母、数字、下划线'))
      }else {
        callback();
      }
    };
    const MobileRegex = (rule, value, callback) => {
      let MobileRegex = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
      if (value === '') {
        callback(new Error('请输入手机号'));
      } else if (!MobileRegex.test(value)) {
        callback(new Error('请输入正确的11位手机号码'))
      } else {
        callback();
      }
    };
    return {
      add:true,
      crumbs: [
        {name: '成员管理', route: '/RightsManagement?from=MainRightManagement'},
        {name: '添加成员', route: '/AddMembers?from=MainRightManagement'},
      ],
      userGroupList: [],
      formData: {
        UserName: "",
        useraccount: "",
        userGroup: "",
        mobile: "",
        LoginPassWord: "",
        ReLoginPassWord: "",
        Info: "",
        msg:''
      },

      ruleCustom: {
        UserName: [
          { required: true, message: '请输入用户昵称', trigger: 'blur' }
        ],
        useraccount: [
          { required: true, validator: validateuser, trigger: 'blur' }
        ],
        userGroup: [{
          required: true, message: '请选择角色'
        }],
        mobile: [
          { required: true, validator: MobileRegex, trigger: 'blur' },
        ],
        LoginPassWord: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        ReLoginPassWord: [
          { required: true, validator: validatePassCheck, trigger: 'blur' }
        ],
      }
    };
  }
};
</script>
<style scoped lang = "less">
	
.ivu-input-wrapper{
  width: 50%;
}
.btn {
  width: 120px;
  line-height: 40px;
  text-align: center;
  border-radius: 40px;
  cursor: pointer;
  margin: 20px 0;
}
.savebtn {
  background: #469af8;
  color: #fff;
  box-shadow: 0 0 12px #469af8;
}
.mgSelect{
		display: block;
    width: 50%;
    height: 32px;
    /*line-height: 1.5;*/
    /*padding: 4px 7px;*/
    font-size: 12px;
    border: 1px solid #dddee1;
    border-radius: 4px;
    color: #495060;
    overflow: hidden;
    /*background-color: #fff;*/
    /*position: relative;*/
    cursor: pointer;
   }
   .grInput{
	width: 25%;
}
</style>



