<template>
  <div id="LessonDetails">
    <panel-title :crumbs="[
      {
        name:$t('courseTablepage.courseTablepage'),//机构课表
        route:'/home',
      },{
        name:$t('calendarSchedule.subtitle'),//课程列表
        route:{
          path:'/MainLessonList',
          query:{
            from:'MainIndex',
            date:this.$route.query.dateCur,
          }
        }
      },
      {
        name:$t('LessonDetail.subtitle'),//课程详情
        route:'',
      }
    ]" />
    <Card class='br8 cardP0 mg-bottom-20' dis-hover>
      <Row>
        <Col span="24">
          <div class="evaluation-box">
            <div class="evaluation-item">
              <div class="eval-header-img fl">
                <!--课程封页-->
                <img :src="lessonInfo.listPeriodinfo.courseimage || defaultCover" @error="_imageError">
              </div>
              <div class="eval-content">
                <ul>
                  <li>
                    <!--课程名称-->
                    <div class="fl eval-name">
                      {{lessonInfo.listPeriodinfo.coursename}}
                    </div>
                  </li>
                  <li>
                    <!--开始时间：-->
                    <div class="fl eval-time mr67">
                      <!-- 开始时间： -->
                      {{$t('calendarSchedule.starttime')}}{{$t('Common.colon')}}{{starttime}}
                    </div>
                    <!--结束时间：-->
                    <div class="fl eval-time mr67">
                     <!-- 结束时间： -->
                     {{$t('calendarSchedule.endtime')}}{{$t('Common.colon')}}{{endtime}}
                    </div>
                  </li>
                  <li>
                    <!--班级名称：-->
                     <div class="fl mr67">
                      {{$t('calendarSchedule.periodname')}}{{$t('Common.colon')}}{{lessonInfo.listPeriodinfo.gradename}}
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Card>
    <Card class='br8 cardP0 mg-bottom-20 lesson-wrapper' dis-hover>
      <!--课时详情-->
      <div class="lessonMessage">
        <!-- 课时详情 -->
        <span>{{$t('LessonDetail.periodtitle')}}</span>
      </div>

      <!--课程简介-->
      <template>
        <div class="lessonInfo">
          <img src="./assets/icon-lessoninfo.png" alt="">
          <span>{{$t('LessonDetail.curriculum')}}</span>
        </div>
        <!--课程名称：-->
        <p class="lessonName">
          <!-- 课程名称： -->
          {{$t('LessonDetail.coursetitle')}}{{$t('Common.colon')}}{{lessonInfo.listPeriodinfo.coursename}}
        </p>
      </template>

      <!-- 课件列表 -->
      <template>
        <div class="lessonInfo">
          <img src="./assets/icon_lessonlist.png" alt="">
          <span>
            <!-- 课件列表 -->
            {{$t('LessonDetail.Courseware')}} 
          </span>
          <!-- <template v-if="isChangeFile">
            <a class="fr CoursewareEditBtn" v-if="isShowEditBtn" @click="isShowEditBtn = false">
               <!-- 编辑  
              {{$t('Common.edit')}}
            </a>
            <a class="fr CoursewareEditBtn" v-else @click="isShowEditBtn = true">
              <!-- 保存  
              {{$t('Common.save')}}
            </a>
          </template> -->
        </div>
        <ul class="lessonDetail">
          <template v-if='lessonInfo.ware.data'>
            <li v-for="(el, i) in lessonInfo.ware.data" :key="i">
              <Icon type="android-arrow-dropright-circle" class="playIcon"></Icon>
              <span>{{el.showname}}</span>
              <!-- 添加课件  -->
              <span class="lessonPPt fr"  v-if="isChangeFile" @click="getDelId(el)">
                <img :src="delIcon">
              </span>
            </li>
          </template>
          <template v-else>
            <li>
              <span>{{$t('Common.nodata')}}</span>
            </li>
          </template>
        </ul>
        <div  v-if="isChangeFile">
          <Button class="fr mg-bottom-20 addppt-btn" @click="showFolderModal">
            <img :src="addIcon">
            <!-- 添加课件  -->
            {{$t('LessonDetail.addCourseware')}}
          </Button>
        </div>
      </template>

      <!--课程回放-->
      <template v-if="!isChangeFile&&ifChrome">
        <div class="lessonInfo" >
          <img src="./assets/icon_replay.png" alt="">
          <span style=" margin-bottom: 20px; display: inline-block;">
            <!-- 课程回放 -->
            {{$t('LessonDetail.replay')}}
          </span>
        </div>
        <div style="width:1280px; height: 400px">
            <div v-if="dataflag" class="novideo">
                <img :src="icon">
                <p>
                  <!-- 课程回放制作中 -->
                  {{$t('LessonDetail.replaytip')}}
                </p>
            </div>
            <div v-else>
                <div style="display: inline-block;width:661.5px;float:left;position: relative;">
                    <img :src= stop class="stopimg" @click="play" v-show="imgflag">
                    <iframe name ="video" :src="playpath|| bgpic" width="100%" height="357px" frameborder="0" style="border:1px #CFDCE7 solid">></iframe>
                </div>
                <div class="menuList">
                    <Menu :theme="theme3" active-name="1" :class="{'replay-active': index ===checkindex }"   :active-key="index" v-for="(item,index) in lessonInfoData.video " :key="index">
                        <span @click="videoShow(index)" class="listTest">
                          <!-- 第{{index+1}}片段 -->
                          <span style="margin-left: 15px;">●</span><span style="margin-left: 15px ">第{{index+1}}片段</span> <span style="margin-left:40px">{{item.duration}}</span>
                        </span>
                    </Menu>
                </div>
            </div>
        </div>
      </template>
      <div class="playArea" v-if="isChangeFile == 'false'&&ifChrome"></div>
      <!--上课详情-->
      <template v-if="!isChangeFile">
        <div class="lessonInfo">
          <img src="./assets/icon_student.png" alt="">
          <span>
            上课详情
          </span>
        </div>
        <template>
          <i-table :columns="columns" :data="studentlist"></i-table>
        </template>
        <!--分页-->
        <pagination :total="total1" :display="display1" :currentPage="current1" @pagechange="pageChange1" v-if="studentlist.length > 0"></pagination>
      </template>
      <!--学生列表-->
      <template v-if="isChangeFile">
        <div class="lessonInfo">
          <img src="./assets/icon_student.png" alt="">
          <span>
            学生列表
          </span>
        </div>
        <template>
          <i-table :columns="columns1" :data="studentData"></i-table>
        </template>
        <!--分页-->
        <pagination :total="total2" :display="display2" :currentPage="current2" @pagechange="pageChange2" v-if="studentData.length > 0"></pagination>
      </template>

      <!--评论列表-->
      <template v-if="!isChangeFile">
        <div class="lessonInfo">
          <img src="./assets/icon_evaluate.png" alt="">
          <span>
            {{$t('LessonDetail.comment')}}
          </span>
        </div>
        <div class="studentEvaluate">
          <span>
            <!-- 评论数量   -->
            {{$t('LessonDetail.commentnum')}}
          </span>
          <!-- 评论数量 -->
          <span class="evaluateCount">（{{commentList.length}}）</span>
        </div>
        <div class="ratting-panel" v-for="(comment, i) in commentList" :key="i">
          <div class="person-avatar">
            <Avatar :src="comment.studentinfo.imageurl" class="avatar"/>
          </div>
          <div class="preson-content">
            <div class="content-main">
              <h3>{{comment.studentinfo.nickname}}</h3>
            </div>
            <p class="content">{{comment.content}}</p>
            <div class="ratting-footer">
              <p class='mr67' style="color: green">
               
                评分{{$t('Common.colon')}}{{ comment.score }}分</p>
              
              <p class="ratting-time">{{comment.addtimestr}}</p>
            </div>
          </div>
        </div>
      </template>
      <!--分页-->
      <pagination :total="total" :display="display" :currentPage="current" @pagechange="pageChange" v-if="commentList.length > 0"></pagination>
    </Card>
    <!--文件夹列表-->
    <Modal v-model="isShowFolderModal" width="800">
      <div slot="header">
        <img :src="PrevIcon" style="width: 10px; cursor: pointer; float: left" @click="isShowFolderWrap = true;" v-if="!isShowFolderWrap">
          <!-- 添加课件 -->
          {{$t('LessonDetail.addCourseware')}}
      </div>
      <div class="type-content" ref="MaterScroll">
        <div class="folder-wrap" v-if="isShowFolderWrap">
          <ul class="folder-list" v-if='folderList.length > 0'>
            <li class="list-item" v-for="(el, i) in folderList" :key="i"
                @click="folderFatherId = el.fileid; getLessonFileList(); ">
              <div class="folder-img">
                <img :src="FolderIcon">
                <!-- 个 -->
                <div class="folder-num">{{el.juniorcount}}{{$t('materialManagepage.unit')}}</div>
              </div>
              <div class="folder-name" title="">{{el.showname}}</div>
            </li>
          </ul>
          <defaultShow v-else :text="$t('Common.nodata')" style='margin:-50px 0'/>
        </div>
        <div class="filelist-wrap" v-else>
          <ul class="file-list" v-if='fileList.length > 0'>
            <li class="list-item" v-for="(el, i) in fileList">
              <Checkbox v-model="el.active" @on-change="checkboxChange"></Checkbox>
              {{el.showname}}
            </li>
          </ul>
          <defaultShow v-else :text="$t('Common.nodata')" style='margin:-50px 0'/>
        </div>
        <!--<pagination :total="folderTotal" :display="folderPageDisplay" :currentPage="folderPageNum" @currentPage="folderPageChange"></pagination>-->
      </div>
      <div slot="footer">
        <!-- 确认 -->
        <Button type="primary" @click="addLessonPpt()"  v-if="!isShowFolderWrap">{{$t('Common.sure')}}</Button>
        <!-- 取消 -->
        <Button @click="isShowFolderModal = false; isShowFolderWrap = true;"  v-if="!isShowFolderWrap">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    <!--确认删除-->
    <Modal v-model="isShowDelFileModal" :closable="false">
      <div slot="header">
        <!-- 温馨提示 -->
        {{$t('Common.kindlyreminder')}}
      </div>
      <div>
        <!-- 确认删除此课件吗？ -->
        {{$t('materialManagepage.remind7')}}
      </div>
      <div slot="footer">
        <!-- 确认 -->
        <Button type="primary" @click="deleteLessonFile()">{{$t('Common.sure')}}</Button>
        <!-- 取消 -->
        <Button @click="isShowDelFileModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal> 
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import CardTitle from "@/components/common/CardTitle/CardTitle";
import right from "@/assets/images/right.png";
// import bookImg from "../assets/bookImg.png";
import stop from "@/assets/images/iconstop.png";
// import Ratting from "@/components/Ratting";
import {
  Card,
  Row,
  Col,
  Button,
  Icon,
  Avatar,
  Rate,
  Modal,
  Checkbox,
  Table
} from "iview";
import Server from "@/service/request";
import requestPath from "@/service/Path";
import defaultCover from "@/assets/images/defaultCover.png";
import addIcon from "./assets/icon-add.png";
import delIcon from "./assets/icon-del.png";
import FolderIcon from "./assets/MaterIcon.png";
import PrevIcon from "./assets/icon-back.png";
import icon from "@/assets/images/icon.png";
import bgpic from "@/assets/images/bg.png";
import defaultShow from '@/components/defaultShow';
export default {
  name: "LessonDetailsEdit",
  components: {
    panelTitle,
    right,
    Card,
    Row,
    Col,
    pagination,
    // bookImg,
    CardTitle,
    Button,
    Icon,
    Avatar,
    // Ratting,
    Rate,
    Server,
    requestPath,
    Modal,
    Checkbox,
    icon,
    bgpic,
    stop,
    defaultShow,
    "i-table": Table
  },
  created() {
    let status = this.$route.query.isChangeFile;
    if (status == 0) {
      //结束
      this.isChangeFile = false;
    } else if (status == 1) {
      //进教室
      this.isChangeFile = true;
    } else if (status == 2) {
      //未开始
      this.isChangeFile = true;
      //this.isbegin = true;
    }

    //do something after creating vue instance
    this.crumbs[0].route = this.$route.fullPath;
    this.getCourseInfo();
    this.getLessonPptFolder();
    
    if (!this.isChangeFile) {
      this.getCourseComment(this.current);
      this.getClassStudent();
    } else if(this.isChangeFile){
      this.getLessonsStudent();
    }
    

    // this.isChangeFile = true;
    // 判断是否是谷歌浏览器
    var ua = navigator.userAgent.toLocaleLowerCase();
    if(ua.match(/tencenttraveler/) != null || ua.match(/qqbrowse/) != null){
      this.ifChrome = false;
    }else if (ua.match(/firefox/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/ubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/opera/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/bidubrowser/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/metasr/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/msie/) != null || ua.match(/trident/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/maxthon/) != null) {
      this.ifChrome = false;
    }else if (ua.match(/chrome/) != null) {
      var is360 = _mime("type", "application/vnd.chromium.remoting-viewer");
      function _mime(option, value) {
        var mimeTypes = navigator.mimeTypes;
        for (var mt in mimeTypes) {
          if (mimeTypes[mt][option] == value) {
            return true;
          }
        }
        return false;
      }
      if(is360){
        this.ifChrome = false;
      }else{
        this.ifChrome = true;
      }
    }else{
      this.ifChrome = false;
    }
  },
  mounted() {
    //元素内滚动加载
    let nScrollHight = 0; //滚动距离总长(注意不是滚动条的长度)
    let nScrollTop = 0; //滚动到的当前位置
    let nDivHight = 500; //元素高度
    let that = this;
    this.$refs.MaterScroll.onscroll = function() {
      nScrollHight = that.$refs.MaterScroll.scrollHeight;
      nScrollTop = that.$refs.MaterScroll.scrollTop;
      if (nScrollTop + nDivHight >= nScrollHight) {
        if (that.flag) {
          that.folderPageNum++;
          that.getLessonPptFolder(that.folderPageNum);
        }
      }
    };
  },
  data() {
    return {
      ifChrome:false,
      defaultCover,
      addIcon,
      delIcon,
      FolderIcon,
      PrevIcon,
      icon,
      bgpic,
      crumbs: [{ name: "课时详情", route: "" }],
      right,
      total: 0,
      current: 1,
      display: 10,
      total1: 0,
      current1: 1,
      display1: 20,
      total2: 0,
      current2: 1,
      display2: 20,
      bookImg:null,
      stop,
      valueCustomText: 4,
      lessonInfo: {
        listPeriodinfo: {
          coursename: null,
          courseimage: null,
          periodname: null,
          type: null,
          gradename: null,
          subhead: null,
          sum: 0
        },
        studentlists: [],
        playback: {
          code: "30311",
          data: null,
          info: this.$t('Common.nodata') //没有数据
        },
        ware: []
      },
      starttime: this.$route.query.starttime,
      endtime: this.$route.query.endtime,
      commentList: [],
      isShowFolderModal: false,
      isShowFolderWrap: true,
      isShowDelFileModal: false,
      isShowEditBtn: true,
      folderPageNum: 1,
      folderPageDisplay: 20,
      folderTotal: 0,
      folderFatherId: 0,
      folderList: [],
      fileList: [],
      idsArr: [],
      fileidArr: [],
      fileArr: [],
      delFileId: 0,
      isChangeFile: false,
      isbegin: false,
      flag: true,
      lessonInfoVideo: "",
      dataflag: true,
      lessonInfoData: "",
      playpath: "",
      videoa: true,
      checkindex: "",
      theme3: "light",
      imgflag: true,
      columns: [{
        title: "编号",
        align: "center",
        key: 'id'
      },{
        title: "姓名",
        align: "center",
        key: 'nickname'
      },{
        title: "角色",
        align: "center",
        key: 'typestr'
      },{
        title: "出勤明细",
        align: "center",
        key: 'attendancestatus',
        render: (h, params, index) => {          
          let _this = this;
          let texts = '';
          if(params.row.attendancestatus == 0){
              texts = "未出勤"
          }else if(params.row.attendancestatus == 1){
              texts = "已出勤"
          }
          return h('div', {  
            props: {
              },
          },texts)
        }       
      }],
      studentlist:[],
      attend:'',
      studentData: [],
      columns1: [
      {
        //编号
        title: "序号",
        align: "center",
        key: "id"
      }, {
      //学生姓名
        title: "学生姓名",
        align: "center",
        key: "nickname"
      }, {
        //学生手机号
        title: "学生手机号",
        align: "center",
        key: "mobile",
        render: (h, params) => {
            return h('div', {}, '+86 ' +params.row.mobile)
          }
      }]
    }
  },
  methods: {
    //转换班型
    changeType(value) {
      if (value == 1) {
        return (value = "录播课");//1对1
      } else if (value == 2) {
        return (value = "直播课");//小班课
      }
		},
    play() {
      this.checkindex = 0;
      this.playpath = this.lessonInfoData.video[0].playpath;
      this.imgflag = false;
    },
    videoShow(index) {
      this.checkindex = index;
      this.playpath = this.lessonInfoData.video[index].playpath;
      this.imgflag = false;
      //console.log(this.lessonInfoData.video[index].playpath)
    },
    imgonerror(e) {
      return (e.target.src = this.bookImg);
    },
    //提交所选的课件
    submitFileList() {},
    showFolderModal() {
      this.isShowFolderModal = true;
    },
    // 文件夹分页
    folderPageChange() {},
    checkboxChange() {
      let arr = [];
      this.fileList.forEach(el => {
        if (el.active === true) {
          arr.push(el.fileid);
          this.fileArr.push(el);
        }
      });
      this.idsArr = arr;
    },
    // 查询课时详情
    getCourseInfo() {
      let { toteachtimeid, id, date, starttime, endtime } = this.$route.query;
      let param = {
        toteachtimeid: this.$route.query.toteachtimeid,
        id: this.$route.query.id,
        starttime: this.$route.query.starttime,
        endtime: this.$route.query.endtime,
        date: this.$route.query.dateCur
      };
      Server.post(
        requestPath.getPeriodinfo,
        {
          toteachtimeid: param.toteachtimeid, //toteachtime表的主键id
          id: param.id, //lessonsid
          starttime: param.starttime, //开始时间
          endtime: param.endtime, //结束时间
          date: param.date //当天日期
        },
        res => {
          this.lessonInfo.listPeriodinfo = res.data.listPeriodinfo;
          this.lessonInfo.studentlists = res.data.studentlists;
          this.lessonInfo.ware = res.data.ware.data;
          this.lessonInfoData = res.data.playback.data;
          if (this.lessonInfoData.length === 0) {
            this.dataflag = true;
          } else {
            if (res.data.playback.data.video.length === 0) {
              this.dataflag = true;
            } else {
              this.dataflag = false;
            }
          }
        }
      );
    },
   // 查询课时详情评论
    getCourseComment(current) {
      let { dateCur } = this.$route.query;
      Server.post(
        requestPath.getComment,
        {
          id: this.$route.query.curriculumid,
          pagenum: current,
          pagesize: this.display,
          lessonsid: this.$route.query.id
        },
        res => {
          if (res.code === 0) {
            this.commentList = res.data.data;
            this.current = res.data.pageinfo.pagenum;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
          }
        }
      );
    },
    //评论分页
    pageChange(currentPage) {
      this.getCourseComment(currentPage);
    },
    //查询课程详情－学生列表
    getClassStudent() {
      Server.post(requestPath.getClassStudent,{
        id: this.$route.query.id
      }, res => {
        let {code, data} = res;
        if(code == 0){
          this.studentlist = data.data;
          this.current1 = data.pageinfo.pagesize;
          this.total1 = data.pageinfo.total;
          this.display1 = data.pageinfo.pagenum;
        }
      })
    },
    //
    //详情学生分页
    pageChange1(currentPage) {
      this.getClassStudent(currentPage);
    },
    //查询课程未开始-学生列表
    getLessonsStudent() {
      Server.post(requestPath.getLessonsStudent, {
        pagenum: this.current2,
        id: this.$route.query.id
      }, res => {
        let { code, data } = res;
        if(code === 0){
          this.studentData = data.data;
          this.current2 = data.pageinfo.pagenum;
          this.display2 = data.pageinfo.pagesize;
          this.total2 = data.pageinfo.total;
        }else {
          this.$Message.error(info);
        }
      })
    },
    //详情学生分页
    pageChange2(currentPage) {
      this.getLessonsStudent(currentPage);
    },
    // 查询课件文件夹
    getLessonPptFolder(folderPageNum = 1) {
      let { folderFatherId } = this;
      let { teacherid } = this.$route.query;
      Server.post(
        requestPath.tch_getFileList,
        {
          showname: "",
          pagenum: folderPageNum,
          fatherid: folderFatherId,
          teacherid: teacherid
        },
        res => {
          let { code, data } = res;
          if (code === 0 && data !== null) {
            if (folderPageNum == 1) {
              this.folderList = [...data.data];
            } else {
              this.folderList = [...this.folderList, ...data.data];
            }
            if (data.data.length < data.pageinfo.pagesize) {
              this.flag = false;
            }
          } else {
            this.flag = false;
          }
        }
      );
    },
    //查询资源列表
    getLessonFileList(folderPageNum = 1) {
      let { folderFatherId } = this;
      let { teacherid } = this.$route.query;
      Server.post(
        requestPath.tch_getFileList,
        {
          showname: "",
          pagenum: folderPageNum,
          fatherid: folderFatherId,
          teacherid: teacherid
        },
        res => {
          let { code, data } = res;
          if (code === 0 && data !== null) {
            this.isShowFolderWrap = false;
            this.folderPageDisplay = data.pageinfo.pagesize;
            this.folderPageNum = data.pageinfo.pagenum;
            this.folderTotal = data.pageinfo.total;
            this.fileList = data.data;
            this.fileList.forEach(el => {
              this.$set(el, "active");
            });
          }
        }
      );
    },
    // 添加课件
    addLessonPpt() {
      if (this.idsArr.length === 0) {
        return;
      }
      Server.post(
        requestPath.addWarefile,
        {
          id: this.$route.query.id, //lessons主键
          fileid: this.idsArr //Filemanage表的主键
        },
        res => {
          let { code, data } = res;
          if (code === 0 && data !== null) {
            this.getCourseInfo();
            if (!this.isChangeFile) {
              this.getCourseComment(this.current);
            }
            this.isShowFolderModal = false;
          } else {
            this.$Message.info(res.info);
          }
        }
      );
    },
    //获取要删除的id
    getDelId(el) {
      this.delFileId = el.fileid;
      this.isShowDelFileModal = true;
    },
    //删除课件
    deleteLessonFile() {
      Server.post(
        requestPath.delWarefile,
        {
          id: this.$route.query.id,
          fileid: [this.delFileId]
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.isShowDelFileModal = false;
            this.getCourseInfo();
            if (!this.isChangeFile) {
              this.getCourseComment(this.current);
            }
          } else {
            this.$Message.info(res.info);
          }
        }
      );
    }
    
  },
  watch: {
    isShowFolderModal(val) {
      if (!val) {
        this.isShowFolderWrap = true;
      }
    }
  }
};
</script>
