      font-size: 16px;
      text-align: center;
      color: @fz-Color1;
    }
  }
}
// 评价
.ratting-panel {
  padding: 30px 40px 25px;
  border: 1px solid #eee;
  background: rgba(255,255,255,1);
  border-radius: 10px;
  box-shadow: 0px 6px 10px rgba(140,157,158,0.16);
  .person-avatar {
    float: left;
    height: 100%;
