<template>
  <div>
    <!--$t('classifyManagepage.classifyManagepage')-->
    <panel-title
      :crumbs="[
        {
          name:'知识配置',
          route:{
            name:'KnowledgeConfig',
            query:{from:'TeachingAffairs'}
          }
        },
        {
          name:'背景图', //
          route:'',
        },
      ]"
    />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
      <div class="head">
        <img :src="backgroundImage">
        <span>背景图</span>
      </div>
      <!--背景图片展示-->
     <div class="demo-upload-list" v-for="item in imgArr" :key="item.id">
          <img :src="'http://'+item.imageurl">
          <div class="demo-upload-list-cover">
              <Icon class="del" type="ios-trash-outline" @click.native="handleRemove(item.id)"></Icon>
          </div>
      </div>
    </Card>
    <!--删除背景图模块框-->
    <Modal
      v-model="deleteItemModal"
     :closable="false">
        <div slot="header">温馨提示</div>
        <p> 您确定要删除这张背景图片吗？</p>
      <div slot="footer">
        <!--确认-->
        <Button type="primary" @click="okDelete">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button @click="deleteItemModal = false;">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import backgroundImage from '../img/background-image@2x.png';
import Server from "@/service/request";
import Path from "@/service/Path";
import { Card,Icon,Modal,Button} from "iview";
export default {
  name: "",
  components: {panelTitle,pagination,Card,Icon,Modal,Button},
   data(){
    return {
      backgroundImage,
      imgArr:[],
      deleteItemModal:false,
      delId:'',
    }
  },
  methods: {
    //签到背景图列表
    getSigninbgiList () {
       Server.post(Path.getSigninbgiList,{},
        res => {
            if (res.code === 0) {
                this.imgArr = res.data.data;
            }else{
              this.imgArr=[]
            }
        }
        )
    },
    //弹出删除框
    handleRemove (id) {
         this.deleteItemModal=true;
         this.delId=id;
    },
    //删除签到背景图
    okDelete () {
         Server.post(Path.deleteSignInBgi,{
           id:this.delId
         },
        res => {
            if (res.code === 0) {
                 this.$Message.success(res.info);
                 this.getSigninbgiList()
                  this.deleteItemModal=false;
            }
        }
        )
    },
  },
  created() {
    this.getSigninbgiList() 
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>
.head{
    display: flex;
    align-items: center;
    margin:9px 0 50px;
    padding-bottom:20px;
    border-bottom: solid 1px #E6E6E6; 
    img{
      width: 18px;
      height: 18px;
      margin-right: 20px;
    }
    span{
      font-size:16px;
      font-family:MicrosoftYaHei-Bold;
      font-weight:bold;
      color:#666;
    }
  }
//背景图片
.demo-upload-list{
        display: inline-block;
        width: 192px;
        height: 280px;
        text-align: center;
        line-height: 60px;
        border: 1px solid transparent;
        // border-radius: 4px;
        overflow: hidden;
        background: #fff;
        position: relative;
        // box-shadow: 0 1px 1px rgba(0,0,0,.2);
        margin-right: 20px;
        margin-bottom: 20px;
    }
    .demo-upload-list img{
        width: 100%;
        height: 100%;
    }
    .demo-upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
    }
    .demo-upload-list:hover .demo-upload-list-cover{
        display: block;
    }
    .demo-upload-list-cover i{
        color: #fff;
        font-size: 20px;
        cursor: pointer;
        margin: 0 2px;
    }
    .del{
      line-height: 280px;
    }
</style>



