<template>
  <div>
   <panel-title :crumbs="[
      {
        name:'作业管理',
        route:{path:'/homework',query:{from:'TeachingAffairs'}}
      },
      {
        name:'未批阅',
        route:{path:'/homework',query:{from:'TeachingAffairs'}}
      },
      {
        name:'查看详情',
        route:''
      },
    ]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
       <div class="mytheme-mg-top-20">
        <!--班级/课程名称-->
        <span>学生姓名：</span>
        <!--输入关键字 -->
        <Input 
              class="input-max-height input-normal-width"
               @on-enter="getStudentHomeworkList( current, studentName)"
               v-model="studentName" placeholder="学生姓名"/>

          <Button type="primary" class="mytheme-mg-left-70" @click="getStudentHomeworkList(current, studentName)">搜索</Button>

       </div>
          <!--年级题库列表-->
          <i-table class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="colData" :data="tabData"></i-table>
          <!--分页-->
          <pagination  class="mg-top-20" v-if='total>0' :total="total" :display="display" :currentPage="current" @pagechange="pagechange">
          </pagination>
    </Card>
  </div>
</template>
<script>
import { Card, Input, Table, Button} from "iview";
import panelTitle from "@/components/panelTitle";
import pagination from '@/components/common/pagination';
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  name: "studentsHomework",
  components: {panelTitle,Card,Input,"i-table": Table,Button,pagination},
   data(){
    return {
      studentName:'',
     colData:[{
              title: '编号',
              key: 'id',
             align: 'center'
          },{
            title: '学员姓名',
              key: 'nickname',
              align: 'center'
          },{
            title: '提交时间',
              key: 'submittime',
              align: 'center' 
          },{
            title: '学生是否提交',
              // key: 'issubmited',
              align: 'center' ,
               render: (h, params) => {
                 let isSubmited = '未提交';
                 if(params.row.issubmited==0){
                    isSubmited = '未提交';
                  }else{
                    isSubmited = '已提交';
                  }
                return h("div",{},isSubmited)}
          },{
            title: '批阅状态',
              key: 'reviewstatus',
              align: 'center',
               render: (h, params) => {
                 let status = '未批阅';
                 if(params.row.reviewstatus==0){
                    status = '未批阅';
                  }else{
                    status = '已批阅';
                  }
                return h("div",{},status)}
          },{
            title: '操作',
              key: 'handle',
              align: 'center',
               render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                  to:'/homeworkDetail?from=TeachingAffairs&type=notSH&classid='+params.row.classid+'&lessonid='+params.row.lessonid+'&studentid='+params.row.studentid
                },
                style:{
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block',
                  marginRight:'10px'
                },
                attrs:{
                  class: 'tablebtn blue'
                }
              },'查看')//查看
            ])
          }
          }],
     tabData:[],
     total:0,
     display:20,
     current:1,
    }
  },
  methods: {
       //学员提交作业明细
     getStudentHomeworkList(current = 1,name) {
      Server.post(Path.getStudentHomeworkList,{
        classid: this.$route.query.classid,
        lessonid: this.$route.query.lessonid,
        nickname: name,
        pagenum:current,
      },
        res => {
          if (res.code === 0) {
            this.tabData = res.data.data;
            this.total = res.data.pageinfo.total;
            this.display = res.data.pageinfo.pagesize;
            this.current = Number(res.data.pageinfo.pagenum);
          }else{
             this.tabData = [];
          }
        }
      );
    },
    //分页
     pagechange(currentPage) {
      this.getStudentHomeworkList(currentPage ,this.studentName);
    },
  },
  created() {
     this.getStudentHomeworkList(this.current,this.studentName)
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>

</style>



