<template>
    <div id="NextBtn" class="custom-btn-warning">
      <div class="text">
          <slot></slot>
      </div>
      <div class="icon">
          <img :src="right" alt="">
      </div>
    </div>
</template>
<script>
import right from "@/components/NextBtn/images/right.png";
export default {
  name: "NextBtn",
  data: () => ({
    right
  })
};
</script>
<style scoped>
#NextBtn {
  width: 105px;
  color: #fff;
  cursor: pointer;
  border-radius: 21px;
  padding-left: 0;
  padding-right: 0;
}
#NextBtn * {
  display: inline-block;
  vertical-align: middle;
}
#NextBtn .text {
  width: 67%;
  height: 100%;
  text-align: center;
}
.icon {
  width: 28px;
  height: 28px;
  background: #FF8E14;
  text-align: center;
  line-height: 28px;
  border-radius: 50%;
}
.icon img{
  width: 35%;
}
</style>



