<template>
  <div>
    <panel-title :crumbs="crumbs"></panel-title>
    <template>
       <Tabs class='mytheme-tab' type="card" :animated="false">
          <TabPane label="公共资源">
              <material-management></material-management>
          </TabPane>
          <TabPane label="个人资源">
            <person-list></person-list>
          </TabPane>
      </Tabs>
    </template>
  </div>
</template>

<script>
 import {Tabs, TabPane} from "iview";
import panelTitle from '@/components/panelTitle'
import MaterialManagement from './index.vue'
import PersonList from './PersonList'

  export default {
    name: "MateriaListPage",
    components: {
      Tabs,
      TabPane,
      panelTitle,
      MaterialManagement,
      PersonList
    },
    data() {
      return {
        activeItem:0,
        btnList: [
          {
            Name:"公共资源",
            Type:1,
            id:1
          },
          {
            Name:"个人资源",
            Type:2,
            id:2
          }
        ],
        crumbs: [{
          name:"资源文件夹",
          route:''
        }]
      }
    },
    created() {
    },
    methods: {
      // 筛选
      Screen(index) {
        this.activeItem = index;
        if(index==0){
          this.$router.push({
            path: '/MaterialList',
            query: {from:'MainLessonManagement'}
          })
        }else{
          this.$router.push({
            path:'/PersonList',
            query:{from:'MainLessonManagement',type:index }
          })
        }

      },
    },
    computed: {
    }
  }
</script>

<style scoped lang='less'>
  @import "~@/assets/less/tab.less";
</style>



