<template>
  <div>
     <panel-title :crumbs="routeArr" />
    <Tabs class='mytheme-tab' type="card" :animated="false" @on-click='msg' >
          <TabPane label="柠檬教育" name='柠檬教育'>
              <lemon></lemon>
          </TabPane>
          <TabPane label="超级作文本" name='超级作文本'>
            <essay></essay>
          </TabPane>
      </Tabs>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import {Tabs, TabPane} from "iview";
import lemon from "./module/lemon";
import essay from "./module/essay";
export default {
  name: "ChangeShift",
  components: {panelTitle,Tabs,
    TabPane,lemon,essay},
   data(){
    return {
      routeArr:[{name:'学生管理',route:''},{name:'柠檬教育',route:''}]
    }
  },
  methods: {
      msg(name){
     this.routeArr=[{name:'学生管理',route:''},{name:name,route:''}];
    }
  },
  created() {
  
  },
 mounted(){},
 computed:{},
 watch:{}
};
</script>
<style scoped lang='less'>

</style>



