<template>
  <div id="basc-info">
    <panel-title
      :crumbs="[
        {
          name:'企业设置',//认证资料查看
          route:'',
        },
      ]"
    />
    <template>
      <Card dis-hover class='br8 cardP0'>
        <Form :label-width="180" class='form-confrol'>
        	
          <Row>
            <Col span="20" style="height: 140px;">
              <!-- 基本信息 -->
              <card-title style='margin-top:55px'>{{$t('showBasicInfo.BasicInfo')}}</card-title>
              <router-link :to="{ path: '/basicInformation', query: { from: 'MainSet'}}">
                <Button style="float: right;" class="ivu-btn-warning" size="large">
                  <!-- 编辑 -->
                  {{$t('Common.edit')}}
                </Button>
              </router-link>
            </Col>
          </Row>
                    
           <Row>
            <Col span="12" offset="2">
            <!-- 经营名： -->
              <FormItem :label="$t('showBasicInfo.schoolName')+$t('Common.colon')">
                <span class='text'>{{formData.organname}}</span>
                <!-- <Input placeholder="请输入经营名" style="width: 520px;" v-model="formData.organname"/>
                <div>*（在门课网上用以展示和传播的名称，12字以内）</div> -->
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="2">
            <!-- 学堂概述： -->
              <FormItem :label="$t('showBasicInfo.schoolSummary')+$t('Common.colon')">
                <span class='text'>{{formData.summary}}</span>
                <!-- <Input placeholder="机构LOGO" type="textarea" :rows="6" style="width: 700px;" v-model="formData.summary"/>
                <div>*（简要介绍自己课堂的优势、特色信息，方便用户获得直观印象，100字内）</div> -->
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="2">
            	<!--logo-->
              <FormItem :label="$t('showBasicInfo.logo')+$t('Common.colon')">
                <img :src="formData.imageurl" style="height:56px;" alt="">
              </FormItem>
            </Col>
          </Row>          
          
          <Row>
            <Col span="12" offset="2">
            <!-- 客服热线： -->
              <FormItem :label="$t('showBasicInfo.phone')+$t('Common.colon')">
                <span class='text'>{{formData.hotline}}</span>
                <!-- <Input placeholder="请输入内容" style="width: 520px;" v-model="formData.phone"/> -->
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="2">
            <!-- 客服邮箱： -->
              <FormItem :label="$t('showBasicInfo.email')+$t('Common.colon')">
                <span class='text'>{{formData.email}}</span>
                <!-- <Input placeholder="请输入内容" style="width: 520px;" v-model="formData.email"/> -->
              </FormItem>
            </Col>
          </Row>          
          
          <Row>
            <Col span="12" offset="2">
              <!-- 联系人姓名 -->
              <FormItem :label="$t('showBasicInfo.contactname')+$t('Common.colon')">
                <span class='text'>{{formData.contactname}}</span>
                <!-- <Input placeholder="请输入用户名" style="width: 520px;" v-model="formData.contactname"/> -->
              </FormItem>
            </Col>
          </Row>
          
          <Row>
            <Col span="12" offset="2">
            <!-- 电话号码： -->
              <FormItem :label="$t('showBasicInfo.contactphone')+$t('Common.colon')">
                <span class='text'>{{formData.prephone}} {{formData.contactphone}}</span>
                <!-- <Input placeholder="请输入电话号码" style="width: 520px;" v-model="formData.contactphone" /> -->
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span="12" offset="2">
            <!-- 联系邮箱： -->
              <FormItem :label="$t('showBasicInfo.contactemail')+$t('Common.colon')">
                <span class='text'>{{formData.contactemail}}</span>
                <!-- <Input placeholder="请输入联系邮箱" style="width: 520px;" v-model="formData.contactemail"/> -->
              </FormItem>
            </Col>
          </Row>
          



          

           <!-- <div>*（课堂首页的网址，一旦添加便不可修改，比如填写的域名是abc,则课堂首页的地址为http://abc.51menke.com）</div> -->
           <!-- 域名： -->
          <!-- <Row>
            <Col span="20" offset="2">
              <FormItem :label="$t('showBasicInfo.domain')+$t('Common.colon')">
                <span class='text'>{{formData.domain}}</span>
              </FormItem>
            </Col>
          </Row> -->
        </Form>
      </Card>
    </template>
  </div>
</template>

<script>
  import panelTitle from '@/components/panelTitle'
  import cardTitle from '@/components/common/CardTitle/CardTitle'
  import {Card, Form, FormItem, Input, Upload, Button, Col, Row, RadioGroup, Radio} from 'iview';

  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "basc-info",
    components: {
      panelTitle,
      Card,
      Form,
      FormItem,
      Input,
      Upload,
      Button,
      Col,
      Row,
      cardTitle,
      RadioGroup,
      Radio
    },
    data() {
      return {
        formData:{
          baseinfoid:0,
          "contactname": "",
          "contactphone": "",
          "contactemail": "",
          "summary": "",
          "phone": "",
          "email": "",
          "organname": "",
          "imageurl": "",
          domain: "",
          "prephone":'',
        }
      }
    },
    created() {
      //do something after creating vue instance
      this.GetBascInfo();
    },
    methods: {
      GetBascInfo(){
        let { from } = this.$route.query;
        let organid  = JSON.parse(window.localStorage.getItem('userinfo')).organid;
        Server.post(Path.getOrganBaseInfo,{
          organid
        },res=>{
          let {code,data}=res;
          if(code===0){
            this.formData = {...res.data};
          }else{
            this.$Message.error(res.info);
          }
        })
      }
    }
  }
</script>

<style scoped>
.ivu-form .ivu-form-item-label{
  color: #5E6266;
}
  .id-card-wrap {
    display: inline-block;
    padding: 24px 0;
    width: 220px;
    height: 150px;
    background: rgba(207, 220, 231, .2);
  }

  .id-card-content {
    width: 160px;
    height: 98px;
    margin: auto;
    text-align: center;
  }

  /*.id-card {
    background: url(../../assets/images/id_card@2x.png) no-repeat;
    background-size: 100% 100%;
  }

  .id-card-reverse {
    background: url(../../assets/images/id_card_reverse@2x.png) no-repeat;
    background-size: 100% 100%;
  }*/
  .id-card-content img {
    margin-top: 24px;
  }

  .spuerb-cut-off-rule {
    width: 100%;
    height: 1px;
    border-top: 1px dashed #CFDCE7;
  }

  .btn {
    width: 120px;
    line-height: 40px;
    text-align: center;
    border-radius: 40px;
    cursor: pointer;
    margin: 20px auto;
  }

  .savebtn {
    background: #31C2A6;
    color: #fff;
    -webkit-box-shadow: 0 0 12px #31C2A6;
    box-shadow: 0 0 12px #31C2A6;
  }
</style>



