<template>
  <div id="LessonInfo">
    <panel-title
      :crumbs="crumbs"
    />
    <template>
        <ul class="layout">
          <li>
              <class-info :data="data"></class-info>
          </li>
          <li>
              <class-time :data="data"></class-time>
          </li>
          <li>
             <class-student :datalist="datalist"></class-student>
          </li>
          
        </ul>
    </template>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import classInfo from "@/container/classManagement/classView/classInfo";
import classTime from "@/container/classManagement/classView/classtime";
import classStudent from "@/container/classManagement/classView/classstudent";
//import ClockIcon from "@/container/ClassManagement/classView/assets/icon_clock@2x.png";

import Server from '@/service/request';
import Path from '@/service/Path';

export default {
  name: "classView",
  components: {
    panelTitle,
    classInfo,
    classTime,
    classStudent

  },
  created() {
    let { id, curriculumid } = this.$route.query;
    this.getSchedulingInfo();
    this.getStudentList();
  },
  methods: {
    getSchedulingInfo() {
      let { id, curriculumid } = this.$route.query;
      Server.post(Path.getSchedulingInfo,{
        id,
        curriculumid,
        type: 2
      },(res)=>{
        let { code, data } = res;
        if(code == 0){
          this.data = data;
          //console.log(1, JSON.stringify(this.data));
          
        }
      })
    },
    getStudentList() {
      let { id, curriculumid } = this.$route.query;
      Server.post(Path.getStudentList,{
        id
      }, (res) => {
        let { code, data } = res;
        if(code == 0){
          this.datalist = data;
          //console.log(2, JSON.stringify(this.datalist));
          
        } 
      })
    }
  },
  data(){
    return {
      data: {},
      datalist: {},
     // ClockIcon,
      crumbs: [
        {
          name: "班级管理 ", 
          route:{
            path:'/ClassManagement',
            query:{
              from:'MainLessonManagement'
            }
          }
        },
        {
          name: "班级查看",
          route:'',
        }
      ]
    }
  }
}
</script>
<style scoped>
.layout li{
  margin-bottom: 20px;
}
</style>



