<template>
  <div id="Course">
    <panel-title :crumbs="[
      {
        name:'套餐列表',
        route:''
      },
    ]" />
    <Card dis-hover class="mytheme-card-padding mytheme-noborder mytheme-mg-bottom-30">
     <div class="mytheme-mg-top-20">
		        <span>套餐名称：</span>
		        <Input
             class="input-normal-width input-max-height"
		          v-model="SearchName"
		          placeholder="套餐名称"
		          @on-enter="getAdminList(SearchName,current)"/>
          <Button type="primary" class="mytheme-mg-left-70" @click="getAdminList(SearchName,current)">搜索</Button>

		        <router-link class="fg" :to="{ name: 'AddSetMeal',query:{'from':'MainBuyManagement'}}">
		          <Button type="warning">添加套餐</Button>
		        </router-link>
	        </div>

	        <i-table :loading="loading" class="mytheme-mg-top-30 mytheme-table thead-max-height tbody-min-height noborder-table" stripe :columns="columns1" :data="data1"></i-table>
          
          <pagination :total="total" class="mg-top-20 " :current-page='current' :display="display" @pagechange="pagechange" v-if='total>0'></pagination>
     </Card>

    <Modal v-model="isShowDelModal" :closable="false">
      <div slot="header">温馨提示</div>
      <div>确认删除套餐？</div>
      <div slot="footer">
        <Button type="primary" @click="deleteAdmin(deleteid)">确认</Button>
        <Button @click="isShowDelModal = false">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import pagination from "@/components/common/pagination";
import { Card, Input, Table, Switch, Button, Modal } from "iview";
import Server from '@/service/request'
import Path from '@/service/Path'
export default {
  name: "",
  components: {
    panelTitle,
    Card,
    Input,
    "i-table": Table,
    pagination,
    "i-switch": Switch,
    Button,
    Modal
  },
  created() {
    //do something after creating vue instance
    this.getAdminList(this.SearchName,this.current);
  },
  methods: {
     stamp(times) {//时间戳转换器
      let date = new Date(times * 1000);
      let Y = date.getFullYear() + '-';
      let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      let D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      return Y+M+D;
    },
    changeStatus(id,flag){
      Server.post(Path.getswitch,{
        setid:id,
        shelf:Number(flag)
      },res=>{
      	
        let {code,data}=res;
        if(code===0){
        	this.getAdminList(this.SearchName,this.current);
 					this.$Message.success(res.info);
        }else{
          this.$Message.error(res.info);
        }
      })
    },
    getAdminList (name='',page=1){
      Server.post(Path.mealList,{
        'setname':name,
	    	'pagenum':page,
      },res=>{
        let { code, data } = res;
        if(code===0){
        	this.loading = false;
					this.data1 = res.data.setlist;
					for (let itmes in this.data1) {
							let model = this.data1[itmes];
						 	model.efftype === 1 ? model.expiry = '已过期' : model.expiry = '未过期';
						 	model.timetype = model.efftype === 1 ? (this.stamp(model.effstarttime)+'至'+this.stamp(model.effendtime)): `领取后有效期${model.efftime}天`;
							switch(model.trialtype)
									{
									case 1:
									  model.trialtype = '全部课程';
									  break;
									case 2:
									   model.trialtype = '指定分类';
									  break;
									default:
									   model.trialtype = '指定课程 ';
									}
					}
          this.total = res.data.pageinfo.total;
          this.display = Number(res.data.pageinfo.pagesize);
        }else{
          this.data1 = [];
          
        }
        this.loading = false;
      })
    },
    pagechange: function(currentPage) {
      this.current = currentPage;
      this.loading = true;
      this.getAdminList(this.SearchName,currentPage);
      // ajax请求, 向后台发送 currentPage, 来获取对应的数据
    },
    deleteAdmin(id) {
      Server.post(Path.delMeal, {
        setid: id
      }, (res) => {
        if(res.code === 0) {
          this.getAdminList(this.SearchName,this.current);
          this.isShowDelModal = false;
          this.$Message.success(res.info);
        }else{
           this.$Message.error(res.data);
        }
      });
    }
  },
  data(){
    return {
      total: 0, // 记录总条数
      display: 20, // 每页显示条数
      current: 1, // 当前的页数
      loading:true,
      isShowDelModal: false,
      SearchName:'',
      InputText: "",
      setname: "",
      id:'',
      tips: '无',
      deleteid: '',
      columns1: [
        {
          title: "编号",
          align: "center",
          key: "id"
        },
        {
          title: "套餐名称",
          align: "center",
          key: "setmeal"
        },
        {
          title: "可使用课程",
          align: "center",
          key: "trialtype"
        },
        {
          title: "使用门槛",
          align: "center",
          key: "threshold"
        },
        {
          title: "价格",
          align: "center",
          key: "setprice"
        },
        {
          width:200,
          title: "有效期",
          align: "center",
          key: "timetype"
        },
        // {
        //   title: "状态",
        //   align: "center",
        //   key: "expiry"
        // },
        {
          title: "上/下架",
          align: "center",
          key: "TopPrice",
          render: (h, params, index) => {
            return h("div", [
              h(
                "i-switch",
                {
                  on: {
                    "on-change": value => {
                      this.changeStatus(params.row.id,value ? '1' : '0' );
                    }
                  },
                  props:{
                    value : Boolean(params.row.shelf)
                  }
                }
              )
            ]);
          }
        },
        {
          title: "操作",
          align: "center",
          key: "Make",
          render: (h, params, index) => {
            return h('div',[
              h('router-link',{
                props:{
                    to:'/GetMealList?id='+params.row.id+'&from=MainBuyManagement'
                },
                style: {
                  cursor:'pointer',
                  color: '#3F9CE8',
                  display: 'inline-block',
                  marginRight:'25px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'查看'),
              h('div',{
                on: {
                  'click': () => {
                     Server.post(Path.GetmealList, {
                        setid: params.row.id
                      }, (res) => {
                        let {data,code}=res;
                        if(code==0){
                          this.$router.push('/AddMealList?id='+params.row.id+'&from=MainBuyManagement')
                        }else if(code==60000){
                          this.$Message.error(res.info);
                        }
                      })
                  }
                },
                style: {
                  cursor:'pointer',
                  color: '#FFA80D',
                  display: 'inline-block',
                  marginRight:'25px'
                },
                attrs:{
                  class: 'tablebtn blue',
                }
              },'编辑'),
              h('div',{
                on: {
                  'click': () => {
                    this.isShowDelModal = true;
                    this.deleteid = params.row.id;
                  }
                },
                style: {
                  cursor:'pointer',
                  color: '#ED3825',
                  display: 'inline-block'
                },
                attrs:{
                  class: 'tablebtn red'
                }
              },'删除')
            ])
          }
        }
      ],
      data1: []
    }
  }
};
</script>
<style scoped>
	@import "~assets/common.css"
	.mg-bottom-20{
		border-radius: 8px;
	}
	.mg-search{
		font-size: 18px;
    color: #7a8599;
    padding: 0 0 0 14px;
	}
	.mg-query{
		float: right;
		width: 106px;
	}
.br21{
	border-radius:21px ;
}
.custom-btn-cancel{
	  display: inline-block;
    padding: 0 34px;
    height: 38px;
    line-height: 38px;
    border-radius: 21px;
    cursor: pointer;
    text-align: center;
    font-size: 16px;
		border: none;
}
</style>



