<template>
  <div id="LessonInfo">
    <panel-title
      :crumbs="[
        {
          name:this.$t('courseManagepage.courseManage'), //课程管理
          route:{
            path:'/Small',
            query:{
              from:'MainLessonManagement'
            }
          }
        },
        {
          name:this.$t('courseManagepage.courseInfo'), //课程信息
          route:''
        }
      ]"
    />
    <template>
        <ul class="layout">
          <li>
            <BasicInfoMation :data="data"/>
          </li>
          <li>
            <ClassTimeInfoMation :data="data" :ClockIcon="ClockIcon"/>
          </li>
          <!--<li>
            <AllEvalution :lessonid="lessonid" :curriculumid="curriculumid"/>
          </li>-->
          <li>
            <student-list :data="data" v-if="$route.query.classType == 1"></student-list>
          </li>
        </ul>
    </template>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import BasicInfoMation from "@/container/CourseManagement/CourseInfo/BasicInfoMation";
import ClassTimeInfoMation from "@/container/CourseManagement/CourseInfo/ClassTimeInfoMation";
import studentList from "@/container/CourseManagement/CourseInfo/studentList";
// import AllEvalution from "@/container/CourseManagement/CourseInfo/AllEvaluation";
import ClockIcon from "@/container/ArrangingManagement/assets/icon_clock@2x.png";
import Server from '@/service/request';
import Path from '@/service/Path';

export default {
  name: "LessonInfo",
  components: {
    panelTitle,
    BasicInfoMation,
    ClassTimeInfoMation,
    studentList
    //AllEvalution,
  },
  created() {
    let { id } = this.$route.params;
    let { curriculumid } = this.$route.params;
    this.lessonid = id;
    this.curriculumid = curriculumid;
    // console.log(1,this.$route.params)
    // alert(this.curriculumid)
    this.getCourseInfo(this.lessonid);

  },
  methods: {
    getCourseInfo(id) {
      Server.post(Path.getCurricukumId,{
        id
      },(res)=>{
        let { code, data } = res;
        if(code === 0){
          this.data = data;
         // console.log(1, JSON.stringify(this.data));
          // if(data.teachername == ""){
          //   this.data.teachername = this.$t('Common.NO') //无
          // }
        }
      })
    }
  },
  data(){
    return {
      lessonid: 0,
      curriculumid:0,
      data:{},
      ClockIcon
    }
  }
}
</script>
<style scoped>
.layout li{
  margin-bottom: 20px;
}
</style>



