<template>
  <div id="" class='wrapper'>
    <!--头部-->
    <nav class="navbar">
      <div class="container">
        <router-link to="login" class="navbar-brand"><img :src="logo"></router-link>
        <span class="line"></span>
        <span class="logo-text">管理系统登录</span>
      </div>
    </nav>

    <!--登录-->
    <section id='page-content' :style="{height: contentHeight.height, minHeight: '539px',}">
      <div class='container'>
        <div class="login-wrapper">
          <Form>
            <FormItem label="" >
              <div class="form-group" style="margin-top: 109px;">
                <!-- 请输入手机号 -->
                <input type="text" class='form-control' autofocus="autofocus" id="UserName" placeholder="请输入登录名" v-model.trim="loginInfo.loginUserName">
              </div>
            </FormItem>
            <FormItem label="" >
              <div class="form-group" style="margin-top:49px;">
                <!-- 请输入登录密码 -->
                <input type="password" class='form-control' id="PassWord" placeholder="请输入密码" v-model.trim="loginInfo.loginpwd" @keyup.enter="login()">
              </div>
            </FormItem>
          <div class="btn-wrapper" style="margin-top: 61px;">
            <button type="button" class="btn-block" id="Login"  @click='login()'>
              <!-- 登录 -->
              登&nbsp;&nbsp;&nbsp;&nbsp;录
            </button>
          </div>
          </Form>
        </div>
      </div>
    </section>
    <footer class="footer">
      <v-feet></v-feet>
    </footer>
  </div>
</template>
<script>
import {Form, FormItem, Input,} from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import logo from "@/assets/images/logo.png";
import areaCode from "@/components/areaCode";
import feet from "@/components/feet1/feet"
import "@/assets/areaCode.css";
export default {
  name: "",
  components: {
    Form, FormItem, Input,'v-areaCode':areaCode, "v-feet": feet
  },
  data() {
    return {
      contentHeight:{
        height:document.documentElement.clientHeight-120-289+'px'
      },
      logo:logo,
      loginInfo:{
        "loginUserName":'',//手机号
        "loginpwd":'' //密码
      },
      publickey:'', //公钥
    };
  },
  created() {
    this.getPublicKey() // 获取公钥
    // this.windowHeight = $(window).height();
  },
  mounted() {
    if (
      sessionStorage.getItem("userinfo") != null &&
      sessionStorage.getItem("userinfo") != "" &&
      sessionStorage.getItem("key") != null &&
      sessionStorage.getItem("key") != ""
    ){
      this.$router.push('/login')
    }
    this.getFocus();
  },
  methods: {
    //获取光标
    getFocus() {
      document.getElementById("UserName").focus();
    },
    //登录
    login(){
      var key = this.randomString(10);
      let {loginUserName, loginpwd} = this.loginInfo;
      if(loginUserName == ''){
        this.$Message.error("用户名不能为空");//手机号不能为空
        return;
      }
      if(loginpwd == ''){
        this.$Message.error(this.$t('rest.tip19'));//密码不能为空
        return;
      }
      //type : 1 老师 2机构 3学生 4官方管理员登录
      var postData = this.wrap_encrypt(JSON.stringify({
        username: loginUserName,
        password: loginpwd,
        source:'web',
        type:2,
        key:key
        
      }));
      Server.post(Path.login,
        {
          data:postData
        }, (res) => {
          let {code,data}=res;
          if (res.code === 0) {
            window.sessionStorage.setItem('key',key);
            window.sessionStorage.setItem('userinfo',JSON.stringify(data));
            this.$router.push('/')
          }else{
            this.$Message.error(res.info);
          }
        }
      );
    },
    //公钥加密
    wrap_encrypt(content) {
      var encryptobj = new JSEncrypt();
      encryptobj.setPublicKey(this.publickey);
      var result = "";
      var step = 117;
      var strlen = content.length;
      var count = Math.ceil(strlen / step);
      for (var i = 0; i < count; i++) {
        let chunk = content.substring(step * i, step * (1 + i));
        result += encryptobj.encrypt(chunk);
      }
      return result;
    },
    // 生成随机字符串
    randomString(len, charSet) {
      charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      var randomString = "";
      for (var i = 0; i < len; i++) {
        var randomPoz = Math.floor(Math.random() * charSet.length);
        randomString += charSet.substring(randomPoz, randomPoz + 1);
      }
      return randomString;
    },
    //获取公钥
    getPublicKey(){
      Server.post(Path.getPublicKey,{
      },res=>{
      let {code,data}=res;
        if(code===0){
          this.publickey = res.key;
        }else{
          this.$message.error(res.info);
        }
      })
    }
  }
};
</script>
<style scoped lang = "less">
@import "~@/assets/base.less";
input{
  outline: none
}
.container {
  width: 1190px;
  margin: 0 auto;
}
/*顶部导航 begin*/
.wrapper .navbar {
  min-width: 1350px;
  height:120px;
  background: #ffffff;
  box-shadow: 6px 0px 13px rgba(176, 175, 175, 0.16);
}

.wrapper .navbar-brand {
  height: 60px;
  line-height: 60px;
  padding: 0;
  width: 180px;
  margin-top: 35px;
  display: inline-block;
}
.wrapper .navbar-brand img {
  height: 60px;
  display: block;
}
/*顶部导航 end*/
#page-content {
  min-width: 1350px; 
  background: url('./assets/images/login_bg.png') repeat-x;
  /* background-size: cover; */
}
#page-content .container {
  position: relative;
}
.login-img {
  position: absolute;
  margin: 150px 0 0 125px;
}
.login-wrapper {
  position: absolute;
  top: 60px;
  right: 0px;
  width: 390px;
  height: 460px;
  padding: 0 30px;
  background: #ffffff;
  border-radius: 4px;
  box-shadow: 6px 0px 16px rgba(114, 114, 117, 0.22);
}
.login-wrapper h2 {
  font-size: 16px;
  line-height: 16px;
  color: #fff;
  padding: 50px 0 38px;
  overflow: hidden;
  font-weight: bold;
}
.login-wrapper .form-group {
  height: 48px;
  line-height: 48px;
  background: #fff;
  padding: 0 15px 0 50px;
  border: 1px solid #D9D9D9;
  position: relative;
}
.login-wrapper .form-group .form-control {
  font-size: 14px;
  line-height: 14px;
  display: block;
  padding: 14px 0;
  width: 100%;
  box-sizing: border-box;
  border: none;
  background: none;
}
.login-wrapper .forget {
  font-size: 12px;
  overflow: hidden;
  padding: 0 6px;
  margin-bottom: 60px;
}
.login-wrapper .forget .left {
  float: left;
  color: #D7D7D7;
}
.login-wrapper .forget .right {
  float: right;
  color: #4cb1ff
;
}
.login-wrapper .forget a:hover {
  color: #44a2eb;
  text-decoration: underline;
}
.login-wrapper .btn-wrapper .btn-block {
  width: 100%;
  text-align: center;
  line-height: 42px;
  font-size: 16px;
  color: #fff;
  border: none;
  background: #3F9CE8;
  border-radius: 4px;
  cursor: pointer;
  transition: all .3s;
}
.placeholder {
  position: absolute;
  top: 1px;
  color: #888;
}
.login-wrapper .btn-wrapper .btn-block:hover {
  background: #44a2eb;
}
 .login-wrapper .form-group:nth-child(1) {
  background: url('./assets/images/user.png') no-repeat 16px #fff;
  background-size: 22px 22px;
}
.login-wrapper .ivu-form-item:nth-child(2) .form-group {
  background: url('./assets/images/password.png') no-repeat 16px #fff;
  background-size: 22px 22px;
} 

 /* prephone */
 .intl-tel-input .country-list .country {
   margin-bottom: 0;
 }

 .intl-tel-input.separate-dial-code .selected-dial-code {
   padding-right: 20px;
 }
 .footer {
   /* position:fixed;
   bottom:0;
   left:0; */
   width:100%;
   height: 289px;
   background: #e8e8e8;
}
.line{
  display: inline-block;
  width: 2px;
  height: 49px;
  background:#3F9BE7;
  margin: 40px 37px 0;
  vertical-align: top;
}
.logo-text{
  height: 28px;
  line-height: 28px;
  font-size: 28px;
  color: #3F9BE7;
  letter-spacing: 2px;
  vertical-align: top;
  display: inline-block;
  margin-top: 54px;
}
.ivu-form-item{
  margin-bottom: 0;
}
.login-wrapper .form-group .form-control{
  padding: 16px 0 14px 0;
  color: #666;
}
input::-webkit-input-placeholder { /* WebKit browsers */
    color:#cccccc;
}
input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color:#cccccc;
}
input::-moz-placeholder { /* Mozilla Firefox 19+ */
    color:#cccccc;
}
input:-ms-input-placeholder { /* Internet Explorer 10+ */
    color:#cccccc;
}



</style>



