<template>
  <div id="SendCode">
    <span class="send-btn green" @click="sendCode">{{Text}}</span>
  </div>
</template>
<script>
import Server from "@/service/request";
export default {
  name: "SendCode",
  components: {
    Server
  },
  props:{
    url:{
      type: String,
      default: ''
    },
    data:{
      type: Object,
      default:{}
    }
  },
  data() {
    return {
      Text: this.$t('rest.sendValidate'),//发送验证码
      like:true,
      count:60,
      formData:{
        organid:1
      }
    };
  },
  methods: {
    sendCode() {
      let {like}=this;
      if(like){
        Server.post(this.url,{
          ...this.data
        },res=>{
          let {code, data}=res;
          if(code===0){
            this.$Message.success(res.info);
            this.like = false;
            var timer = setInterval(e=>{
              this.count -= 1;
              this.Text = this.count + 's';
              if(this.count < 1){
                this.count = 60;
                this.like = true;
                this.Text = this.$t('rest.sendValidate'),//发送验证码;
                clearInterval(timer);
              }
            },1000)
          }else{
            this.$Message.error(res.info);
            this.$emit('getSendCodeStatus',1)
          }
        })
      }

    }
  },

};
</script>
<style scoped>
.send-btn {
  cursor: pointer;
}
</style>



