<template>
  <div id="EditLesson">
    <template>
      <Card dis-hover class='br8 cardP0 mg-bottom-20'>
        <p slot="title" class="title">
            <!--基本信息-->
          {{$t('courseManagepage.basicinformation')}}
        </p>
        <Form :label-width="120" class='form-confrol mg-top-20' ref="formValidate" :model="formData" :rules="ruleValidate">
          <Row>
            <!--课程封页-->
            <Col span="20" offset="2">
              <FormItem :label="$t('courseManagepage.Coursespage')+$t('Common.colon')" prop='LessonImgUrl'>
                <Upload
                :data="uploadid"
                @success="getHeaderUrl">
                  <div class="loadImg">
                      <img class="photo" :src="formData.LessonImgUrl" alt="">
                  </div>
                </Upload>
                <div class=" hui">(为保证显示效果，图片尺寸为1920*1080，图片格式支持jpg、png)</div>
              </FormItem>
            </Col>
            <!--课程分类-->
            <Col span="20" offset="2">
              <FormItem :label="$t('courseManagepage.lessoncourse')+$t('Common.colon')" prop='LevelActiveStr'>
                <p class='cate'>
                  {{formData.LevelActiveStr}}
                  <!-- 编辑 -->
                  <span class="orange" @click="isShowLessonTypeModelBox()">
                    <span style="margin-left:24px;">{{$t('Common.edit')}}</span>
                  </span> 
                </p>
              </FormItem>
            </Col>
            <!--课程名称-->
            <Col span="20" offset="2">
              <FormItem :label="$t('courseManagepage.coursetitle')+$t('Common.colon')" prop='LessonName'>
                 <i-input placeholder="请输入课程名称,长度不超过20个汉字" v-model.trim="formData.LessonName"  autocomplete="off" :maxlength="20" style=" width: 360px;"/>
              </FormItem>
            </Col>
            <!--副标题-->
            <Col span="20" offset="2">
              <FormItem :label="$t('courseManagepage.subtitle')+$t('Common.colon')" prop='LessonTitle'>
                 <i-input placeholder="请输入课程副标题,长度不超过100个汉字" v-model.trim="formData.LessonTitle" :maxlength="100" style=" width: 360px;"/>
              </FormItem>
            </Col>
            <!--老师-->
            <Col v-if="$route.query.classType == 1" span="20" offset="2">
              <FormItem label="老师：" prop='teacherid'>
                 <i-select v-model="formData.teacherid" size="large" style="width: 360px; border: 1px solid #CFDCE7; border-radius: 2px">
                    <i-option
                      style=" width: 300px; font-size: 16px;"
                      v-for="item in teacherlist"
                      :value="item.teacherid"
                      :key="item.teacherid"
                    >{{item.nickname}}</i-option>
                </i-select>
              </FormItem>
            </Col>
            <!--课程概述-->
            <Col span="20" offset="2">
              <FormItem :label="$t('courseManagepage.courseoverview')+$t('Common.colon')" prop='LessonDesc'>
                <div class="input-box form-confrol form-confrol2" style="height: initial;" v-if='isShowEditor'>
                  <wangeditor :content='formData.LessonDesc' :catchData="catchData" ></wangeditor>
                </div>
                <div class="hui">(课程概述为该门课程的详细介绍，包括课程结构、课程内容、教学目标等，为保证官网的展示效果，不要超过1000字，支持中英文输入)</div>
              </FormItem>
            </Col>
          </Row>
        </Form>
        
        <div class="btnbox">
          <!-- 下一步 -->
          <Button type="primary"  @click.native="saveData('formValidate')">下一步</Button>
          <!-- <next-btn  @click.native="saveData('formValidate')">下一步</next-btn> -->
        </div>
      </Card>
    </template>
    <template>
      <!--选择课程分类-->
      <Modal
        v-model="LessonTypeModel"
        :closable="false">
        <!-- @on-ok="ok"
        @on-cancel="cancel" -->
        <div slot="header">{{this.$t('courseManagepage.courseclassification')}}</div>
        <div style='max-height: 600px;overflow-y: auto;overflow-x: hidden;'>
        <div class="type-item" v-for="Level,index in Levels" :key="index"  >
          <div class="type-item-title" >
             <!--{{index+1}}级分类-->
            <p v-if="index===0">{{$t('courseManagepage.firstclassification')}}</p>
            <!--一级分类-->
            <p v-if="index===1">{{$t('courseManagepage.secondclassification')}}</p>
            <p v-if="index===2">{{$t('courseManagepage.thirdclassification')}}</p>
          </div>
          <div class="type-item-content">
            <ul>
              <li v-if="d.status === 1" v-for="d,i in Level.Levels" @click="oneSelect(d.id,index)" :class="{'active':d.id==Level.activeItem}" :style="{pointerEvents:''}" :key="d.id">
                 {{d.categoryname}}
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div slot="footer">
        <!--确定-->
        <Button type="primary" @click="ok">{{$t('Common.sure')}}</Button>
        <!--取消-->
        <Button  @click="cancel">{{$t('Common.cancel')}}</Button>
      </div>
    </Modal>
    </template>
  </div>
</template>
<script>
import panelTitle from "@/components/panelTitle";
import step from "@/components/step";
import NextBtn from "@/components/NextBtn/NextBtn";
import { Card, Input, Modal, Tag , Row, Col, Form, FormItem, Select , Option, Button } from "iview";
import Server from "@/service/request";
import Path from "@/service/Path";
import Upload from "@/components/UploadPicture";
import defaultImg from './images/lesson_default.png';
import wangeditor from '@/components/wangEditor';

export default {
  name: "EditLesson",
  components: {
    step,
    panelTitle,
    Card,
    Upload,
    'i-input':Input,
    NextBtn,
    Modal,
    Tag,
    Row,
    Col,
    Form,
    FormItem,
    "i-select": Select ,
    "i-option": Option,
    wangeditor,
    Button
  },
  data() {
    const numLimit = (rule, value, callback) => {//价格正则
      let limitRegex = /^([\u4e00-\u9fa5]|[0-9_a-zA-Z]){1,1000}$/;
      if (!limitRegex.test(value)) {
        callback(new Error('课程概述不超过1000字'))
      } else {
        callback();
      }
    };
    return {
      LessonTypeModel: false,
      formData: {
        LessonImgUrl:defaultImg, //课程封页
        LessonName: "", //课程名称
        LevelActiveStr: "", // 保存课程分类显示数据
        LessonTitle: "", //课程副标题
        teacherid: '', //老师
        LessonDesc: "", //课程概述
      },
      ruleValidate: {
        LessonImgUrl: [
          {required: true, message: "课程封页不能为空" } 
        ],
        LevelActiveStr: [
          {required: true, message: "请选择课程分类！" } 
        ],
        LessonName: [
          {required: true, message: "课程名字不能为空！", trigger: 'blur'},
        ],
        LessonTitle: [
          {required: true, message: "副标题不能为空！", trigger: 'blur' },
        ],
        teacherid: [
          {required: true, message: "老师不能为空!" } 
        ],
        // LessonDesc: [
        //   {required: true,  validator: numLimit, trigger: 'blur'}
        // ]
      },
      LevelActiveId: [], //课程分类ID
      Levels: [], //课程分类数据
      LessonLabelModel: false,
      LessonImgUrl:defaultImg,
      uploadid:[1,1,1],
      classtypes: '',
      teacherlist: [], //老师姓名
      id: '',
      content:'',
      isShowEditor:false, //默认隐藏wangedit
    };
  },
  watch:{
    $route:'isHaveId'
  },
  created() {
    this.isHaveId();
    this.classtypes = this.$route.query.classType;
    this.id = this.$route.query.id;
    var origin = JSON.parse(window.localStorage.getItem("userinfo"));
    this.getTeacheList();
  },
  computed: {
    getLength() {
      let str = this.formData.LessonDesc;
      str = str.replace(/(\n)/gim, "");
      str = str.replace(/(\t)/gim, "");
      str = str.replace(/(\r)/gim, "");
      str = str.replace(/<\/?[^>]*>/gim, "");
      str = str.replace(/[&nbsp;]/igm, "");
      str = str.replace(/\s*/g, "");
      return str;
    }
  },
  methods: {
    // 获取富文本编辑器内容
    catchData(value){
      this.formData.LessonDesc = value;      //在这里接受子组件传过来的参数，赋值给data里的参数
    },
    //上传图片
    getHeaderUrl(data){
      this.formData.LessonImgUrl =  data;
    },
    //编辑数据
    isHaveId(){
      let {id} = this.$route.params;
      if(id){
        let { LessonName, LessonTitle, teacherid, LessonDesc, LevelActiveId, LessonImgUrl, LevelActiveStr, classtypes, formData } = this;
        Server.post(Path.getCurricukumEditId, {
          id
        }, res => {
          let { code, data } = res;

          if (code === 0) {
            this.formData.LessonName = data.coursename;
            this.formData.LessonTitle = data.subhead;
            this.formData.LessonDesc = data.generalize;
            this.formData.LessonImgUrl = data.imageurl;
            this.formData.LevelActiveStr = data.categorystrname;
            this.LevelActiveId = data.categorystr;
            this.formData.teacherid = data.teacherid;
            let tmp = this.LevelActiveId.split('-');
            this.LevelActiveId = tmp;
            this.classtypes = data.classtypes;
            var inperiod = data.inperiod;
            if(inperiod){
              window.localStorage.setItem('inperiod',JSON.stringify(inperiod));
            }
            window.localStorage.setItem('LessonData',JSON.stringify({
              categorystr:data.categorystr,
              classtypes:data.classtypes,
              price:data.price,
              status:data.status,
              id:data.id,
              giftstatus: data.giftstatus,
              giftjson: data.giftjson
            }))
            this.isShowEditor = true;
          }else{
            this.$Message.error(res.info);
          }
        });

      }else{
        this.isShowEditor = true;
        window.localStorage.removeItem('inperiod');
        window.localStorage.removeItem('LessonData');
        window.localStorage.removeItem('addLessonMsesage');
      }

    },
    
    //下一步 校验－保存数据
    saveData(name){
      this.$refs[name].validate((valid) => {
        if (valid) {
          if(this.getLength.length > 1000){
            //console.log(this.getLength.length);
            this.$Message.error("课程概述长度不能超过1000个汉字!")
            return false;
          }
        let { id } = this.$route.params;
        let { LessonName, LessonTitle, teacherid, LessonDesc, LevelActiveId, LessonImgUrl, LevelActiveStr, classtypes, formData } = this;
          Server.post(Path.addOneCurricukum, {
            'classtypes': classtypes,
            'coursename':formData.LessonName,
            'subhead': formData.LessonTitle,
            'imageurl': formData.LessonImgUrl,
            'categorystr':LevelActiveId.join('-'),
            'categoryid':LevelActiveId[LevelActiveId.length-1],
            'generalize':formData.LessonDesc,
            'teacherid': formData.teacherid,
            'id' : id ? id : '',
          }, res => {
            let { code, data } = res;
            let { query } = this.$route;
            if (code === 0) {
              window.localStorage.setItem('addLessonMsesage',JSON.stringify({
                imageurl:formData.LessonImgUrl,
                categorystrname:this.formData.LevelActiveStr,
                coursename: formData.LessonName
              }))
              setTimeout(e=>{
                this.$router.push({
                  name:'EditLessonTime',
                  query:{step:'2', id: this.$route.query.type == 'edit' ? id : res.id,teacherid:this.formData.teacherid,classtypes: this.$route.query.classType,from: 'MainLessonManagement'},
                })
              },500)
            }else{
              this.$Message.error(res.info);
            }
          });
        }
      })
      
    },
    //课程分类
    isShowLessonTypeModelBox(){
      this.Levels = [];
      this.isShowLessonTypeModel();
    },
    isShowLessonTypeModel(Id = 0) {
      this.LessonTypeModel = true;
      Server.post(
        Path.getCurricukumCategoryList,
        {
          fatherid: Id
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            var obj = {};
            obj.activeItem = 0;
            obj.Levels = data.data;
            //console.log(obj.Levels);
            this.Levels.push(obj);
          }
        }
      );
    },
    oneSelect(id, index) {
      this.Levels[index].activeItem = id;
      if (index < this.Levels.length) {
        this.Levels.splice(index + 1, 2);
      }
      this.isShowLessonTypeModel(id);
    },
    
    //课程分类选中确定按钮
    ok() {
      var array = [];
      if (
        this.Levels.some((el, index) => {
          return el.activeItem == 0;
        })
      ) {
        this.$Message.info("请分类选完！");
        return false;
      } //判断课程分类是否选完
      this.LevelActiveId = [];
      this.Levels.forEach((el, index) => {
        var _id = el.activeItem;
        this.LevelActiveId.push(_id); //保存选中的分类id
        el.Levels.forEach((e, i) => {
          if (_id == e.id) {
            array.push(e.categoryname);
          }
        });
      });
      this.formData.LevelActiveStr = array.join("/");
      this.LessonTypeModel = false;
    },
    cancel() {
      this.Levels = []; //清空数据
      this.LessonTypeModel = false;
    },
    //获取老师列表
    getTeacheList() {
      Server.post(
        Path.getTeacherList,
        {
          type: this.classType,
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.teacherlist = data;
          } else{
            this.teachelist = [];
          }
        }
      );
    }
  }
};
</script>
<style scoped lang='less'>
@import "~@/assets/base.less";
.title {
  color: #5e6266;
  font-size: 18px;
  font-weight: normal;
}
.photo {
  width: 300px;
  height: 180px;
}
.orange {
  display: inline-block;
  vertical-align: middle;
}
.error {
  font-size: 12px;
  display: block !important;
  padding-left: 205px;
  padding-top: 8px;
}
.input-box * {
  vertical-align: middle !important;
  overflow: hidden;
}

.btnbox {
  overflow: hidden;
  padding: 5px 0 33px;
  margin-left: 250px;
}
.type-item {
  padding: 0 10px;
}
.type-item-title {
  position: relative;
  padding-left: 20px;
  font-size: 16px;
  color: @fz-Color1;
}
.type-item-title::before {
  content: "";
  width: 14px;
  height: 14px;
  background: url(./images/icon.png) no-repeat;
  position: absolute;
  left: 0;
  top: 33%;
  margin-top: -3px;
}
.type-item-title::after {
  content: "";
  width: 100%;
  position: absolute;
  left: 100px;
  height: 1px;
  background-color: #d3dfe9;
  top: 50%;
}
.type-item-content ul {
  overflow: hidden;
  padding: 10px 0 0 20px;
}
.type-item-content ul li {
  padding: 0 5px 0 5px;
  height: 40px;
  text-align: center;
  line-height: 20px;
  color: #757a80;
  font-size: 14px;
  line-height: 40px;
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  margin-right: 25px;
  margin-bottom: 15px;
  float: left;
  cursor: pointer;
  min-width: 80px;
}
.type-item-content ul li.active {
  color: #fff;
  background-color: @color1;
}
.loadImg{
  cursor: pointer;
  overflow: hidden;
  width: 180px;
  height: 135px;
  img{
    display: block;
    width: 100%;
    height: 100%;
  }
}
.type-wrapper{
  min-height: 300px;
  overflow-y: auto;
}
.ivu-modal-body{
  max-height: 600px;
  overflow-y: auto;
}
.input-box{
  height: 40px;
  width: 70%;
}
.ivu-select-dropdown-list li{
  margin-bottom: 0;
}
.cate{
  font-size: 16px;
  padding-top: 5px;
}

</style>



